import EventEmitter from 'eventemitter3';
import Config from 'config';
import {} from '../utils/snaptoolkit';
import ShapesInteraction from 'shapes-interaction';

export default class LassoManager extends EventEmitter {
  constructor(editor) {
    super();
    this._editor = editor;
    this._paper = editor._paper;
    this._lassoRect = null;

    this._onHandleDrag = this._onHandleDrag.bind(this);
    this._onHandleDragStart = this._onHandleDragStart.bind(this);
    this._onHandleDragEnd = this._onHandleDragEnd.bind(this);
  }

  enableLasso(isOn) {
    this._paper.undrag();
    this._paper.removeClass('selectionable');
    if (isOn) {
      this._paper.removeClass('hand');
      this._paper.removeClass('connectionable');
      this._paper.addClass('selectionable');
      this._paper.drag(
        this._onHandleDrag,
        this._onHandleDragStart,
        this._onHandleDragEnd,
      );
    } else if (this._lassoRect) {
      this._lassoRect.remove();
    }
  }

  _onHandleDragStart(x, y, evt) {
    if (evt.target.id === 'app') {
      const ep = this._paper.getEventPoint(evt);
      this._lassoRect = this._paper.rect(ep.x, ep.y, 0, 0).attr({
        class: 'lasso_selection',
      });
      this._lassoRect.data('ox', ep.x);
      this._lassoRect.data('oy', ep.y);
    }
  }

  _onHandleDrag(dx, dy, x, y, evt) {
    if (evt.target.id === 'app' && this._lassoRect) {
      if (evt.x > 0 && evt.y > 0) {
        const ep = this._paper.getEventPoint(evt);
        const wx = ep.x - this._lassoRect.data('ox');
        const wy = ep.y - this._lassoRect.data('oy');

        this._lassoRect.attr({
          width: Math.abs(wx),
          height: Math.abs(wy),
        });

        if (wx < 0) {
          this._lassoRect.attr({
            x: this._lassoRect.data('ox') + wx,
          });
        }

        if (wy < 0) {
          this._lassoRect.attr({
            y: this._lassoRect.data('oy') + wy,
          });
        }
      }
    }
  }

  _onHandleDragEnd() {
    if (this._lassoRect) {
      for (let i = 0, len = this._editor.elements.length; i < len; i++) {
        const el = this._editor.elements[i];
        if (el.graphic) {
          if (this._checkShapesSelectable(this._lassoRect.node, el)) {
            el.selected = true;
            el.toFront();
          }
        }
      }
      this._lassoRect.remove();
    }
  }

  _checkShapesSelectable(lasso, el) {
    const lassoBounds = lasso.getBoundingClientRect();
    const lassoBox = [
      [lassoBounds.x, lassoBounds.y],
      lassoBounds.width,
      lassoBounds.height,
    ];
    let isIntersecting = false;

    switch (el.elementType) {
      case Config.CONNECTOR:
        const segmentIsValid = [];
        for (let i = 0, len = el._points.length; i < len - 1; i++) {
          const pt1 = this._convertCoordsToGlobal(
            el._points[i].x,
            el._points[i].y,
          );
          const pt2 = this._convertCoordsToGlobal(
            el._points[i + 1].x,
            el._points[i + 1].y,
          );
          const seg = [
            [pt1.x, pt1.y],
            [pt2.x, pt2.y],
          ];
          segmentIsValid[i] = this._checkIfValidSelection(seg, lassoBox);
        }
        isIntersecting = segmentIsValid.every((e) => {
          return e === true;
        });
        break;
      case Config.START_EVENT:
      case Config.END_EVENT:
      case Config.INTERMEDIATE_EVENT:
        const eventBounds = el.selectionBox.node.getBoundingClientRect();
        const cx = eventBounds.left + eventBounds.width / 2;
        const cy = eventBounds.top + eventBounds.height / 2;
        const eventCircle = [[cx, cy], el.radius];
        isIntersecting = this._checkIfValidSelection(eventCircle, lassoBox);
        break;
      case Config.TASK:
      case Config.SUBPROCESS:
      case Config.DATA_OBJECT:
      case Config.DATA_STORE:
      case Config.DATA_INPUT:
      case Config.DATA_OUTPUT:
      case Config.GROUP:
      case Config.TEXT:
      case Config.GATEWAY:
      case Config.LANE:
      case Config.POOL:
        const elBounds = el.selectionBox.node.getBoundingClientRect();
        const elBox = [
          [elBounds.x, elBounds.y],
          elBounds.width,
          elBounds.height,
        ];
        isIntersecting = this._checkIfValidSelection(elBox, lassoBox);
        break;
      default:
    }
    return isIntersecting;
  }

  _checkIfValidSelection(obj, objLasso) {
    return ShapesInteraction.contain(objLasso, obj);
    // return ShapesInteraction.intersection(obj, objLasso)
    //   || ShapesInteraction.contain(objLasso, obj);
  }

  _convertCoordsToGlobal(x, y) {
    const pt = this._paper.createPoint(x, y);
    const matrix = pt.matrixTransform(
      this._editor._viewport.node.getScreenCTM(),
    );
    return matrix;
  }
}
