import Config from 'config';
import TemplatePanel from './templatePanel';
import { normalizeText } from './facilis/validator';

export default class DataPanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'data');
    this._init();
    this._initEvents();
  }

  _init() {
    this._dataTypeInput = this._panel.querySelector('#data_type_input');
    this._nameInput = this._panel.querySelector('#data_name_input');
    this._stateInput = this._panel.querySelector('#data_state_input');
    this._collectionInput = this._panel.querySelector('#data_collection_input');
    this._unlimitInput = this.panel.querySelector('#data_unlimit_input');
    this._capacityInput = this.panel.querySelector('#data_capacity_input');
    if (this.element) {
      this._checkCapacity(this.element);
    }
  }

  _initEvents() {
    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onNameInputChanged();
      },
      this._nameInput,
      'input',
    );

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onStateInputChanged();
      },
      this._stateInput,
      'input',
    );

    this._collectionInput.addEventListener('input', () => {
      this._onCollectionInputChanged();
    });

    this._unlimitInput.addEventListener('input', () => {
      this._onUnlimitInputChanged();
    });

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onCapacityInputChanged();
      },
      this._capacityInput,
      'input',
    );
  }

  set dataTypeInput(value) {
    this._dataTypeInput.value = value.toUpperCase();
  }

  get nameInput() {
    return this._nameInput.value;
  }

  get stateInput() {
    return this._stateInput.value;
  }

  get collectionInput() {
    return this._collectionInput.checked;
  }

  get capacityInput() {
    return this._capacityInput.value;
  }

  get unlimitInput() {
    return this._unlimitInput.checked;
  }

  set unlimitInput(value) {
    this._unlimitInput.checked = value;
  }

  set nameInput(value) {
    this._nameInput.value = value;
  }

  set stateInput(value) {
    this._stateInput.value = value;
  }

  set collectionInput(value) {
    this._collectionInput.checked = value;
  }

  set capacityInput(value) {
    this._capacityInput.value = value;
  }

  show(el) {
    const [, dataType] = el.elementType.toLowerCase().split('data');
    this.dataTypeInput = dataType;
    this.nameInput = el.dataName;
    this.stateInput = el.state;
    this.documentationInput = el.documentation;
    const isDataStore = this.element.elementType.toLowerCase() === 'datastore';
    if (isDataStore) {
      this.unlimitInput = el.unlimit;
      this.capacityInput = el.capacity;
    } else {
      this.collectionInput = el.collection;
    }
    this._changeVisibility(
      this._collectionInput.parentNode.parentNode,
      !isDataStore,
    );
    this._changeVisibility(
      this._unlimitInput.parentNode.parentNode,
      isDataStore,
    );
    this._changeVisibility(
      this._capacityInput.parentNode.parentNode,
      isDataStore,
    );
    this._checkCapacity(el);
    this._changeVisibility(this._panel, true);
  }

  _onNameInputChanged() {
    if (this.element) {
      const { elementType, name } = this.element;
      const isDataElement = elementType === Config.DATA_STORE
        || elementType === Config.DATA_INPUT
        || elementType === Config.DATA_OUTPUT
        || elementType === Config.DATA_OBJECT;
      const actual = isDataElement ? name : undefined;
      this.nameInput = normalizeText(this.nameInput, /["]/g);
      if (actual !== this.nameInput && this.nameInput !== '' && isDataElement) {
        this.element.name = this.nameInput;
      }
    }
  }

  _onStateInputChanged() {
    if (this.element) {
      const actual = this.element.state;
      this.stateInput = normalizeText(this.stateInput, /["]/g);
      if (actual !== this.stateInput) this.element.state = this.stateInput;
    }
  }

  _onCollectionInputChanged() {
    this.element.collection = this.collectionInput;
  }

  _checkCapacity(el) {
    if (el.elementType.toLowerCase() === 'datastore') {
      const container = this._capacityInput.parentNode.parentNode;
      const isUnlimit = el.unlimit;
      if (isUnlimit) this.capacityInput = '';
      this._changeVisibility(container, !isUnlimit);
    }
  }

  _onUnlimitInputChanged() {
    if (this.element) {
      const actual = this.element.unlimit;
      if (actual !== this.unlimitInput) this.element.unlimit = this.unlimitInput;
      this._checkCapacity(this.element);
    }
  }

  _onCapacityInputChanged() {
    if (this.element) {
      const actual = this.element.capacity;
      if (actual !== this.capacityInput) {
        this.element.capacity = parseInt(this.capacityInput, 10);
      }
    }
  }
}
