/* eslint-disable no-cond-assign */
import Config from 'config';
import cloneDeep from 'lodash/cloneDeep';
import Snap from 'snapsvg-cjs';
import {} from '../../utils/snaptoolkit';
import Element from './element';
import { getProcessElementByID, getSquareIntersection, getClosestMultiple } from '../../utils/util';
// import PathfinderManager from '../../pathfinderManager';

export default class Connector extends Element {
  constructor(
    id,
    viewport,
    properties,
    elements,
    startMarker,
    endMarker,
    expressionMarker,
    wizardMarker,
    isMovable,
  ) {
    super(Config.CONNECTOR, id, viewport, null, null, properties, isMovable, [Config.DELETE]);
    this._path = null;
    this._selectionPath = null;
    this._elements = elements;
    this._endMarker = endMarker;
    this._startMarker = startMarker;
    this._expressionMarker = expressionMarker;
    this._wizardMarker = wizardMarker;
    this._segmentPoints = Snap.set();
    this._fromEl = getProcessElementByID(this._properties.from, this._elements);
    this._toEl = getProcessElementByID(this._properties.to, this._elements);
    this._CONTROL_POINT_RADIUS = 4;
    this._clickCount = 0;
    this._timeout = 300;
    this._ox = 0;
    this._oy = 0;
    this._oldElMoving = { srcEl: null, x: 0, y: 0 };
    // this._pathfinderManager = new PathfinderManager(viewport);
    // if from/to element don't yet exist, exit
    if (!this._fromEl || !this._toEl) {
      return;
    }

    if (!this.properties.points) {
      this.properties.points = [];
    }

    this._points = cloneDeep(this.properties.points) || [];

    if (properties.type === Config.CONNECTOR_TYPE_ASSOCIATION && !properties.direction) {
      properties.direction = 'none';
    }
    if (properties.type === Config.CONNECTOR_TYPE_TRANSITION && !properties.class) {
      properties.class = 'N';
    }
    if (this._fromEl.elementType === Config.GATEWAY) {
      if (this._fromEl.type === 'exclusive' || this._fromEl.type === 'inclusive') {
        properties.kind = 'expression';
      } else if (this._fromEl.type === 'parallel' || this._fromEl.type === 'event') {
        properties.kind = 'none';
      }
    }

    this._onConnectorMouseOver = this._onConnectorMouseOver.bind(this);
    this._onConnectorMouseOut = this._onConnectorMouseOut.bind(this);
    this._onDblClick = this._onDblClick.bind(this);

    this.drawElement();
    this.initElement();
  }

  // getters methods

  get connectorName() {
    return this._properties.name || '';
  }

  get from() {
    return this._properties.from;
  }

  get to() {
    return this._properties.to;
  }

  get connectorClass() {
    return this._properties.class || 'n';
  }

  get expression() {
    return this._properties.expression;
  }

  get kind() {
    return this._properties.kind || 'none';
  }

  get direction() {
    return this._properties.direction || 'none';
  }

  get type() {
    return this._properties.type;
  }

  get executionOrder() {
    return this._properties.executionOrder;
  }

  // setters methods

  set connectorName(value) {
    this.emit('element_onpropertychange');
    this._properties.name = value;
    this._updateNameLabel();
  }

  set from(value) {
    this._properties.from = value;
  }

  set to(value) {
    this._properties.to = value;
  }

  set connectorClass(value) {
    this.emit('element_onpropertychange');
    this._properties.class = value;
    this._updateConnectorClass();
  }

  set kind(value) {
    this.emit('element_onpropertychange');
    this._properties.kind = value;
    if (this._fromEl.elementType !== Config.GATEWAY) {
      this._updateConditionType();
    }
  }

  set expression(value) {
    this.emit('element_onpropertychange');
    this._properties.expression = value;
  }

  set executionOrder(value) {
    this.emit('element_onpropertychange');
    this._properties.executionOrder = value;
  }

  // Only when the connector type is association

  set direction(value) {
    this.emit('element_onpropertychange');
    this._properties.direction = value;
    this._updateDirection(value);
  }

  setInitialDirection(value) {
    this._properties.direction = value;
    this._updateDirection(value);
  }

  set points(value) {
    // restore start/end points to element's center for future compat
    // set first points to elements center
    this.properties.points = cloneDeep(value);
    this.properties.points[0] = {
      x: this._fromEl.graphic.matrix.e + this._fromEl.width / 2 - Config.ELEMENT_SELECT_SIZE / 2,
      y: this._fromEl.graphic.matrix.f + this._fromEl.height / 2 - Config.ELEMENT_SELECT_SIZE / 2,
    };
    // set last points to elements center
    this.properties.points[this.properties.points.length - 1] = {
      x: this._toEl.graphic.matrix.e + this._toEl.width / 2 - Config.ELEMENT_SELECT_SIZE / 2,
      y: this._toEl.graphic.matrix.f + this._toEl.height / 2 - Config.ELEMENT_SELECT_SIZE / 2,
    };
  }

  drawElement() {
    let pathString = null;
    for (let i = 0, len = this._points.length; i < len; i++) {
      if (i === 0) {
        pathString = `M ${this._points[0].x} ${this._points[0].y} `;
      } else {
        pathString += `L ${this._points[i].x} ${this._points[i].y} `;
      }
    }
    this._path = this._viewport.path(pathString).attr({
      class: `connector--${this.properties.type}`,
      markerEnd: this._endMarker,
    });

    this._selectionPath = this._viewport.path(pathString).attr({
      class: 'connector__selection',
    });

    if (this._isMovable) this._path.dblclick(this._onDblClick);

    const bounds = this._path.getBBox();
    const selectionBox = this.drawSelectionBox(bounds, 'selectionBox selectionBox--connector');

    const pathlen = this._path.getTotalLength() / 2;
    const centerCoords = this._path.getPointAtLength(pathlen);

    this._label = this._drawForeignObjLabel(
      centerCoords.x,
      centerCoords.y,
      this.connectorName,
      'connector__group__text',
      `text_${this.id}`,
    );
    this._label.select('g').attr({
      class: 'connector__group',
      dy: -10,
    });

    this._graphic = this._viewport
      .g(this._selectionPath, selectionBox, this._path, this._label, this._segmentPoints)
      .click(this.onElementMouseDown)
      .attr({
        class: 'connector',
        id: `${Config.CONNECTOR}_${this._properties.id}`,
      });

    // send after grid
    this.toBack();

    if (this._isMovable) {
      this._graphic.mouseover(this._onConnectorMouseOver).mouseout(this._onConnectorMouseOut);
    }

    this._updateConditionType();
    this._updateConnectorClass();
    if (this.properties.type === Config.CONNECTOR_TYPE_ASSOCIATION) {
      this._updateDirection();
    }
    this.updateConnection();
  }

  _drawForeignObjLabel(x, y, title, classname, id) {
    const fobjectSVG = `<g><foreignObject 
      x="${x}"
      y="${y}"
      width="80" 
      height="80"
      id="${id}">
    <p class="${classname}">${title}</p></foreignObject></g>`;
    return Snap.parse(fobjectSVG);
  }

  _updateConnectorClass() {
    switch (this.connectorClass.toUpperCase()) {
      case 'N':
        this._path.removeClass('connector--loopback');
        this._path.attr({
          markerEnd: this._endMarker,
        });
        break;
      case 'W':
        this._path.attr({
          markerEnd: this._wizardMarker,
        });
        break;
      case 'L':
        this._path.addClass('connector--loopback');
        break;
      default:
        break;
    }
  }

  _updateConditionType() {
    switch (this.kind.toUpperCase()) {
      case 'NONE':
        this._path.attr({
          markerStart: '',
        });
        break;
      case 'EXPRESSION':
        if (this._fromEl.elementType !== Config.GATEWAY) {
          this._path.attr({
            markerStart: this._expressionMarker,
          });
        }
        break;
      default:
        break;
    }
  }

  _updateDirection() {
    switch (this.direction.toUpperCase()) {
      case 'NONE':
        this._path.attr({
          markerEnd: '',
          markerStart: '',
        });
        break;
      case 'DIRECTIONAL':
        this._path.attr({
          markerEnd: this._endMarker,
        });
        break;
      case 'BOTH':
        this._path.attr({
          markerEnd: this._endMarker,
          markerStart: this._startMarker,
        });
        break;
      default:
        break;
    }
  }

  updateConnectionStart() {
    // send to back when parent element on move starts
    this.toBack();
  }

  updateConnection(offset = { x: 0, y: 0 }, isPoolMoving = false) {
    if (this._fromEl === null || this._toEl === null) {
      return;
    }
    const selectionSize = Config.ELEMENT_SELECT_SIZE / 2;
    let w1 = this._fromEl.width / 2;
    let h1 = this._fromEl.height / 2;
    let w2 = this._toEl.width / 2;
    let h2 = this._toEl.height / 2;
    let pathString = null;
    let cx1;
    let cy1;
    let cx2;
    let cy2;
    let dy;
    let dx;
    let p1;
    let p2;

    if (this._points.length < 3) {
      // one segment path

      // Straight line
      cx1 = this._fromEl.graphic.matrix.e - selectionSize + w1;
      cy1 = this._fromEl.graphic.matrix.f - selectionSize + h1;
      cx2 = this._toEl.graphic.matrix.e - selectionSize + w2;
      cy2 = this._toEl.graphic.matrix.f - selectionSize + h2;

      dy = cy2 - cy1;
      dx = cx2 - cx1;

      p1 = getSquareIntersection(dx, dy, cx1, cy1, w1, h1);
      p2 = getSquareIntersection(-dx, -dy, cx2, cy2, w2, h2);

      // replace NaN with offscreen value
      if (p1.includes(NaN) || p2.includes(NaN)) {
        p1 = p1.map((e) => {
          return isNaN(e) ? -40000 : e;
        });
        p2 = p2.map((e) => {
          return isNaN(e) ? -4000 : e;
        });
      }

      for (let i = 0, len = this._points.length; i < len; i++) {
        if (i === 0) {
          pathString = `M ${p1[0]} ${p1[1]} `;
          this._points[i] = { x: p1[0], y: p1[1] };
        } else {
          pathString += `L ${p2[0]} ${p2[1]} `;
          this._points[i] = { x: p2[0], y: p2[1] };
        }
      }
      this._path.attr({
        d: pathString,
      });

      this._selectionPath.attr({
        d: pathString,
      });

      // Pathfinder ---
      // const pathFound = this._pathfinderManager.findPath(
      //   this._fromEl,
      //   this._toEl,
      // );
      // this._path.attr({
      //   d: pathFound,
      // });
      // -------------
    } else {
      // 2 or more segments
      for (let i = 0, len = this._points.length; i < len; i++) {
        if (i === 0) {
          w1 = this._fromEl.width / 2;
          h1 = this._fromEl.height / 2;
          w2 = 0;
          h2 = 0;
          cx1 = this._fromEl.graphic.matrix.e - selectionSize + w1;
          cy1 = this._fromEl.graphic.matrix.f - selectionSize + h1;
          cx2 = this._points[i + 1].x + offset.x / 2;
          cy2 = this._points[i + 1].y + offset.y / 2;
          dy = cy2 - cy1;
          dx = cx2 - cx1;
          p1 = getSquareIntersection(dx, dy, cx1, cy1, w1, h1);

          if (!p1.includes(NaN)) {
            pathString = `M ${p1[0]} ${p1[1]} `;
            this._points[i] = { x: p1[0], y: p1[1] };
          } else {
            pathString = `M ${cx1} ${cy1} `;
            this._points[i] = { x: cx1, y: cy1 };
          }
        } else if (i > 0 && i < len - 1) {
          // middle segments
          const p = {};
          // if snap is on, then get on the grid, (doesnt work with pool moving)

          if (this._isSnapOn && !isPoolMoving) {
            // get closest grid if not moved by pool
            p.x = Math.round(
              getClosestMultiple(this._points[i].x + offset.x / 2, this._snapSize)
                + this._snapSize / 2,
            );
            p.y = Math.round(
              getClosestMultiple(this._points[i].y + offset.y / 2, this._snapSize)
                + this._snapSize / 2,
            );
          } else {
            p.x = this._points[i].x + offset.x / 2;
            p.y = this._points[i].y + offset.y / 2;
          }
          pathString += `L ${p.x} ${p.y} `;
          // save all middle points and leave first/last to the center object
          this._points[i] = {
            x: p.x,
            y: p.y,
          };
        } else {
          w1 = 0;
          h1 = 0;
          w2 = this._toEl.width / 2;
          h2 = this._toEl.height / 2;
          cx1 = this._points[i - 1].x + offset.x / 2;
          cy1 = this._points[i - 1].y + offset.y / 2;
          cx2 = this._toEl.graphic.matrix.e - selectionSize + w2;
          cy2 = this._toEl.graphic.matrix.f - selectionSize + h2;
          dy = cy2 - cy1;
          dx = cx2 - cx1;
          p2 = getSquareIntersection(-dx, -dy, cx2, cy2, w2, h2);
          if (!p2.includes(NaN)) {
            pathString += `L ${p2[0]} ${p2[1]} `;
            this._points[i] = { x: p2[0], y: p2[1] };
          }
        }
      }
      this._path.attr({
        d: pathString,
      });

      this._selectionPath.attr({
        d: pathString,
      });
    }

    // selection update
    // update selection box w/ new coordenates after elemetns move
    if (this._graphic) {
      const bounds = this._path.getBBox();
      this._selectionBox.attr({
        x: Math.round(bounds.x - Config.ELEMENT_SELECT_SIZE / 2),
        y: Math.round(bounds.y - Config.ELEMENT_SELECT_SIZE / 2),
        width: Math.round(bounds.width + Config.ELEMENT_SELECT_SIZE),
        height: Math.round(bounds.height + Config.ELEMENT_SELECT_SIZE),
      });
    }

    // Label Position update
    this._updateNameLabel();

    // update points
    this.points = this._points;
  }

  _updateNameLabel() {
    const pathlen = this._path.getTotalLength() / 2;
    const centerCoords = this._path.getPointAtLength(pathlen);
    const fo = this._graphic.select('foreignObject');
    const p = this._graphic.select('p');
    const pBounds = p.node.getBoundingClientRect();
    fo.attr({
      x: Math.round(centerCoords.x - pBounds.width / 2),
      y: Math.round(centerCoords.y),
    });
    this.updateTextLabel('connector__group__text', this.connectorName);
  }

  _onDblClick(evt) {
    this._addNewSegmentToConnector(evt);
  }

  _onConnectorMouseOver() {
    this._path.attr({ strokeWidth: 5 });
  }

  _onConnectorMouseOut() {
    this._path.attr({ strokeWidth: 2 });
  }

  _onSelected() {
    this._drawSegmentPoints();
    this.emit('element_selected');
  }

  _onDeselected() {
    this._removeSegmentPoints();
    this.emit('element_unselected');
  }

  _addNewSegmentToConnector(evt) {
    const clientPoint = this._viewport.getInverseScreenPoint(evt.clientX, evt.clientY);
    const point = this._closestPoint(this._path, [clientPoint.x, clientPoint.y]);
    const pointLength = this._getLengthForPointInPath(point, this._path);
    const positionToInsert = this._getNextPositionInPointsArray(pointLength);
    // save to history
    this.emit('connector_point_new');
    this._points.splice(positionToInsert, 0, point);
    this.points = this._points;
    this._removeSegmentPoints();
    this._drawSegmentPoints();
  }

  _removeSegmentPoints() {
    this._segmentPoints.forEach((handle) => {
      handle.undrag();
      handle.unmouseout();
      handle.unmouseover();
      handle.unclick();
      handle.remove();
    });
    this._segmentPoints.clear();
  }

  _drawSegmentPoints() {
    const _this = this;
    for (let i = 0, len = this._points.length; i < len; i++) {
      if (i > 0 && i < len - 1) {
        this._segmentPoints.push(
          this._graphic
            .circle(
              Math.round(this._points[i].x),
              Math.round(this._points[i].y),
              this._CONTROL_POINT_RADIUS,
            )
            .data('point', i)
            .attr({ class: 'connector__segment_point' })
            .mouseover(function onMouseOver() {
              this.attr({ r: _this._CONTROL_POINT_RADIUS * 2 });
            })
            .mouseout(function onMouseOut() {
              this.attr({ r: _this._CONTROL_POINT_RADIUS });
            })
            .click(function ondblclick(event) {
              // this hack bc ffox doesnt support the drag (single click?) & dblclick together
              _this._clickCount++;
              if (_this._clickCount === 1) {
                event.preventDefault();
                setTimeout(() => {
                  if (_this._clickCount === 1) {
                    // one click
                  } else if (_this._clickCount === 2) {
                    // remove segment point
                    _this._contextMenu.hide();
                    _this.emit('connector_point_deleted');
                    _this._points.splice(this.data('point'), 1);
                    _this._removeSegmentPoints();
                    // update connector
                    _this.updateConnection();
                    _this._drawSegmentPoints();
                    _this._onConnectorMouseOut();
                    _this.points = _this._points;
                    _this._contextMenu.show();
                    event.stopPropagation();
                  }
                  _this._clickCount = 0;
                }, _this._timeout || 300);
              }
            })
            .drag(
              function onMove(dx, dy, x, y, evt) {
                // const p = _this._viewport.getEventPoint(evt);
                const p = _this._viewport.getMousePoint(evt, 0, 0);
                if (_this._isSnapOn) {
                  p.x = Math.round(getClosestMultiple(p.x, _this._snapSize) + _this._snapSize / 2);
                  p.y = Math.round(getClosestMultiple(p.y, _this._snapSize) + _this._snapSize / 2);
                }
                // move control dot
                this.attr({ cx: p.x, cy: p.y });
                // update points array with new position
                _this._points[this.data('point')] = {
                  x: p.x,
                  y: p.y,
                };
                _this.updateConnection();
              },
              // eslint-disable-next-line prefer-arrow-callback
              function onStart() {
                _this._ox = this.attr('cx');
                _this._oy = this.attr('cy');
                _this.emit('connector_point_move_start');
              },
              // eslint-disable-next-line prefer-arrow-callback
              function onEnd() {
                if (_this._ox !== this.attr('cx') || _this._oy !== this.attr('cy')) {
                  _this.emit('connector_point_move_end');
                  _this.points = _this._points;
                } else {
                  _this.emit('connector_point_no_move_end');
                }
              },
            ),
        );
      }
    }
  }

  updateSegmentsPosition(elementMoving) {
    // called when multiple selection of both to and from are moved
    // and segment points need to be updated
    if (this._toEl.selected && this._fromEl.selected) {
      if (this._points.length > 2) {
        this._removeSegmentPoints();
        for (let i = 1, len = this._points.length - 1; i < len; i++) {
          // skip first and last
          if (this._oldElMoving.srcEl === elementMoving) {
            this._points[i].x += elementMoving.mx - this._oldElMoving.x;
            this._points[i].y += elementMoving.my - this._oldElMoving.y;
          } else {
            this._points[i].x += elementMoving.mx;
            this._points[i].y += elementMoving.my;
          }
        }
        this._oldElMoving = {
          srcEl: elementMoving,
          x: elementMoving.mx,
          y: elementMoving.my,
        };
        this._drawSegmentPoints();
        this.updateConnection();
      }
    }
  }

  resetMovingPointSegment() {
    this._oldElMoving = { srcEl: null, x: 0, y: 0 };
    // update props for undo
    this.points = this._points;
  }

  _getNextPositionInPointsArray(pointLength) {
    for (let i = 0, len = this._points.length; i < len; i++) {
      const pl = this._getLengthForPointInPath(this._points[i], this._path);
      if (pl > pointLength) {
        return i;
      }
    }
    return 0;
  }

  _closestPoint(pathNode, point) {
    // find closest point in a path from a given point
    const pathLength = pathNode.getTotalLength();
    let precision = 8;
    let best;
    let bestLength;
    let bestDistance = Infinity;
    let scan;
    let scanDistance;
    // linear scan for coarse approximation
    for (let scanLength = 0; scanLength <= pathLength; scanLength += precision) {
      if (
        (scanDistance = this._distance2((scan = pathNode.getPointAtLength(scanLength)), point))
        < bestDistance
      ) {
        best = scan;
        bestLength = scanLength;
        bestDistance = scanDistance;
      }
    }
    // binary search for precise estimate
    precision /= 2;
    while (precision > 0.5) {
      let before;
      let after;
      let beforeLength;
      let afterLength;
      let beforeDistance;
      let afterDistance;

      if (
        (beforeLength = bestLength - precision) >= 0
        && (beforeDistance = this._distance2(
          (before = pathNode.getPointAtLength(beforeLength)),
          point,
        )) < bestDistance
      ) {
        best = before;
        bestLength = beforeLength;
        bestDistance = beforeDistance;
      } else if (
        (afterLength = bestLength + precision) <= pathLength
        && (afterDistance = this._distance2((after = pathNode.getPointAtLength(afterLength)), point))
          < bestDistance
      ) {
        best = after;
        bestLength = afterLength;
        bestDistance = afterDistance;
      } else {
        precision /= 2;
      }
    }

    best = {
      x: Math.round(best.x),
      y: Math.round(best.y),
      // dist: Math.sqrt(bestDistance),
    };
    return best;
  }

  _distance2(p, point) {
    const dx = p.x - point[0];
    const dy = p.y - point[1];
    return dx * dx + dy * dy;
  }

  _getLengthForPointInPath(p, path) {
    // return length for a given point in a path
    const pathLength = path.getTotalLength();
    const precision = 100;
    const division = pathLength / precision;
    let theRecord = pathLength;
    let theSegment;
    for (let i = 0; i < precision; i++) {
      // get a point on the path for this distance
      const _p = path.getPointAtLength(i * division);
      // get the distance between the new point _p and the point p
      const theDistance = this._distance2(_p, [p.x, p.y]);
      if (theDistance < theRecord) {
        // if the distance is smaller than the record set the new record
        theRecord = theDistance;
        theSegment = i;
      }
    }
    return theSegment * division;
  }
}
