import sax from 'sax';

export default class Converter {
  constructor() {
    this.arrayJs = [];
    this.convert = sax.parser(true);
    this.textValid = false;
  }

  _isValidTag(name) {
    return (
      name === 'VALUE'
      || name === 'EVENT'
      || name === 'TASK'
      || name === 'CALENDAR'
      || name === 'STATUS'
    );
  }

  convertToJson(xml) {
    const data = [];
    let detail = {};
    this.convert.onopentag = (tag) => {
      const name = tag.name.toUpperCase();
      switch (name) {
        case 'LEVEL': {
          detail[tag.attributes.name] = tag.attributes.value;
          break;
        }
        case 'EVENT':
        case 'CALENDAR':
        case 'TASK':
        case 'STATUS': {
          detail.id = tag.attributes.id;
          detail.name = tag.attributes.name;
          break;
        }
        case 'MESSAGE':
        case 'EXCEPTION': {
          this.textValid = true;
          break;
        }
        default:
          break;
      }
    };
    this.convert.onclosetag = (name) => {
      if (this._isValidTag(name.toUpperCase())) {
        data.push(detail);
        detail = {};
      }
      if (this.textValid) {
        this.textValid = false;
        data.push({ message: 'OK' });
      }
    };

    this.convert.ontext = (text) => {
      if (this.textValid) {
        data.push({ message: text });
        this.textValid = false;
      }
    };

    this.convert.write(xml).close();
    return data;
  }
}
