import { saveSvgAsPng, svgAsDataUri } from 'save-svg-as-png';
import { saveAs } from 'file-saver';
import { getSVGMapBBox, getName } from '../utils/util';

export default class Documentation {
  constructor(editor) {
    this._editor = editor;
  }

  async getPNGCanvas() {
    // used by facilis to generate canvas of image in PDF file
    this._editor.unSelectAllElements();
    // check grid state to turn off if  on.
    const gridState = this._editor._gridManager.isGridOn;
    this._editor.unSelectAllElements();
    // reset viewport view
    this._editor.resetView();
    if (gridState) {
      this._editor.showGrid(false);
    }
    const mapBounds = getSVGMapBBox(this._editor.elements);
    const svguri = await svgAsDataUri(document.getElementById('app'), {
      backgroundColor: '#fff',
      width: mapBounds.width + 40,
      height: mapBounds.height + 40,
      left: mapBounds.x - 20,
      top: mapBounds.y - 20,
      excludeUnusedCss: false,
      scale: 1,
    });
    const canvasObj = this._addImage(svguri).then((image) => {
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');
      canvas.width = image.width;
      canvas.height = image.height;
      context.drawImage(image, 0, 0, image.width, image.height);
      return canvas;
    });
    if (gridState) {
      this._editor.showGrid(true);
    }

    return canvasObj;
  }

  generateXPDLFile() {
    // downloads current xpdl file directly to browser, used by online apaia
    const xpdl = this._editor.buildXMLProcess();
    const blob = new Blob([xpdl], { type: 'text/plain;charset=utf-8' });
    saveAs(blob, `${getName(this._editor.workflow.properties.name)}.xpdl`);
  }

  generateProcessPNG() {
    // downloads a PNG file to browser, used by apia online
    const mapBounds = getSVGMapBBox(this._editor.elements);
    const app = document.getElementById('app');
    this._editor.unSelectAllElements();
    saveSvgAsPng(app, `${getName(this._editor.workflow.properties.name)}.png`, {
      backgroundColor: '#fff',
      width: mapBounds.width + 40,
      height: mapBounds.height + 40,
      left: mapBounds.x - 20,
      top: mapBounds.y - 20,
      excludeUnusedCss: false,
      scale: 1,
    });
  }

  async getPNGDataURL() {
    // returns the diagram as a png data url,
    // used by facilis renderer class to export image
    const gridState = this._editor._gridManager.isGridOn;
    this._editor.unSelectAllElements();
    this._editor.resetView();
    if (gridState) {
      this._editor.showGrid(false);
    }
    const mapBounds = getSVGMapBBox(this._editor.elements);
    const svguri = await svgAsDataUri(document.getElementById('app'), {
      backgroundColor: '#fff',
      width: mapBounds.width + 40,
      height: mapBounds.height + 40,
      left: mapBounds.x - 40,
      top: mapBounds.y - 40,
      excludeUnusedCss: false,
      scale: 1,
    });
    const urlData = this._addImage(svguri).then((image) => {
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');
      canvas.width = image.width;
      canvas.height = image.height;
      context.drawImage(image, 0, 0, image.width, image.height);
      const url = canvas
        .toDataURL('image/png')
        .substring('data:image/png;base64,'.length);
      return url;
    });
    if (gridState) {
      this._editor.showGrid(true);
    }
    return urlData;
  }

  _addImage(uri) {
    return new Promise((resolve, reject) => {
      const image = document.createElement('img');
      image.onload = () => {
        return resolve(image);
      };
      image.onerror = reject;
      image.src = uri;
    });
  }
}
