import EventEmitter from 'eventemitter3';

export default class PropertiesPanel extends EventEmitter {
  constructor() {
    super();
    this._sidebar = document.querySelector('.main__sidepanel');
    this._sidebarHandle = document.querySelector('#sidebar_handle');
    this._btnToggleVisible = document.querySelector('#sidebar_toggle');

    const style = window.getComputedStyle(document.documentElement);
    // get sidebar width value from stylesheet
    this._sidebarMinWidth = style.getPropertyValue('--sidebar-width');
    this._newSideBarWidth = this._sidebarMinWidth;

    this._onSidebarHandleMouseUp = this._onSidebarHandleMouseUp.bind(this);
    this._onSidebarHandleMouseMove = this._onSidebarHandleMouseMove.bind(this);

    // user changeable panel's width
    this._sidebarHandle.addEventListener('mousedown', () => {
      document.addEventListener('mouseup', this._onSidebarHandleMouseUp);
      document.addEventListener('mousemove', this._onSidebarHandleMouseMove);
    });

    window.addEventListener('resize', () => {
      const mq = window.matchMedia('(max-width: 992px)');
      if (mq.matches) {
        // display full width
        this._sidebar.setAttribute('style', ' width:100%');
      } else {
        // display default size
        this._sidebar.setAttribute('style', `width:${this._newSideBarWidth}px`);
      }
    });

    // toggle sidebar visibility
    this._btnToggleVisible.addEventListener(
      'click',
      () => {
        const state = this._sidebar.getAttribute('data-state');
        this._newSideBarWidth = parseInt(this._newSideBarWidth, 10);
        if (state === 'off' || state === '') {
          document.documentElement.style.setProperty(
            '--sidebar-current-width',
            `-${this._newSideBarWidth}px`,
          );
          this._sidebar.setAttribute('data-state', 'on');
        } else {
          document.documentElement.style.setProperty(
            '--sidebar-current-width',
            '0px',
          );
          this._sidebar.setAttribute('data-state', 'off');
        }
        this.emit('resize');
      },
      false,
    );
  }

  _onSidebarHandleMouseUp() {
    document.removeEventListener('mousemove', this._onSidebarHandleMouseMove);
    document.removeEventListener('mouseup', this._onSidebarHandleMouseUp);
  }

  _onSidebarHandleMouseMove(evt) {
    const docWidth = document.documentElement.offsetWidth;
    this._newSideBarWidth = docWidth - evt.clientX;
    if (this._newSideBarWidth < parseInt(this._sidebarMinWidth, 10)) {
      this._newSideBarWidth = parseInt(this._sidebarMinWidth, 10);
    } else if (this._newSideBarWidth >= docWidth / 2) {
      this._newSideBarWidth = docWidth / 2;
    }
    this._sidebar.setAttribute('style', `width:${this._newSideBarWidth}px`);
    this._sidebar.style.width = `width:${this._newSideBarWidth}px`;
    this._sidebarHandle.setAttribute(
      'style',
      `right:${this._newSideBarWidth}px`,
    );
    this._btnToggleVisible.setAttribute(
      'style',
      `right:${this._newSideBarWidth}px`,
    );
  }
}
