/* eslint-disable no-mixed-operators */
import Snap from 'snapsvg-cjs';
import deburr from 'lodash/deburr';
import Config from 'config';

export function intersectRect(r1, r2) {
  const obj1 = r1.getBoundingClientRect();
  const obj2 = r2.getBoundingClientRect();
  return !(
    obj2.left > obj1.right
    || obj2.right < obj1.left
    || obj2.top > obj1.bottom
    || obj2.bottom < obj1.top
  );
}

export function getPool(elements) {
  let pool;
  for (let i = 0, len = elements.length; i < len; i++) {
    if (elements[i].elementType === 'Pool') {
      pool = elements[i];
    }
  }
  return pool;
}

export function getAllLanes(elements) {
  const lanesArray = [];
  for (let i = 0, len = elements.length; i < len; i++) {
    const el = elements[i];
    if (el.elementType === 'Lane') {
      lanesArray.push(el);
    }
  }
  return lanesArray;
}

export function getPreviousLaneByYPos(array, y) {
  const len = array.length;
  for (let i = 0; i < len; i++) {
    if (array[i].y > y) {
      return array[i - 1] || array[0];
    }
  }
  return array[len - 1];
}

export function getNextLaneByYPos(array, y) {
  const len = array.length;
  for (let i = 0; i < len; i++) {
    if (array[i].y > y) {
      return array[i] || array[len - 1];
    }
  }
  return array[len - 1];
}

export function getSelected(elArray) {
  const selectedArray = [];
  let el;
  for (let i = 0, len = elArray.length; i < len; i++) {
    el = elArray[i];
    if (el.selected) {
      selectedArray.push(el);
    }
  }
  return selectedArray;
}

export function numberOfSelectedElements(elArray) {
  return getSelected(elArray).length;
}

export function getMinX(array) {
  const minXElement = array.reduce((prev, current) => {
    return prev.x < current.x ? prev : current;
  });
  return minXElement;
}

export function getMaxX(array) {
  const maxXElement = array.reduce((prev, current) => {
    return prev.selectionBox.node.getBoundingClientRect().right
      > current.selectionBox.node.getBoundingClientRect().right
      ? prev
      : current;
  });
  return maxXElement;
}

export function getMinY(array) {
  const minYElement = array.reduce((prev, current) => {
    return prev.y < current.y ? prev : current;
  });
  return minYElement;
}

export function getMaxY(array) {
  const maxYElement = array.reduce((prev, current) => {
    return prev.selectionBox.node.getBoundingClientRect().bottom
      > current.selectionBox.node.getBoundingClientRect().bottom
      ? prev
      : current;
  });
  return maxYElement;
}

export function getClosestMultiple(value, roundTo) {
  const r = Math.floor(value / roundTo) * roundTo;
  const f = Math.ceil(value / roundTo) * roundTo;
  const numbers = [r, f];
  const closest = numbers.reduce((prev, curr) => {
    return Math.abs(curr - value) < Math.abs(prev - value) ? curr : prev;
  });
  return closest;
}

export function getElementsOfType(type, elements) {
  // returns array w/ all elements of certain type
  const elArray = [];
  for (let i = 0, len = elements.length; i < len; i++) {
    if (elements[i].elementType === type) {
      elArray.push(elements[i]);
    }
  }
  return elArray;
}

export function getProcessElementByID(id, elements) {
  for (let i = 0, len = elements.length; i < len; i++) {
    if (parseInt(elements[i].id, 10) === parseInt(id, 10)) {
      return elements[i];
    }
  }
  return null;
}

export function getProcessElementByGraphicID(id, elements) {
  const cleanID = id.substr(id.indexOf('_') + 1);
  return getProcessElementByID(cleanID, elements);
}

export function getElementsByConnectedType(element, type, elements) {
  const elArray = [];
  elements.forEach((connector) => {
    // console.log(connector);
    if (
      connector.elementType === Config.CONNECTOR
      && parseInt(connector.properties.from, 10) === parseInt(element.id, 10)
    ) {
      const elTo = getProcessElementByID(connector.properties.to, elements);
      if (elTo.elementType === type) {
        elArray.push(elTo);
      }
    }
    if (
      connector.elementType === Config.CONNECTOR
      && parseInt(connector.properties.to, 10) === parseInt(element.id, 10)
    ) {
      const elFrom = getProcessElementByID(connector.properties.from, elements);
      if (elFrom.elementType === type) {
        elArray.push(elFrom);
      }
    }
  });
  return elArray;
}

export function getSquareIntersection(dx, dy, cx, cy, w, h) {
  // returns array of points for line
  if (Math.abs(dy / dx) < h / w) {
    // vertical edge
    return [Math.round(cx + (dx > 0 ? w : -w)), Math.round(cy + (dy * w) / Math.abs(dx))];
  }
  // horizontal edge
  return [Math.round(cx + (dx * h) / Math.abs(dy)), Math.round(cy + (dy > 0 ? h : -h))];
}

export function getSVGMapBBox(elArray) {
  const map = Snap.set();
  // make set to get full map bounds
  for (let i = 0, len = elArray.length; i < len; i++) {
    map.push(elArray[i].graphic);
  }
  const mapBounds = map.getBBox();
  return mapBounds;
}

export function getViewPortBBox(viewport) {
  const viewportElements = viewport.children();
  const elList = Snap.set();
  for (let i = 0, len = viewportElements.length; i < len; i++) {
    const el = viewportElements[i];
    if (!el.hasClass('grid_bg')) {
      elList.push(el);
    }
  }
  return elList.getBBox();
}

export function getName(gotName) {
  let name;
  if (gotName) {
    name = deburr(gotName).replace(/\s/g, '_');
  } else {
    name = 'Untitled';
  }
  return name;
}

export function downloadPDF(data, filename) {
  let binary = '';
  const bytes = new Uint8Array(data);
  const len = bytes.byteLength;
  for (let i = 0; i < len; i++) {
    binary += String.fromCharCode(bytes[i]);
  }
  const link = document.createElement('a');
  link.href = `data:text/plain;base64,${window.btoa(binary)}`;
  link.download = filename;
  link.click();
}
/**
 * return the array conectors (transitions) from element
 */
export function getTransitionsByElement(element, elements) {
  return elements.filter((connector) => {
    return (
      connector.elementType === Config.CONNECTOR
      && connector.type === Config.CONNECTOR_TYPE_TRANSITION
      && connector.from === element.id
    );
  });
}

export function getConnectorsByFromTo(element, elements, fromTo) {
  const connectors = [];
  elements.forEach((connector) => {
    if (connector.elementType === Config.CONNECTOR && connector[fromTo] === element.id) {
      connectors.push(connector);
    }
  });
  return connectors;
}

/**
 * return next element in the process flow starting from element selected
 */
export function getNextElement(element, elements) {
  const transitions = getTransitionsByElement(element, elements);
  if (transitions.length > 0) {
    const [firstTransition] = transitions;
    return getProcessElementByID(firstTransition.to, elements);
  }
  return null;
}

export function getLastYElBounds(element, elements) {
  // returns bounds for the last element in both x and y coords
  const transitions = getTransitionsByElement(element, elements);
  const ctxElementBounds = element.selectionBox.node.getBoundingClientRect();
  let maxYBoundElement = { y: 0 };
  transitions.forEach((transition) => {
    const el = getProcessElementByID(transition.to, elements);
    const elBounds = el.selectionBox.node.getBoundingClientRect();
    // if element Y bigger and x too
    if (elBounds.y > maxYBoundElement.y && elBounds.left > ctxElementBounds.right) {
      maxYBoundElement = elBounds;
    }
  });
  if (maxYBoundElement.y === 0) {
    return null;
  }
  return maxYBoundElement;
}

export function convertUnit(unit, value, type = 'MS_TO_UNIT') {
  let factor = 0;
  switch (unit) {
    case 'MINUTES':
      factor = 60000;
      break;
    case 'HOURS':
      factor = 3600000;
      break;
    case 'DAYS':
      factor = 86400000;
      break;
    case 'WEEKS':
      factor = 604800000;
      break;
    default:
      break;
  }
  const result = type === 'MS_TO_UNIT' ? value / factor : value * factor;
  return parseInt(result, 10);
}

export function visibilityComponent(wrapper, value) {
  wrapper.setAttribute('data-visible', value);
}

export function mapInOutParameters(params) {
  const inout = {
    input: 'IN',
    output: 'OUT',
    'input-output': 'IN-OUT',
  };
  return params.map((param) => {
    return { ...param, inout: inout[param.inout] };
  });
}

export function indexByProperty(collection = [], property) {
  return collection.reduce(
    (acc, el) => ({
      ...acc,
      [el[property]]: el,
    }),
    {},
  );
}

export default {
  intersectRect,
  getPool,
  getAllLanes,
  getSelected,
  getPreviousLaneByYPos,
  getNextLaneByYPos,
  getMinX,
  getMaxX,
  getMinY,
  getMaxY,
  numberOfSelectedElements,
  getClosestMultiple,
  getElementsOfType,
  getElementsByConnectedType,
  getProcessElementByID,
  getProcessElementByGraphicID,
  getSVGMapBBox,
  getName,
  downloadPDF,
  getViewPortBBox,
  convertMilliseconsToUnit: convertUnit,
  getNextElement,
  getTransitionsByElement,
  getLastYElBounds,
  visibilityComponent,
  mapInOutParameters,
  getConnectorsByFromTo,
};
