import ToastMessage from '../../components/ToastMessage';

export const validateName = (name) => {
  const regex = /^[a-zA-Z0-9_-\s]+$/gi;
  return regex.test(name);
};

export const validateInputValue = (input, property, field = 'Name') => {
  const value = input.value.trim();
  if (!validateName(value === '' ? ' ' : value)) {
    const message = new ToastMessage();
    message.showWrongMessage({
      title: 'Error',
      message: `The <b>"${property} ${field}"</b> cannot contain invalid characters`,
    });
    return false;
  }
  return true;
};

export const checkEmptyName = (element, oldValue) => {
  if (element.value === '') {
    element.value = oldValue;
  }
};

export const normalizeText = (text, expression = undefined, replace = '') => {
  const regex = expression || /[&/\\"<>]/g;
  return text.replace(regex, replace);
};

export const validateApiaName = (name) => {
  const regex = /^[a-zA-Z][a-zA-Z0-9_]+[a-zA-Z0-9]$/gi;
  return regex.test(name);
};

export const validatePositiveInteger = (value, minValue = 1) => {
  const number = parseInt(value, 10);
  return !isNaN(number) && number >= minValue;
};

export const validateDate = (value) => {
  return !isNaN(Date.parse(value));
};

export const validateField = (field) => {
  const { type, value } = field;
  let isValid = true;
  if (value.trim() !== '') {
    switch (type.toUpperCase()) {
      case 'NUMBER': {
        isValid = validatePositiveInteger(value);
        if (!isValid) {
          isValid = true;
          field.value = 0;
        }
        field.value = parseInt(value, 10);
        break;
      }
      case 'DATE': {
        isValid = validateDate(value);
        if (!isValid) {
          isValid = true;
          field.value = Date.now();
        }
        break;
      }
      default:
        break;
    }
  }
  return isValid;
};

export default {
  validateName,
  validateInputValue,
  checkEmptyName,
  normalizeText,
  validateApiaName,
  validatePositiveInteger,
  validateDate,
};
