import key from 'mousetrap';
import { getSelected } from '../utils/util';

export default class KeyManager {
  constructor(editor) {
    this._editor = editor;
    this.initKeyBindings();
    this._currentMouseMode = null;
    this._allElementsSelected = false;
    this._isCtrlPressed = false;
  }

  initKeyBindings() {
    // Delete Element
    key.bind('del', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        this._editor.deleteMultipleSelectedElements();
      }
    });
    // Copy Element
    key.bind(['ctrl+c', 'meta+c'], (evt) => {
      evt.preventDefault();
      this._editor.copyElements();
    });
    // Paste Element
    key.bind(['ctrl+v', 'meta+v'], (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        this._editor.pasteElements();
      }
    });
    // Redo History
    key.bind(['shift+ctrl+z', 'shift+meta+z'], (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        this._editor.redoAction();
      }
    });
    // Undo History
    key.bind(['ctrl+z', 'meta+z'], (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        this._editor.undoAction();
      }
    });
    // toogle grid
    key.bind('g', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('grid_btn').click();
      }
    });
    // toogle properties panel
    key.bind('p', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('sidebar_toggle').click();
      }
    });
    // hand tool
    key.bind('h', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('hand_btn').click();
      }
    });
    // lasso tool
    key.bind('s', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('lasso_btn').click();
      }
    });
    // connector tool
    key.bind('c', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('connector_btn').click();
      }
    });
    // Tabbing tasks
    key.bind('t', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        this._editor.doTabProcessElements();
      }
    });
    // zoom in
    key.bind('+', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('zoom_in').click();
      }
    });
    // zoom out
    key.bind('-', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('zoom_out').click();
      }
    });
    // Pan Around
    key.bind(
      ['ctrl', 'meta'],
      () => {
        this._currentMouseMode = this._editor.mode;
        this._editor._panZoomManager.enablePanning(true);
        this._editor._lassoManager.enableLasso(false);
        this._editor._connectorManager.enableConnection(false);
        this._isCtrlPressed = true;
      },
      'keydown',
    );
    // Stop Pan Around
    key.bind(
      ['ctrl', 'meta'],
      () => {
        this._editor._panZoomManager.enablePanning(false);
        this._editor.mode = this._currentMouseMode;
        this._isCtrlPressed = false;
      },
      'keyup',
    );
    // Reset view
    key.bind('r', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        document.getElementById('reset_view').click();
      }
    });
    // Select All Elements
    key.bind('a', (evt) => {
      if (event.target.tagName === 'BODY' || event.target.tagName === 'A') {
        evt.preventDefault();
        const selEls = getSelected(this._editor.elements);
        if (!this._allElementsSelected || selEls.length < 1) {
          this._editor.selectAllElements();
          this._allElementsSelected = true;
        } else {
          this._editor.unSelectAllElements();
          this._allElementsSelected = false;
        }
      }
    });
  }
}
