import TemplatePanel from './templatePanel';
import { normalizeText } from './facilis/validator';

export default class TextPanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'text');

    this._textInput = this._panel.querySelector('#text_text_input');
    this._setTimeoutOnPropertyChanged(
      undefined,
      () => this._onTextInput(),
      this._textInput,
      'input',
      100,
    );
  }

  get textInput() {
    return this._textInput.value;
  }

  set textInput(newValue) {
    this._textInput.value = newValue;
  }

  show(el) {
    this.textInput = el.text;
    this.documentationInput = el.documentation;
    this._changeVisibility(this._panel, true);
  }

  _onTextInput() {
    if (this.element) {
      const actual = this.element.text;
      this.textInput = normalizeText(this.textInput, /["]/g);
      if (actual !== this.textInput) this.element.text = this.textInput;
    }
  }
}
