import * as Snap from 'snapsvg-cjs';
import Config from 'config';
import EventElement from './eventElement';

export default class IntermediateEvent extends EventElement {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(
      Config.INTERMEDIATE_EVENT,
      id,
      x,
      y,
      viewport,
      properties,
      isMovable,
      [
        Config.DELETE,
        Config.TASK,
        Config.SUBPROCESS,
        Config.END_EVENT,
        Config.INTERMEDIATE_EVENT,
        Config.GATEWAY,
        Config.TEXT,
        Config.CONNECTOR,
      ],
    );
    this.initElement();
  }

  set name(value) {
    this.emit('element_onpropertychange');
    super.name = value;
    this.updateTextLabel('intermediate_event__group__text', value);
  }

  set type(value) {
    this.emit('element_onpropertychange');
    this._properties.type = value;
    if (value === 'message') {
      this._properties.message = { catchThrow: 'catch' };
    } else {
      delete this._properties.message;
    }
    this._updateDetailTypeIcon();
  }

  get catchThrowMessage() {
    return this.message ? this.message.catchThrow : undefined;
  }

  set catchThrowMessage(value) {
    const isDifferent = value !== this.catchThrowMessage;
    super.catchThrowMessage = value;
    this._updateDetailTypeIcon();
    if (isDifferent) {
      this.updateMessageProperties(value);
    }
  }

  updateMessageProperties(value) {
    if (value === 'throw') {
      delete this._properties.message.attrProc;
      delete this._properties.message.attrEnt;
      delete this._properties.message.webService;
    } else {
      delete this._properties.events;
    }
  }

  setTypeWithoutSave(type) {
    this._properties.type = type;
    this._updateDetailTypeIcon();
  }

  get isAttached() {
    return this._properties.isAttached;
  }

  get target() {
    return this._properties.target;
  }

  set isAttached(value) {
    this._properties.isAttached = value;
  }

  set target(value) {
    this._properties.target = value;
  }

  drawElement() {
    const outerCircle = this._viewport
      .circle(20, 20, this._radius)
      .attr({ class: 'intermediate_event__bg' });
    const innerCircle = this._viewport
      .circle(20, 20, this._radius - 4)
      .attr({ class: 'intermediate_event__inner' });

    const intermediateEvent = outerCircle.appendTo(this._viewport);
    const bounds = intermediateEvent.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    const label = this.drawForeignObjLabel(
      -40,
      48,
      120,
      80,
      this.eventName,
      'intermediate_event__group__text',
      `text_${this.id}`,
    );
    label.select('g').attr({
      class: 'intermediate_event__group',
    });

    const icon = this._drawEventDetailTypeIcon();

    this._graphic = this._viewport
      .g(selectionBox, intermediateEvent, innerCircle, label, icon)
      .attr({
        class: 'intermediate_event',
        id: `${Config.INTERMEDIATE_EVENT}_${this.properties.id}`,
      })
      .mousedown(this.onElementMouseDown);

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }
  }

  _drawEventDetailTypeIcon() {
    let typeIcon;
    let typeIconGroup;
    switch (this.eventType.toUpperCase()) {
      case 'MESSAGE':
        typeIcon = Snap.parse(
          `<g id="typeicon">
            <path 
              class="start_event__typeicon__message" 
              transform="scale(1, -1) 
              translate(0, -100)" 
              d="M95 618l0-535 811 0 0 535-811 0z m83-35l644 
              0-322-212-322 212z m-48-10l370-244 371 244 0-455-741 0 0 455z" 
            /></g>`,
        );
        if (this.catchThrowMessage === 'throw') {
          typeIcon = Snap.parse(
            `<g id="typeicon">
              <path 
                class="task__typeicon" 
                transform="scale(1, -1) 
                translate(0, -100)" 
                d="M99 608l401-227 401 227-802 0z 
                m1-84l0-448 802 0 0 448-402-224-400 224z" 
              />
            </g>`,
          );
        }
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.02, T${10},${25}`);
        break;
      case 'TIMER':
        typeIcon = Snap.parse(
          `<g id="typeicon"><g>
          <path 
            class="start_event__typeicon" 
            d="M47.5,0C21.3,0,0,21.3,0,47.5S21.3,95,47.5,95S95,73.7,95,47.5S73.7,0,47.5,0z M47.5,90.7c-23.8,0-43.2-19.4-43.2-43.2 S23.7,4.3,47.5,4.3s43.2,19.4,43.2,43.2S71.3,90.7,47.5,90.7z"/>
            <path 
              class="start_event__typeicon" 
              d="M47.5,19.4c1.2,0,2.2-1,2.2-2.2v-5.3c0-1.2-1-2.2-2.2-2.2s-2.2,1-2.2,2.2v5.3C45.3,18.5,46.3,19.4,47.5,19.4z"/>
            <path 
              class="start_event__typeicon" 
              d="M30.5,22.4c0.4,0.7,1.1,1.1,1.9,1.1c0.4,0,0.7-0.1,1.1-0.3c1-0.6,1.4-1.9,0.8-2.9l-2.6-4.6c-0.6-1-1.9-1.4-2.9-0.8 c-1,0.6-1.4,1.9-0.8,2.9L30.5,22.4z"/>
            <path class="start_event__typeicon" d="M22.4,30.5l-4.6-2.6c-1-0.6-2.3-0.2-2.9,0.8c-0.6,1-0.2,2.3,0.8,2.9l4.6,2.6c0.3,0.2,0.7,0.3,1.1,0.3 c0.7,0,1.5-0.4,1.9-1.1C23.8,32.4,23.4,31.1,22.4,30.5z"/><path class="start_event__typeicon" d="M19.4,47.5c0-1.2-1-2.2-2.2-2.2h-5.3c-1.2,0-2.2,1-2.2,2.2s1,2.2,2.2,2.2h5.3C18.5,49.7,19.4,48.7,19.4,47.5z"/><path class="start_event__typeicon" d="M20.2,60.8l-4.6,2.6c-1,0.6-1.4,1.9-0.8,2.9c0.4,0.7,1.1,1.1,1.9,1.1c0.4,0,0.7-0.1,1.1-0.3l4.6-2.6 c1-0.6,1.4-1.9,0.8-2.9C22.6,60.5,21.3,60.2,20.2,60.8z"/><path class="start_event__typeicon" d="M33.5,71.8c-1-0.6-2.3-0.2-2.9,0.8L28,77.2c-0.6,1-0.2,2.3,0.8,2.9c0.3,0.2,0.7,0.3,1.1,0.3c0.7,0,1.5-0.4,1.9-1.1 l2.6-4.6C34.8,73.7,34.5,72.4,33.5,71.8z"/><path class="start_event__typeicon" d="M47.5,75.6c-1.2,0-2.2,1-2.2,2.2v5.3c0,1.2,1,2.2,2.2,2.2s2.2-1,2.2-2.2v-5.3C49.6,76.5,48.7,75.6,47.5,75.6z"/><path class="start_event__typeicon" d="M64.5,72.6c-0.6-1-1.9-1.4-2.9-0.8s-1.4,1.9-0.8,2.9l2.6,4.6c0.4,0.7,1.1,1.1,1.9,1.1c0.4,0,0.7-0.1,1.1-0.3 c1-0.6,1.4-1.9,0.8-2.9L64.5,72.6z"/><path class="start_event__typeicon" d="M79.3,63.4l-4.6-2.6c-1-0.6-2.3-0.2-2.9,0.8c-0.6,1-0.2,2.3,0.8,2.9l4.6,2.6c0.3,0.2,0.7,0.3,1.1,0.3 c0.7,0,1.5-0.4,1.9-1.1C80.7,65.3,80.3,64,79.3,63.4z"/><path class="start_event__typeicon" d="M83,45.4h-5.3c-1.2,0-2.2,1-2.2,2.2s1,2.2,2.2,2.2H83c1.2,0,2.2-1,2.2-2.2S84.2,45.4,83,45.4z"/><path class="start_event__typeicon" d="M73.7,34.5c0.4,0,0.7-0.1,1.1-0.3l4.6-2.6c1-0.6,1.4-1.9,0.8-2.9c-0.6-1-1.9-1.4-2.9-0.8l-4.7,2.6c-1,0.6-1.4,1.9-0.8,2.9 C72.2,34.1,72.9,34.5,73.7,34.5z"/><path class="start_event__typeicon" d="M66.3,14.9c-1-0.6-2.3-0.2-2.9,0.8l-2.6,4.6c-0.6,1-0.2,2.3,0.8,2.9c0.3,0.2,0.7,0.3,1.1,0.3c0.7,0,1.5-0.4,1.9-1.1 l2.6-4.6C67.7,16.8,67.4,15.5,66.3,14.9z"/><path class="start_event__typeicon" d="M47.5,49.7h13c1.2,0,2.2-1,2.2-2.2s-1-2.2-2.2-2.2H49.6V28.8c0-1.2-1-2.2-2.2-2.2s-2.2,1-2.2,2.2v18.7 C45.3,48.7,46.3,49.7,47.5,49.7z"/></g></g>`,
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.22, T${9.5},${9.5}`);
        break;
      case 'SIGNAL':
        typeIcon = Snap.parse(
          '<g id="typeicon"><polygon class="start_event__signalicon" points="50 15, 100 100, 0 100"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.17, T${11.5},${8.5}`);
        break;
      case 'NONE':
        typeIcon = Snap.parse('<g id="typeicon"></g>');
        typeIconGroup = typeIcon.select('g');
        break;
      case 'ERROR':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="end_event__typeicon" transform="scale(1, -1) translate(0, -100)" d="M0,0c39,114,77,228,116,342c42-55,85-110,128-166c34,70,68,139,102,209c-27-124-55-248-82-372 c-45,51-89,103-134,154C87,111,43,55,0,0z"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.07, T${8},${25}`);
        break;
      case 'MULTIPLE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><polygon class="intermediate_event__multipleicon" points="294,3 585.246118,214.602691 474,556.983037 114,556.983037 2.753882,214.602691"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.04, T${8},${8}`);
        break;
      default:
        break;
    }
    return typeIconGroup;
  }

  _updateDetailTypeIcon() {
    // delete old icon
    const oldIcon = this._graphic.select('#typeicon');
    oldIcon.remove();
    // append new icon
    const typeIconGroup = this._drawEventDetailTypeIcon();
    typeIconGroup.appendTo(this._graphic);
  }
}
