import throttle from 'lodash/throttle';
import * as Snap from 'snapsvg-cjs';
import EventEmitter from 'eventemitter3';
import Config from 'config';
import Graph from 'graph-data-structure';

import PanZoomManager from './editor/panZoomManager';
import HistoryManager from './editor/historyManager';
import PoolManager from './editor/poolManager';
import LaneManager from './editor/laneManager';
import AlignManager from './editor/alignManager';
import ClipboardManager from './editor/clipboardManager';
import GridManager from './editor/gridManager';
import LassoManager from './editor/lassoManager';
import KeyManager from './editor/keyManager';
import ConnectorManager from './editor/connectorManager';
import BoundaryEventsManager from './editor/boundaryEventsManager';
import ElementFactory from './editor/elementFactory';
// eslint-disable-next-line import/extensions
import DocManager from './editor/docManager-APP_TARGET';
import {
  numberOfSelectedElements,
  getSelected,
  getProcessElementByID,
  getElementsOfType,
  getLastYElBounds,
} from './utils/util';

// eslint-disable-next-line import/extensions
import Panels from './ui/panels/panels-APP_TARGET';
import Parser from './parser';

export default class Editor extends EventEmitter {
  constructor() {
    super();
    this._paper = Snap('#app');
    this._paper.mousedown(this._onPaperClick, this);

    this._keyManager = new KeyManager(this);
    this._gridManager = new GridManager(this, Config.GRID_SIZE);
    this._historyManager = new HistoryManager(this);
    const btnRedo = document.querySelector('#redo_btn');
    const btUndo = document.querySelector('#undo_btn');

    this._historyManager.on('undo', (isAvailable) => {
      if (!isAvailable) {
        btUndo.setAttribute('data-state', 'disabled');
      } else {
        btUndo.setAttribute('data-state', 'enabled');
      }
    });

    this._historyManager.on('redo', (isAvailable) => {
      if (!isAvailable) {
        btnRedo.setAttribute('data-state', 'disabled');
      } else {
        btnRedo.setAttribute('data-state', 'enabled');
      }
    });

    this._panZoomManager = new PanZoomManager(this, true);
    this._panZoomManager.on('zoomed', () => {
      // reposition context menu on selected
      this._repositionCtxMenu();
    });
    this._panZoomManager.on('panned', () => {
      // reposition context menu on selected
      this._repositionCtxMenu();
    });
    this._poolManager = new PoolManager(this);
    this._laneManager = new LaneManager(this);
    this._alignManager = new AlignManager(this, this._historyManager);
    this._clipboardManager = new ClipboardManager(this, this._historyManager);
    this._lassoManager = new LassoManager(this);
    this._connectorManager = new ConnectorManager(this, this._historyManager);
    this._boundaryEventsManager = new BoundaryEventsManager(this);
    this._boundaryEventsManager.on('boundary_attached', (el) => {
      if (this.elementSelected) {
        if (this.elementSelected.elementType === Config.INTERMEDIATE_EVENT) {
          this._panels.show(Config.INTERMEDIATE_EVENT, el);
        }
      }
    });
    this._elementFactory = new ElementFactory(this);
    this._docManager = new DocManager(this);
    this._panels = new Panels(this);
    this._panels.on('resize', () => {
      // panel open/close
    });
    this._parser = null;

    this._elements = [];
    this._connections = [];
    this._graph = Graph();
    this._idIndex = 1;
    this._elementSelected = null;
    this._firstSelectedElementFromMultiple = null;
    this._currentlySelectedNumber = 0;
    this._isSnapToGridOn = true;
    this._selectedElementFirstMove = false;
    this._elementsSelectedToMove = [];
    this._mode = Config.HAND_MODE;
    this._workflow = null;
    // markers go in defs
    this._startMarker = null;
    this._endMarker = null;
    this._expressionMarker = null;
    this._wizardMarker = null;
    this._fileName = null;
    this._THROTTLE_CONNECTORS_MS = 150;

    // create viewport layer
    this._viewport = this._paper.group();
    this._viewport.attr({
      class: 'viewPort',
    });

    // create menu layer
    this._ctxMenuLayer = this._paper.group();
    this._ctxMenuLayer.attr({
      class: 'ctxmenu',
      id: 'ctxMenuLayer',
    });

    this.throttledUpdateConnectors = throttle(
      this.updateElementConnectors,
      this._THROTTLE_CONNECTORS_MS,
    );
  }

  set idIndex(value) {
    this._idIndex = value;
  }

  get idIndex() {
    return this._idIndex;
  }

  set fileName(value) {
    this._fileName = value;
  }

  get fileName() {
    return this._fileName;
  }

  setTitle(version, filename) {
    // set the titlebar for facilis to recognize filename
    if (Config.ENV === 'Offline') {
      document.title = `Apia Facilis ${version} - ${filename}`;
    }
  }

  buildEmptyProcess(version = '', filename = '', proAction = 'C') {
    this.setTitle(version, filename);
    // reset ids
    /**
     *  because is need to have a margin to
     *  differentiate the IDs of the activities
     *  start in "1000"
     */
    this.idIndex = 1000;
    // creates empty map
    this._clearEditorElements();
    this._historyManager.init();
    this._panZoomManager.init();
    this._gridManager.init();
    this.createDefMarkers();
    this._parser = null;
    // create workflow element only
    if (!this.workflow) {
      const processId = this.idIndex++;
      const properties = {
        id: processId,
        proAction,
        defaultPool: {
          id: this.idIndex++,
          processId,
        },
        packageId: this.idIndex++,
      };
      this._workflow = this.createElement(Config.WORKFLOW, 0, 0, properties, false, false);
    }
    this._panels.show(Config.WORKFLOW, this.workflow);
  }

  buildProcess(xpdl, version = '', filename = '') {
    this.setTitle(version, filename);
    // builds the map from the xpdl
    this._clearEditorElements();
    this._historyManager.init();
    this._panZoomManager.init();
    this._gridManager.init();
    this.createDefMarkers();
    this._parser = new Parser(xpdl, this);
    this._parser.on('xpdl-error', (error) => {
      console.error('Error Parsing XPDL', error);
    });
    this._parser.on('xpdl-parsed', () => {
      // console.log('XPDL parsed');
      // insert first the workflow
      if (this.elements.length > 1) {
        const [workflow] = getElementsOfType(Config.WORKFLOW, this.elements);
        const newElements = [];
        newElements.push(workflow);
        this.elements.forEach((element) => {
          if (element.elementType !== Config.WORKFLOW) {
            newElements.push(element);
          }
        });
        this.elements = newElements;
      }
      // show workflow panel by default
      this._panels.show(Config.WORKFLOW, this.workflow, Config.ENV === 'Offline');
    });
    // get the Process Map
    this._parser.buildProcessMap();
  }

  buildXMLProcess() {
    // returns the xpdl map
    this._parser = new Parser('', this);
    return this._parser.buildXmlProcessMap();
  }

  set mode(value) {
    if (value === Config.HAND_MODE) {
      this._lassoManager.enableLasso(false);
      this._connectorManager.enableConnection(false);
      this._panZoomManager.enablePanning(true);
    } else if (value === Config.LASSO_MODE) {
      this._panZoomManager.enablePanning(false);
      this._connectorManager.enableConnection(false);
      this._lassoManager.enableLasso(true);
    } else if (value === Config.CONNECTOR_MODE) {
      this._lassoManager.enableLasso(false);
      this._panZoomManager.enablePanning(false);
      this._connectorManager.enableConnection(true);
    }
    this._mode = value;
  }

  get mode() {
    return this._mode;
  }

  get workflow() {
    return this._workflow;
  }

  set workflow(value) {
    this._workflow = value;
  }

  get elements() {
    return this._elements;
  }

  set elements(value) {
    this._elements = value;
  }

  get elementSelected() {
    return this._elementSelected;
  }

  createElement(type, x, y, properties, saveHistory, isFromToolbar) {
    if (properties && parseInt(properties.id, 10) > this.idIndex) {
      this.idIndex = parseInt(properties.id, 10);
    } else if (!properties) {
      this.idIndex++;
    }

    if (saveHistory === undefined) {
      saveHistory = true;
    }
    const element = this._elementFactory.createElement(
      type,
      this.idIndex,
      x,
      y,
      this._viewport,
      properties,
      true,
    );

    if (!element) return;

    // graph used to tabulate between elements
    if (element.elementType === Config.CONNECTOR) {
      const fromEl = getProcessElementByID(element.properties.from, this.elements);
      const toEl = getProcessElementByID(element.properties.to, this.elements);

      if (fromEl && toEl) {
        let fw = 5;
        if (fromEl.elementType === Config.START_EVENT) {
          fw = 10;
        } else if (toEl.elementType === Config.END_EVENT) {
          fw = 1;
        }
        this._graph.addNode(element.properties.from);
        this._graph.addNode(element.properties.to);
        this._graph.addEdge(element.properties.from, element.properties.to, fw);
      } else {
        console.log(
          `element not found: from: ${element.properties.from} To: ${element.properties.to}`,
        );
      }
    }

    element.on('element_mouse_down', (event) => {
      this._currentlySelectedNumber = numberOfSelectedElements(this._elements);
      if (!event.shiftKey && this._currentlySelectedNumber === 0) {
        element.selected = true;
        this._currentlySelectedNumber = 1;
      }
      if (!event.shiftKey && !element.selected && this._currentlySelectedNumber >= 1) {
        // select single element
        this.unSelectAllElements();
        element.selected = true;
        this._currentlySelectedNumber = 1;
      }
      if (event.shiftKey && this._currentlySelectedNumber >= 0) {
        // shift select or unselect multiple, skip lanes
        if (this.mode !== Config.CONNECTOR_MODE) {
          if (element.selected) {
            element.selected = false;
            this._currentlySelectedNumber--;
          } else {
            element.selected = true;
            this._currentlySelectedNumber++;
          }
        }
      }
    });

    element.on('element_move_start', () => {
      // Move multiple selection
      if (this._currentlySelectedNumber > 1 && element.selected) {
        this._moveStartMultipleSelection(element);
      } else if (this._currentlySelectedNumber === 1 && element.selected) {
        // if pool move children also
        this._poolManager.moveStart(element);
        // check if any boundary events are attached
        this._boundaryEventsManager.moveStart(element);
      }
      // connect line if on connect mode
      this._connectorManager.connect(element);
      this._selectedElementFirstMove = true;
    });

    element.on('element_moving', () => {
      if (this._selectedElementFirstMove === true) {
        element._contextMenu.hide();
        this._historyManager.save();
      }
      this._selectedElementFirstMove = false;

      if (this._currentlySelectedNumber === 1) {
        // move pool and contents
        this._poolManager.moving(element);
        // move any attached boundary events
        this._boundaryEventsManager.moving(element);
      }
      // Move multiple selection
      if (this._currentlySelectedNumber > 1 && element.selected) {
        this._moveMultipleSelection(element);
      }
      // update any connectors for this element
      // throttled version
      this.throttledUpdateConnectors(element);
      // live version
      // this.updateElementConnectors(element);
    });

    element.on('element_move_end', () => {
      if (this._currentlySelectedNumber > 1 && element.selected) {
        this._moveEndMultipleSelection(element);
        element._contextMenu.hide();
      } else if (this._currentlySelectedNumber === 1) {
        this._poolManager.moveEnd(element);
        // move end any boundary events
        this._boundaryEventsManager.moveEnd(element);
        if (this._mode === Config.CONNECTOR_MODE) {
          element._contextMenu.hide();
        } else {
          element._contextMenu.show();
        }
      }
    });

    element.on('element_no_move_end', () => {
      // element was dropped back to initial place
      if (this._currentlySelectedNumber === 1 && element.selected) {
        this._poolManager.noMoveEnd(element);
        this._boundaryEventsManager.noMoveEnd(element);
        if (this._mode === Config.CONNECTOR_MODE) {
          element._contextMenu.hide();
        } else {
          element._contextMenu.show();
        }
      }
    });

    element.on('element_snap_to_grid', () => {
      // fires on snap to grid function called
      this.updateElementConnectors(element);
    });

    element.on('element_deleted', () => {
      if (element.elementType === Config.LANE) {
        this._laneManager.onLaneDeleted(element);
      }
      if (element.elementType === Config.POOL) {
        this._poolManager.onElementDelete(element);
      }
      this._elementSelected = null;
      this._panels.hide(element.elementType);
      this._panels.show(Config.WORKFLOW, this.workflow);
      this._connectorManager.reset();
    });

    element.on('element_on_remove_button_clicked', () => {
      this._deleteElement(element);
    });

    element.on('element_onpropertychange', () => {
      this._historyManager.save();
    });

    element.on('connector_point_move_start', () => {
      this._historyManager.save();
      element._contextMenu.hide();
    });

    element.on('connector_point_move_end', () => {
      if (numberOfSelectedElements(this._elements) === 1) {
        element._contextMenu.show();
      }
    });

    element.on('connector_point_no_move_end', () => {
      if (numberOfSelectedElements(this._elements) === 1) {
        element._contextMenu.show();
      }
    });

    element.on('connector_point_new', () => {
      this._historyManager.save();
    });

    element.on('connector_point_deleted', () => {
      this._historyManager.save();
    });

    element.on('element_selected', () => {
      if (numberOfSelectedElements(this._elements) === 1) {
        this._elementSelected = element;
        this._firstSelectedElementFromMultiple = element;
        this._panels.show(element.elementType, element);
        this._panels.hide(Config.WORKFLOW);
        if (this._mode !== Config.CONNECTOR_MODE) {
          element._contextMenu.show();
        }
      } else if (numberOfSelectedElements(this._elements) > 1) {
        // multiple selected
        if (this._firstSelectedElementFromMultiple) {
          this._panels.hide(this._firstSelectedElementFromMultiple.elementType);
        }
        this._panels.hide(Config.WORKFLOW);
        // hide context menu when multiple selected
        const selectedArray = getSelected(this._elements);
        for (let h = 0, len = selectedArray.length; h < len; ++h) {
          if (selectedArray[h].elementType !== Config.WORKFLOW) {
            selectedArray[h]._contextMenu.hide();
          }
        }
      } else if (numberOfSelectedElements(this._elements) === 0) {
        // none selected
        this._panels.hide(this._firstSelectedElementFromMultiple.elementType);
        this._panels.show(Config.WORKFLOW, this.workflow);
      }
    });

    element.on('element_unselected', () => {
      element._contextMenu.hide();
      this._elementSelected = null;
      this._panels.hide(element.elementType);
    });

    element.on('lane_resize_start', () => {
      this._historyManager.save();
      this._laneManager.resizeStart(element);
    });

    element.on('lane_resizing', (height) => {
      this._laneManager.resize(element, height);
    });

    element.on('lane_resize_end', () => {
      this._laneManager.resizeEnd(element);
      if (this._mode !== Config.CONNECTOR_MODE) {
        element._contextMenu.show();
      }
    });

    element.on('element_resize_start', () => {
      this._historyManager.save();
      if (element.elementType === Config.POOL) {
        this._poolManager.onResizeStart();
      }
      if (element.elementType === Config.TEXT || element.elementType === Config.GROUP) {
        element.onResizingStart();
      }
    });

    element.on('element_resizing', (width, height) => {
      if (element.elementType === Config.POOL) {
        this._poolManager.onResize(element, width, height);
      }
      if (element.elementType === Config.TEXT || element.elementType === Config.GROUP) {
        element.onResizing(width, height);
        this.updateElementConnectors(element);
      }
    });

    element.on('element_resize_end', () => {
      if (element.elementType === Config.POOL) {
        if (this._mode !== Config.CONNECTOR_MODE) {
          element._contextMenu.show();
        }
        this._poolManager.onResizeEnd();
        this.updateElementConnectors(element);
      }
      if (element.elementType === Config.TEXT || element.elementType === Config.GROUP) {
        if (this._mode !== Config.CONNECTOR_MODE) {
          element._contextMenu.show();
        }
        element.onResizeEnd();
      }
    });

    element.on('element_on_connect_button', () => {
      // connect context button pressed
      element._contextMenu.hide();
      this._connectorManager.connectOneTime(element);
    });

    element.on('element_on_open_subprocess', () => {
      this._openSubprocess(element);
      if (Config.ENV !== 'Offline') this._openProcessInNewTab(element);
    });

    element.on('process_name_changed', () => {
      this.fileName = this.workflow.name;
    });

    element.on('element_on_append', (elementType) => {
      let newElement;
      let posX = 0;
      let extraY = 0;
      let centerY = 0;
      const RIGHT_OFFSET = 70; // to fit grid
      const bounds = element.selectionBox.node.getBoundingClientRect();
      const bottomRight = this._viewport.getInverseScreenPoint(bounds.right, bounds.bottom);
      const leftTop = this._viewport.getInverseScreenPoint(bounds.left, bounds.top);
      const maxYEl = getLastYElBounds(element, this.elements);

      if (maxYEl) {
        const rightElLeftTop = this._viewport.getInverseScreenPoint(maxYEl.left, maxYEl.bottom);
        posX = rightElLeftTop.x;
        extraY = rightElLeftTop.y;
        centerY = 60;
      } else {
        posX = bottomRight.x + RIGHT_OFFSET;
        centerY = Math.round(leftTop.y + element.height / 2);
      }

      switch (elementType) {
        case Config.TASK:
        case Config.SUBPROCESS:
          newElement = this.createElement(
            elementType,
            posX,
            centerY - Config.TASK_SIZE.height / 2 + extraY,
          );
          break;
        case Config.GATEWAY:
        case Config.START_EVENT:
        case Config.INTERMEDIATE_EVENT:
        case Config.END_EVENT:
          newElement = this.createElement(
            elementType,
            posX,
            centerY - Config.GATEWAY_SIZE.height / 2 + extraY,
          );
          break;
        case Config.TEXT:
          newElement = this.createElement(
            Config.TEXT,
            posX,
            leftTop.y - Config.TEXT_SIZE.height * 2,
          );
          break;
        case Config.LANE:
          this._historyManager.save();
          newElement = this._laneManager.createLane(element);
          element.removeResizeHandles();
          element.drawResizeHandles();
          break;
        default:
          break;
      }

      // deselect current element and select new one here
      if (elementType !== Config.LANE && elementType !== Config.CONNECTOR) {
        // create new connection
        let connType;
        let points;
        if (newElement.elementType === Config.TEXT) {
          connType = Config.CONNECTOR_TYPE_ASSOCIATION;
          points = [
            { x: 0, y: 0 },
            { x: 0, y: 0 },
          ];
        } else {
          connType = Config.CONNECTOR_TYPE_TRANSITION;
          if (maxYEl) {
            points = [
              { x: 0, y: 0 },
              {
                x: element.x + element.width / 2 - Config.ELEMENT_SELECT_SIZE / 2,
                y: newElement.y + newElement.height / 2 - Config.ELEMENT_SELECT_SIZE / 2,
              },
              { x: 0, y: 0 },
            ];
          } else {
            points = [
              { x: 0, y: 0 },
              { x: 0, y: 0 },
            ];
          }
        }
        const props = {
          id: ++this.idIndex,
          from: element.id,
          to: newElement.id,
          type: connType,
          points,
        };
        if (connType === Config.CONNECTOR_TYPE_ASSOCIATION) props.direction = 'none';
        if (connType === Config.CONNECTOR_TYPE_TRANSITION) props.class = 'N';
        this.createElement(Config.CONNECTOR, null, null, props, false, false);
        this.unSelectAllElements();
        newElement.selected = true;
      }
    });

    // snap to grid if not in grid
    if (element.elementType !== Config.WORKFLOW) {
      element.snap(this._isSnapToGridOn, Config.GRID_SIZE);
    }

    if (saveHistory) {
      this._historyManager.save();
    }

    // check if element dropped on boundary
    this._boundaryEventsManager.moveEnd(element);

    if (element.elementType === Config.CONNECTOR) {
      this._connections.push(element);
    }
    this._elements.push(element);
    // Restore Panel Selection from history
    if (numberOfSelectedElements(this._elements) === 1) {
      if (element.selected && this._elementSelected) {
        // hide previous selected
        this._panels.hide(this._elementSelected.elementType);
        // refresh reference for element selected
        this._elementSelected = element;
        // show panel again for selected when undo/redo
        this._panels.show(element.elementType, element);
      } else if (element.selected) {
        // when selecting workflow (paper) and hitting
        // undo should re-show previously selected panel
        this._elementSelected = element;
        this._panels.hide(Config.WORKFLOW);
        this._panels.show(element.elementType, element);
      }
    } else if (this._elementSelected) {
      // none selected, hide previous panel and show Workflow panel
      this._panels.hide(this._elementSelected.elementType);
      this._elementSelected = null;
      this._panels.show(Config.WORKFLOW, this.workflow);
    } else if (numberOfSelectedElements(this._elements) > 1) {
      // redo after deleting more than one el
      this._panels.hide(element.elementType);
    }

    if (isFromToolbar) {
      // select newly created element
      this.unSelectAllElements();
      element.selected = true;
      this._elementSelected = element;
    }
    // eslint-disable-next-line consistent-return
    return element;
  }

  createDefMarkers() {
    const startArrow = this._paper
      .polyline('0,8, 4,0, 8,8')
      .attr({ class: 'arrow_marker' })
      .transform('r-90');
    this._startMarker = startArrow.marker(0, 0, 10, 10, 1, 4);
    const endArrow = this._paper
      .polyline('0,8, 4,0, 8,8')
      .attr({ class: 'arrow_marker' })
      .transform('r90');
    this._endMarker = endArrow.marker(0, 0, 10, 10, 7, 4);
    const rhomboid = this._paper
      .polygon(5, 0, 0, 5, 5, 10, 10, 5)
      .attr({ class: 'expression_marker' });
    this._expressionMarker = rhomboid.marker(0, 0, 10, 10, 0, 5);
    const doubleArrow = this._paper
      .polyline('0,8, 4,0, 8,8, 4,8, 12,24, -4,24, 4,8, 0,8')
      .attr({ class: 'arrow_marker' })
      .transform('r90');
    this._wizardMarker = doubleArrow.marker(0, 0, 40, 40, 15, 12);
  }

  _onPaperClick(e) {
    if (e.target.id === 'app') {
      this.unSelectAllElements();
      this._panels.show(Config.WORKFLOW, this.workflow);
    }
  }

  _repositionCtxMenu() {
    const elArray = getSelected(this.elements);
    if (elArray.length === 1) elArray[0]._contextMenu.show();
  }

  unSelectAllElements() {
    for (let i = 0, len = this._elements.length; i < len; i++) {
      this._elements[i].selected = false;
    }
    this._elementSelected = null;
  }

  selectAllElements() {
    for (let i = 0, len = this._elements.length; i < len; i++) {
      if (this._elements[i].elementType !== Config.WORKFLOW) {
        this._elements[i].selected = true;
      }
    }
  }

  _moveStartMultipleSelection(element) {
    if (element.elementType === Config.POOL) {
      this._poolManager.moveStart(element);
    } else {
      this._elementsSelectedToMove = getSelected(this.elements);
    }
  }

  _moveMultipleSelection(element) {
    let el;
    let elType;
    if (element.elementType === Config.POOL) {
      this._poolManager.moving(element);
    } else {
      for (let i = 0, len = this._elementsSelectedToMove.length; i < len; i++) {
        el = this._elementsSelectedToMove[i];
        elType = el.elementType;
        if (
          el.selected
          && elType !== Config.LANE
          && elType !== Config.POOL
          && elType !== Config.CONNECTOR
          && el.id !== element.id
        ) {
          // move all selected elements
          el._contextMenu.hide();
          this._updateElementPosition(el, element);
        } else if (
          this._elementsSelectedToMove.some((e) => e.elementType === Config.POOL)
          && elType === Config.LANE
          && elType === Config.POOL
          && el.id !== element.id
        ) {
          el._contextMenu.hide();
        } else if (el.selected && elType === Config.CONNECTOR) {
          // move control points only if both from and to are selected
          this._updatePointSegments(el, element);
        }
        if (el.selected && elType === Config.TASK && el.id !== element.id) {
          // move boundary event
          for (let a = 0, l = el.boundaryEvents.length; a < l; a++) {
            const bEvent = getProcessElementByID(el.boundaryEvents[a], this._elements);
            if (!bEvent.selected) {
              // move all attached boundary events too
              this._updateElementPosition(bEvent, element);
            }
          }
        }
      }
    }
  }

  _moveEndMultipleSelection(element) {
    let el;
    let elType;
    if (element.elementType === Config.POOL) {
      this._poolManager.moveEnd(element);
    } else {
      for (let i = 0, len = this._elementsSelectedToMove.length; i < len; i++) {
        el = this._elementsSelectedToMove[i];
        elType = el.elementType;
        if (
          el.selected
          && elType !== Config.LANE
          && elType !== Config.POOL
          && elType !== Config.CONNECTOR
        ) {
          this._updateElementEndPosition(el, element);
        } else if (
          this._elementsSelectedToMove.some((e) => e.elementType === Config.POOL)
          && elType === Config.LANE
          && elType === Config.POOL
        ) {
          // this._updateElementEndPosition(el, element);
          this.toggleElementConnectorsVisibility(true);
        } else if (el.selected && elType === Config.CONNECTOR) {
          // reset moving element
          el.resetMovingPointSegment();
        }
        if (el.selected && elType === Config.TASK) {
          for (let a = 0, l = el.boundaryEvents.length; a < l; a++) {
            const elB = getProcessElementByID(el.boundaryEvents[a], this._elements);
            if (!elB.selected) {
              this._updateElementEndPosition(elB, element);
            }
          }
        }
      }
    }
    this._elementsSelectedToMove = [];
  }

  _updatePointSegments(connector, element) {
    // when both to and from move together update segment points
    connector.updateSegmentsPosition(element);
  }

  _updateElementPosition(el, element) {
    el.moveElement(Math.round(element.mx + el.x), Math.round(element.my + el.y));
    this.updateElementConnectors(el);
  }

  _updateElementEndPosition(el, element) {
    el.moveElementEnd(Math.round(element.mx + el.x), Math.round(element.my + el.y));
  }

  updateElementConnectors(element, offset = { x: 0, y: 0 }, isStart = false, isPoolMoving = false) {
    const elMovedID = element.properties.id;
    for (let c = 0, len = this._connections.length; c < len; ++c) {
      const props = this._connections[c].properties;
      if (
        parseInt(props.from, 10) === parseInt(elMovedID, 10)
        || parseInt(props.to, 10) === parseInt(elMovedID, 10)
      ) {
        if (!isStart) {
          this._connections[c].updateConnection(offset, isPoolMoving);
        } else {
          this._connections[c].updateConnectionStart();
        }
      }
    }
  }

  toggleElementConnectorsVisibility(isVisible) {
    for (let c = 0, len = this._connections.length; c < len; ++c) {
      if (!isVisible) {
        this._connections[c].graphic.addClass('connector--hidden');
      } else {
        this._connections[c].graphic.removeClass('connector--hidden');
      }
    }
  }

  _deleteElement(element, saveHistory = true) {
    for (let i = 0, len = this._elements.length; i < len; i++) {
      if (this._elements[i] === element) {
        if (saveHistory) {
          this._historyManager.save();
        }
        this._elements.splice(i, 1);
        this._graph.removeNode(element.id);
        // delete all connectors attached to element
        this._deleteAttachedConnectors(element);
        // delete all boundary events as well
        if (element.elementType === Config.TASK) {
          for (let b = 0, lenB = element.boundaryEvents.length; b < lenB; b++) {
            const bEvent = getProcessElementByID(element.boundaryEvents[b], this._elements);
            this._deleteElement(bEvent, false);
          }
        }
        element.deleteEl();
        if (element.elementType === Config.CONNECTOR) {
          this._deleteFromConnectorsList(element);
        }
      }
    }
  }

  _deleteAttachedConnectors(element) {
    const elMovedID = element.properties.id;
    let i = this._connections.length;
    while (i--) {
      // delete from connections array
      if (
        this._connections[i].properties.from === elMovedID
        || this._connections[i].properties.to === elMovedID
      ) {
        const connector = this._connections[i];
        this._graph.removeEdge(
          this._connections[i].properties.from,
          this._connections[i].properties.to,
        );
        this._connections.splice(i, 1);
        let e = this._elements.length;
        // delete also from elements array
        while (e--) {
          if (this._elements[e] === connector) {
            this._elements.splice(e, 1);
          }
        }
        connector.deleteEl();
      }
    }
  }

  _deleteFromConnectorsList(element) {
    for (let c = 0, connLen = this._connections.length; c < connLen; c++) {
      if (this._connections[c] === element) {
        this._connections.splice(c, 1);
      }
    }
  }

  deleteMultipleSelectedElements() {
    this._historyManager.save();
    let i = this._elements.length;
    while (i--) {
      if (this._elements[i]) {
        if (this._elements[i].selected) {
          const el = this._elements[i];
          this._deleteElement(el, false);
          this._graph.removeNode(el.id);
        }
      }
    }
  }

  zoomIn() {
    this._panZoomManager.zoomIn();
  }

  zoomOut() {
    this._panZoomManager.zoomOut();
  }

  resetView() {
    this._panZoomManager.resetViewPort();
    this._repositionCtxMenu();
  }

  undoAction() {
    this._historyManager.undo();
  }

  redoAction() {
    this._historyManager.redo();
  }

  alignSelectionLeft() {
    this._alignManager.alignLeft();
  }

  alignSelectionRight() {
    this._alignManager.alignRight();
  }

  alignSelectionTop() {
    this._alignManager.alignTop();
  }

  alignSelectionBottom() {
    this._alignManager.alignBottom();
  }

  alignSelectionHorizontal() {
    this._alignManager.alignHorizontalCenter(getSelected(this.elements), true);
  }

  alignSelectionVertical() {
    this._alignManager.alignVerticalCenter();
  }

  distSelectionHorizontal() {
    this._alignManager.distHorizontal();
  }

  distSelectionVertical() {
    this._alignManager.distVertical();
  }

  copyElements() {
    this._clipboardManager.copy();
  }

  pasteElements() {
    this._clipboardManager.paste();
  }

  showGrid(toShow) {
    if (toShow) {
      this._gridManager.show();
    } else {
      this._gridManager.hide();
    }
  }

  snapToGrid(value) {
    this._isSnapToGridOn = value;
  }

  _clearEditorElements() {
    // remove all svg child nodes but keep grid
    const viewportElements = this._viewport.children();
    for (let i = 0, len = viewportElements.length; i < len; i++) {
      const el = viewportElements[i];
      if (!el.hasClass('grid_bg')) {
        el.remove();
      }
    }
    // clean ctx menu layer
    this._ctxMenuLayer.clear();
    this._elements = [];
    this._connections = [];
    this._graph = Graph();
    this._workflow = null;
  }

  doTabProcessElements() {
    const selected = getSelected(this.elements)[0] || this.elements[0];
    const graphArr = this._graph.topologicalSort();
    let index = graphArr.findIndex((el) => parseInt(el, 10) === parseInt(selected.id, 10));
    index++;
    if (index === graphArr.length) {
      index = 0;
    }
    getProcessElementByID(selected.id, this.elements).selected = false;
    getProcessElementByID(parseInt(graphArr[index], 10), this.elements).selected = true;
    getProcessElementByID(parseInt(graphArr[index], 10), this.elements).toFront();
  }

  loadLocalXPDL(evt) {
    // loads xpdl file from file input
    const { files } = evt.target;
    if (files[0]) {
      const reader = new FileReader();
      reader.readAsText(files[0], 'UTF-8');
      reader.onload = (event) => {
        this.buildProcess(event.target.result);
      };
      reader.onerror = () => {
        console.warn('error reading file');
      };
    }
  }

  saveLocalXPDL() {
    // downloads xpdl file directly to browser
    this._docManager.generateXPDLFile();
  }

  saveToPDF() {
    // downloads PDF Documentation directly to browser
    this._docManager.generateProcessDocumentation();
  }

  getPDF() {
    // for facilis
    return this._docManager.generateProcessDocumentation();
  }

  saveToImage() {
    // downloads PNG directly to browser
    this._docManager.generateProcessPNG();
  }

  async getPNGData() {
    // for facilis
    const pngDataURL = await this._docManager.getPNGDataURL();
    return pngDataURL;
  }

  _openSubprocess(el) {
    // for facilis integration
    this.emit('editor_on_open_subprocess', el.properties);
  }

  _openProcessInNewTab(element) {
    if (element && Config.ENV !== 'Offline') {
      const subprocessId = element.properties.activitySetId || element.properties.subprocessId;
      if (subprocessId !== 1 && element.name.toLowerCase() !== 'default_process') {
        const { tabContainer } = window.top;
        const baseUrl = Config.SUBPROCESS_OPEN_NEW_TAB_URL;
        const [, tokenId] = Config.TAB_KEYS.split('&tokenId=');
        const tabId = Date.now();
        const subpUrl = `${baseUrl}&tokenId=${tokenId}&tabId=${tabId}&id=${subprocessId}_0`;
        tabContainer.addNewTab(element.name, subpUrl, null);
      }
    }
  }
}
