import Config from 'config';
import Snap from 'snapsvg-cjs';
import Element from './element';

export default class DataElement extends Element {
  constructor(type, id, x, y, viewport, properties, isMovable, menu) {
    super(type, id, viewport, x, y, properties, isMovable, menu);
    this._shapeNamePrefix = '';
    this._typeId = type;
    const dataName = this.elementType.split('Data');
    if(this._properties.name===this._typeId){
      this._properties.name = `Data ${dataName[1]}`;
    }
  }

  get dataName() {
    return this._properties.name;
  }

  get state() {
    return this._properties.state || '';
  }

  set state(value) {
    this.emit('element_onpropertychange');
    this._properties.state = value;
    const stateValue = this._getCurrentState(value);
    this.updateTextLabel(
      `${this._shapeNamePrefix}__group__text__state`,
      stateValue,
    );
  }

  set name(value) {
    this.emit('element_onpropertychange');
    this._properties.name = value;
    this.updateTextLabel(`${this._shapeNamePrefix}__group__text`, value);
  }

  get collection() {
    return this._properties.collection || false;
  }

  set collection(value) {
    this.emit('element_onpropertychange');
    this._properties.collection = value;
    this._updateCollIcon();
  }

  get capacity() {
    return this._properties.capacity || '';
  }

  set capacity(value) {
    this.emit('element_onpropertychange');
    this._properties.capacity = value;
  }

  get unlimit() {
    return this._properties.unlimit || false;
  }

  set unlimit(value) {
    this.emit('element_onpropertychange');
    this._properties.unlimit = value;
    if (value) delete this._properties.capacity;
  }

  _getCurrentState(value) {
    return value === undefined || value === '' ? '' : `[${value}]`;
  }

  drawElement(shape, name, transform) {
    this._shapeNamePrefix = name;
    const icon = Snap.parse(`<g id="icon">${shape}</g>`);
    const dataElIcon = icon.select('g').attr({
      class: `${name}__icon`,
    });

    dataElIcon.transform(transform);
    const bgBox = this._viewport
      .rect(0, 0, Config.DATA_SIZE.width, Config.DATA_SIZE.height)
      .attr({ stroke: 'none', fill: 'none' });
    const dataEl = dataElIcon.appendTo(this._viewport);
    const bounds = bgBox.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);
    const collIcon = this._drawCollectionIcon();

    const label = this.drawForeignObjLabel(
      -40,
      48,
      120,
      80,
      this._properties.name,
      this._getCurrentState(this._properties.state),
      `${name}__group__text`,
      `text_${this.id}`,
    );
    label.select('g').attr({
      class: `${name}__group`,
    });

    this._graphic = this._viewport
      .g(selectionBox, dataEl, collIcon, label, bgBox)
      .mousedown(this.onElementMouseDown)
      .attr({
        class: name,
        id: `${this._typeId}_${this._properties.id}`,
      });

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }
  }

  _drawCollectionIcon() {
    let collIcon;
    switch (this.collection) {
      case true:
        collIcon = Snap.parse(
          '<g id="collIcon"><path class="data__collIcon" transform="scale(1, -1) translate(0, -100)" d="M250 650l0-600 100 0 0 600-100 0z m200 0l0-600 100 0 0 600-100 0z m200 0l0-600 100 0 0 600-100 0z" /></g>',
        );
        break;
      case false:
      case '':
      case undefined:
        collIcon = Snap.parse('<g id="collIcon"></g>');
        break;
      default:
        break;
    }

    const collIconGroup = collIcon.select('g');
    collIconGroup.transform('s0.020, T10,34');
    return collIconGroup;
  }

  _updateCollIcon() {
    // delete old icon
    const oldIcon = this._graphic.select('#collIcon');
    oldIcon.remove();
    // append new icon
    const collIconGroup = this._drawCollectionIcon();
    collIconGroup.appendTo(this._graphic);
  }

  drawForeignObjLabel(x, y, width, height, title, state, classname, id) {
    const fobjectSVG = `<g><foreignObject 
      x="${x}"
      y="${y}"
      width="${width}" 
      height="${height}"
      id="${id}">
    <p class="${classname}">${title}</p>
    <p class="${classname}__state">${state}</p>
    </foreignObject></g>`;
    return Snap.parse(fobjectSVG);
  }
}
