import {} from '../utils/snaptoolkit';
import EventEmitter from 'eventemitter3';

export default class PanZoomManager extends EventEmitter {
  constructor(editor, isPanOn) {
    super();
    this._editor = editor;
    this._paper = editor._paper;
    this._viewport = null;
    this._lastX = 0;
    this._lastY = 0;
    this._MAX_ZOOM_OUT = 0.2;
    this._MAX_ZOOM_IN = 2;
    this._ZOOM_SCALE = 0.1;
    this._PAN_FACTOR_SCALE = 0.1;
    this._PAN_FACTOR_INIT = 1.5;
    this._pan_factor = this._PAN_FACTOR_INIT;
    this._pos = { x: 0, y: 0 };
    this._ZOOM_INIT = 1;
    this._zoom = this._ZOOM_INIT;

    this._mousemoveHandler = this._mousemoveHandler.bind(this);
    this._mouseupHandler = this._mouseupHandler.bind(this);

    this._paper.node.addEventListener('wheel', this._onWheel.bind(this));

    if (isPanOn) {
      this.enablePanning(true);
    }
  }

  init() {
    this._viewport = this._editor._viewport;
    this.resetViewPort();
  }

  resetViewPort() {
    this._pos.x = 0;
    this._pos.y = 0;
    this._zoom = this._ZOOM_INIT;
    this._pan_factor = this._PAN_FACTOR_INIT;
    this._viewport.transform('T0,0 S1');
  }

  enablePanning(isOn) {
    this._paper.removeClass('hand');
    this._paper.unmousedown(this._mousedownHandler);
    if (isOn) {
      this._paper.removeClass('selectionable');
      this._paper.removeClass('connectionable');
      this._paper.addClass('hand');
      this._paper.mousedown(this._mousedownHandler, this);
    } else {
      this._mouseupHandler();
    }
  }

  _mousedownHandler(event) {
    if (event.target === this._paper.node) {
      this._paper.removeClass('hand');
      this._paper.addClass('draggable');
      this._lastX = event.clientX;
      this._lastY = event.clientY;
      window.addEventListener('mousemove', this._mousemoveHandler, false);
      window.addEventListener('mouseup', this._mouseupHandler, false);
    }
  }

  _mousemoveHandler(event) {
    this._paper.removeClass('draggable');
    this._paper.addClass('dragging');
    event.preventDefault();
    this._pan(event.clientX - this._lastX, event.clientY - this._lastY);
    this._lastX = event.clientX;
    this._lastY = event.clientY;
  }

  _pan(dx, dy) {
    this._pos.x += dx * this._pan_factor;
    this._pos.y += dy * this._pan_factor;
    this._viewport.transform(`T${this._pos.x} ${this._pos.y} S${this._zoom}`);
    this.emit('panned');
  }

  _mouseupHandler() {
    this._paper.removeClass('dragging');
    this._paper.removeClass('draggable');
    this._paper.addClass('hand');
    window.removeEventListener('mousemove', this._mousemoveHandler);
    window.removeEventListener('mouseup', this._mouseupHandler);
  }

  zoomIn() {
    if (this._zoom < this._MAX_ZOOM_IN) {
      this._zoom += this._ZOOM_SCALE;
      this._pan_factor += this._PAN_FACTOR_SCALE;
      this._zoomIt();
    }
  }

  zoomOut() {
    if (this._zoom > this._MAX_ZOOM_OUT) {
      this._zoom -= this._ZOOM_SCALE;
      this._pan_factor -= this._PAN_FACTOR_SCALE;
      this._zoomIt();
    }
  }

  _zoomIt() {
    this._viewport.transform(`T${this._pos.x} ${this._pos.y} S${this._zoom}`);
    this.emit('zoomed');
  }

  _onWheel(evt) {
    evt.preventDefault();
    if (this._editor._keyManager._isCtrlPressed) {
      if (evt.deltaY < 0) {
        this.zoomIn();
      } else {
        this.zoomOut();
      }
    } else {
      this._pan(evt.deltaX * -1, evt.deltaY * -1);
    }
  }
}
