// -- Import declarations -- //
import EventEmitter from 'eventemitter3';
import sax from 'sax';
import builder from 'xmlbuilder';
import Config from 'config';
import cloneDeep from 'lodash/cloneDeep';
import uniqueId from 'lodash/uniqueId';
import format from 'date-format';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import Extractor from './extractor';
import { getElementsOfType, getConnectorsByFromTo } from './utils/util';

// -- Class parser declarations -- //

export default class Parser extends EventEmitter {
  constructor(xml = '', editor) {
    super();
    this.xml = xml;
    this.parser = sax.parser({
      normalize: true,
      strict: false,
      trim: true,
    });
    this.editor = editor;
    this.builder = builder;
    this.tagsTypes = {
      Task: 'Task',
      Event: 'Event',
      Start: 'Start',
      Intermediate: 'Intermediate',
      End: 'End',
      Gateway: 'Gateway',
      Connector: 'Connector',
      Pool: 'Pool',
      Lane: 'Lane',
      Subprocess: 'Subprocess',
      DataObject: 'DataObject',
      DataStore: 'DataStore',
      DataInput: 'DataInput',
      DataOutput: 'DataOutput',
      Group: 'Group',
      TextAnnotation: 'TextAnnotation',
      Artifact: 'Artifact',
      Activity: 'Activity',
      DataInputOutput: 'DataInputOutput',
      Workflow: 'Workflow',
    };
    this.xpdl = Config.ENV !== 'Offline' ? '' : 'xpdl:';
    // this.xpdl = 'xpdl:';
    this._id = 0;
  }

  set id(value) {
    this._id = value;
  }

  _toIntegerId(id) {
    return parseInt(id, 10);
  }

  _getConnectorByIdContains(elements, id) {
    const connectors = elements.filter((element) => {
      return element.elementType === Config.CONNECTOR;
    });
    const filtered = connectors.filter((connector) => {
      return connector.from === id || connector.to === id;
    });
    return filtered;
  }

  _checkBoundaryEvents(elements, element, newId) {
    const eventsAttached = elements.filter((evt) => {
      return (
        evt.elementType === Config.INTERMEDIATE_EVENT
        && evt.isAttached
        && evt.properties.target === element.id
      );
    });
    if (eventsAttached.length > 0) {
      eventsAttached.forEach((boundary) => {
        boundary.properties.target = newId;
      });
    }
  }

  _checkTaskSchedule(element, elements, newId) {
    if (element.elementType === Config.TASK) {
      const tasks = getElementsOfType(Config.TASK, elements);
      tasks.forEach((task) => {
        if (task.scheduleTask && !task.scheduleSubprocessName) {
          const { schedule } = task.properties;
          const equalId = parseInt(element.id, 10) === parseInt(schedule.task, 10);
          if (equalId) {
            schedule.task = newId;
          }
        }
      });
    }
  }

  _attachConnectors(elements) {
    elements.forEach((element) => {
      if (element.elementType !== Config.WORKFLOW && element.elementType !== Config.CONNECTOR) {
        element.properties.connectors = {
          connectorsFrom: getConnectorsByFromTo(element, elements, 'from'),
          connectorsTo: getConnectorsByFromTo(element, elements, 'to'),
        };
      }
    });
  }

  _updateConnectors(element, newId) {
    const { connectorsFrom, connectorsTo } = element.properties.connectors;
    this._updateIdEachProperties('from', newId, connectorsFrom);
    this._updateIdEachProperties('to', newId, connectorsTo);
    console.log(element.properties);
  }

  _updateIdEachProperties(prop, newId, connectors) {
    connectors.forEach((connector) => {
      connector.properties[prop] = newId;
      console.log(connector.properties);
    });
  }

  _elementsIdRemapping(elements) {
    // First attach connectors fro each elements
    this._attachConnectors(elements);
    console.log(elements);
    let newId = Config.START_ID_REMAPPING;
    let shortId = uniqueId();
    const workflow = elements.find((w) => w.elementType === Config.WORKFLOW);
    if (workflow) {
      const prop = workflow.properties;
      prop.packageId = shortId++;
      prop.defaultPool.id = shortId++;
      if (Config.ENV === 'Offline') {
        prop.id = shortId++;
        prop.defaultPool.processId = prop.id;
      }
      if (prop.documentation) prop.documentation.id = shortId++;
    }
    elements.forEach((element) => {
      const { elementType, properties } = element;
      if (elementType !== Config.WORKFLOW) {
        const condition = elementType !== Config.POOL
          && elementType !== Config.LANE
          && elementType !== Config.DATA_INPUT
          && elementType !== Config.DATA_OUTPUT
          && elementType !== Config.DATA_OBJECT
          && elementType !== Config.DATA_STORE
          && elementType !== Config.GROUP
          && elementType !== Config.TEXT
          && elementType !== Config.CONNECTOR;

        const { documentation } = properties;
        if (documentation) {
          documentation.id = shortId++;
        }
        // A connector don't have a connectors
        if (elementType !== Config.CONNECTOR) {
          this._updateConnectors(element, condition ? newId : shortId);
        }
        // Task is a only element that has an boundary events
        if (elementType === Config.TASK) {
          this._checkBoundaryEvents(elements, element, newId);
        }
        element.id = condition ? newId : shortId;
        if (condition) {
          newId++;
        } else {
          shortId++;
        }
      }
    });
    return [elements, shortId];
  }

  _existAnyParticipant(elements) {
    for (let i = 0; i < elements.length; i++) {
      const element = elements[i];
      if (this._isTask(element) && element.properties.groups) return true;
    }
    return false;
  }

  _existAnyArtifact(elements) {
    for (let i = 0; i < elements.length; i++) {
      const element = elements[i];
      if (this._isArtifact(element)) return true;
    }
    return false;
  }

  _existAnySubprocessEmbedded(elements) {
    for (let i = 0; i < elements.length; i++) {
      const element = elements[i];
      if (this._isSubprocess(element) && element.properties.type === 'embedded') return true;
    }
    return false;
  }

  _existAnyAssociation(elements) {
    for (let i = 0; i < elements.length; i++) {
      const element = elements[i];
      const { elementType } = element;
      if (
        elementType === Config.CONNECTOR
        && element.properties.type === Config.CONNECTOR_TYPE_ASSOCIATION
      ) return true;
    }
    return false;
  }

  buildXmlProcessMap() {
    const [elementList, shortId] = this._elementsIdRemapping(cloneDeep(this.editor.elements));
    let dataStoreId = shortId;
    const root = this._getPackageElement();
    let participants;
    if (this._existAnyParticipant(elementList)) {
      participants = this._getParticipantsElement(root);
    }
    const pools = this._getPoolsElement(root);
    let associations;
    let artifacts;
    if (this._existAnyArtifact(elementList)) {
      artifacts = this._getArtifactsElement(root);
    }
    if (this._existAnyAssociation(elementList)) {
      associations = this._getAssociationsElement(root);
    }
    const datastores = this._getDataStoresElement(root);
    const workflows = this._getWorkflowProcessesElement(root);
    // Add deafult pool
    const defaultPool = this._createPoolElement(pools, undefined, elementList);
    this._createNodeGraphicsInfosElement(defaultPool, undefined);

    // list of participants to avoid reduction of the associated element //
    const participantsList = [];

    const workflowProcess = this._getWorkflowProcessElement(workflows);
    this._getProcessHeaderElement(workflowProcess);
    let activitySets;
    if (this._existAnySubprocessEmbedded(elementList)) {
      activitySets = this._getActivitySetsElement(workflowProcess);
    }
    const activities = this._getActivitiesElement(workflowProcess);
    const dataInputOutputs = this._getDataInputOutputs(workflowProcess, elementList);
    const dataStoreReferences = this._getDataStoresReferencesElement(workflowProcess, elementList);
    const transitions = this._getTransitionsElement(workflowProcess);
    this._createtWorkflowProcessElement(workflowProcess, root, elementList);
    const reusableProcess = [];

    // identify first task
    this._identifyFirstTask(elementList);

    elementList.forEach((value) => {
      let type = value.elementType;
      if (this._isArtifact(value)) {
        type = this.tagsTypes.Artifact;
      }
      if (this._isActivity(value)) {
        type = this.tagsTypes.Activity;
      }
      if (this._isDataInput(value) || this._isDataOutput(value)) {
        type = this.tagsTypes.DataInputOutput;
      }
      switch (type) {
        case this.tagsTypes.Pool: {
          const pool = this._createPoolElement(pools, value, elementList);
          this._getLanesElement(pool, elementList);
          this._createDocumentationElement(pool, value);
          this._createNodeGraphicsInfosElement(pool, value);
          break;
        }
        case this.tagsTypes.Artifact: {
          const artifact = this._createArtifactAcoordingToType(artifacts, value);
          this._createDocumentationElement(artifact, value);
          this._createNodeGraphicsInfosElement(artifact, value);
          break;
        }
        case this.tagsTypes.DataStore: {
          const { datastore, newId } = this._createDataStoreElement(
            datastores,
            value,
            dataStoreId++,
          );
          this._createDocumentationElement(datastore, value);
          this._createDataStoreReference(dataStoreReferences, value, newId);
          break;
        }
        case this.tagsTypes.Activity: {
          const activity = this._createActivityAcoordingToType(
            activities,
            activitySets,
            value,
            participants,
            participantsList,
            reusableProcess,
          );
          this._createDocumentationElement(activity, value);
          this._createNodeGraphicsInfosElement(activity, value);
          break;
        }
        case this.tagsTypes.Connector: {
          const connector = this._createConnectorAcoordingToType(associations, transitions, value);
          this._createDocumentationElement(connector, value);
          this._createConnectorGraphicsInfosElement(connector, value);
          break;
        }
        case this.tagsTypes.DataInputOutput: {
          const dataInputOuput = this._createDataInputOrOutputElement(dataInputOutputs, value);
          this._createDocumentationElement(dataInputOuput, value);
          this._createNodeGraphicsInfosElement(dataInputOuput, value);
          break;
        }
        default:
          break;
      }
    });

    // If exist any reusable process -> print more workflowprocess tag for each subprocess
    if (reusableProcess.length > 0) {
      reusableProcess.forEach((reusable) => {
        const wReusableProcess = this._getWorkflowProcessElement(workflows);
        const { subprocessId, name } = reusable;
        wReusableProcess.att('Id', subprocessId);
        wReusableProcess.att('Name', name);
        this._getProcessHeaderElement(wReusableProcess);
      });
    }

    return root.end({ pretty: true });
  }

  _identifyFirstTask(elementList) {
    let startID = '';
    const firstTasks = [];
    elementList.forEach((value) => {
      if (value._elementType === 'StartEvent') {
        startID = value.id;
      }
    });

    elementList.forEach((value) => {
      if (value._elementType === 'Task') {
        value._properties.connectors.connectorsTo.forEach((conn) => {
          if (conn._fromEl && conn._fromEl.id === startID) {
            console.log('tarea inicial');
            value._properties.inFirsTask = true;
            firstTasks.push(value.id);
          }
        });
      }
    });

    elementList.forEach((value) => {
      if (value._elementType === 'IntermediateEvent') {
        const count = firstTasks.find((obj) => obj === value.target);
        if (count) {
          value._properties.inFirsTask = true;
        }
      }
    });
  }

  _existsDataStores(elementList) {
    const exists = elementList.filter((element) => {
      return this._isDataStore(element);
    });
    return exists.length !== 0;
  }

  _getDataStoresReferencesElement(workflowProcess, elementList) {
    if (this._existsDataStores(elementList)) {
      return workflowProcess.ele(`${this.xpdl}DataStoreReferences`);
    }
    return workflowProcess;
  }

  _getLanesElement(pool, elementList) {
    const lanes = this._getLaneList(elementList);
    if (lanes !== undefined && lanes.length !== 0) {
      const lanesElement = pool.ele('xpdl:Lanes');
      lanes.forEach((value) => {
        const lane = lanesElement.ele('xpdl:Lane', {
          Id: value.properties.id,
        });
        if (Config.ENV !== 'Offline') lane.att('apia:ProEleDesignXML', this._getProEleDesignXML(value));
        if (value.properties.name !== '') {
          lane.att('Name', value.properties.name);
        }
        this._createDocumentationElement(lane, value);
        this._createNodeGraphicsInfosElement(lane, value);
      });
    }
    return pool;
  }

  _getDataInputOutputs(workflowProcess, elementList) {
    if (this._existsDataStores(elementList)) {
      return workflowProcess.ele(`${this.xpdl}DataInputOutputs`);
    }
    return workflowProcess;
  }

  _getTransitionsElement(workflowProcess) {
    return workflowProcess.ele(`${this.xpdl}Transitions`);
  }

  _getAssociationsElement(root) {
    return root.ele('xpdl:Associations');
  }

  getTransitionsElement(workflowProcess) {
    return workflowProcess.ele('Transitions');
  }

  _getReferences() {
    const ref = {
      xmlns: 'http://www.statum.biz/2009/APIA.XPDL2.1',
      xpdl: 'http://www.wfmc.org/2008/XPDL2.1',
      apia: 'http://www.statum.biz/2009/APIA.XPDL2.1',
      newVersion: 'http://www.apia.com/2020/VERSION',
    };
    return ref;
  }

  _getProcessHeaderElement(workflow) {
    return workflow.ele(`${this.xpdl}ProcessHeader`);
  }

  _getActivitySetsElement(workflow) {
    return workflow.ele(`${this.xpdl}ActivitySets`);
  }

  _getPackageHeaderElement(packageEle) {
    const date = new Date();
    // const nameSpace = Config.ENV === 'Offline' ? this.xpdl : 'xpd:';
    const header = packageEle.ele(`${this.xpdl}PackageHeader`);
    header.ele(`${this.xpdl}XPDLVersion`, {}, '2.1');
    header.ele(`${this.xpdl}Vendor`, {}, 'STATUM');
    header.ele(`${this.xpdl}Created`, {}, date.toLocaleString());
  }

  _getPackageElement() {
    const pack = this.builder.create(
      `${this.xpdl}Package`,
      {
        enconding: 'UTF-8',
        headless: true,
      },
      { noDoubleEncoding: true },
    );
    if (Config.ENV !== 'Offline') {
      pack.att('xmlns', this._getReferences().xmlns);
    } else {
      this._getPackageHeaderElement(pack);
    }
    pack.att('xmlns:xpdl', this._getReferences().xpdl);
    // To identify the new version of Facilis and draw the corresponding elements
    pack.att('xmlns:edition', this._getReferences().newVersion);
    // pack.att(
    //   `xmlns:${Config.ENV === 'Offline' ? 'xpdl' : 'xpd'}`,
    //   this._getReferences().xpdl,
    // );
    return pack;
  }

  _getParticipantsElement(root) {
    return root.ele(`${this.xpdl}Participants`);
  }

  _getPoolsElement(root) {
    // let strPools = 'Pools';
    // if (Config.ENV !== 'Offline') {
    //  strPools = 'xpdl:Pools';
    // }
    // return root.ele(`${this.xpdl}Pools`);
    const strPools = 'xpdl:Pools'; // always is xpdl:Pools
    return root.ele(strPools);
  }

  _getArtifactsElement(root) {
    return root.ele(`${this.xpdl}Artifacts`);
  }

  _getProEleDesignXML(element) {
    const xCoordinate = parseInt(this._getCoordinates(element).x, 10);
    const yCoordinate = parseInt(this._getCoordinates(element).y, 10);
    return `DESIGN y=${xCoordinate} x=${yCoordinate}`;
  }

  _getDataStoresElement(root) {
    return root.ele(`${this.xpdl}DataStores`);
  }

  _getWorkflowProcessesElement(root) {
    return root.ele(`${this.xpdl}WorkflowProcesses`);
  }

  _getActivitiesElement(workflow) {
    return workflow.ele(`${this.xpdl}Activities`);
  }

  _getWorkflowProcessElement(workflows) {
    return workflows.ele(`${this.xpdl}WorkflowProcess`);
  }

  _getImplementationElement(node) {
    return node.ele(`${this.xpdl}Implementation`);
  }

  _getTaskTypeElement(task, element) {
    const type = element.properties.taskType;
    const tag = type.charAt(0).toUpperCase() + type.slice(1);
    let taskType;
    if (type !== 'none') {
      taskType = task.ele(`${this.xpdl}Task${tag}`);
      const tskTypeCondition = type === 'service' || type === 'receive' || type === 'send' || type === 'script';
      if (tskTypeCondition && Config.ENV !== 'Offline') {
        taskType.att('Implementation', 'WebService');
      }
      if (type === 'script' && Config.ENV !== 'Offline') {
        taskType.ele(`${this.xpdl}${tag}`);
      }
    }
    return taskType;
  }

  _getCoordinates(element = undefined) {
    const coordinates = {};
    if (element === undefined) {
      coordinates.x = 0;
      coordinates.y = 0;
    } else {
      coordinates.x = element.x;
      coordinates.y = element.y;
    }
    return coordinates;
  }

  _getSize(element) {
    let size = { width: 0, height: 0 };
    if (element !== undefined) {
      if (element.properties.size !== undefined) {
        ({ size } = element.properties);
      }
    }
    return size;
  }

  _getArtifactType(element) {
    if (element.elementType === this.tagsTypes.TextAnnotation) {
      return 'Annotation';
    }
    return element.elementType;
  }

  _getApiaExtensionsElement(node) {
    return node.ele('ApiaExtensions');
  }

  _isDataStore(element) {
    return element.elementType === this.tagsTypes.DataStore;
  }

  _isArtifact(element) {
    return this._isDataObject(element) || this._isTextAnnotation(element) || this._isGroup(element);
  }

  _isActivity(element) {
    return (
      this._isEvent(element)
      || this._isTask(element)
      || this._isGateway(element)
      || this._isSubprocess(element)
    );
  }

  _isEvent(element) {
    const type = element.elementType;
    return (
      type === `${this.tagsTypes.Start}Event`
      || type === `${this.tagsTypes.End}Event`
      || type === `${this.tagsTypes.Intermediate}Event`
    );
  }

  _isLane(element) {
    return element.elementType === this.tagsTypes.Lane;
  }

  _isTask(element) {
    return element.elementType === this.tagsTypes.Task;
  }

  _isGateway(element) {
    return element.elementType === this.tagsTypes.Gateway;
  }

  _isSubprocess(element) {
    return element.elementType === this.tagsTypes.Subprocess;
  }

  _isDataObject(element) {
    return element.elementType === this.tagsTypes.DataObject;
  }

  _isTextAnnotation(element) {
    return element.elementType === 'TextAnnotation';
  }

  _isGroup(element) {
    return element.elementType === this.tagsTypes.Group;
  }

  _isDataInput(element) {
    return element.elementType === this.tagsTypes.DataInput;
  }

  _isDataOutput(element) {
    return element.elementType === this.tagsTypes.DataOutput;
  }

  _fillInputOutputsList(elementList) {
    const inputOutputs = elementList.filter((value) => {
      return this._isDataInput(value) || this._isDataOutput(value);
    });
    return inputOutputs;
  }

  _filterWorkflowProcess(elementList) {
    const workflow = elementList.find((value) => {
      return value.elementType === this.tagsTypes.Workflow;
    });
    return workflow;
  }

  _createDataStoreReference(dataStoreReferences, element, newId) {
    const props = element.properties;
    const dataStoreRef = dataStoreReferences.ele('DataStoreReference', {
      Id: props.id,
      DataStoreRef: newId,
      State: props.state,
    });
    this._createNodeGraphicsInfosElement(dataStoreRef, element);
    return dataStoreRef;
  }

  _createDataInputOrOutputElement(dataInputOutputs, element) {
    const props = element.properties;
    const tag = this._isDataInput(element) ? `${this.xpdl}DataInput` : `${this.xpdl}DataOutput`;
    const dataInputOuput = dataInputOutputs.ele(tag, {
      Id: props.id,
      Name: props.name,
      IsCollection: props.collection,
    });
    if (Config.ENV !== 'Offline') {
      dataInputOuput.att('apia:ProEleDesignXML', this._getProEleDesignXML(element));
    }
    if (props.state !== '') {
      dataInputOuput.att('State', props.state);
    }
    return dataInputOuput;
  }

  _createCoordinateElement(node, point) {
    return node.ele('xpdl:Coordinates', {
      YCoordinate: point.y,
      XCoordinate: point.x,
    });
  }

  _createConnectorGraphicsInfosElement(connector, element) {
    const { points } = element.properties || [];
    if (points) {
      const graphicsInfo = connector
        .ele('xpdl:ConnectorGraphicsInfos')
        .ele('xpdl:ConnectorGraphicsInfo');
      points.forEach((value) => {
        this._createCoordinateElement(graphicsInfo, value);
      });
    }
    return connector;
  }

  _createExpressionConditionFragment(connector, props) {
    if (props.expression !== '') {
      const condition = connector.ele(`${this.xpdl}Condition`, {
        type: 'CONDITION',
      });
      const expression = condition.ele(`${this.xpdl}Expression`);
      // const expressionText = escape(props.expression);
      expression.text(props.expression);
    }
    return connector;
  }

  _createConnectorAcoordingToType(associations, transitions, element) {
    const props = element.properties;
    const { type } = props;
    let connector = null;
    const typeClass = Config.ENV === 'Offline' ? 'Type' : 'apia:Type';
    if (type === 'transition') {
      connector = transitions.ele(`${this.xpdl}Transition`, {
        Id: props.id,
        From: props.from,
        To: props.to,
      });
      connector.att(typeClass, props.class);
      if (props.kind === 'expression' && props.expression) {
        this._createExpressionConditionFragment(connector, props);
      }
    } else {
      const direction = props.direction.charAt(0).toUpperCase() + props.direction.slice(1);
      connector = associations.ele('xpdl:Association', {
        Id: props.id,
        Target: props.to,
        Source: props.from,
        AssociationDirection:
          // direction === 'Directional' && Config.ENV === 'Offline' ? 'From' : direction,
          direction === 'Directional' ? 'From' : direction,
      });
    }
    if (props.name !== '') connector.att('Name', props.name);
    if (props.executionOrder) connector.att('apia:ExecutionOrder', props.executionOrder);
    return connector;
  }

  _createRoleRefElement(task, element) {
    if (element.properties.role !== undefined && element.properties.role.id !== undefined) {
      return task.ele(`${this.xpdl}RoleRef`, {
        'apia:RoleId': element.properties.role.id,
        'apia:RoleName': element.properties.role.name,
      });
    }
    return task;
  }

  _createPoolElement(root, element = undefined, elementsList = []) {
    const props = element !== undefined ? element.properties : {};
    const [workflow] = getElementsOfType(Config.WORKFLOW, elementsList);
    const { id } = workflow.properties.defaultPool;

    // let poolStr = 'Pool';
    // if (Config.ENV !== 'Offline') {
    //  poolStr = 'xpdl:Pool';
    // }
    const poolStr = 'xpdl:Pool';

    // const pool = root.ele(`${this.xpdl}Pool`, {
    const pool = root.ele(poolStr, {
      Id: element ? props.id : id,
      BoundaryVisible: !(element === undefined),
      Orientation: 'HORIZONTAL',
    });
    pool.att('Process', workflow.properties.id);

    if (Config.ENV !== 'Offline') {
      pool.att('apia:ProEleDesignXML', this._getProEleDesignXML(element));
      pool.att('xmlns:apia', this._getReferences().apia);
    }
    if (element === undefined) {
      pool.att('MainPool', true);
    } else {
      pool.att('Name', props.name);
    }
    return pool;
  }

  _createtWorkflowProcessElement(workflow, root, elementList) {
    elementList.forEach((value) => {
      if (value.elementType === 'Workflow') {
        workflow.att('Id', value.properties.id);
        if (Config.ENV !== 'Offline') {
          workflow.att('apia:ProAction', value.properties.proAction);
          workflow.att('xmlns:apia', this._getReferences().apia);
          const { proId, versionId } = value.properties;
          if (proId) workflow.att('apia:ProId', proId);
          if (versionId) workflow.att('apia:ProVerId', versionId);
        }
        if (Config.ENV === 'Offline' && value.properties.name) {
          workflow.att('Name', value.properties.name);
        }
        if (Config.ENV !== 'Offline') this._createApiaProcEventsElement(workflow, value);
        this._createDocumentationElement(workflow, value);
        root.att('Id', value.properties.packageId);
      }
    });
    return workflow;
  }

  _createArtifactElement(artifacts, element) {
    const artifact = artifacts.ele(`${this.xpdl}Artifact`, {
      Id: element.properties.id,
      ArtifactType: this._getArtifactType(element),
    });
    if (Config.ENV !== 'Offline') {
      artifact.att('apia:ProEleDesignXML', this._getProEleDesignXML(element));
      artifact.att('xmlns:apia', this._getReferences().apia);
    }
    return artifact;
  }

  _createArtifactAcoordingToType(artifacts, element) {
    const artifact = this._createArtifactElement(artifacts, element);
    const artifactType = element.elementType;
    switch (artifactType) {
      case this.tagsTypes.DataObject: {
        this._createDataObjectElement(artifact, element);
        artifact.att('Name', element.properties.name);
        break;
      }
      case this.tagsTypes.Group: {
        artifact.att('Group', element.properties.name);
        artifact.att('Name', element.properties.name);
        break;
      }
      case this.tagsTypes.TextAnnotation: {
        artifact.att('TextAnnotation', element.properties.text);
        break;
      }
      default:
        break;
    }
    return artifact;
  }

  _createDataObjectElement(artifact, element) {
    const dataObject = artifact.ele(`${this.xpdl}DataObject`, {
      Id: element.properties.id,
      Name: element.properties.name,
      IsCollection: element.properties.collection,
    });
    if (element.properties.state) dataObject.att('State', element.properties.state);
    return dataObject;
  }

  _createDocumentationElement(node, element) {
    const documentationObj = element.properties.documentation || {};
    const { elementType } = element;
    const condition = Config.ENV === 'Offline' && (elementType === Config.WORKFLOW || elementType === Config.TASK);
    const props = element.properties;
    if (
      (documentationObj.text !== '' && documentationObj.text)
      || (condition && (props.forms !== undefined || props.events !== undefined))
    ) {
      const documentationEle = node
        .ele(`${this.xpdl}Object`, {
          Id: documentationObj.id || this.editor.idIndex++,
        })
        .ele(`${this.xpdl}Documentation`);
      if (
        Config.ENV === 'Offline'
        && (elementType === Config.WORKFLOW || elementType === Config.TASK)
      ) {
        const extractor = new Extractor([]);
        const { documentation, forms, events } = extractor.writeExtract(this.builder, element);
        documentationEle.text(`${documentation || ''}${forms || ''}${events || ''}`);
      } else if (documentationObj.text && documentationObj.text !== '') {
        documentationEle.text(documentationObj.text);
      }
      return documentationEle;
    }
    return node;
  }

  _createNodeGraphicsInfosElement(node, element) {
    const coordinate = this._getCoordinates(element);
    const size = this._getSize(element);
    const nodeGraphicInfos = node.ele('xpdl:NodeGraphicsInfos');
    const graphicInfo = nodeGraphicInfos.ele('xpdl:NodeGraphicsInfo', {
      Height: size.height || (element ? element.height - 10 : 0),
      Width: size.width || (element ? element.width - 10 : 0),
    });
    if (element) {
      const { elementType, properties } = element;
      if (
        (elementType === Config.TASK || elementType === Config.SUBPROCESS)
        && element.properties.color
        && ((element.properties.color !== Config.DEFAULT_SUBP_COLOR
          && elementType === Config.SUBPROCESS)
          || (element.properties.color !== Config.DEFAULT_TASK_COLOR && elementType === Config.TASK))
      ) {
        graphicInfo.att('FillColor', properties.color);
      }
    }
    this._createCoordinateElement(graphicInfo, coordinate);
    return nodeGraphicInfos;
  }

  _createDataStoreElement(datastores, element, newId) {
    const { name, capacity, unlimit } = element.properties;
    const datastore = datastores.ele(`${this.xpdl}DataStore`, {
      Id: newId,
      Name: name,
      IsUnlimited: unlimit,
    });
    if (capacity) {
      datastore.att('Capacity', capacity);
    }
    return { datastore, newId };
  }

  _createApiaProcEventsBusClaParBindingsElement(apiaProEvent, bindings) {
    const busClaParBindings = apiaProEvent.ele('BusClaParBindings');
    bindings.forEach((binding) => {
      const bindingValue = binding.busClaParBndValue || '';
      const bindingAttr = binding.attId || -1;
      if (bindingValue !== '' || bindingAttr !== -1) {
        const busClaParBinding = busClaParBindings.ele('BusClaParBinding', {
          BusClaParId: binding.busClaParId,
          BusClaParName: binding.busClaParName,
          BusClaParType: binding.busClaParType,
          BusClaParBndValue: binding.busClaParBndValue,
          // BusClaParBndType: binding.busClaParBndType, //BusClaParBndValue
        });
        if (binding.busClaParBndType === 'V') {
          let binValue = binding.busClaParBndValue || '';
          if (binding.busClaParType === 'D' && binValue !== '') {
            const arrDte = binValue.split('-');
            const dte = new Date(arrDte[0], arrDte[1] - 1, arrDte[2]);
            binValue = format.asString(Config.DATE_FORMAT, dte);
          }
          busClaParBinding.att('BusClaParBndType', 'V');
          busClaParBinding.att('BusClaParBndValue', binValue);
        } else {
          busClaParBinding.att('AttId', binding.attId);
          busClaParBinding.att('AttTooltip', binding.attTooltip);
          busClaParBinding.att('AttName', binding.attName);
          busClaParBinding.att('BusClaParBndType', binding.busClaParBndType);
        }
      }
    });
    return busClaParBindings;
  }

  _createApiaProcEventsElement(workflow, element) {
    const { events } = element;
    if (events !== undefined && events.length !== 0) {
      const apiaProEvents = workflow.ele('ApiaProEvents');
      events.forEach((value) => {
        const apiaProEvent = apiaProEvents.ele('ApiaProEvent', {
          EvtId: value.eventId,
          BusClaId: value.busClaId,
          EvtName: value.name,
          BusClaName: value.busClaName,
          ProEvtBusClaExecOrder: value.order,
        });
        if (value.condition && value.condition !== '') apiaProEvent.att('SkipCond', value.condition);
        const bindings = value.bindings || [];
        if (value.bindings.length > 0) {
          this._createApiaProcEventsBusClaParBindingsElement(apiaProEvent, bindings);
        }
        return apiaProEvents;
      });
    }
    return false;
  }

  _refactorNumberDate(number) {
    return number < 10 ? `0${number}` : number;
  }

  // _toDateString(date) {
  //   const year = date.getFullYear();
  //   const month = this._refactorNumberDate(date.getMonth());
  //   const day = this._refactorNumberDate(date.getDay());
  //   const hours = this._refactorNumberDate(date.getHours());
  //   const minutes = this._refactorNumberDate(date.getMinutes());
  //   const seconds = this._refactorNumberDate(date.getSeconds());
  //   return `${year}/${month}/${day} ${hours}:${minutes}:${seconds}`;
  // }

  _createEventAcoordingToType(activity, element) {
    const event = activity.ele(`${this.xpdl}Event`);
    const { type, isAttached, target } = element.properties;
    const typeEvent = event.ele(`${this.xpdl}${element.elementType}`);
    if (element.elementType === 'EndEvent') {
      typeEvent.att('Result', type.charAt(0).toUpperCase() + type.slice(1));
    } else {
      typeEvent.att('Trigger', type.charAt(0).toUpperCase() + type.slice(1));
    }
    if (element.properties.implementation !== undefined) {
      typeEvent.att('Implementation', element.properties.implementation);
    }
    if (isAttached && target) {
      if (Config.ENV !== 'Offline') typeEvent.att('apia:IsAttached', isAttached);
      typeEvent.att('Target', target);
    }

    if (
      !element.properties.message
      && element.elementType === 'EndEvent'
      && element.properties.events
      && element.properties.events.length > 1
      && type === 'message'
    ) {
      element.properties.message = { catchThrow: 'throw' };
    }

    if (Config.ENV !== 'Offline' && (element.properties[type] || type === 'multiple')) {
      if (type === 'timer' || type === 'multiple') {
        console.log(type);
        const attr = {
          'xmlns:apia': this._getReferences().apia,
          'apia:TimerAttType': 'P',
        };
        if (element.properties.timer) {
          if (element.properties.timer.attribute) {
            const { attribute } = element.properties.timer;
            attr['apia:TimerAttId'] = attribute.id;
            attr['apia:TimerAttName'] = attribute.name;
            attr['apia:TimerAttType'] = attribute.type.toUpperCase().charAt(0);
          }
          const date = element.properties.timer.date || {};
          if (
            date.start
            && (element.elementType === Config.START_EVENT
              || element.elementType === Config.INTERMEDIATE_EVENT)
          ) {
            const { start } = element.properties.timer.date;
            attr.TimeDate = format.asString('yyyy/MM/dd hh:mm:ss', start);
          }
          if (element.properties.timer.frequency) {
            const { unit, value } = element.properties.timer.frequency;
            attr['apia:TimeUnit'] = unit;
            attr.TimeCycle = value;
          }
          if (element.elementType === Config.START_EVENT && date.end) {
            const { end } = element.properties.timer.date;
            attr['apia:EndDate'] = format.asString('yyyy/MM/dd hh:mm:ss', end);
          }
          if (attr['apia:EndDate'] || attr.TimeDate) {
            attr['apia:TimeUnit'] = 'Milliseconds';
          }
          typeEvent.ele(`${this.xpdl}TriggerTimer`, attr);
        }
      }
      if (type === 'message' || type === 'multiple') {
        const message = typeEvent.ele(`${this.xpdl}TriggerResultMessage`);
        if (element.properties.message.catchThrow) {
          message.att('CatchThrow', element.properties.message.catchThrow.toUpperCase());
        }
        if (Config.ENV !== 'Offline') {
          message.ele('ApiaExtensions');
          this._createWebServiceMappingElement(message, element);
          this._createApiaEventsElement(event, element);
        }
      }
    }
    this._createTransitionsRefElement(activity, element);
    return event;
  }

  _getAllTransitionConnectors() {
    const { elements } = this.editor;
    const connectors = [];
    for (let index = 0; index < elements.length; index++) {
      const element = elements[index];
      if (element.elementType === Config.CONNECTOR) {
        if (element.type === Config.CONNECTOR_TYPE_TRANSITION) {
          connectors.push(element);
        }
      }
    }
    return connectors;
  }

  _isIdealElementToHaveTransitionRef(element) {
    const type = element.elementType;
    return (
      type === Config.TASK
      || type === Config.START_EVENT
      || type === Config.END_EVENT
      || type === Config.INTERMEDIATE_EVENT
      || type === Config.SUBPROCESS
      || type === Config.GATEWAY
    );
  }

  _getTransitionArrayFromElements(element) {
    const transitions = [];
    const connectors = this._getAllTransitionConnectors();
    if (this._isIdealElementToHaveTransitionRef(element)) {
      connectors.forEach((connector) => {
        if (connector.from === element.id) {
          transitions.push(connector.id);
        }
      });
    }
    if (transitions.length < 2) return [];
    return transitions;
  }

  _createTransitionsRefElement(node, element) {
    const transitions = this._getTransitionArrayFromElements(element);
    // const { type } = element.properties;
    if (transitions.length > 1) {
      const splitType = 'Parallel';
      // if (type !== undefined) splitType = type.charAt(0).toUpperCase() + type.slice(1);
      // if (this._isEvent(element)) splitType = 'Paralell';
      if (transitions !== undefined) {
        const transitionRefs = node
          .ele(`${this.xpdl}TransitionRestrictions`)
          .ele(`${this.xpdl}TransitionRestriction`)
          .ele(`${this.xpdl}Split`, {
            Type: splitType,
          })
          .ele(`${this.xpdl}TransitionRefs`);
        transitions.forEach((value) => {
          transitionRefs.ele(`${this.xpdl}TransitionRef`, {
            Id: value,
          });
        });
        return transitionRefs;
      }
    }
    return node;
  }

  _createWebServiceMappingElement(message, element) {
    const messageProps = element.properties.message;
    if (
      messageProps.webService !== undefined
      && (messageProps.attrEnt !== undefined || messageProps.attrProc !== undefined)
      && (messageProps.attrEnt !== [] || messageProps.attrProc !== [])
    ) {
      const tagType = element._elementType === 'IntermediateEvent' && !element._properties.inFirsTask
        ? 'WEBSERVICE_TSK'
        : 'WEBSERVICE_PRO';
      const tagTypeAttribute = element._elementType === 'IntermediateEvent' && !element._properties.inFirsTask
        ? 'WS_ATTRIBUTE_TSK'
        : 'WS_ATTRIBUTE_PRO';
      const webServiceMapping = message.ele('WebServiceMapping');
      const webServicePro = webServiceMapping.ele(tagType, {
        ws_method_name: messageProps.webService,
      });
      messageProps.attrProc = messageProps.attrProc || [];
      messageProps.attrEnt = messageProps.attrEnt || [];
      const both = messageProps.attrEnt.concat(messageProps.attrProc);
      both.forEach((value) => {
        webServicePro.ele(tagTypeAttribute, {
          name: value.name,
          attribute_uk: value.uk ? 'T' : 'F',
          multivalued: value.multivalued ? 'T' : 'F',
          attribute_id: value.id,
          attribute_type: value.type,
        });
      });
      return webServicePro;
    }
    return message;
  }

  _createActivityElement(activities, element) {
    const {
      name, id, proId, versionId, proEleId,
    } = element.properties.activity;
    const nameValue = name !== '' && name !== undefined ? name : element.properties.name;
    const activity = activities.ele(`${this.xpdl}Activity`, {
      Id: id,
    });
    const isConectedApia = Config.ENV !== 'Offline';
    if (nameValue !== '') activity.att('Name', nameValue);
    if (isConectedApia) activity.att('apia:ProEleDesignXML', this._getProEleDesignXML(element));
    if (proEleId && isConectedApia && element.elementType === Config.TASK) {
      activity.att('apia:ProEleId', proEleId);
    }
    if (proId && isConectedApia) activity.att('apia:ProId', proId);
    if (versionId && isConectedApia) activity.att('apia:ProVerId', versionId);
    return activity;
  }

  _createGatewayAcoordingToType(activity, element) {
    const { type } = element.properties;
    const gateway = activity.ele(`${this.xpdl}Route`, {
      GatewayType: type.charAt(0).toUpperCase() + type.slice(1),
      MarkerVisible: element.properties.visible || true,
    });
    const execution = element.properties.executionType;
    if (execution && Config.ENV !== 'Offline') {
      gateway.att('apia:ExecutionType', execution.replace(/^./, execution[0].toUpperCase()));
    }
    if (type === 'exclusive') gateway.att('ExlusiveType', 'Data');
    this._createTransitionsRefElement(activity, element);
    return gateway;
  }

  _createSubprocessAcoordingToType(activity, activitySets, element, reusableProcess) {
    const { type } = element.properties;
    let tagType = '';
    let props = {};
    if (type === 'embedded') {
      tagType = `${this.xpdl}BlockActivity`;
      props = {
        ActivitySetId: element.properties.activitySetId,
      };
      activitySets.ele(`${this.xpdl}ActivitySet`, {
        Id: props.ActivitySetId,
        Name: element.properties.name,
      });
      activity.ele(tagType, props);
    } else {
      tagType = `${this.xpdl}SubFlow`;
      const { reusable, subprocessId, name } = element.properties;
      reusableProcess.push({ subprocessId, name });
      props = {
        Id: subprocessId,
        Name: name,
      };
      if (reusable && Config.ENV !== 'Offline') {
        const sync = reusable.execution === 'synchronized' ? 'SYNCHR' : 'ASYNCHR';
        props = {
          ...props,
          'apia:ProEleAttBusEntId': reusable.entityId,
          'apia:ProEleAttBusEntName': reusable.entityAttrName,
          'apia:ApiaExecution': reusable.skipFirst ? `${sync}_SKIP` : sync,
        };
        if (reusable.skipFirst) props['apia:SkipFirstTask'] = reusable.skipFirst;
      }
      const subflow = this._getImplementationElement(activity).ele(tagType, props);
      this._createFormRefsElement(subflow, element);
    }
    this._createTransitionsRefElement(activity, element);
    this._createLoopElement(activity, element);
    return activity;
  }

  _createForm(node, forms, element, step) {
    forms.forEach((value) => {
      const formRef = node.ele('FormRef', {
        ProEleFrmStepId: step,
      });

      formRef.att('ProEleFrmOrder', value.order);
      formRef.att('FrmType', value.formType);
      formRef.att('FrmName', value.name);
      formRef.att('FrmId', value.id);
      if (element.elementType === this.tagsTypes.Task) {
        formRef.att('ProEleFrmReadOnly', value.readOnly);
        formRef.att('ProEleFrmMultiply', value.multiply);
      }
      const condition = value.condition || '';
      const conditionDoc = value.conditionDoc || '';
      if (conditionDoc !== '') formRef.att('ConditionDoc', value.conditionDoc);
      if (condition !== '') formRef.att('ProEleFrmEvalCond', value.condition);
    });
    return node;
  }

  _createFormRefsElement(node, element) {
    const props = element.properties;
    let { forms } = props || [];
    if (this._isSubprocess(element) && props.reusable) {
      forms = props.reusable.forms;
    }
    if (forms !== undefined) {
      const formRefs = node.ele(`${this.xpdl}FormsRef`);
      let count = 1;
      forms.forEach((value) => {
        if (this._isSubprocess(element)) {
          const formRef = formRefs.ele(`${this.xpdl}FormRef`, {
            ProEleFrmStepId: count++,
          });
          formRef.att('ProEleFrmOrder', value.order);
          formRef.att('FrmType', value.formType);
          formRef.att('FrmName', value.name);
          formRef.att('FrmId', value.id);
        } else {
          const entityForms = value.EForms || [];
          const processForms = value.PForms || [];
          const formsJoin = entityForms.concat(processForms);
          // console.log(formsJoin);
          this._createForm(formRefs, formsJoin, element, count);
          count++;
        }
      });
    }
    return node;
  }

  _getDefaultApiaEvent() {
    return {
      eventId: 11,
      busClaId: 102,
      WS: false,
      order: 0,
      name: 'ONREADY',
      busClaName: 'BPMNAutoComplete',
    };
  }

  _createApiaEventsElement(node, element) {
    const props = element.properties;
    let events = props.events || [];
    const { outputMessageRef, taskType } = props || {};
    if (taskType === 'script' && events.length === 0) {
      events.push(this._getDefaultApiaEvent());
    }
    if (outputMessageRef) events = events.concat(outputMessageRef);
    if (events !== undefined) {
      const apiaEvents = node.ele('ApiaEvents');
      events.forEach((value) => {
        const attr = {
          EvtId: value.eventId,
          BusClaId: value.busClaId,
          BusClaName: value.busClaName,
          EvtName: value.name,
          ProEleEvtBusClaExecOrder: value.order,
        };
        if (value.condition !== '') {
          attr.SkipCond = value.condition;
        }
        if (value.WS) attr.WS = value.WS;
        const eventEl = apiaEvents.ele('ApiaEvent', attr);
        const { bindings } = value || [];
        if (bindings !== undefined) {
          const bindingsEl = eventEl.ele('BusClaParBindings');
          bindings.forEach((bin) => {
            const binValue = bin.busClaParBndValue || '';
            const binAttr = bin.attId || -1;
            if (binValue !== '' || binAttr !== -1) {
              let bindingAttr = {
                BusClaParName: bin.busClaParName,
                BusClaParId: bin.busClaParId,
                BusClaParType: bin.busClaParType,
                AttTooltip: bin.attTooltip,
                BusClaParBndValue: bin.busClaParBndValue,
                BusClaParBndType: bin.busClaParBndType,
                AttName: bin.attName,
                AttId: bin.attId,
              };
              if (bindingAttr.BusClaParType === 'D' && binValue !== '') {
                const arrDte = binValue.split('-');
                const dte = new Date(arrDte[0], arrDte[1] - 1, arrDte[2]);
                bindingAttr.BusClaParBndValue = format.asString(Config.DATE_FORMAT, dte);
              }
              const isEntity = bindingAttr.busClaParBndType === 'E';
              const isProcess = bindingAttr.busClaParBndType === 'P';
              if ((isProcess || isEntity) && bindingAttr.AttId === -1) {
                bindingAttr = {
                  ...this._deleteAttrProperties(bindingAttr),
                  BusClaParBndType: 'V',
                  BusClaParBndValue: '',
                };
              }
              bindingsEl.ele('BusClaParBinding', bindingAttr);
            }
          });
        }
      });
    }
    return node;
  }

  _deleteAttrProperties(binding) {
    delete binding.AttTooltip;
    delete binding.AttName;
    delete binding.AttId;
    return binding;
  }

  _createLoopElement(activity, element) {
    const props = element.properties;
    const { loopType } = props;
    if (loopType === 'multiinstance' || loopType === 'standard') {
      const loopName = 'Loop';
      const loop = activity.ele(`${this.xpdl}${loopName}`);
      let tagType = '';
      if (props.loopType === 'standard') {
        tagType = 'Standard';

        if (!props.standard) {
          props.standard = {};
        }
        const standard = loop.ele(`${this.xpdl}${loopName}${tagType}`, {
          TestTime: props.standard.testTime || 'After',
        });
        if (props.standard.condition !== '') standard.att('LoopCondition', props.standard.condition);
        if (props.standard.conditionDoc !== '') standard.att('apia:ConditionDoc', props.standard.conditionDoc);
      } else if (props.loopType === 'multiinstance') {
        tagType = 'MultiInstance';
        const multiInstance = loop.ele(`${this.xpdl}${loopName}${tagType}`);
        multiInstance.att('MI_FlowCondition', 'All');
        multiInstance.att('MI_Ordering', 'Parallel');
        const multi = props.multiInstance || {};
        if (multi.attribute !== '' && Config.ENV !== 'Offline') {
          multiInstance.att('apia:MultiplierAttName', multi.attribute);
          multiInstance.att('apia:MultiplierAttId', multi.attributeId);
        }
        if (props.multiplyCount || multi.value) multiInstance.att('MI_Condition', props.multiplyCount || multi.value);
      }
      loop.att('LoopType', tagType);
    }
    return activity;
  }

  _participantExist(participant, participantsList) {
    return participantsList.find((element) => element.id === participant.id);
  }

  _getDefaultPerformer() {
    return {
      id: 2,
      name: 'busClass',
    };
  }

  createPerformersElement(activity, element, participants, participantsList) {
    const props = element.properties;
    const { taskType } = props;
    const tskTypeCondition = taskType === 'service'
      || taskType === 'receive'
      || taskType === 'send'
      || taskType === 'script';
    if (props.groups !== undefined || (tskTypeCondition && Config.ENV !== 'Offline')) {
      const performers = activity.ele(`${this.xpdl}Performers`);
      const groups = props.groups || [];
      if (tskTypeCondition && groups.length === 0 && Config.ENV !== 'Offline') {
        groups.push(this._getDefaultPerformer());
      }
      groups.forEach((value) => {
        let attrs;
        if (Config.ENV !== 'Offline') {
          attrs = {
            'apia:PerfId': value.id,
            'apia:PerfName': value.name,
          };
        }
        if (value.conditionDoc) attrs['apia:ConditionDoc'] = value.conditionDoc;
        if (value.condition) attrs['apia:ProElePerfEvalCond'] = value.condition;
        if (Config.ENV !== 'Offline') {
          performers.ele(`${this.xpdl}Performer`, attrs);
        } else {
          performers.ele(`${this.xpdl}Performer`, value.id);
        }
        if (value.name !== 'busClass' && !this._participantExist(value, participantsList)) {
          participantsList.push(value);
          const participantType = `${this.xpdl}ParticipantType`;
          participants
            .ele(`${this.xpdl}Participant`, {
              Id: value.id,
              Name: value.name,
            })
            .ele(participantType, { Type: 'ROLE' });
        }
      });
    }
    return activity;
  }

  _createApiaTaskStates(task, element) {
    const { states } = element.properties || [];
    if (states !== undefined) {
      const apiaStates = task.ele('ApiaTskStates');
      states.forEach((value) => {
        const tag = apiaStates.ele('ApiaTskState', {
          EvtName: value.event,
          EntStaId: value.idName,
          EvtId: value.idEvent,
          StaName: value.name,
        });
        if (value.condition !== '') {
          tag.att('ProEleBusEntStaEvalCond', value.condition);
        }
        if (value.conditionDoc !== '') {
          tag.att('ConditionDoc', value.conditionDoc);
        }
      });
    }
    return task;
  }

  _createTaskAcoordingToType(activity, element, participants, participantsList) {
    const props = element.properties;
    const implementation = this._getImplementationElement(activity);
    const offline = Config.ENV === 'Offline';
    const task = offline
      ? implementation.ele(`${this.xpdl}Task`)
      : implementation.ele(`${this.xpdl}Task`, {
        'apia:TskId': props.taskId,
        'apia:TskTitle': props.title,
        'apia:TskName': props.name,
        'apia:highlight_comments': props.comments,
      });
    if (props.condition !== '' && !offline) {
      task.att('apia:SkipTskCond', props.condition);
      task.att('apia:SkipTsk', true);
    } else if (props.condition === '' && !offline) {
      task.att('apia:SkipTsk', false);
    }
    if (props.schedule && !offline) {
      const { schedule } = props;
      if (schedule.task !== '' && schedule.id !== '' && schedule.type !== '') {
        task.att('apia:ActiveTskId', schedule.task);
        task.att('apia:TskSchId', schedule.id);
        task.att('apia:AsignType', schedule.type);

        if (schedule.subprocess !== '') task.att('apia:ActivePrcId', schedule.subprocess);
        if (schedule.subprocessName !== '') task.att('apia:ActivePrcName', schedule.subprocessName);
      }
    }
    const taskTypeEl = this._getTaskTypeElement(task, element);
    if (!offline) {
      const { taskType } = element.properties;
      if (taskType !== 'none') {
        this._getApiaExtensionsElement(taskTypeEl);
        this._createWebServiceMappingTaskElement(taskTypeEl, element);
      }
      this._createRoleRefElement(task, element);
      this._createFormRefsElement(task, element);
      this._createApiaTaskStates(task, element);
      this._createTransitionsRefElement(activity, element);
      this._createApiaEventsElement(activity, element);
    }
    this.createPerformersElement(activity, element, participants, participantsList);
    this._createLoopElement(activity, element);
    // completar para otros elementos que aparezcan
    return activity;
  }

  _createWebServiceMappingTaskElement(task, element) {
    const { inputMessageRef } = element.properties;
    if (inputMessageRef) {
      const wsTagName = element._properties.inFirsTask ? 'WEBSERVICE_PRO' : 'WEBSERVICE_TSK';

      const webServiceTsk = task.ele('WebServiceMapping').ele(wsTagName, {
        ws_method_name: inputMessageRef.wsMethodName,
      });
      this._createWebServiceAttributeTaskElement(webServiceTsk, inputMessageRef);
    }
    return task;
  }

  _createWebServiceAttributeTaskElement(webServiceTask, inputMessageRef) {
    const { attrEnt, attrProc } = inputMessageRef;
    if (attrEnt || attrProc) {
      let attrs = [];
      if (attrEnt) attrs = attrs.concat(attrEnt);
      if (attrProc) attrs = attrs.concat(attrProc);
      console.log(attrEnt, attrProc);
      const wsAttTagName = webServiceTask.name === 'WEBSERVICE_PRO' ? 'WS_ATTRIBUTE_PRO' : 'WS_ATTRIBUTE_TSK';
      for (let index = 0; index < attrs.length; index++) {
        webServiceTask.ele(wsAttTagName, {
          attribute_id: attrs[index].id,
          name: attrs[index].name,
          multivalued: attrs[index].multivalued ? 'T' : 'F',
          attribute_type: attrs[index].type,
          attribute_uk: attrs[index].uk ? 'T' : 'F',
        });
      }
    }
    return webServiceTask;
  }

  _createActivityAcoordingToType(
    activities,
    activitySets,
    element,
    participants,
    participantsList,
    reusableProcess,
  ) {
    const activity = this._createActivityElement(activities, element);
    if (this._isEvent(element)) {
      this._createEventAcoordingToType(activity, element);
    }
    if (this._isGateway(element)) {
      this._createGatewayAcoordingToType(activity, element);
    }
    if (this._isSubprocess(element)) {
      this._createSubprocessAcoordingToType(activity, activitySets, element, reusableProcess);
    }
    if (this._isTask(element)) {
      this._createTaskAcoordingToType(activity, element, participants, participantsList);
    }
    return activity;
  }

  // Methods that return properties read from the nodes of the xpdl

  _filterTagName(tag) {
    let tagName = tag.split(':');
    if (tagName.length > 1) {
      [, tagName] = tagName;
    } else {
      [tagName] = tagName;
    }
    return tagName.toLowerCase();
  }

  _addActivityProperties(attributes) {
    const activity = {
      id: this._toIntegerId(attributes.Id),
      name: unescape(attributes.Name || ''),
    };
    if (attributes['apia:ProEleId']) {
      activity.proEleId = this._toIntegerId(attributes['apia:ProEleId']);
    }
    if (attributes['apia:ProId']) {
      activity.proId = this._toIntegerId(attributes['apia:ProId']);
    }
    if (attributes['apia:ProVerId']) {
      activity.versionId = this._toIntegerId(attributes['apia:ProVerId']);
    }
    return activity;
  }

  _addTaskProperties(attributes, properties) {
    properties.taskId = this._toIntegerId(attributes['apia:TskId']) || properties.activity.id || '';
    properties.id = properties.activity.id;
    properties.taskType = 'none';
    properties.name = unescape(attributes['apia:TskName'] || '') || properties.activity.name || '';
    properties.title = unescape(attributes['apia:TskTitle'] || '') || properties.activity.name || '';
    properties.comments = JSON.parse(attributes['apia:highlight_comments'] || false);
    properties.condition = unescape(attributes['apia:SkipTskCond'] || '');
    properties.loopType = 'none';
    return properties;
  }

  _addScheduleTaskProperties(attributes, properties) {
    if (attributes['apia:TskSchId'] !== undefined) {
      const schedule = {
        id: this._toIntegerId(attributes['apia:TskSchId']) || '',
        type: attributes['apia:AsignType'] || '',
        subprocess: this._toIntegerId(attributes['apia:ActivePrcId']) || '',
        subprocessName: attributes['apia:ActivePrcName'] || '',
        task: this._toIntegerId(attributes['apia:ActiveTskId']) || '',
      };
      properties.schedule = schedule;
    }
    return properties;
  }

  _addRoleTaskProperties(attributes, properties) {
    const role = {
      id: this._toIntegerId(attributes['apia:RoleId']),
      name: unescape(attributes['apia:RoleName'] || ''),
    };
    properties.role = role;
    return properties;
  }

  _addGroupTaskproperties(attributes, properties) {
    let textPerf = false;
    if (attributes['apia:PerfId'] && attributes['apia:PerfName']) {
      const group = {
        id: this._toIntegerId(attributes['apia:PerfId']),
        name: unescape(attributes['apia:PerfName'] || ''),
      };
      if (attributes['apia:ConditionDoc']) {
        group.conditionDoc = unescape(attributes['apia:ConditionDoc'] || '');
      }
      if (attributes['apia:ProElePerfEvalCond']) {
        group.condition = unescape(attributes['apia:ProElePerfEvalCond'] || '');
      }
      properties.groups = this._addArrayProperties(group, properties.groups);
    } else {
      textPerf = true;
    }
    return { properties, textPerf };
  }

  _getStepIndex(stepId, steps) {
    let exist = 0;
    if (steps.length > 0) {
      exist = steps.findIndex((value) => {
        return parseInt(value.id, 10) === parseInt(stepId, 10);
      });
    }
    return exist;
  }

  _addFormsProperties(attributes, build, properties) {
    let stepIndex = this._getStepIndex(attributes.ProEleFrmStepId, properties.forms || []);
    const form = {
      id: this._toIntegerId(attributes.FrmId),
      name: unescape(attributes.FrmName || ''),
      // step: attributes.ProEleFrmStepId,
      order: parseInt(attributes.ProEleFrmOrder, 10),
      // formType: attributes.FrmType,
    };

    if (attributes.ConditionDoc !== undefined) form.conditionDoc = unescape(attributes.ConditionDoc || '');
    if (build !== this.tagsTypes.Subprocess) {
      form.formType = attributes.FrmType;
      form.readOnly = JSON.parse(attributes.ProEleFrmReadOnly || 'false');
      form.multiply = JSON.parse(attributes.ProEleFrmMultiply || 'false');
      form.condition = unescape(attributes.ProEleFrmEvalCond || '');
      const step = { id: this._toIntegerId(attributes.ProEleFrmStepId) };
      if (stepIndex === -1) {
        stepIndex = properties.forms.length;
        properties.forms = this._addArrayProperties(step, properties.forms);
      } else if (stepIndex === 0 && !properties.forms) {
        properties.forms = this._addArrayProperties(step, properties.forms);
      }
      properties.forms[stepIndex][`${attributes.FrmType}Forms`] = this._addArrayProperties(
        form,
        properties.forms[stepIndex][`${attributes.FrmType}Forms`],
      );
      // properties.forms = this._addArrayProperties(form, properties.forms);
    } else if (properties.reusable !== undefined) {
      form.step = parseInt(attributes.ProEleFrmStepId, 10);
      form.formType = attributes.FrmType;
      properties.reusable.forms = this._addArrayProperties(form, properties.reusable.forms);
    }
    return properties;
  }

  _addEventsProperties(attributes, properties, tagToBuild, webServiceCtrl) {
    const events = properties.events || [];
    let count;
    if (JSON.parse(attributes.WS || 'false') && tagToBuild === this.tagsTypes.Task) {
      count = !properties.outputMessageRef ? 1 : properties.outputMessageRef.length + 1;
    } else {
      count = events.length + 1;
    }
    const event = {
      id: count,
      eventId: this._toIntegerId(attributes.EvtId),
      condition: attributes.SkipCond || '',
      busClaId: this._toIntegerId(attributes.BusClaId),
      busClaName: attributes.BusClaName,
      name: attributes.EvtName,
      order: this._toIntegerId(attributes.ProEleEvtBusClaExecOrder),
      WS: JSON.parse(attributes.WS || 'false'),
      bindings: [],
    };
    event.haveCond = event.condition && event.condition !== '' ? 'Yes' : 'No';
    const taskType = properties.taskType || '';
    const conditionTask = taskType === 'user' || taskType === 'recive' || taskType === 'send' || taskType === 'service';
    if (event.WS && tagToBuild === this.tagsTypes.Task && conditionTask) {
      properties.outputMessageRef = this._addArrayProperties(event, properties.outputMessageRef);
      webServiceCtrl = 'outputMessageRef';
    } else {
      properties.events = this._addArrayProperties(event, properties.events);
      webServiceCtrl = 'events';
    }
    // const eventType = {
    //   StartEvent: 'StartEvent',
    //   EndEvent: 'EndEvent',
    //   IntermediateEvent: 'IntermediateEvent',
    // };
    // const conditionEvt = (tagToBuild === eventType.StartEvent)
    //     || (tagToBuild === eventType.EndEvent)
    //     || (tagToBuild === eventType.EndEvent);
    // if (event.WS && conditionEvt) {
    //   properties.service = {};
    //   properties.service.outMessage = {};
    //   properties.service.outMessage.webService = this._addArrayProperties(event,
    //     properties.service.outMessage.webService);
    // }
    return { properties, webServiceCtrl };
  }

  _addBusinessClassProperties(attributes, properties, type) {
    const events = properties[type];
    if (events !== undefined) {
      const event = events.pop();
      const binding = {
        busClaParName: attributes.BusClaParName,
        busClaParId: this._toIntegerId(attributes.BusClaParId),
        busClaParType: attributes.BusClaParType,
        busClaParBndType: attributes.BusClaParBndType,
      };
      if (binding.busClaParBndType === 'V') {
        let dateValue = attributes.BusClaParBndValue;
        if (binding.busClaParType === 'D') {
          if (attributes.BusClaParBndValue === '') dateValue = Date.now();

          dayjs.extend(customParseFormat);
          binding.busClaParBndValue = dayjs(dateValue, Config.DATE_FORMAT.toUpperCase()).format(
            'YYYY-MM-DD',
          );
        } else {
          binding.busClaParBndValue = attributes.BusClaParBndValue;
        }
      }
      if (binding.busClaParBndType === 'E' || binding.busClaParBndType === 'P') {
        binding.attId = this._toIntegerId(attributes.AttId);
        binding.attTooltip = attributes.AttTooltip;
        binding.attName = attributes.AttName;
      }
      event.bindings = this._addArrayProperties(binding, event.bindings);
      properties[type] = this._addArrayProperties(event, properties[type]);
    }
    return properties;
  }

  _addLoopTypeProperties(attributes, properties) {
    properties.loopType = attributes.LoopType !== undefined ? attributes.LoopType.toLowerCase() : 'none';
    return properties;
  }

  _addMultiInstanceLoopProperties(attributes, properties) {
    let hasAttribute = false;
    let hasValue = false;

    if (attributes['apia:MultiplierAttName'] && attributes['apia:MultiplierAttId']) {
      properties.multiInstance = {
        type: 'attribute',
        attribute: attributes['apia:MultiplierAttName'],
        attributeId: this._toIntegerId(attributes['apia:MultiplierAttId']),
        condition: attributes.MI_FlowCondition,
        ordering: attributes.MI_Ordering,
      };
      hasAttribute = true;
    }
    if (attributes.MI_Condition && Config.ENV === 'Offline') {
      properties.multiplyCount = parseInt(attributes.MI_Condition, 10);
      hasValue = true;
    }
    if (Config.ENV !== 'Offline' && attributes.MI_Condition) {
      properties.multiInstance = {
        type: 'value',
        value: parseInt(attributes.MI_Condition, 10),
      };
      hasValue = true;
    }

    if (!hasValue && !hasAttribute) {
      properties.multiInstance = {
        type: 'value',
        value: parseInt(attributes.MI_Condition, 10),
      };
    }

    return properties;
  }

  _addStandardLoopProperties(attributes, properties) {
    properties.standard = {
      condition: attributes.LoopCondition || '',
      conditionDoc: attributes['apia:ConditionDoc'] || '',
      testTime: attributes.TestTime,
    };
    return properties;
  }

  _addStateTaskProperties(attributes, properties) {
    const states = properties.states || [];
    const count = states.length + 1;
    const state = {
      id: count,
      condition: attributes.ProEleBusEntStaEvalCond,
      idEvent: this._toIntegerId(attributes.EvtId),
      event: attributes.EvtName,
      idName: this._toIntegerId(attributes.EntStaId),
      name: attributes.StaName,
      conditionDoc: attributes.ConditionDoc,
    };
    properties.states = this._addArrayProperties(state, properties.states);
    return properties;
  }

  _addEventTypeProperties(attributes, properties) {
    let type = attributes.Trigger || attributes.Result || 'none';
    type = type.toLowerCase();
    properties.type = type;
    if (attributes.Target) {
      properties.target = this._toIntegerId(attributes.Target);
      properties.isAttached = true;
    }
    if (attributes['apia:IsAttached']) properties.isAttached = JSON.parse(attributes['apia:IsAttached']);
    return properties;
  }

  _addEventImplementationProperties(attributes, properties) {
    if (attributes.Implementation !== undefined) {
      properties.implementation = attributes.Implementation;
    }
    return properties;
  }

  _addTriggerResultMessageProperties(attributes, properties) {
    if (attributes.CatchThrow !== undefined) {
      properties.message = {};
      properties.message.catchThrow = attributes.CatchThrow.toLowerCase();
    }
    return properties;
  }

  _addTriggerTimerProperties(attributes, properties) {
    const timer = {};
    if (attributes.TimeDate) {
      if (!timer.date) timer.date = {};
      timer.date.start = new Date(attributes.TimeDate);
    }
    if (attributes['apia:EndDate']) {
      if (!timer.date) timer.date = {};
      timer.date.end = new Date(attributes['apia:EndDate']);
    }
    if (attributes['apia:TimeUnit'] && attributes.TimeCycle) {
      timer.frequency = {
        // el flash tenia un bug y mandaba milliseconds cuando era minutos
        unit:
          attributes['apia:TimeUnit'] === 'Milliseconds' ? 'Minutes' : attributes['apia:TimeUnit'],
        value: attributes.TimeCycle || 0,
      };
    }
    if (attributes['apia:TimerAttId'] && attributes['apia:TimerAttName']) {
      timer.attribute = {
        id: this._toIntegerId(attributes['apia:TimerAttId']),
        name: attributes['apia:TimerAttName'],
        type: attributes['apia:TimerAttType'] === 'P' ? 'process' : 'entity',
      };
    }
    return { ...properties, timer };
  }

  _addWebServiceAttributeProcessOrTaskProperties(attributes, properties, clasification) {
    const wsAttribute = {
      id: this._toIntegerId(attributes.attribute_id),
      name: attributes.name,
      multivalued: attributes.multivalued === 'T',
      type: attributes.attribute_type,
      uk: attributes.attribute_uk === 'T',
    };
    if (wsAttribute.type === 'E') {
      properties[clasification].attrEnt = this._addArrayProperties(
        wsAttribute,
        properties[clasification].attrEnt,
      );
    } else {
      properties[clasification].attrProc = this._addArrayProperties(
        wsAttribute,
        properties[clasification].attrProc,
      );
    }
    return properties;
  }

  _addRouteGatewayProperties(attributes, properties) {
    properties.id = properties.activity.id;
    properties.type = attributes.GatewayType !== undefined ? attributes.GatewayType.toLowerCase() : 'none';
    properties.name = properties.activity.name || '';
    if (attributes.MarkerVisible) properties.visible = JSON.parse(attributes.MarkerVisible);
    if (attributes['apia:ExecutionType'] !== undefined) {
      properties.executionType = attributes['apia:ExecutionType'].toLowerCase();
    }
    if (attributes.ExclusiveType !== undefined) {
      properties.exclusiveType = attributes.ExclusiveType;
    }
    return properties;
  }

  _addTransitionReferenceProperties(attributes, properties) {
    properties.transitions = this._addArrayProperties(
      // parseInt(attributes.Id, 10),
      this._toIntegerId(attributes.Id),
      properties.transitions,
    );
    return properties;
  }

  _addTransitionOrAssociationProperties(attributes, connectorType) {
    // console.log(connectorType);
    const connector = {
      id: this._toIntegerId(attributes.Id),
      type: connectorType,
      name: unescape(attributes.Name || ''),
      from: this._toIntegerId(attributes.From) || this._toIntegerId(attributes.Source),
      to: this._toIntegerId(attributes.To) || this._toIntegerId(attributes.Target),
    };
    if (attributes['apia:ExecutionOrder']) {
      connector.executionOrder = this._toIntegerId(attributes['apia:ExecutionOrder']);
    }
    if (connectorType === 'transition') connector.class = attributes['apia:Type'] || attributes.Type;
    if (connectorType === 'association') {
      const direction = attributes.AssociationDirection.toLowerCase() === 'from'
        ? 'directional'
        : attributes.AssociationDirection.toLowerCase();
      connector.direction = direction || 'none';
    }
    return connector;
  }

  _addBlockActivitySubprocessProperties(attributes, properties) {
    properties.id = properties.activity.id;
    properties.activitySetId = this._toIntegerId(attributes.ActivitySetId);
    properties.name = properties.activity.name;
    properties.type = 'embedded';
    properties.loopType = 'none';
    return properties;
  }

  _addSubflowSubprocessProperties(attributes, properties) {
    properties.id = properties.activity.id;
    properties.subprocessId = this._toIntegerId(attributes.Id);
    properties.name = unescape(attributes.Name || '');
    properties.type = 'reusable';
    properties.loopType = 'none';
    if (attributes['apia:ApiaExecution']) {
      if (!properties.reusable) properties.reusable = {};
      properties.reusable.execution = attributes['apia:ApiaExecution'] === 'SYNCHR_SKIP'
        || attributes['apia:ApiaExecution'] === 'SYNCHR'
        ? 'synchronized'
        : 'not_synchronized';
    }
    if (attributes['apia:SkipFirstTask']) {
      properties.reusable.skipFirst = JSON.parse(attributes['apia:SkipFirstTask']);
    }
    if (attributes['apia:ProEleAttBusEntId']) {
      properties.reusable.entityId = this._toIntegerId(attributes['apia:ProEleAttBusEntId']);
      properties.reusable.entityAttrName = attributes['apia:ProEleAttBusEntName'];
    }
    return properties;
  }

  _addArtifactProperties(attributes, properties) {
    properties = {
      id: this._toIntegerId(attributes.Id),
      type: attributes.ArtifactType,
    };
    properties.type = properties.type.toLowerCase();
    if (properties.type === 'group') {
      properties.name = unescape(attributes.Name || '');
    }
    if (properties.type === 'annotation') {
      properties.text = unescape(attributes.TextAnnotation || '');
    }
    return properties;
  }

  _defineArtifactToBuildAcoordingToType(properties) {
    let build;
    if (properties.type === 'group') {
      build = this.tagsTypes.Group;
    }
    if (properties.type === 'annotation') {
      build = this.tagsTypes.TextAnnotation;
    }
    return build;
  }

  _addPoolProperties(attributes) {
    if (JSON.parse(attributes.BoundaryVisible)) {
      return {
        id: this._toIntegerId(attributes.Id),
        name: attributes.Name || '',
        processId: this._toIntegerId(attributes.Process),
        visible: JSON.parse(attributes.BoundaryVisible),
      };
    }
    return {};
  }

  _addDefaultPoolProperties(attributes) {
    if (!JSON.parse(attributes.BoundaryVisible)) {
      return {
        id: this._toIntegerId(attributes.Id),
        processId: this._toIntegerId(attributes.Process),
      };
    }
    return {};
  }

  _addLanesProperties(attributes, lanesList) {
    const lane = {
      id: this._toIntegerId(attributes.Id),
      name: unescape(attributes.Name || ''),
    };
    lanesList.push(lane);
    return lanesList;
  }

  _addDataStoreProperties(attributes, dataStoreList) {
    const dataStore = {
      referenceId: this._toIntegerId(attributes.Id),
      name: unescape(attributes.Name || ''),
    };
    if (attributes.IsUnlimited) dataStore.unlimit = JSON.parse(attributes.IsUnlimited);
    if (attributes.Capacity) dataStore.capacity = attributes.Capacity;
    dataStoreList.push(dataStore);
    return dataStoreList;
  }

  _addDataObjectInputOutputProperties(attributes, properties) {
    properties.id = this._toIntegerId(attributes.Id);
    properties.name = unescape(attributes.Name || '');
    properties.state = unescape(attributes.State || '');
    properties.collection = attributes.IsCollection ? JSON.parse(attributes.IsCollection) : false;
    return properties;
  }

  _addDataStoreReferenceProperties(attributes, dataStoreList) {
    const properties = dataStoreList.find((element) => {
      return parseInt(element.referenceId, 10) === parseInt(attributes.DataStoreRef, 10);
    });
    if (properties) {
      properties.id = this._toIntegerId(attributes.Id);
      delete properties.referenceId;
    }
    if (attributes.State) properties.state = unescape(attributes.State || '');
    return properties;
  }

  _addDataStoreDocumentationProperties(attributes, build, dataStoreList) {
    if (build === this.tagsTypes.DataStore) {
      const dataStore = dataStoreList.pop();
      dataStore.documentation = { id: this._toIntegerId(attributes.Id) };
      dataStoreList.push(dataStore);
    }
    return dataStoreList;
  }

  _addObjectDocumentationProperties(attributes, build, properties) {
    if (build !== this.tagsTypes.DataStore) {
      properties.documentation = { id: this._toIntegerId(attributes.Id) };
    }
    return properties;
  }

  _addCoordinateProperties(attributes, build, points, ratio, laneFlag) {
    if (build !== '' && !laneFlag) {
      const point = {
        x: parseInt(attributes.XCoordinate, 10) * ratio,
        y: parseInt(attributes.YCoordinate, 10) * ratio,
      };
      points.push(point);
    }
    return points;
  }

  _addApiaProEventsProperties(attributes, properties) {
    const event = {
      id: !properties.events ? 1 : properties.events.length + 1,
      eventId: this._toIntegerId(attributes.EvtId),
      condition: attributes.SkipCond || '',
      busClaId: this._toIntegerId(attributes.BusClaId),
      busClaName: attributes.BusClaName,
      name: attributes.EvtName,
      order: attributes.ProEvtBusClaExecOrder,
      bindings: [],
    };
    event.haveCond = event.condition && event.condition !== '' ? 'Yes' : 'No';
    properties.events = this._addArrayProperties(event, properties.events);
    return { properties, webServiceCtrl: 'events' };
  }

  _isContaintSizeProperties(build) {
    return (
      build === this.tagsTypes.Group
      || build === this.tagsTypes.TextAnnotation
      || build === this.tagsTypes.Pool
    );
  }

  _addLanesSizeProperties(attributes, build, lanesList, ratio) {
    if (build === this.tagsTypes.Pool) {
      if (lanesList.length !== 0 && lanesList[lanesList.length - 1].size === undefined) {
        lanesList[lanesList.length - 1].size = {
          width: parseInt(attributes.Width, 10) * ratio,
          height: parseInt(attributes.Height, 10) * ratio,
        };
      }
    }
    return lanesList;
  }

  _addLanesCoordinatesProperties(attributes, build, lanesList, ratio) {
    if (
      build === this.tagsTypes.Pool
      && lanesList.length > 0
      && lanesList[lanesList.length - 1].x === undefined
      && lanesList[lanesList.length - 1].y === undefined
    ) {
      lanesList[lanesList.length - 1].x = parseInt(attributes.XCoordinate, 10) * ratio;
      lanesList[lanesList.length - 1].y = parseInt(attributes.YCoordinate, 10) * ratio;
    }
    return lanesList;
  }

  _addNodeGraphicsInfoProperties(attributes, build, properties, ratio) {
    if (this._isContaintSizeProperties(build)) {
      const size = {
        width: parseInt(attributes.Width, 10) * ratio,
        height: parseInt(attributes.Height, 10) * ratio,
      };
      properties.size = size;
    }
    return properties;
  }

  _buildLanesElement(lanesList, poolProps) {
    lanesList.forEach((value, index) => {
      if (index > 0) {
        const previous = lanesList[index - 1];
        const coord = this._checkCoordinateLane({ x: value.x, y: value.y }, previous);
        value.x = coord.x;
        value.y = coord.y;
      }
      const poolWidth = poolProps.size.width;
      if (poolWidth - 30 !== value.size.width) {
        value.size.width = poolWidth - 30;
      }
      this.editor.createElement('Lane', value.x, value.y, value, false);
      // console.log(` -Lane - Coordenadas:[x:${value.x} y:${value.y}]`, value);
    });
  }

  _addWorkflowProperties(attributes, properties) {
    properties.id = this._toIntegerId(attributes.Id);
    properties.proAction = attributes['apia:ProAction'] || 'C';
    if (Config.ENV === 'Offline') properties.name = attributes.Name || 'Default_Process';
    if (attributes['apia:ProId']) {
      properties.proId = this._toIntegerId(attributes['apia:ProId']);
    }
    if (attributes['apia:ProVerId']) {
      properties.versionId = this._toIntegerId(attributes['apia:ProVerId']);
    }
    return properties;
  }

  _addConnectorsPointsProperties(points, properties) {
    properties.points = [];
    properties.points = points;
    return properties;
  }

  _addPackageProperties(attributes) {
    const { elements } = this.editor;
    const workflow = elements.find((element) => element.elementType === 'Workflow');
    workflow.properties.packageId = this._toIntegerId(attributes.Id);
  }

  _addLaneDocumentationProperties(text, lanesList) {
    if (lanesList.length > 0) {
      lanesList[lanesList.length - 1].documentation.text = text;
    }
    return lanesList;
  }

  _addLaneObjectProperties(attributes, flag, lanesList) {
    if (flag && lanesList.length > 0) {
      lanesList[lanesList.length - 1].documentation = {
        id: this._toIntegerId(attributes.Id),
      };
    }
    return lanesList;
  }

  _addColorProperties(attributes, build, properties) {
    if (
      (build === this.tagsTypes.Task || build === this.tagsTypes.Subprocess)
      && attributes.FillColor
    ) {
      properties.color = attributes.FillColor || '';
    }
    return properties;
  }

  _addParticipantProperties(attributes, participants) {
    const id = Number.parseInt(attributes.Id, 10);
    const participant = {
      id: Number.isNaN(id) ? attributes.Id : id,
      name: unescape(attributes.Name || ''),
    };
    participants.push(participant);
    return participants;
  }

  _addParticipantTypeProperties(attributes, participants, ratio) {
    const participant = participants.pop();
    const type = attributes.Type.split(':');
    participant.type = type.length === 1 ? type[0] : type[1];
    participants.push(participant);
    if (attributes['xmlns:edition']) ratio = 1;
    return { participants, ratio };
  }

  _addPerformerTextProperties(text, participants, properties) {
    const findParticipant = participants.find((value) => {
      const id = Number.parseInt(value.id, 10);
      if (Number.isNaN(id)) return value.id.toUpperCase() === text.toUpperCase();
      return id === Number.parseInt(text, 10);
    });
    if (findParticipant) {
      const { id, name } = findParticipant;
      properties.groups = this._addArrayProperties(
        {
          id,
          name,
        },
        properties.groups,
      );
    }
    return properties;
  }

  _canHaveGroup(type) {
    return type === 'manual' || type === 'user' || type === 'none';
  }

  // -- Build the process map from an input xml file -- //
  buildProcessMap() {
    // -- variables declarations -- //
    let tagPoints = [];
    let tagName = '';
    let tagToBuild = '';
    let tagProperties = {};
    let dataStoreList = [];
    let wsChecking = [];
    let lanesList = [];
    let laneFlag = false;
    let workflowDoc = {};
    let defaultPool = {};
    let participants = [];
    let performerText = false;
    let ratio = 2;
    let associations = [];
    let transitions = [];

    // -- Starting to launch events -- //
    // this.parser.oncdata = (cdata) => {
    //   ratio = cdata.trim() === `VERSION-${Config.VERSION}` ? 1 : ratio;
    // };
    // -- For each label that open news properties of elements are adds -- //
    this.parser.onopentag = (tag) => {
      // -- Split and filter the tag name -- //
      tagName = this._filterTagName(tag.name);
      // console.log(tagName);
      // -- Extract only the attributes of the current tag -- //
      const attr = tag.attributes;

      switch (tagName) {
        case 'package':
          if (attr['xmlns:edition']) ratio = 1;
          console.log('RATIO', ratio);
          break;
        case 'activity': {
          tagProperties.activity = this._addActivityProperties(attr);
          break;
        }
        case 'task': {
          tagToBuild = this.tagsTypes.Task;
          tagProperties = this._addTaskProperties(attr, tagProperties);
          tagProperties = this._addScheduleTaskProperties(attr, tagProperties);
          break;
        }
        case 'taskuser': {
          tagProperties.taskType = 'user';
          break;
        }
        case 'taskservice': {
          tagProperties.taskType = 'service';
          break;
        }
        case 'tasksend': {
          tagProperties.taskType = 'send';
          break;
        }
        case 'taskreceive': {
          tagProperties.taskType = 'receive';
          break;
        }
        case 'taskmanual': {
          tagProperties.taskType = 'manual';
          break;
        }
        case 'taskscript': {
          tagProperties.taskType = 'script';
          break;
        }
        case 'taskbusinessrule': {
          tagProperties.taskType = 'businessRule';
          break;
        }
        case 'roleref': {
          tagProperties = this._addRoleTaskProperties(attr, tagProperties);
          break;
        }
        case 'performer': {
          if (Config.ENV !== 'Offline' && this._canHaveGroup(tagProperties.taskType)) {
            const { properties, textPerf } = this._addGroupTaskproperties(
              attr,
              tagProperties,
              performerText,
            );
            tagProperties = properties;
            performerText = textPerf;
          } else {
            performerText = true;
          }
          break;
        }
        case 'formref': {
          tagProperties = this._addFormsProperties(attr, tagToBuild, tagProperties);
          break;
        }
        case 'apiaevent': {
          const { properties, webServiceCtrl } = this._addEventsProperties(
            attr,
            tagProperties,
            tagToBuild,
            wsChecking,
          );
          tagProperties = properties;
          wsChecking = webServiceCtrl;
          break;
        }
        case 'busclaparbinding': {
          tagProperties = this._addBusinessClassProperties(attr, tagProperties, wsChecking);
          break;
        }
        case 'loop': {
          tagProperties = this._addLoopTypeProperties(attr, tagProperties);
          break;
        }
        case 'loopmultiinstance': {
          tagProperties = this._addMultiInstanceLoopProperties(attr, tagProperties);
          break;
        }
        case 'loopstandard': {
          tagProperties = this._addStandardLoopProperties(attr, tagProperties);
          break;
        }
        case 'apiatskstate': {
          tagProperties = this._addStateTaskProperties(attr, tagProperties);
          break;
        }
        case 'event': {
          tagToBuild = this.tagsTypes.Event;
          tagProperties.id = tagProperties.activity.id;
          break;
        }
        case 'startevent': {
          tagToBuild = `${this.tagsTypes.Start}${tagToBuild}`;
          tagProperties = this._addEventTypeProperties(attr, tagProperties);
          tagProperties = this._addEventImplementationProperties(attr, tagProperties);
          break;
        }
        case 'intermediateevent': {
          tagToBuild = `${this.tagsTypes.Intermediate}${tagToBuild}`;
          tagProperties = this._addEventTypeProperties(attr, tagProperties);
          tagProperties = this._addEventImplementationProperties(attr, tagProperties);
          break;
        }
        case 'endevent': {
          tagToBuild = `${this.tagsTypes.End}${tagToBuild}`;
          tagProperties = this._addEventTypeProperties(attr, tagProperties);
          tagProperties = this._addEventImplementationProperties(attr, tagProperties);
          break;
        }
        case 'triggerresultmessage': {
          tagProperties = this._addTriggerResultMessageProperties(attr, tagProperties);
          break;
        }
        case 'triggertimer': {
          tagProperties.timer = {};
          tagProperties = this._addTriggerTimerProperties(attr, tagProperties);
          break;
        }
        case 'webservice_pro': {
          if (!tagProperties.message) {
            tagProperties.inputMessageRef = {
              wsMethodName: attr.ws_method_name || '',
            };
            tagProperties.clasification = 'inputMessageRef';
          } else {
            tagProperties.message.webService = attr.ws_method_name || '';
            tagProperties.clasification = 'message';
          }

          break;
        }
        case 'ws_attribute_pro': {
          tagProperties = this._addWebServiceAttributeProcessOrTaskProperties(
            attr,
            tagProperties,
            tagProperties.clasification,
          );
          break;
        }
        case 'webservice_tsk': {
          const clasification = tagToBuild === `${this.tagsTypes.Intermediate}Event` ? 'message' : 'inputMessageRef';
          if (clasification === 'message') {
            tagProperties.message.webService = attr.ws_method_name || '';
          } else {
            tagProperties[clasification] = {
              wsMethodName: attr.ws_method_name || '',
            };
          }
          break;
        }
        case 'ws_attribute_tsk': {
          const clasification = tagToBuild === `${this.tagsTypes.Intermediate}Event` ? 'message' : 'inputMessageRef';
          tagProperties = this._addWebServiceAttributeProcessOrTaskProperties(
            attr,
            tagProperties,
            clasification,
          );
          break;
        }
        case 'route': {
          tagToBuild = this.tagsTypes.Gateway;
          tagProperties = this._addRouteGatewayProperties(attr, tagProperties);
          break;
        }
        case 'transition': {
          tagToBuild = this.tagsTypes.Connector;
          tagProperties = this._addTransitionOrAssociationProperties(attr, tagName);
          break;
        }
        case 'condition': {
          if (tagToBuild === this.tagsTypes.Connector) tagProperties.kind = 'expression';
          break;
        }
        case 'association': {
          tagToBuild = this.tagsTypes.Connector;
          tagProperties = this._addTransitionOrAssociationProperties(attr, tagName);
          break;
        }
        case 'blockactivity': {
          tagToBuild = this.tagsTypes.Subprocess;
          tagProperties = this._addBlockActivitySubprocessProperties(attr, tagProperties);
          break;
        }
        case 'subflow': {
          tagToBuild = this.tagsTypes.Subprocess;
          tagProperties = this._addSubflowSubprocessProperties(attr, tagProperties);
          break;
        }
        case 'artifact': {
          tagProperties = this._addArtifactProperties(attr, tagProperties);
          tagToBuild = this._defineArtifactToBuildAcoordingToType(tagProperties);
          break;
        }
        case 'pool': {
          tagProperties = this._addPoolProperties(attr);
          tagToBuild = tagProperties.visible !== undefined ? this.tagsTypes.Pool : '';
          defaultPool = this._addDefaultPoolProperties(attr);
          break;
        }
        case 'pools': {
          /**
           * For the first time it is loaded in "Apia" it can
           * read the new version attribute
           */
          if (attr['xmlns:edition']) ratio = 1;
          break;
        }
        case 'lane': {
          lanesList = this._addLanesProperties(attr, lanesList);
          laneFlag = true;
          break;
        }
        case 'datastore': {
          tagToBuild = this.tagsTypes.DataStore;
          dataStoreList = this._addDataStoreProperties(attr, dataStoreList);
          break;
        }
        case 'datastorereference': {
          tagToBuild = this.tagsTypes.DataStore;
          tagProperties = this._addDataStoreReferenceProperties(attr, dataStoreList);
          break;
        }
        case 'dataobject': {
          tagToBuild = this.tagsTypes.DataObject;
          tagProperties = this._addDataObjectInputOutputProperties(attr, tagProperties);
          break;
        }
        case 'datainput': {
          tagToBuild = this.tagsTypes.DataInput;
          tagProperties = this._addDataObjectInputOutputProperties(attr, tagProperties);
          break;
        }
        case 'dataoutput': {
          tagToBuild = this.tagsTypes.DataOutput;
          tagProperties = this._addDataObjectInputOutputProperties(attr, tagProperties);
          break;
        }
        case 'object': {
          tagProperties = this._addObjectDocumentationProperties(attr, tagToBuild, tagProperties);
          dataStoreList = this._addDataStoreDocumentationProperties(
            attr,
            tagToBuild,
            dataStoreList,
          );
          lanesList = this._addLaneObjectProperties(attr, laneFlag, lanesList);
          break;
        }
        case 'participant': {
          participants = this._addParticipantProperties(attr, participants);
          break;
        }
        case 'participanttype': {
          const props = this._addParticipantTypeProperties(attr, participants, ratio);
          participants = props.participants;
          ratio = props.ratio;
          break;
        }
        case 'nodegraphicsinfo': {
          tagProperties = this._addNodeGraphicsInfoProperties(
            attr,
            tagToBuild,
            tagProperties,
            ratio,
          );
          lanesList = this._addLanesSizeProperties(attr, tagToBuild, lanesList, ratio);
          tagProperties = this._addColorProperties(attr, tagToBuild, tagProperties);
          break;
        }
        case 'coordinates': {
          tagPoints = this._addCoordinateProperties(attr, tagToBuild, tagPoints, ratio, laneFlag);
          lanesList = this._addLanesCoordinatesProperties(
            attr,
            tagToBuild,
            lanesList,
            ratio,
            tagPoints,
          );
          break;
        }
        case 'apiaproevent': {
          const { properties, webServiceCtrl } = this._addApiaProEventsProperties(
            attr,
            tagProperties,
          );
          tagProperties = properties;
          wsChecking = webServiceCtrl;
          break;
        }
        default:
          break;
      }
    };

    this.parser.oncdata = (text) => {
      const textValue = text.trim();
      if (tagToBuild === this.tagsTypes.Connector && tagProperties.kind === 'expression') {
        tagProperties.expression = textValue;
      }
    };

    // -- For capture the documentation text in the associated text node -- //
    this.parser.ontext = (text) => {
      const { tag } = this.parser;
      const textValue = text.trim();
      tagName = this._filterTagName(tag ? tag.name : '');
      if (tagName === 'documentation' && textValue !== '') {
        if (tagProperties.documentation !== undefined) {
          tagProperties.documentation.text = textValue;
          if (tagToBuild === '') {
            workflowDoc = tagProperties.documentation;
            if (Config.ENV !== 'Offline') workflowDoc = this._extractRealDocumentationInfo(workflowDoc);
          }
          if (tagToBuild === Config.TASK && Config.ENV !== 'Offline') {
            tagProperties.documentation = this._extractRealDocumentationInfo(
              tagProperties.documentation || {},
            );
          }
        }
        // When the documentation is add to datastore list //
        if (tagToBuild === this.tagsTypes.DataStore) {
          const elem = dataStoreList.pop();
          elem.documentation.text = textValue;
          dataStoreList.push(elem);
          tagToBuild = '';
        }
        // When the documentation is add to lane list //
        if (laneFlag) {
          this._addLaneDocumentationProperties(textValue, lanesList);
        }
      }
      if (performerText) {
        tagProperties = this._addPerformerTextProperties(textValue, participants, tagProperties);
        performerText = false;
      }
      if (
        tagName === 'expression'
        && tagToBuild === this.tagsTypes.Connector
        && tagProperties.kind === 'expression'
      ) {
        tagProperties.expression = textValue;
      }
    };

    // -- For each label that closes a new graphic element is constructed -- //
    this.parser.onclosetag = (name) => {
      const { tag } = this.parser;
      const attr = tag.attributes;
      tagName = this._filterTagName(name);

      // -- Compare if the current tag is a valid tag to build -- //
      if (this._isElementToBuild(tagName) && tagToBuild !== '') {
        // -- Compare if the current tag is a connector tag to build -- //
        if (tagName === 'transition' || tagName === 'association') {
          tagProperties = this._addConnectorsPointsProperties(tagPoints, tagProperties);
          if (tagName === 'association') {
            associations.push(tagProperties);
          } else {
            transitions.push(tagProperties);
          }
          // console.log(
          //   ` - ${tagToBuild}(${tagProperties.type}) - ${tagPoints.length} punto(s):`,
          //   tagProperties,
          // );
        }
        // -- In this case however is another element graphic to build -- //
        if (tagToBuild !== this.tagsTypes.Connector) {
          const point = tagPoints.pop();
          const xCoord = parseInt(point.x, 10);
          const yCoord = parseInt(point.y, 10);
          if (tagToBuild === this.tagsTypes.Pool) {
            this._buildLanesElement(lanesList, tagProperties);
          }
          // console.log(
          //   ` - ${tagToBuild} - Coordenadas:[x:${xCoord} y:${yCoord}]`, '\n',
          //   tagProperties,
          // );
          this.editor.createElement(tagToBuild, xCoord, yCoord, tagProperties, false);
        }
        // -- Always clean these variables -- //
        tagProperties = {};
        tagToBuild = '';
        tagPoints = [];
      }
      // Reset the lane flag when de lane element is finish
      if (tagName === 'lane') laneFlag = false;

      if (tagName === 'workflowprocess') {
        tagProperties = this._addWorkflowProperties(attr, tagProperties);
        tagProperties.defaultPool = defaultPool;
        if (workflowDoc !== {}) tagProperties.documentation = workflowDoc;
        const [existWorkflow] = getElementsOfType(Config.WORKFLOW, this.editor.elements);
        if (!existWorkflow) {
          this.editor.createElement('Workflow', 0, 0, tagProperties, false);
          console.log('tags', tagProperties);
          // console.log(' - WorkflowProcess', tagProperties);
          console.log(1);
          this._createAllConnectors([...associations, ...transitions]);
          associations = [];
          transitions = [];
        }
        tagProperties = {};
        workflowDoc = {};
      }

      if (tagName === 'package') {
        this._addPackageProperties(attr);
      }
      // if (tagName === 'performer') performerText = false;
      this.parser.onerror = (e) => {
        this.parser.error = null;
        this.parser.resume();
        this.emit('xpdl-error', e);
      };
    };
    this.parser.write(this.xml).close();

    if (Config.ENV === 'Offline') {
      const extractor = new Extractor(this.editor.elements);
      extractor.readExtract(this.parser);
    }

    this.emit('xpdl-parsed');
  }

  _extractRealDocumentationInfo(documentation) {
    const extractParser = sax.parser(true, {
      normalize: true,
      trim: true,
    });
    const { textDoc, otherTags } = Extractor.extractUnmodifiedInfo(
      documentation.text || '',
      extractParser,
    );
    if (otherTags) documentation.text = '';
    if (textDoc !== '') {
      documentation.text = textDoc;
    }
    return documentation;
  }

  _createAllConnectors(connectors) {
    connectors.forEach((connector) => {
      this.editor.createElement(Config.CONNECTOR, 0, 0, connector, false, false);
    });
  }

  // -- Check if the tag is a valid tag to build -- //
  _isElementToBuild(tagName) {
    if (
      tagName === 'activity'
      || tagName === 'pool'
      || tagName === 'association'
      || tagName === 'artifact'
      || tagName === 'datainput'
      || tagName === 'dataoutput'
      || tagName === 'datastorereference'
      || tagName === 'transition'
    ) {
      return true;
    }
    return false;
  }

  // -- Method that adds elements to an array of properties -- //
  _addArrayProperties(property, propertyList) {
    if (propertyList === undefined) {
      propertyList = [];
    }
    propertyList.push(property);
    return propertyList;
  }

  _getLaneList(elementsList) {
    const lanes = elementsList.filter((value) => this._isLane(value));
    return lanes;
  }

  // Check the Coordinate and fixed if the point is incorrect
  _checkCoordinateLane(currentCoord, previousLane) {
    if (previousLane !== undefined) {
      const height = parseInt(previousLane.size.height, 10);
      const y = parseInt(previousLane.y, 10);
      const realCoordY = height + y || undefined;
      if (currentCoord.y !== realCoordY) {
        // console.log(currentCoord, realCoordY);
        return { x: currentCoord.x, y: realCoordY };
      }
    }
    return currentCoord;
  }
}
