import * as Snap from 'snapsvg-cjs';
import Config from 'config';
import EventElement from './eventElement';

export default class EndEvent extends EventElement {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(Config.END_EVENT, id, x, y, viewport, properties, isMovable, [
      Config.DELETE,
      Config.TEXT,
    ]);
    this.initElement();
  }

  set name(value) {
    this.emit('element_onpropertychange');
    super.name = value;
    this.updateTextLabel('end_event__group__text', value);
  }

  set type(value) {
    this.emit('element_onpropertychange');
    this._properties.type = value;
    this._updateDetailTypeIcon();
  }

  drawElement() {
    const circle = this._viewport
      .circle(20, 20, this._radius)
      .attr({ class: 'end_event__bg' });

    const endEvent = circle.appendTo(this._viewport);
    const bounds = endEvent.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    const label = this.drawForeignObjLabel(
      -40,
      48,
      120,
      80,
      this.eventName,
      'end_event__group__text',
      `text_${this.id}`,
    );
    label.select('g').attr({
      class: 'end_event__group',
    });
    const icon = this._drawEventDetailTypeIcon();

    this._graphic = this._viewport
      .g(selectionBox, endEvent, label, icon)
      .mousedown(this.onElementMouseDown)
      .attr({
        class: 'end_event',
        id: `${Config.END_EVENT}_${this._properties.id}`,
      });

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }
  }

  _drawEventDetailTypeIcon() {
    let typeIcon;
    let typeIconGroup;
    switch (this.eventType.toUpperCase()) {
      case 'MESSAGE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="end_event__typeicon" transform="scale(1, -1) translate(0, -100)" d="M99 608l401-227 401 227-802 0z m1-84l0-448 802 0 0 448-402-224-400 224z" /></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.02, T${10},${25}`);
        break;
      case 'SIGNAL':
        typeIcon = Snap.parse(
          '<g id="typeicon"><polygon class="end_event__signalicon" points="50 15, 100 100, 0 100"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.17, T${11.5},${8.5}`);
        break;
      case 'TERMINATE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><circle class="end_event__typeicon" cx="0" cy="0" r="12"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s1, T${20},${20}`);
        break;
      case 'ERROR':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="end_event__typeicon" transform="scale(1, -1) translate(0, -100)" d="M0,0c39,114,77,228,116,342c42-55,85-110,128-166c34,70,68,139,102,209c-27-124-55-248-82-372 c-45,51-89,103-134,154C87,111,43,55,0,0z"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.07, T${8},${25}`);
        break;
      case 'NONE':
        typeIcon = Snap.parse('<g id="typeicon"></g>');
        typeIconGroup = typeIcon.select('g');
        break;
      default:
        break;
    }
    return typeIconGroup;
  }

  _updateDetailTypeIcon() {
    // delete old icon
    const oldIcon = this._graphic.select('#typeicon');
    oldIcon.remove();
    // append new icon
    const typeIconGroup = this._drawEventDetailTypeIcon();
    typeIconGroup.appendTo(this._graphic);
  }
}
