import Config from 'config';
import Element from './element';

export default class Lane extends Element {
  constructor(id, x, y, viewport, properties) {
    super(Config.LANE, id, viewport, x, y, properties, false, [Config.DELETE]);

    if (properties) {
      this._height = this._properties.size.height;
      this._width = this._properties.size.width;

      if (!properties.name || properties.name === '') {
        properties.name = Config.LANE;
      }
    }
    this._minHeight = 160;
    this._maxHeight = Number.POSITIVE_INFINITY;
    this._nameBoxWidth = 30;
    this._laneBox = null;
    this._laneNameBox = null;
    this._label = null;
    this.drawElement();
    this.initElement();
  }

  get name() {
    return this._properties.name;
  }

  set name(value) {
    this.emit('element_onpropertychange');
    this._properties.name = value;
    this._updateNameLabel();
  }

  get width() {
    return this._width;
  }

  get height() {
    return this._height;
  }

  set maxHeight(value) {
    this._maxHeight = value;
  }

  get maxHeight() {
    return this._maxHeight;
  }

  drawElement() {
    this._laneBox = this._viewport
      .rect(this._nameBoxWidth, 0, this._width, this._height)
      .attr({ class: 'lane__outer_box' });

    this._laneNameBox = this._viewport
      .rect(this._nameBoxWidth, 0, this._nameBoxWidth, this._height)
      .attr({ class: 'lane__name_box' });

    const box = this._laneBox.appendTo(this._viewport);
    const bounds = box.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    this._label = this.drawForeignObjLabel(
      -this.height,
      this._nameBoxWidth,
      this.height,
      this._nameBoxWidth,
      this.name,
      'lane__group__text',
      `lane_${this.id}`,
    );
    const lblGroup = this._label.select('g');
    lblGroup.attr({
      class: 'lane__group',
    });
    lblGroup.transform('R-90');

    this._graphic = this._viewport
      .g(selectionBox, this._laneBox, this._laneNameBox, this._label)
      .attr({ class: 'lane', id: `${Config.LANE}_${this._properties.id}` })
      .mousedown(this.onElementMouseDown)
      .click(this.onElementClick);

    this._updateNameLabel();
  }

  _updateNameLabel() {
    // reposition label
    const bounds = this._laneNameBox.getBBox();
    const fo = this._graphic.select(`#lane_${this.id}`);
    fo.attr({
      x: -bounds.height,
      width: bounds.height,
    });
    this.updateTextLabel('lane__group__text', this.name);
  }

  resizeWidth(w) {
    this._width = w;
    this._laneBox.attr({
      width: w,
    });
    this._selectionBox.attr({
      width: w + this.SELECTSIZE,
    });
  }

  resizeHeight(h) {
    this._laneBox.attr({
      height: h,
    });
    this._laneNameBox.attr({
      height: h,
    });
    this._selectionBox.attr({
      height: h + this.SELECTSIZE,
    });
    this._height = h;
    this._updateNameLabel();
  }

  _onSelected() {
    this._drawResizeBar();
    this.emit('element_selected');
  }

  _onDeselected() {
    this._removeResizeBar();
    this.emit('element_unselected');
  }

  _drawResizeBar() {
    const bounds = this._selectionBox.getBBox();
    this._handle = this._viewport
      .rect(
        bounds.x2 - bounds.width / 2 - this._hSize / 2,
        bounds.y2 - this._hSize / 2,
        this._hSize,
        this._hSize,
      )
      .attr({ class: 'resize__handle--row', id: 'resize__handle' });

    const handlesGroup = this._viewport
      .g(this._handle)
      .drag(this.onHandleDrag, this.onHandleDragStart, this.onHandleDragEnd)
      .attr({ class: 'resize', id: 'resize_bar' });
    this._graphic.append(handlesGroup);
  }

  _removeResizeBar() {
    if (this._graphic.select('#resize_bar')) {
      this._graphic.select('#resize_bar').remove();
    }
  }

  onHandleDragStart() {
    this.emit('lane_resize_start');
  }

  onHandleDrag(dx, dy, x, y, e) {
    const selBox = this._selectionBox;

    const snapInvMatrix = selBox.transform().diffMatrix.invert();
    snapInvMatrix.e = 0;
    snapInvMatrix.f = 0;
    const tdy = Math.round(snapInvMatrix.y(dx, dy) + this.SELECTSIZE / 2);
    const ep = this._viewport.getMousePoint(e, 0, 0);

    const offY = Math.round(ep.y - this._graphic.matrix.f);

    if (offY >= this._minHeight && offY <= this._maxHeight) {
      this.emit('lane_resizing', offY);
    }
    // update handle position
    this._handle.transform(`t${[0, tdy - offY + this._height]}`);
  }

  onHandleDragEnd() {
    this.emit('lane_resize_end');
    this._removeResizeBar();
    this._drawResizeBar();
  }
}
