import EventEmitter from 'eventemitter3';
import {} from '../utils/snaptoolkit';
import Config from 'config';
import {
  getSquareIntersection,
  getProcessElementByGraphicID,
  getProcessElementByID,
} from '../utils/util';

export default class ConnectorManager extends EventEmitter {
  constructor(editor, history) {
    super();
    this._editor = editor;
    this._paper = editor._paper;
    this._historyManager = history;
    this._isEnabled = false;
    this._source = null;
    this._target = null;
    this._currentTarget = null;
    this._createFromContextMenu = false;
    this._mousemoveHandler = this._mousemoveHandler.bind(this);
    this._mouseupHandler = this._mouseupHandler.bind(this);
    this._mousedownHandler = this._mousedownHandler.bind(this);
  }

  enableConnection(isOn) {
    this._isEnabled = isOn;
    this._paper.removeClass('selectionable');
    this._paper.removeClass('hand');
    this._paper.addClass('connectionable');
    if (!isOn) {
      this._paper.removeClass('connectionable');
      this.reset();
    }
  }

  areConnected(to, from) {
    return this._isConnectorOn(to, from);
  }

  _createGuideLine(element) {
    const mp = this._editor._viewport.getMousePoint(event, 0, 0);
    this._guideLine = this._editor._viewport
      .line(
        element.graphic.matrix.e + element.width / 2,
        element.graphic.matrix.f + element.height / 2,
        mp.x,
        mp.y,
      )
      .attr({
        class: 'connection_guideline',
        id: 'connection_guideline',
      });
  }

  connectOneTime(element) {
    this.reset();
    this._createFromContextMenu = true;
    this._source = element;
    this._target = null;
    this._createGuideLine(element);
    element.toFront();
    for (let i = 0, len = element.boundaryEvents.length; i < len; i++) {
      const el = getProcessElementByID(
        element.boundaryEvents[i],
        this._editor.elements,
      );
      el.toFront();
    }
    window.addEventListener('mousedown', this._mousedownHandler, false);
    window.addEventListener('mousemove', this._mousemoveHandler, false);
    window.addEventListener('mouseup', this._mouseupHandler, false);
  }

  connect(element) {
    if (
      element.elementType !== Config.CONNECTOR
      && element.elementType !== Config.POOL
      && element.elementType !== Config.LANE
      && element.elementType !== Config.GROUP
    ) {
      if (!this._source && this._isEnabled) {
        if (
          element.elementType !== Config.END_EVENT
          && element.elementType !== Config.TEXT
        ) {
          this._source = element;
          this._target = null;
          this._createGuideLine(element);
          if (this._editor._mode === Config.CONNECTOR_MODE) {
            element._contextMenu.hide();
          }
          window.addEventListener('mousemove', this._mousemoveHandler, false);
          window.addEventListener('mouseup', this._mouseupHandler, false);
        } else {
          this._source = null;
        }
      } else if (!this._isEnabled && this._createFromContextMenu) {
        this._target = element;
        this._connectElements(this._source, this._target);
        this.reset();
      } else if (this._isEnabled) {
        this._target = element;
        this._connectElements(this._source, this._target);
        this.reset();
      }
    }
  }

  _mousedownHandler() {
    this._createFromContextMenu = false;
  }

  _mouseupHandler(evt) {
    // console.log(evt.target);
    if (
      (evt.target.id === 'app' && !this._createFromContextMenu)
      || evt.target.closest('.tool_bar')
    ) {
      this.reset();
    }
  }

  reset() {
    if (this._guideLine) this._guideLine.remove();
    this._source = null;
    this._target = null;
    this._createFromContextMenu = false;
    window.removeEventListener('mousemove', this._mousemoveHandler);
    window.removeEventListener('mouseup', this._mouseupHandler);
    window.removeEventListener('mousedown', this._mousedownHandler);
    this._paper.removeClass('invalid-connection');
  }

  _mousemoveHandler(evt) {
    const mp = this._editor._viewport.getMousePoint(evt, 0, 0);
    const w1 = this._source.width / 2;
    const h1 = this._source.height / 2;
    const w2 = 10;
    const h2 = 10;
    const cx1 = this._source.graphic.matrix.e - 10 + w1;
    const cy1 = this._source.graphic.matrix.f - 10 + h1;
    const cx2 = mp.x;
    const cy2 = mp.y;
    const dx = cx2 - cx1;
    const dy = cy2 - cy1;
    const p1 = getSquareIntersection(dx, dy, cx1, cy1, w1, h1);
    const p2 = getSquareIntersection(-dx, -dy, cx2, cy2, w2, h2);
    this._guideLine.attr({
      x1: p1[0],
      y1: p1[1],
      x2: p2[0],
      y2: p2[1],
    });
    this._connectorVisualClues(this._source, evt.target);
  }

  _connectElements(from, to) {
    if (this._isConnectionValid(from, to)) {
      this._currentTarget.graphic.removeClass('element--connectable-hover');
      this._historyManager.save();
      const connectorType = this._getConnectorType(from, to);
      const connector = this._editor.createElement(
        Config.CONNECTOR,
        null,
        null,
        {
          id: this._editor.idIndex + 1,
          from: from.id,
          to: to.id,
          points: [
            { x: 0, y: 0 },
            { x: 0, y: 0 },
          ],
          type: connectorType,
        },
        false,
        false,
      );
      // Set Default Connector Types
      if (to.elementType === Config.DATA_OUTPUT) {
        connector.setInitialDirection('directional');
      }
      if (from.elementType === Config.DATA_INPUT) {
        connector.setInitialDirection('directional');
      }
    }
  }

  _getConnectorType(from, to) {
    if (
      from.elementType === Config.DATA_STORE
      || to.elementType === Config.DATA_STORE
    ) {
      return Config.CONNECTOR_TYPE_ASSOCIATION;
    }
    if (
      from.elementType === Config.DATA_OBJECT
      || to.elementType === Config.DATA_OBJECT
    ) {
      return Config.CONNECTOR_TYPE_ASSOCIATION;
    }
    if (from.elementType === Config.DATA_INPUT) {
      return Config.CONNECTOR_TYPE_ASSOCIATION;
    }
    if (to.elementType === Config.DATA_OUTPUT) {
      return Config.CONNECTOR_TYPE_ASSOCIATION;
    }
    if (from.elementType === Config.TEXT || to.elementType === Config.TEXT) {
      return Config.CONNECTOR_TYPE_ASSOCIATION;
    }
    return Config.CONNECTOR_TYPE_TRANSITION;
  }

  _isConnectionValid(from, to) {
    if (
      from !== to
      && to.elementType !== Config.START_EVENT
      && from.elementType !== Config.END_EVENT
      && from.elementType !== Config.TEXT
      && from.elementType !== Config.DATA_OUTPUT
      && to.elementType !== Config.DATA_INPUT
      && !this._isConnectorOn(from, to)
      && this._isNotBoundaryEvent(from, to)
    ) {
      switch (from.elementType) {
        case Config.DATA_OBJECT:
        case Config.DATA_INPUT:
          if (to.elementType === Config.DATA_OUTPUT) return false;
          if (to.elementType === Config.DATA_INPUT) return false;
          if (to.elementType === Config.DATA_OBJECT) return false;
          if (to.elementType === Config.DATA_STORE) return false;
          break;
        case Config.DATA_STORE:
          if (to.elementType === Config.DATA_OUTPUT) return false;
          if (to.elementType === Config.DATA_INPUT) return false;
          if (to.elementType === Config.DATA_OBJECT) return false;
          if (to.elementType === Config.DATA_STORE) return false;
          if (to.elementType === Config.GATEWAY) return false;
          if (to.elementType === Config.START_EVENT) return false;
          if (to.elementType === Config.INTERMEDIATE_EVENT) return false;
          if (to.elementType === Config.END_EVENT) return false;
          break;
        default:
          break;
      }
      return true;
    }
    return false;
  }

  _isNotBoundaryEvent(from, to) {
    if (
      (from.elementType === Config.TASK
        || to.elementType === Config.SUBPROCESS)
      && to.elementType === Config.INTERMEDIATE_EVENT
    ) {
      if (to.isAttached && to.target === from.id) {
        return false;
      }
    }
    if (
      from.elementType === Config.INTERMEDIATE_EVENT
      && (to.elementType === Config.TASK || to.elementType === Config.SUBPROCESS)
    ) {
      if (from.isAttached && from.target === to.id) {
        return false;
      }
    }
    return true;
  }

  _isConnectorOn(from, to) {
    // from -> to && to --> from
    for (let i = 0, len = this._editor._connections.length; i < len; i++) {
      const connector = this._editor._connections[i];
      if (
        (connector.properties.from === parseInt(from.id, 10)
          && connector.properties.to === parseInt(to.id, 10))
        || (connector.properties.to === parseInt(from.id, 10)
          && connector.properties.from === parseInt(to.id, 10))
      ) {
        return true;
      }
    }
    return false;
  }

  _connectorVisualClues(from, to) {
    // add invalid connection to paper by default
    if (to.closest('#app')) {
      to.closest('#app').classList.add('invalid-connection');
    }
    if (this._currentTarget) {
      this._currentTarget.graphic.removeClass('element--connectable-hover');
      this._guideLine.removeClass('connection_guideline--connectable');
    }
    // get parent elements from event target
    [
      '.task',
      '.subprocess',
      '.intermediate_event',
      '.end_event',
      '.gateway',
      '.data_store',
      '.data_input',
      '.data_output',
      '.data_object',
      '.annotation',
    ].forEach((className) => {
      if (to.closest(className) !== null) {
        const target = getProcessElementByGraphicID(
          to.closest(className).id,
          this._editor.elements,
        );
        if (this._currentTarget !== target && this._currentTarget) {
          this._currentTarget.graphic.removeClass('element--connectable-hover');
          this._guideLine.removeClass('connection_guideline--connectable');
          this._currentTarget = null;
        }
        if (!this._isConnectionValid(from, target)) {
          this._paper.addClass('invalid-connection');
          this._guideLine.removeClass('connection_guideline--connectable');
        } else {
          this._paper.removeClass('invalid-connection');
          target.graphic.addClass('element--connectable-hover');
          this._guideLine.addClass('connection_guideline--connectable');
          this._currentTarget = target;
        }
      }
    });
  }
}
