import { escape, unescape } from 'html-escaper';
import Config from 'config';

export default class Extractor {
  constructor(elements) {
    this.elements = elements;
  }

  get types() {
    return {
      FORM: 'form',
      ATTRIBUTE: 'attribute',
      DOCUMENTATION: 'documentation',
      EVENT: 'event',
    };
  }

  // toEscapeXml(xml) {
  //   let escapeXml = '';
  //   for (let index = 0; index < xml.length; index++) {
  //     const character = xml.charAt(index);
  //     let escapeChar;
  //     switch (character) {
  //       case '<': escapeChar = '&lt;';
  //         break;
  //       case '>': escapeChar = '&gt;';
  //         break;
  //       case '"': escapeChar = '&quot;';
  //         break;
  //       default: escapeChar = character;
  //         break;
  //     }
  //     escapeXml += escapeChar;
  //   }
  //   return escapeXml;
  // }

  readExtract(parser) {
    this.parser = parser;
    this.elements.forEach((element) => {
      const { properties, elementType } = element;
      if (
        properties.documentation
        && (elementType === Config.TASK || elementType === Config.WORKFLOW)
      ) {
        const { documentation } = properties;
        const xml = unescape(documentation.text || '');
        this._extractInfo(xml, element);
      }
    });
  }

  writeExtract(builder, element) {
    this.builder = builder;
    const { forms, events, documentation } = element.properties;
    return {
      documentation: this._buildDocumentation(documentation),
      forms:
        element.elementType === Config.WORKFLOW
          ? this._buildWorkflowForms(forms)
          : this._buildTaskForms(forms),
      events: this._buildEvents(events),
    };
  }

  _buildDocumentation(documentation) {
    let result;
    const Types = this.types;
    if (documentation) {
      const { text } = documentation;
      const documentationEle = this.builder.create(Types.DOCUMENTATION, {
        enconding: 'utf-8',
        headless: true,
      });
      documentationEle.text(text || '');
      // result = this.toEscapeXml(documentationEle.end({ pretty: true }));
      result = escape(documentationEle.end({ pretty: true }));
    }
    return result;
  }

  _buildTaskForms(forms) {
    let result = '';
    const Types = this.types;
    if (forms) {
      const formsEle = this.builder.create('forms', {
        enconding: 'utf-8',
        headless: true,
      });
      forms.forEach((stepFrm) => {
        const { step, EForms, PForms } = stepFrm;
        if (EForms && PForms) {
          const joinForms = EForms.concat(PForms);
          joinForms.forEach((form) => {
            const { id, order, type } = form;
            const attributes = {
              frmId: id,
              frmType: type,
              frmStepId: step,
              frmOrder: order,
            };
            formsEle.ele(Types.FORM, attributes);
          });
        }
      });
      // result = this.toEscapeXml(formsEle.end({ pretty: true }));
      result = escape(formsEle.end({ pretty: true }));
    }
    return result;
  }

  _buildWorkflowForms(forms) {
    let result = '';
    if (forms) {
      const formsEle = this.builder.create('forms', {
        enconding: 'utf-8',
        headless: true,
      });
      forms.forEach((form) => {
        const { name, title, id } = form;
        const attributes = {
          frmId: id,
          frmName: name || title || '',
          frmTitle: title || name || '',
        };
        const formEle = formsEle.ele('form', attributes);
        this._buildAttributesWorkflowForm(formEle, form.attributes);
        this._buildEvents(form.events, formEle);
      });
      // result = this.toEscapeXml(formsEle.end({ pretty: true }));
      result = escape(formsEle.end({ pretty: true }));
    }
    return result;
  }

  _buildAttributesWorkflowForm(formEle, attributes) {
    if (attributes) {
      const Types = this.types;
      attributes.forEach((attribute) => {
        const {
          name, label, dataType, fieldType, grid, regex,
        } = attribute;
        const attrs = {
          attName: name || label || '',
          attLabel: label || name || '',
          attTooltip: '',
          fieldtype: fieldType,
          datatype: dataType,
          grid: grid || '',
          regExp: regex || '',
        };
        formEle.ele(Types.ATTRIBUTE, attrs);
      });
    }
    return formEle;
  }

  _buildEvents(events, element = undefined) {
    let eventsEle;
    const Types = this.types;
    if (events) {
      eventsEle = element
        ? element.ele('events')
        : (eventsEle = this.builder.create('events', {
          enconding: 'utf-8',
          headless: true,
        }));
      events.forEach((event) => {
        const {
          name, busClaName, busClaDesc, order,
        } = event;
        const attributes = {
          evtName: name,
          busClaName: busClaName || busClaDesc || '',
          busClaDesc: busClaDesc || busClaName || '',
          evtExecOrder: order,
        };
        eventsEle.ele(Types.EVENT, attributes);
      });
    }
    return element || eventsEle;
  }

  _extractInfo(xml, element) {
    const { documentation } = element.properties;
    const Types = this.types;
    let flagForm = false;
    let flagDoc = false;

    this.parser.onopentag = (tag) => {
      const { name, attributes } = tag;
      switch (name) {
        case Types.FORM:
          this._addFormProperties(element, attributes);
          flagForm = true;
          break;
        case Types.ATTRIBUTE:
          this._addAttributeProperties(element, attributes);
          break;
        case Types.EVENT:
          this._addEventProperties(element, attributes, flagForm);
          break;
        case Types.DOCUMENTATION:
          flagDoc = true;
          break;
        default:
          break;
      }
    };

    this.parser.ontext = (text) => {
      const textValue = text.trim();
      const { name } = this.parser.tag;
      if (name === Types.DOCUMENTATION) {
        documentation.text = textValue || '';
      }
    };

    this.parser.onclosetag = (name) => {
      if (name === Types.FORM) flagForm = false;
    };

    this.parser.onerror = () => {
      this.parser.error = null;
      this.parser.resume();
    };

    this.parser.write(xml).close();
    if (documentation.text === '' || !flagDoc) delete element.properties.documentation;
  }

  static extractUnmodifiedInfo(value, parser) {
    let textDoc = '';
    let flagDocumentation = false;
    let otherDirtyTags = false;
    const xml = unescape(value);
    parser.onopentag = ({ name }) => {
      if (name === 'forms' || name === 'events') {
        otherDirtyTags = true;
      }
      if (name === 'documentation') {
        flagDocumentation = true;
      }
    };
    parser.ontext = (text) => {
      textDoc = flagDocumentation ? text.trim() : '';
    };
    parser.onclosetag = (name) => {
      if (name === 'documentation') flagDocumentation = false;
    };
    parser.onerror = () => {
      parser.error = null;
      parser.resume();
    };
    parser.write(xml).close();
    return { textDoc, otherTags: otherDirtyTags };
  }

  _addFormProperties(element, attributes) {
    const { properties, elementType } = element;
    if (!properties.forms) properties.forms = [];
    const { forms } = properties;
    const { frmId } = attributes;

    if (elementType === Config.WORKFLOW) {
      this._addWorkflowFormProperties(forms, parseInt(frmId, 10), attributes);
    } else {
      this._addTaskFormProperties(forms, parseInt(frmId, 10), attributes);
    }
    // forms.push(form);
  }

  _addWorkflowFormProperties(forms, id, attributes) {
    const { frmName, frmTitle } = attributes;
    const form = {
      id,
      name: frmName,
      title: frmTitle,
    };
    forms.push(form);
  }

  _addTaskFormProperties(forms, id, attributes) {
    const { frmType, frmStepId, frmOrder } = attributes;
    let step = forms.find(
      (formStep) => formStep.step === parseInt(frmStepId, 10),
    );

    if (!step) {
      step = {
        // id: forms.length + 1,
        id: forms.length,
        step: parseInt(frmStepId, 10),
        name: 'Step',
        EForms: [],
        PForms: [],
        entity: '0',
        process: '0',
      };
      forms.push(step);
    }
    const typeForms = frmType === 'E' ? step.EForms || [] : step.PForms || [];
    typeForms.push({ id, order: parseInt(frmOrder, 10), type: frmType });
    const type = frmType === 'E' ? 'entity' : 'process';
    step[type] = typeForms.length.toString();
  }

  _addAttributeProperties(element, tagAttrs) {
    const { forms } = element.properties;
    const lastForm = forms.pop();
    if (!lastForm.attributes) lastForm.attributes = [];
    const { attributes } = lastForm;
    const {
      attName, attLabel, fieldtype, datatype, regExp, grid,
    } = tagAttrs;
    attributes.push({
      id: attributes.length + 1,
      name: attName,
      label: attLabel,
      fieldType: fieldtype,
      dataType: datatype,
      regex: regExp,
      grid,
    });
    forms.push(lastForm);
  }

  _addEventProperties(element, attributes, flagForm) {
    const { properties } = element;
    const {
      evtName, busClaName, busClaDesc, evtExecOrder,
    } = attributes;
    const event = {
      name: evtName,
      busClaName,
      busClaDesc,
      order: parseInt(evtExecOrder, 10),
    };
    const { forms } = properties;
    let lastForm = {};
    if (flagForm) lastForm = forms.pop();
    if (flagForm && !lastForm.events) lastForm.events = [];
    if (!flagForm && !properties.events) properties.events = [];
    const events = flagForm ? lastForm.events : properties.events;
    events.push({ id: events.length + 1, ...event });
    if (flagForm) forms.push(lastForm);
  }
}
