import Config from 'config';

import Task from '../ui/elements/task';
import StartEvent from '../ui/elements/startEvent';
import EndEvent from '../ui/elements/endEvent';
import IntermediateEvent from '../ui/elements/intermediateEvent';
import Gateway from '../ui/elements/gateway';
import Subprocess from '../ui/elements/subprocess';
import Pool from '../ui/elements/pool';
import Lane from '../ui/elements/lane';
import Group from '../ui/elements/group';
import TextAnnotation from '../ui/elements/textAnnotation';
import DataObject from '../ui/elements/dataObject';
import DataInput from '../ui/elements/dataInput';
import DataOutput from '../ui/elements/dataOutput';
import DataStore from '../ui/elements/dataStore';
import Workflow from '../ui/elements/workflow';
import Connector from '../ui/elements/connector';

export default class elementFactory {
  constructor(editor) {
    this._editor = editor;
  }

  createElement(type, id, x, y, paper, props, isMovable = true) {
    let element;
    switch (type) {
      case Config.TASK:
        element = new Task(id, x, y, paper, props, isMovable);
        break;
      case Config.START_EVENT:
        element = new StartEvent(id, x, y, paper, props, isMovable);
        break;
      case Config.END_EVENT:
        element = new EndEvent(id, x, y, paper, props, isMovable);
        break;
      case Config.INTERMEDIATE_EVENT:
        element = new IntermediateEvent(id, x, y, paper, props, isMovable);
        break;
      case Config.GATEWAY:
        element = new Gateway(id, x, y, paper, props, isMovable);
        break;
      case Config.SUBPROCESS:
        element = new Subprocess(id, x, y, paper, props, isMovable);
        this._checkSubprocessID(element);
        break;
      case Config.POOL:
        element = new Pool(id, x, y, paper, props, isMovable);
        break;
      case Config.LANE:
        element = new Lane(id, x, y, paper, props);
        break;
      case Config.GROUP:
        element = new Group(id, x, y, paper, props, isMovable);
        break;
      case Config.TEXT:
        element = new TextAnnotation(id, x, y, paper, props, isMovable);
        break;
      case Config.DATA_OBJECT:
        element = new DataObject(id, x, y, paper, props, isMovable);
        break;
      case Config.DATA_INPUT:
        element = new DataInput(id, x, y, paper, props, isMovable);
        break;
      case Config.DATA_OUTPUT:
        element = new DataOutput(id, x, y, paper, props, isMovable);
        break;
      case Config.DATA_STORE:
        element = new DataStore(id, x, y, paper, props, isMovable);
        break;
      case Config.WORKFLOW:
        element = new Workflow(id, props);
        this._editor.workflow = element;
        break;
      case Config.CONNECTOR:
        element = new Connector(
          id,
          paper,
          props,
          this._editor.elements,
          this._editor._startMarker,
          this._editor._endMarker,
          this._editor._expressionMarker,
          this._editor._wizardMarker,
          isMovable,
        );
        break;
      default:
        break;
    }
    return element;
  }

  _checkSubprocessID(element) {
    const { properties } = element;
    if (Config.ENV === 'Offline' && !properties.activitySetId) {
      properties.activitySetId = this._editor.idIndex++;
    }
  }
}
