import Config from 'config';
import Element from './element';
import { getViewPortBBox } from '../../utils/util';

export default class Pool extends Element {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(Config.POOL, id, viewport, x, y, properties, isMovable, [
      Config.DELETE,
      Config.LANE,
      Config.TEXT,
    ]);
    this.resizable = true;
    this._poolBox = null;
    this._poolNameBox = null;
    this._label = null;
    this._minHeight = 0;

    const vx = getViewPortBBox(this._viewport);

    if (!properties) {
      this._properties.size = {
        width: vx.width > 0 ? vx.width + 120 : Config.POOL_SIZE.width,
        height: vx.height > 0 ? vx.height + 80 : Config.POOL_SIZE.height,
      };
      this.properties.lanes = [];
      this._height = this._properties.size.height;
      this._width = this._properties.size.width;
    } else {
      this._height = this._properties.size.height;
      this._width = this._properties.size.width;
    }
    this.drawElement();
    this.initElement();
  }

  get width() {
    return this._width;
  }

  get height() {
    return this._height;
  }

  set height(value) {
    this._height = value;
  }

  get name() {
    return this._properties.name;
  }

  set name(value) {
    this.emit('element_onpropertychange');
    this._properties.name = value;
    this._updateNameLabel();
  }

  set minHeight(value) {
    this._minHeight = value;
  }

  drawElement() {
    this._poolBox = this._viewport
      .rect(0, 0, this._width, this._height)
      .attr({ class: 'pool__outer_box' });

    this._poolNameBox = this._viewport
      .rect(0, 0, 30, this._height)
      .attr({ class: 'pool__name_box' });

    const box = this._poolBox.appendTo(this._viewport);
    const bounds = box.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    this._label = this.drawForeignObjLabel(
      -this.height,
      0,
      this.height,
      30,
      this.name,
      'pool__group__text',
      `pool_${this.id}`,
    );
    const lblGroup = this._label.select('g');
    lblGroup.attr({
      class: 'pool__group',
    });
    lblGroup.transform('R-90');

    this._graphic = this._viewport
      .g(selectionBox, this._poolBox, this._poolNameBox, this._label)
      .attr({ class: 'pool', id: `${Config.POOL}_${this._properties.id}` })
      .mousedown(this.onElementMouseDown);

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }

    this._updateNameLabel();
  }

  onHandleDragStart() {
    // stop dragging object while resizing
    this._graphic.undrag();
    // reposition handle
    this.removeResizeHandles();
    this.drawResizeHandles();
    this._contextMenu.hide();
    this.emit('element_resize_start');
  }

  onHandleDrag(dx, dy, x, y, e) {
    const selBox = this._selectionBox;
    const [se] = this._handles;

    const snapInvMatrix = selBox.transform().diffMatrix.invert();
    snapInvMatrix.e = 0;
    snapInvMatrix.f = 0;
    const tdx = Math.round(snapInvMatrix.x(dx, dy) + this.SELECTSIZE / 2);
    const tdy = Math.round(snapInvMatrix.y(dx, dy) + this.SELECTSIZE / 2);

    const ep = this._viewport.getMousePoint(e, 0, 0);

    const offX = Math.round(ep.x - this._graphic.matrix.e);
    const offY = Math.round(ep.y - this._graphic.matrix.f);

    if (offY >= this._minHeight) {
      if (offX > 0 && offY > 0) {
        this.emit('element_resizing', offX, offY);
      }
    }
    se.transform(`t${[tdx - offX + this._width, tdy - offY + this._height]}`);
  }

  onHandleDragEnd() {
    this.emit('element_resize_end');
    this._properties.size = { width: this._width, height: this._height };
    // restore dragging
    this._graphic.drag(
      this.onElementMove,
      this.onElementMoveStart,
      this.onElementMoveEnd,
    );
  }

  resize(width, height) {
    this._width = width;
    this._height = height;

    this._poolBox.attr({
      width,
      height,
    });
    this._poolNameBox.attr({ height });

    this._selectionBox.attr({
      width: width + this.SELECTSIZE,
      height: height + this.SELECTSIZE,
    });
    this._updateNameLabel();
    this._properties.size = { width: this._width, height: this._height };
  }

  _updateNameLabel() {
    // reposition label
    const bounds = this._poolNameBox.getBBox();
    const fo = this._graphic.select(`#pool_${this.id}`);
    fo.attr({
      x: -bounds.height,
      width: bounds.height,
    });
    this.updateTextLabel('pool__group__text', this._properties.name);
  }
}
