import {
  TEMPLATE, ICON_CLASS, TYPE_CLASS, POSITION_CLASS, OPTIONS_TOAST,
} from './ConfigToast';

export default class ToastMessage {
  constructor() {
    this.containerToast = !this._exists() ? document.createElement('div') : this._exists();
    this.body = document.querySelector('body');
    if (!this._exists()) {
      this.containerToast.classList.add('toast-container');
      this.containerToast.innerHTML = TEMPLATE;
      this.body.insertAdjacentElement('afterbegin', this.containerToast);
    }
    this._title = this.containerToast.querySelector('div.toast-body h4');
    this._icon = this.containerToast.querySelector('svg.icon');
    this._message = this.containerToast.querySelector('div.toast-body p');
  }

  _exists() {
    const container = document.querySelector('.toast-container');
    return container;
  }

  cleanClass(element, className) {
    if (element) element.classList.remove(className);
  }

  cleanFirstAllClass(element, classType) {
    const entries = Object.entries(classType);
    for (let index = 0; index < entries.length; index++) {
      this.cleanClass(element, entries[index][1]);
    }
  }

  get icon() {
    return this._icon;
  }

  get title() {
    return this._title;
  }

  get message() {
    return this._message;
  }

  set icon(type) {
    this._icon = this.containerToast.querySelector('svg.icon');
    this.cleanFirstAllClass(this._icon, ICON_CLASS);
    this._icon.classList.add(ICON_CLASS[`${type}Class`]);
  }

  set title(title) {
    if (this._title) this._title.innerText = title;
  }

  set message(msg) {
    if (this._message) this._message.innerHTML = msg;
  }

  set position(position) {
    const positions = position.split('-');
    this.cleanFirstAllClass(this.containerToast, POSITION_CLASS);
    if (positions.length > 0) {
      positions.forEach((value) => {
        this.containerToast.classList.add(value);
      });
    }
  }

  moveXY(coordinate) {
    this.containerToast.style.top = `${coordinate.y}px`;
    this.containerToast.style.left = `${coordinate.x}px`;
  }

  waitTime(execFunction, duration) {
    let timeout;
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      execFunction();
      clearTimeout(timeout);
    }, duration);
  }

  fadeIn() {
    this.containerToast.style.display = 'flex';
    this.waitTime(() => {
      this.containerToast.classList.add('show');
    }, 0.2);
  }

  fadeOut() {
    this.containerToast.classList.remove('show');
    this.waitTime(() => {
      this.containerToast.style.display = 'none';
    }, 0.5);
  }

  show(duration) {
    this.fadeIn();
    this.waitTime(() => {
      this.fadeOut();
    }, duration);
  }

  showMessage(options) {
    const {
      type, title, message, duration, position, coordinate,
    } = options;
    this.cleanFirstAllClass(this.containerToast, TYPE_CLASS);
    this.containerToast.classList.add(type || 'neutral');
    this.icon = type || '';
    this.title = title || '';
    this.message = message || '';
    if (coordinate) this.moveXY(coordinate);
    if (position) this.position = position;
    this.show(duration || 2000);
  }

  showWrongMessage(options) {
    const { title, message } = options;
    this.showMessage({
      ...OPTIONS_TOAST,
      type: 'danger',
      title,
      message,
      duration: 4000,
    });
  }
}
