import axios from 'axios';
import i18next from 'i18next';
import Component from './Component';
import Converter from '../../utils/convertToJson';

export default class DropdownListComponent extends Component {
  constructor(
    nameId,
    panel,
    label,
    add = false,
    url = '',
    baseUrl = '',
    hasLabel = false,
    canReload = false,
    placeholder = '',
  ) {
    super('dropdown', nameId, panel, label);
    this._init(add, url, baseUrl, hasLabel, canReload, placeholder);
  }

  get valueEl() {
    return this._inputSearch.value;
  }

  get selectedItem() {
    return this._selectedItem;
  }

  get disabled() {
    return this._inputSearch.disabled;
  }

  set valueEl(value) {
    this._inputSearch.value = value;
  }

  set local(value) {
    this._isLocal = value;
  }

  set selectedItem(value) {
    this._selectedItem = value;
    this.valueEl = value ? value.name : '';
    this._inputSearch.setAttribute('title', value ? value.name : '');
    const condition = value === undefined;
    this._toggleClearInput(condition);
  }

  set placeholder(value) {
    this._inputSearch.placeholder = value;
  }

  set disabled(value) {
    this._inputSearch.disabled = value;
  }

  get url() {
    return this._url;
  }

  set url(value) {
    this._url = value;
    this.reloadData(this._url);
  }

  get baseUrl() {
    return this._baseUrl;
  }

  set data(value) {
    this._appData = value;
    this._cleanList();
    this._fillList(this._appData);
  }

  get data() {
    return this._appData;
  }

  _init(add, url, baseUrl, hasLabel, canReload, placeholder) {
    this._inputSearch = this.wrapper.querySelector('.dropdown-list__input-control');
    this._caretDown = this.wrapper.querySelector('.dropdown-list__input-action');
    this._clearInput = this.wrapper.querySelector('.dropdown-list__input-empty-action');
    this._itemList = this.wrapper.querySelector('.datalist');
    this._addButton = this.wrapper.querySelector('button.btn');
    this._taskTitleInput = this.wrapper.parentNode.querySelector('#task_title_input');
    this._url = url;
    this._baseUrl = baseUrl;
    this._hasLabel = hasLabel;
    this._canReload = canReload;
    this._add = add;
    this._selectedItem = undefined;
    this._hasPreviousFocus = false;
    this._isLoad = false;
    this._isLocal = !url;
    this._appData = [];
    this._actionReload = false;
    this.placeholder = placeholder;
    this._eventsHandlers();
    this._toggleVisibleButton(add);
  }

  _eventsHandlers() {
    this._caretDown.addEventListener('click', () => this._onDropDown());
    this._caretDown.addEventListener('blur', () => this._onBlurCaretDown());
    this._inputSearch.addEventListener('click', () => this._onFousInputSearch());
    this._inputSearch.addEventListener('blur', () => this._onBlurInputSearch());
    this._inputSearch.addEventListener('input', () => this._onFilterInputSearch());
    this._clearInput.addEventListener('click', () => this.onClearInput());
    this._addButton.addEventListener('click', () => this.emit('add_item'));
  }

  _toggleVisibleButton(value) {
    this._addButton.setAttribute('data-visible', value);
  }

  toggleDisabledButton(value) {
    this._addButton.disabled = value;
  }

  selectFirst() {
    if (this.data.length > 0) {
      const [first] = this.data;
      this.selectedItem = first;
    }
  }

  // fecth data from the URL
  async _getData(url) {
    if (!this._isLocal) {
      this._appData = [];
      try {
        const response = await axios.get(url);
        if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
          this.showInvalidSession();
        }
        const converter = new Converter();
        const data = converter.convertToJson(response.data);
        data.forEach((element) => {
          const {
            id, name, label, type,
          } = element;
          const value = label
            ? {
              id,
              name,
              label,
              type,
            }
            : { id, name };
          this._appData.push(value);
        });
      } catch (error) {
        throw new Error(
          `${i18next.t('dropDownListComponent.txtDropDownListComponentErrorMessage')} \n`,
          error,
        );
      }
    }
  }

  // clean all, reload data and fill the list items
  async reloadData(url = this._url) {
    await this._getData(url);
    this._cleanList();
    this._fillList(this._appData);
  }

  // clean the list remove all items
  _cleanList() {
    while (this._itemList.firstChild) {
      this._itemList.removeChild(this._itemList.firstChild);
    }
  }

  // fill the list inserting all items
  _fillList(data) {
    // hotfix just to render 100 itmes
    const visibleData = data.length > 10 ? data.slice(0, 100) : data;
    visibleData.forEach((value) => {
      const item = document.createElement('li');
      const title = document.createElement('span');
      title.innerText = value.name;
      title.classList.add('datalist__title');
      item.append(title);
      item.setAttribute('data-key', value.id);
      item.setAttribute('title', value.name);
      item.classList.add('datalist__item');
      if (value.label) {
        const span = document.createElement('span');
        span.classList.add('datalist__description');
        span.innerText = value.label;
        span.setAttribute('title', value.label);
        item.appendChild(span);
      }
      if (value.id === -1 && this._canReload) {
        const reloadBtn = document.createElement('a');
        reloadBtn.setAttribute('href', '#');
        reloadBtn.classList.add('datalist__reload-button');
        reloadBtn.title = 'Reload Data';
        reloadBtn.addEventListener('mousedown', () => this._onReloadButton());
        item.append(reloadBtn);
      }
      item.addEventListener('mousedown', () => this._onSelect(item));
      this._itemList.appendChild(item);
    });
  }

  // setting the data-empty attribute of the elements
  _toggleClearInput(value) {
    this._clearInput.setAttribute('data-empty', value);
    this._inputSearch.setAttribute('data-empty', value);
  }

  // setting the visualization of the list
  _toggleList(value) {
    this._itemList.setAttribute('data-visible', value);
    this._caretDown.setAttribute('data-down', value);
  }

  // Get the item by ID
  getItemById(id) {
    return this._appData.find((value) => {
      return parseInt(value.id, 10) === parseInt(id, 10);
    });
  }

  // Get th item by NAME
  getItemByName(name) {
    return this._appData.find((value) => {
      return name.toUpperCase().trim() === value.name;
    });
  }

  _onReloadButton() {
    this.reloadData(this._url);
    this._actionReload = true;
  }

  // Event handler when an item is selected
  _onSelect(item) {
    const id = parseInt(item.getAttribute('data-key'), 10);
    const data = this.getItemById(id);
    if (data) {
      this._inputSearch.value = data.name;
      this._inputSearch.setAttribute('title', data.name);
      this._selectedItem = data;
      this._toggleClearInput(false);
    }
    if (id === -1) {
      this.onClearInput();
    }
    this.emit('select_item', this._selectedItem);
  }

  // Event handler when the list is displayed
  _onDropDown() {
    if (!this.disabled) {
      this.reloadData(this._url);
      this._isLoad = true;
      if (this._isLocal && !this._isLoad) {
        this._cleanList();
        const data = this._appData.length === 0
          ? [
            {
              id: -1,
              name: i18next.t('dropDownListComponent.txtDropDownListComponentNotFoundName'),
              label: i18next.t('dropDownListComponent.txtDropDownListComponentNotFoundLabel'),
            },
          ]
          : this._appData;
        this._fillList(data);
      }
      const value = JSON.parse(this._itemList.getAttribute('data-visible'));
      this._toggleList(!value);
    }
  }

  _calculateDatalistTopRender() {
    const hInput = this._inputSearch.clientHeight;
    const posYInput = parseInt(this._inputSearch.getBoundingClientRect().y, 10)
      + parseInt(this._inputSearch.scrollTop, 10);
    const hWindow = window.innerHeight + parseInt(this._inputSearch.scrollTop, 10);
    const totalHeight = hInput + posYInput + 320;

    if (hWindow < totalHeight) {
      this._itemList.classList.add('datalist--top');
      const hList = this._itemList.clientHeight !== 0 ? this._itemList.clientHeight : 320;
      const renderY = (hList + 58) * -1;
      document.documentElement.style.setProperty('--coordY', `${renderY}px`);
    } else {
      document.documentElement.classList.remove('datalist--top');
    }
  }

  // Event handler when the CARET_DOWN button loose focus
  _onBlurCaretDown() {
    const value = JSON.parse(this._itemList.getAttribute('data-visible'));
    if (value) this._toggleList(false);
  }

  // Event handler when th INPUT_SEARCH get focus
  _onFousInputSearch() {
    this._inputSearch.value = '';
    if (this._hasPreviousFocus) {
      this._selectedItem = undefined;
      this._hasPreviousFocus = false;
      this._toggleClearInput(true);
    }
    if (this._selectedItem && !this._hasPreviousFocus) {
      this._cleanList();
      this._fillList(this._appData);
      this._hasPreviousFocus = true;
    }
    if (!this._selectedItem) this.toggleDisabledButton(true);
    this._onDropDown();
  }

  // Event handler when the INPUT_SEARCH loose focus
  _onBlurInputSearch() {
    if (this._selectedItem) {
      this._inputSearch.value = this._selectedItem.name;
    } else if (!this._selectedItem && !this._add && !this._actionReload) {
      this._inputSearch.value = '';
    }
    this._toggleList(false);
    this._hasPreviousFocus = false;
    if (!this._selectedItem && !this._actionReload) {
      this._cleanList();
      this._fillList(this._appData);
      if (this.valueEl === '' && this._add) {
        this._toggleClearInput(true);
        this.toggleDisabledButton(true);
      }
    }
    if (this._actionReload) {
      this._inputSearch.focus();
      this._actionReload = false;
      this._onDropDown();
    }
  }

  _applyFilter(value, data) {
    return data.filter((item) => {
      const { name, label } = item;
      const find = value.toUpperCase().trim();
      if (label) {
        return name.toUpperCase().includes(find) || label.toUpperCase().includes(find);
      }
      return name.toUpperCase().includes(find);
    });
  }

  // Event handler when typed into INPUT_SEARCH
  _onFilterInputSearch() {
    const { value } = this._inputSearch;
    this._selectedItem = undefined;
    this._cleanList();
    const data = this._applyFilter(value, this._appData);
    if (data.length === 0) {
      data.push({
        id: -1,
        name: i18next.t('dropDownListComponent.txtDropDownListComponentNotFoundName'),
        label: i18next.t('dropDownListComponent.txtDropDownListComponentNotFoundLabel'),
      });
    }
    this._fillList(data);

    const visible = JSON.parse(this._itemList.getAttribute('data-visible'));
    if (!visible) this._toggleList(true);
    this._selectedItem = this.getItemByName(value);
    this.emit('select_item', this._selectedItem);
  }

  // Event handler when clean value into INPUT_SEARCH
  onClearInput() {
    this._toggleClearInput(true);
    this._selectedItem = undefined;
    this._inputSearch.value = '';
    this._hasPreviousFocus = false;
    this._cleanList();
    this._fillList(this._appData);
    this.toggleDisabledButton(true);
    this.emit('clear_item');
  }
}
