import Component from './Component';

export default class TabComponent extends Component {
  constructor(nameId, panel, tabs = []) {
    super('tabpanel', nameId, panel);
    this._tabsArray = tabs;
    this._init();
  }

  get activeTab() {
    return this._wrapperTabs.querySelector('li a.active');
  }

  get visibleTabBody() {
    return this.panel.querySelector('div.tabpanel_body[data-visible="true"]');
  }

  set disabledAllTabs(value) {
    this._tabsElements.forEach((tab) => {
      if (value) {
        tab.classList.add('disabled');
      } else {
        tab.classList.remove('disabled');
      }
    });
  }

  getTab(id) {
    return this.panel.querySelector(`.tabpanel_body[data-tab=${id}]`);
  }

  _init() {
    this._wrapperTabs = this.wrapper.querySelector('header.tabpanel_header ul');
    this._buildTabs();
    this._tabsElements = this._wrapperTabs.querySelectorAll('li a');
    this._initEvents();
  }

  _buildTabs() {
    if (this._wrapperTabs && this._tabsArray.length > 0) {
      for (let index = 0; index < this._tabsArray.length; index++) {
        const value = this._tabsArray[index];
        const tab = document.createElement('a');
        tab.setAttribute('href', '#');
        tab.setAttribute('data-target', value.targetId);
        tab.innerText = value.title;
        if (index === 0) tab.classList.add('active');
        const tabBody = this.tabBodyById(tab);
        this._toggleVisible(tabBody, index === 0);
        const li = document.createElement('li');
        this._wrapperTabs.append(li);
        li.append(tab);
      }
    }
  }

  _initEvents() {
    this._tabsElements.forEach((tab) => {
      tab.addEventListener('click', () => {
        this._onActiveTab(tab);
      });
    });
  }

  _isDisabled(tab) {
    return tab.classList.contains('disabled');
  }

  tabBodyById(tab) {
    const id = tab.getAttribute('data-target');
    const tabBody = this.panel.querySelector(`#body_${id}`);
    return tabBody;
  }

  _onActiveTab(tab) {
    if (!this._isDisabled(tab)) {
      this.activeTab.classList.toggle('active');
      tab.classList.toggle('active');
      const tabBody = this.tabBodyById(tab);
      this._toggleVisible(this.visibleTabBody, false);
      this._toggleVisible(tabBody, true);
    }
  }

  _toggleVisible(tabBody, value) {
    tabBody.setAttribute('data-visible', value);
  }
}
