import Gateway from './facilis/gatewayFacilis';

export default class GatewayPanel extends Gateway {
  constructor(editor) {
    super(editor);
    this._init();
  }

  _init() {
    super._init();
    // Variables Initializations
    this._executionType = this._panel.querySelector('#gateway_execution');
    this.wrapExecutionType = this._executionType.parentNode.parentNode;
    // Events Initializations
    this._executionType.addEventListener('input', () => {
      this._onExecutionTypeChanged();
    });
  }

  get executionType() {
    return this._executionType.value;
  }

  set executionType(value) {
    this._executionType.value = value;
  }

  show(el) {
    super.show(el);
    this._showExecution(el);
  }

  _showExecution(el) {
    const execution = el.executionType;
    const { type } = el;
    this.executionType = execution || 'manual';
    const condition = type.toUpperCase() === 'EXCLUSIVE' || type.toUpperCase() === 'INCLUSIVE';
    this._changeVisibility(this.wrapExecutionType, condition);
  }

  _onTypeSelectChanged() {
    super._onTypeSelectChanged();
    const newType = this.typeSelect.toUpperCase();
    const condition = newType === 'EXCLUSIVE' || newType === 'INCLUSIVE';
    this._changeVisibility(this.wrapExecutionType, condition);
  }

  _onExecutionTypeChanged() {
    if (this.element) {
      const oldExec = this.element.executionType;
      const newExec = this.executionType;
      if (oldExec !== newExec) {
        this.element.executionType = newExec;
      }
    }
  }
}
