import cloneDeep from 'lodash/cloneDeep';
import Config from 'config';
import axios from 'axios';
import i18next from 'i18next';
import Manager from '../facilis/managers/manager';
import { OPTIONS_TOAST } from '../ConfigCmp';
import Converter from '../../../utils/convertToJson';
import { indexByProperty, mapInOutParameters, visibilityComponent } from '../../../utils/util';
import { validateApiaName, validateField } from '../facilis/validator';

export default class EventManager extends Manager {
  constructor(id, panel, options = {}) {
    super(id, 'event', panel);
    this._initElements(options);
    this._changeElementIDs(this.elementsCtrl);
    this._initEventsElements();
    this.loadEventList = 0;
  }

  get elementsCtrl() {
    const elements = [
      ...this.inputs,
      this._eventSelect.wrapper,
      this._busClassSelect.wrapper,
      this._eventsList.wrapper,
      this._bindingsList.wrapper,
      this._eventCondition.wrapper,
      this.wrapBindingAttributeType,
    ];
    return elements;
  }

  get eventSelect() {
    return this._eventSelect.selectedItem;
  }

  get busClassSelect() {
    return this._busClassSelect.selectedItem;
  }

  get eventCondition() {
    return this._eventCondition.conditionArea;
  }

  get bindingType() {
    return this._bindingType.value;
  }

  get bindingValue() {
    return this._bindingValue.value.trim();
  }

  get bindingAttributeType() {
    const radios = this.wrapBindingAttributeType;
    return radios.querySelector('input[type=radio]:checked').value;
  }

  get bindingAttributesTypeAll() {
    return this.wrapBindingAttributeType.querySelectorAll('input[type=radio]');
  }

  get bindingAttribute() {
    return this._bindingAttribute.selectedItem;
  }

  set eventSelect(value) {
    this._eventSelect.selectedItem = value;
  }

  set busClassSelect(value) {
    this._busClassSelect.selectedItem = value;
  }

  set eventCondition(value) {
    this._eventCondition.conditionArea = value;
  }

  set bindingType(value) {
    this._bindingType.value = value;
  }

  set bindingValue(value) {
    this._bindingValue.value = value;
  }

  set bindingAttributeType(value) {
    const radio = this.wrapBindingAttributeType.querySelector(`#binding_attr_type_${value}`);
    radio.checked = true;
  }

  set bindingAttribute(value) {
    this._bindingAttribute.selectedItem = value;
  }

  set eventsList(value) {
    this.loadEventList++;
    const condition = this.panel.id === 'manager_workflow_events';
    // stop unnecessary event list refresh
    if (condition && this.loadEventList === 1) {
      this._eventsList.arrayList.data = cloneDeep(value || []);
      this._eventsList.refreshList();
    } else {
      this._eventsList.arrayList.data = cloneDeep(value || []);
      this._eventsList.refreshList();
    }
  }

  set bindingsList(value) {
    this._bindingsList.arrayList.data = value;
    this._bindingsList.refreshList();
  }

  reset() {
    this.eventSelect = undefined;
    this.busClassSelect = undefined;
    this._disableClearAssociatedWithEvent();
    this._eventsList.unSelectAll();
    if (this._eventsList.editStatus) this._eventsList.editStatus = false;
  }

  _initElements(options) {
    this.EVENT_MANAGER = {
      EVENTS_SEARCH: {
        nameId: 'manager_search_events',
        label: i18next.t('eventManager.txtProDesEventManagerEventSearchLabel'),
        add: false,
        hasLabel: false,
        placeholder: i18next.t('eventManager.txtProDesEventManagerEventSearchPlaceholder'),
      },
      BUSCLASS_SEARCH: {
        nameId: 'manager_search_busclass',
        label: i18next.t('eventManager.txtProDesEventManagerBusClasLabel'),
        add: false,
        hasLabel: false,
        placeholder: i18next.t('eventManager.txtProDesEventManagerBusClasPlaceholder'),
      },
      EVENTS_LIST: {
        nameId: 'manager_list_events',
        label: i18next.t('eventManager.txtProDesEventManagerEventListLabel'),
        add: true,
      },
      BINDINGS_LIST: {
        nameId: 'manager_list_bindings_params',
        label: i18next.t('eventManager.txtProDesEventManagerBindingListLabel'),
        add: false,
      },
      CONDITION: {
        nameId: 'manager_condition_event',
        label: i18next.t('eventManager.txtProDesEventManagerConditionLabel'),
        placeholder: i18next.t('eventManager.txtProDesEventManagerConditionLabel'),
      },
      BINDING_ATTR_SEARCH: {
        nameId: 'manager_binding_attr_event',
        label: i18next.t('eventManager.txtProDesEventManagerBindingAttributeLabel'),
        add: true,
        hasLabel: true,
        canReload: true,
        placeholder: i18next.t('eventManager.txtProDesEventManagerBindingAttributePlaceholder'),
      },
      LIST_INTERFACE: {
        header: [
          i18next.t('eventManager.txtProDesEventManagerListInterfaceHeaderEdit'),
          i18next.t('eventManager.txtProDesEventManagerListInterfaceHeaderDeleteAll'),
        ],
        deleteAction: true,
        deleteActionAll: true,
        editAction: true,
        data: [],
        notShow: [],
        actions: [],
      },
      PARAMS_TYPE: {
        S: i18next.t('eventManager.txtProDesEventManagerParameterTypeString'),
        N: i18next.t('eventManager.txtProDesEventManagerParameterTypeNumber'),
        D: i18next.t('eventManager.txtProDesEventManagerParameterTypeDate'),
      },
    };
    this.OPTIONS_TOAST = {
      type: 'info',
      title: i18next.t('eventManager.txtProDesEventManagerToastTitle'),
      duration: 3000,
      position: 'bottom-left',
    };

    const { eventsUrl, busClassUrl, attributesUrl } = options;
    this._options = options;
    const {
      EVENTS_LIST,
      BINDINGS_LIST,
      EVENTS_SEARCH,
      BUSCLASS_SEARCH,
      CONDITION,
      BINDING_ATTR_SEARCH,
    } = this.EVENT_MANAGER;
    EVENTS_SEARCH.url = eventsUrl;
    BUSCLASS_SEARCH.url = busClassUrl;
    BINDING_ATTR_SEARCH.url = attributesUrl;
    EVENTS_LIST.arrayList = this._createEventsArrayList();
    BINDINGS_LIST.arrayList = this._createBindingsParamsArrayList();
    this._defaultAttrUrl = Config.DEFAULT_ATTRS_URL;

    this._bindingType = this._panel.querySelector('#event_binding_type');
    this._bindingValue = this._panel.querySelector('#event_binding_value');
    this.wrapBindingType = this._panel.querySelector('#wrapper_event_binding_type');
    this.wrapBindingAttributeType = this._panel.querySelector('#wrapper_event_binding_attr_type');
    this.wrapBindingValue = this._panel.querySelector('#wrapper_event_binding_value');

    this._eventSelect = this._includeComponent(
      'dropdown',
      this.wrapBindingType,
      'beforebegin',
      EVENTS_SEARCH,
    );
    this._busClassSelect = this._includeComponent(
      'dropdown',
      this._eventSelect.wrapper,
      'afterend',
      BUSCLASS_SEARCH,
    );
    this._eventsList = this._includeComponent(
      'list',
      this._busClassSelect.wrapper,
      'afterend',
      EVENTS_LIST,
    );
    this._eventCondition = this._includeComponent(
      'condition',
      this._eventsList.wrapper,
      'afterend',
      CONDITION,
    );
    this._bindingsList = this._includeComponent(
      'list',
      this._eventCondition.wrapper,
      'afterend',
      BINDINGS_LIST,
    );
    this._bindingAttribute = this._includeComponent(
      'dropdown',
      this.wrapBindingAttributeType,
      'afterend',
      BINDING_ATTR_SEARCH,
    );
    this._bindingAttribute.toggleDisabledButton(true);
    this.eventSelected = null;
    this.bindingSelected = null;
    this.inputs = [this._bindingType, this._bindingValue];
    this.reset();
  }

  _createEventsArrayList() {
    const { LIST_INTERFACE } = this.EVENT_MANAGER;
    const arrayList = {
      ...LIST_INTERFACE,
      header: ['Business Class', 'Event', 'cond', 'edit', 'delete-all'],
      notShow: ['id', 'eventId', 'condition', 'busClaId', 'order', 'bindings', 'WS'],
    };
    return arrayList;
  }

  _createBindingsParamsArrayList() {
    const { LIST_INTERFACE } = this.EVENT_MANAGER;
    const arrayList = {
      ...LIST_INTERFACE,
      header: ['Parameter', 'Type', 'In/Out', 'Binding'],
      deleteAction: false,
      deleteActionAll: false,
      editAction: false,
      notShow: ['id'],
    };
    return arrayList;
  }

  _initEventsElements() {
    this._eventsList.on('selected_item', (item) => {
      this._onSelectEvent(item);
    });
    this._eventsList.on('add_item', () => {
      this._onAddEvent();
    });
    this._eventsList.on('edit_item', (status) => {
      this._onEditEvent(status);
    });
    this._eventsList.on('delete_item', (item) => {
      this._onDeleteEvent(item);
    });
    this._eventsList.on('delete_all_items', () => {
      this._onDeleteAllEvents();
    });
    this._eventsList.on('move', (actualPos, oldPos, actual, old, data = []) => {
      this._onMoveEvent(actualPos, oldPos, actual, old, data);
    });
    this._eventCondition.on('change_condition', (condition) => {
      this._onEventConditionChanged(condition);
    });
    this._bindingsList.on('selected_item', (item) => {
      this._onSelectBinding(item);
    });
    this._bindingType.addEventListener('input', () => {
      this._onSelectBindingTypeChanged();
    });
    this._setTimeoutOnPropertyChanged(
      () => this._onBindingValueChanged(),
      this._bindingValue,
      'input',
      100,
    );
    this._bindingAttribute.on('select_item', (item) => {
      this._onSelectBindingAttribute(item);
    });
    this._bindingAttribute.on('add_item', () => {
      this._onAddNewBindingAttribute();
    });
    this.bindingAttributesTypeAll.forEach((radio) => {
      radio.addEventListener('input', () => {
        this._onSelectBindingAttributeType();
      });
    });
    this._eventSelect.on('select_item', (item) => {
      this._onUpdateEvent(['eventId', 'name'], item);
    });
    this._busClassSelect.on('select_item', (item) => {
      this._onUpdateEvent(['busClaId', 'busClaName'], item);
    });
  }

  _refreshAndEmitEvent(data) {
    this._eventsList.refreshList();
    this.emitChange(data);
  }


  _onSelectEvent(eventSelected) {
    if (eventSelected && this.eventSelected !== eventSelected) {
      this._eventCondition.disabled = false;
      this.eventSelected = eventSelected;
      const { condition, busClaId } = eventSelected;
      this.eventCondition = condition;
      this._eventCondition.disabled = false;
      this.bindingType = '';
      this._bindingType.disabled = true;
      this.bindingSelected = null;
      this.bindingValue = '';
      this.bindingAttribute = undefined;
      this._visibilityBindingsElements(false);
      // visibilityComponent(this.wrapBindingValue, true);
      // // visibilityComponent(this.wrapBindingValue, false);
      // // this.wrapBindingValue.setAttribute('data-visible', false);
      // visibilityComponent(this._bindingAttribute.wrapper, false);
      // visibilityComponent(this.wrapBindingAttributeType, false);
      this._bindingsList.unSelectAll();
      this._fillBindingsParameters(busClaId);
    }
    this._showEventMessageInfo(eventSelected);
  }

  _fillBindingsParameters(busClaId) {
    this._getBindings(busClaId, this._options.bindingsUrl).then((bindings) => {
      this.bindingsList = bindings;
    });
  }

  _onAddEvent() {
    const event = this.eventSelect;
    const busClass = this.busClassSelect;
    const events = this._eventsList.arrayList.data;
    if (event && busClass) {
      const newEvent = {
        id: this._getMaxId(events) + 1,
        busClaId: parseInt(busClass.id, 10),
        busClaName: busClass.name,
        eventId: parseInt(event.id, 10),
        name: event.name,
        condition: '',
        haveCond: 'No',
        bindings: [],
        order: events.length,
      };
      events.push(newEvent);
      this._refreshAndEmitEvent(events);
      this._eventsList._scrollToRow(newEvent, () => this._onSelectEvent(newEvent));
    }
  }

  _onEditEvent(status) {
    const selected = status ? this._eventsList.rowEditableSelected : this.eventSelected;
    const {
      busClaId, busClaName, eventId, name,
    } = selected;
    this.eventSelect = status ? { id: eventId, name } : undefined;
    this.busClassSelect = status ? { id: busClaId, name: busClaName } : undefined;
    this.bindingType = '';
    if (status) {
      this.eventCondition = selected.condition ? selected.condition : '';
      this._eventCondition.disabled = true;
      this._bindingsList.unSelectAll();
      this._bindingsList.disabled = true;
      // this._axiosSource.cancel();
    } else {
      this._eventCondition.disabled = false;
      this._bindingsList.disabled = false;
      this._fillBindingsParameters(busClaId);
    }
    this._bindingType.disabled = true;
    if (this.bindingSelected) {
      this.bindingSelected = null;
      this.bindingValue = '';
      this.bindingAttribute = undefined;
    }
    this._visibilityBindingsElements(false);
  }


  _onUpdateEvent(props, item = { id: '', name: '' }) {
    // DEFAULT VALUE TO NOT BREAK '_onUpdateEvent'
    const [propId, propName] = props;
    const { editStatus } = this._eventsList;
    const { id, name } = item;
    if (item.id !== ' ' && editStatus) {
      this.eventSelected[propId] = parseInt(id, 10);
      this.eventSelected[propName] = name;
      const { data } = this._eventsList.arrayList;
      if (propId === 'busClaId') {
        this.eventSelected.bindings.length = 0;
        this.eventSelected.condition = '';
        this.eventCondition = this.eventSelected.condition;
        this.eventSelected.haveCond = this.eventSelected.condition !== '' ? 'Yes' : 'No';
        this._refreshAndEmitEvent(data);
        this._fillBindingsParameters(this.eventSelected[propId]);
      }
      this._visibilityBindingsElements(false);
      this._refreshAndEmitEvent(data);
    }
  }


  _onDeleteEvent(deleteEvent) {
    const events = this._eventsList.arrayList.data;
    if (deleteEvent) {
      const newEvents = events.filter((event) => {
        return event.id !== deleteEvent.id;
      });
      this._eventsList.arrayList.data = newEvents;
      if (this.eventSelected === deleteEvent) {
        this._disableClearAssociatedWithEvent();
      }
      this._refreshAndEmitEvent(newEvents);
    }
  }

  _onDeleteAllEvents() {
    this._eventsList.arrayList.data = [];
    this._refreshAndEmitEvent([]);
    this._disableClearAssociatedWithEvent();
  }

  _onMoveEvent(actualPosition, oldPosition, actual, old) {
    actual.order = parseInt(actualPosition, 10);
    old.order = parseInt(oldPosition, 10);
    const { data } = this._eventsList.arrayList;
    this._refreshAndEmitEvent(data);
  }

  _onEventConditionChanged(condition) {
    if (this.eventSelected) {
      this.eventSelected.condition = condition;
      this.eventSelected.haveCond = this.eventSelected.condition !== '' ? 'Yes' : 'No';
      const { data } = this._eventsList.arrayList;
      this._refreshAndEmitEvent(data);
    }
  }

  _onSelectBinding(bindingSelected) {
    if (bindingSelected && this.bindingSelected !== bindingSelected) {
      this.bindingSelected = bindingSelected;
      this._bindingType.disabled = false;
      const binding = this._getBindingFromParameter(
        this.bindingSelected,
        this.eventSelected.bindings,
      );
      if (binding) {
        const type = binding.busClaParBndType === 'V' ? 'VALUE' : 'ATTRIBUTE';
        this.bindingType = type.toLowerCase();
        this._showAccordingBindingType(type, binding);
      } else {
        this.bindingType = '';
        this._visibilityBindingsElements(false);
      }
    }
    this._showBindingMessageInfo(bindingSelected);
  }

  _onSelectBindingTypeChanged() {
    const type = this.bindingType.toUpperCase();
    const binding = this._getBindingFromParameter(
      this.bindingSelected,
      this.eventSelected.bindings,
    );
    if (binding) {
      this._cleanValuesAccordingNewType(type, binding);
      if (type !== '') {
        this._transformBindingFromValues(binding, type);
      } else {
        this.eventSelected.bindings = this._deleteBindingParameter(
          binding,
          this.eventSelected.bindings,
        );
        this._fillBindingsParameters(this.eventSelected.busClaId);
      }
    } else {
      const { param, id } = this.bindingSelected;
      this.eventSelected.bindings = this._createNewBindingParameter(
        {
          busClaParId: parseInt(id, 10),
          busClaParName: param,
          busClaParType: this.bindingSelected.type,
        },
        this.eventSelected.bindings,
        type,
      );
    }
    this._showAccordingBindingType(type, binding || {});
    const { data } = this._eventsList.arrayList;
    this._refreshAndEmitEvent(data);
  }

  _onBindingValueChanged() {
    const binding = this._getBindingFromParameter(
      this.bindingSelected,
      this.eventSelected.bindings,
    );
    const isValid = validateField(this._bindingValue);
    this._fillValidBindingValue(binding.busClaParBndValue);
    if (binding && isValid) {
      binding.busClaParBndValue = this.bindingValue;
      const { data } = this._eventsList.arrayList;
      this._fillBindingsParameters(this.eventSelected.busClaId);
      this._refreshAndEmitEvent(data);
    }
  }

  _fillValidBindingValue(oldValue = '') {
    const { type } = this._bindingValue;
    if (type === 'number' && this.bindingValue === '') {
      this.bindingValue = oldValue;
    }
  }

  _onSelectBindingAttribute(attribute) {
    if (attribute) {
      const binding = this._getBindingFromParameter(
        this.bindingSelected,
        this.eventSelected.bindings,
      );
      if (binding) {
        const { id, name, label } = attribute;
        binding.attId = parseInt(id, 10);
        binding.attName = label || name;
        binding.attTooltip = name;
        const { data } = this._eventsList.arrayList;
        this._fillBindingsParameters(this.eventSelected.busClaId);
        this._refreshAndEmitEvent(data);
      }
    }
    const condition = !attribute && this._bindingAttribute.valueEl !== '';
    this._bindingAttribute.toggleDisabledButton(!condition);
  }

  _onSelectBindingAttributeType() {
    console.log(this.bindingAttributeType);
    const attributeType = this.bindingAttributeType;
    const binding = this._getBindingFromParameter(
      this.bindingSelected,
      this.eventSelected.bindings,
    );
    if (binding) {
      binding.busClaParBndType = attributeType;
      const { data } = this._eventsList.arrayList;
      this._refreshAndEmitEvent(data);
    }
  }

  _deleteBindingParameter(binding, bindings) {
    return bindings.filter((bind) => bind.busClaParId !== binding.busClaParId);
  }

  _createNewBindingParameter(parameter, bindings, type) {
    const newBinding = { ...parameter };
    this._transformBindingFromValues(newBinding, type);
    return [...bindings, newBinding];
  }

  _cleanValuesAccordingNewType(type, binding) {
    switch (type) {
      case 'ATTRIBUTE': {
        delete binding.busClaParBndValue;
        break;
      }
      case 'VALUE': {
        delete binding.attId;
        delete binding.attName;
        delete binding.attTooltip;
        break;
      }
      default:
        break;
    }
  }

  _transformBindingFromValues(binding, newType) {
    switch (newType) {
      case 'ATTRIBUTE':
        this._createBindingAttribute(binding);
        break;
      case 'VALUE':
        this._createBindingValue(binding);
        break;
      default:
        break;
    }
  }

  _showAccordingBindingType(type, binding = {}) {
    this._showBindingAttribute(binding, type === 'ATTRIBUTE');
    this._showBindingValue(binding, type === 'VALUE');
  }

  _createBindingAttribute(binding) {
    binding.busClaParBndType = 'E';
    binding.attId = -1;
    binding.attName = '';
    binding.attTooltip = '';
  }

  _createBindingValue(binding) {
    binding.busClaParBndType = 'V';
    binding.busClaParBndValue = '';
  }

  _showEventMessageInfo(eventSelected) {
    const message = `<b>Event:</b> ${eventSelected.name}<br>
      <b>Business Class:</b> ${eventSelected.busClaName}<br>
      <b>Have a Condition:</b> ${eventSelected.haveCond}`;
    this.message.showMessage({ ...OPTIONS_TOAST, message });
  }

  _showBindingMessageInfo(bindingSelected) {
    const { PARAMS_TYPE } = this.EVENT_MANAGER;
    const message = `
      <b>Parameter:</b> ${bindingSelected.param}<br>
      <b>Type:</b> ${PARAMS_TYPE[bindingSelected.type]}<br>
      <b>In/Out:</b> ${bindingSelected.inout}<br>
      <b>Binding:</b> ${bindingSelected.binding}<br>
    `;
    this.message.showMessage({ ...OPTIONS_TOAST, message });
  }

  _showErrorMessage(message) {
    const options = { ...OPTIONS_TOAST };
    options.type = 'danger';
    options.title = 'Error';
    options.message = message;
    this.message.showMessage(options);
  }

  _showSuccessInsertionMessage() {
    const options = { ...OPTIONS_TOAST };
    options.type = 'success';
    options.title = 'Success';
    options.message = 'Attribute successfully added';
    this.message.showMessage(options);
  }

  _visibilityBindingsElements(flag) {
    visibilityComponent(this.wrapBindingValue, flag);
    visibilityComponent(this._bindingAttribute.wrapper, flag);
    visibilityComponent(this.wrapBindingAttributeType, flag);
  }

  _showBindingValue(binding = {}, flag = true) {
    this.setFieldValueEachType(flag);
    this._bindingValue.disabled = !flag;
    this.bindingValue = binding.busClaParBndValue || '';
    visibilityComponent(this.wrapBindingValue, flag);
  }

  _showBindingAttribute(binding = {}, flag = true) {
    const { busClaParBndType, attId, attTooltip } = binding;
    console.log(binding);
    const attrType = busClaParBndType === 'P' ? 'process' : 'entity';
    this.bindingAttributeType = attrType;
    this.bindingAttribute = attId && attTooltip && attId !== -1 && attTooltip !== ''
      ? { id: attId, name: attTooltip }
      : undefined;
    visibilityComponent(this.wrapBindingAttributeType, flag);
    visibilityComponent(this._bindingAttribute.wrapper, flag);
    this.setUrlAttributeEachType(flag);
  }

  setUrlAttributeEachType(flag) {
    if (this.bindingSelected && Config.ENV === 'Production' && flag) {
      const { type } = this.bindingSelected;
      this._bindingAttribute.url = `${this._defaultAttrUrl}&attType=${type}&name=`;
    }
  }

  setFieldValueEachType(flag) {
    // if (this.bindingSelected && Config.ENV === 'Production' && flag) {
    if (this.bindingSelected && flag) {
      const { type } = this.bindingSelected;
      switch (type) {
        case 'N': {
          this._bindingValue.setAttribute('type', 'number');
          break;
        }
        case 'D': {
          this._bindingValue.setAttribute('type', 'date');
          break;
        }
        default: {
          this._bindingValue.setAttribute('type', 'text');
          break;
        }
      }
      this._toggleNumberFieldAttributes(type === 'N');
    }
  }

  _toggleNumberFieldAttributes(add) {
    const hasMinAttribute = this._bindingValue.hasAttribute('min');
    const hasStepAttribute = this._bindingValue.hasAttribute('step');
    if (add && !hasMinAttribute && !hasStepAttribute) {
      this._bindingValue.setAttribute('min', '0');
      this._bindingValue.setAttribute('max', Number.MAX_SAFE_INTEGER);
      this._bindingValue.setAttribute('step', '1');
    } else {
      this._bindingValue.removeAttribute('min');
      this._bindingValue.removeAttribute('step');
    }
  }

  _disableClearAssociatedWithEvent() {
    this.eventSelected = null;
    this.eventCondition = '';
    this._eventCondition.disabled = true;
    this._bindingsList.arrayListData = [];
    this.bindingType = '';
    this._bindingType.disabled = true;
    if (this.bindingSelected) {
      this.bindingSelected = null;
      this.bindingValue = '';
      this.bindingAttribute = undefined;
    }
    this._visibilityBindingsElements(false);
  }

  async _getBindings(classId, url) {
    let bindings = [];
    if (Config.ENV !== 'Development') url = `${url}${classId}`;
    try {
      const response = await axios.get(url);
      if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
        this.showInvalidSession();
      }
      const convert = new Converter();
      bindings = convert.convertToJson(response.data);
    } catch (error) {
      console.warn('Error: ', error);
    }
    const parameters = mapInOutParameters(bindings);
    return this._customBindingsParameters(parameters);
  }

  _customBindingsParameters(parameters) {
    if (this.eventSelected) {
      const { bindings } = this.eventSelected;
      const indexBindings = indexByProperty(bindings || [], 'busClaParId');
      return parameters.map((parameter) => {
        const existBinding = indexBindings[parameter.id];
        return {
          ...parameter,
          binding: existBinding ? this._getEspecificationBinding(existBinding) : 'None',
        };
      });
    }
    return parameters;
  }

  _getEspecificationBinding(binding) {
    const isValue = binding.busClaParBndType === 'V' && binding.busClaParBndValue !== '';
    const isProcess = binding.busClaParBndType === 'P';
    const isEntity = binding.busClaParBndType === 'E';
    const isAttribute = (isProcess || isEntity) && (binding.attId !== -1 && binding.attId);
    if (isValue) return 'Value';
    if (isAttribute) return 'Attribute';
    return 'None';
  }

  _getBindingFromParameter(parameter, bindings) {
    return bindings.find((bind) => {
      return parseInt(bind.busClaParId, 10) === parseInt(parameter.id, 10);
    });
  }

  async _insertNewAttribute(value) {
    const url = `${Config.NEW_BINDINGS_ATTR_URL}&name=${value}&attType=S`;
    const response = await axios.post(url);
    if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
      this.showInvalidSession();
    }
    if (response) {
      const convert = new Converter();
      const [attrCreated] = convert.convertToJson(response.data);
      this._bindingAttribute.selectedItem = attrCreated;
      this._bindingAttribute.reloadData();
      this._bindingAttribute.toggleDisabledButton(true);
    }
  }

  _onAddNewBindingAttribute() {
    const attributeValue = this._bindingAttribute.valueEl;
    const selected = this._bindingAttribute.selectedItem;
    if (!selected && Config.ENV === 'Production') {
      if (validateApiaName(attributeValue)) {
        try {
          this._insertNewAttribute(attributeValue).then(() => this._showSuccessInsertionMessage());
        } catch (error) {
          console.warn('an error occurred when the attribute was trying to insert');
        }
      } else {
        this._showErrorMessage(`The attribute name <b>"${attributeValue}"</b> is not valid`);
      }
    }
  }
}
