import EventEmitter from 'eventemitter3';
import Config from 'config';
import ExpanderComponent from '../components/ExpanderComponent';
import ListComponent from '../components/ListComponent';
import ConditionComponent from '../components/ConditionComponent';
import SearchComponent from '../components/SearchComponent';
import DateComponent from '../components/DateComponent';
import ToastMessage from '../components/ToastMessage';
import TabComponent from '../components/TabComponent';
import DropdownListComponent from '../components/DropdownListComponent';

export default class TemplatePanel extends EventEmitter {
  constructor(editor, id) {
    super();
    this._editor = editor;
    this._panel = document.querySelector(`#${id}_panel`);
    if (!this.toastMessage) this.toastMessage = new ToastMessage();
    if (this._panel === null) {
      this._panel = document.createDocumentFragment();
    } else {
      this._panel.setAttribute('data-visible', false);
      this._documentationInput = this._panel.querySelector(
        `#${id}_documentation_input`,
      );
      this._resizeDocumentationHeight();
      this._setTimeoutOnPropertyChanged(
        undefined,
        () => this._onDocumentationInputChanged(),
        this._documentationInput,
        'input',
        100,
      );
    }
  }

  _resizeDocumentationHeight() {
    if (Config.ENV === 'Offline') {
      this._documentationInput.style.maxHeight = '20em';
      this._documentationInput.style.height = '15em';
    }
  }

  get documentationInput() {
    if (this._documentationInput) {
      return this._documentationInput.value;
    }
    return '';
  }

  get element() {
    return this._editor.elementSelected || this._editor.workflow;
  }

  get panel() {
    return this._panel;
  }

  get editor() {
    return this._editor;
  }

  set panel(panel) {
    this._panel = panel;
  }

  set documentationInput(newValue) {
    if (this._documentationInput !== undefined) {
      this._documentationInput.value = newValue;
    }
  }

  get isVisible() {
    const dataVisible = JSON.parse(this.panel.getAttribute('data-visible'));
    return dataVisible === true;
  }

  _elementVisibility(element) {
    const visibility = element.getAttribute('data-visible');
    return JSON.parse(visibility);
  }

  _changeVisibility(el, value) {
    el.setAttribute('data-visible', value);
  }

  hide() {
    this._changeVisibility(this._panel, false);
  }

  clean(wrapperId) {
    const wrapper = this.panel.querySelector(`#wrapper_${wrapperId}`);
    if (wrapper) {
      wrapper.remove();
    }
  }

  _includeTemplate(templateId) {
    const template = document.getElementById(`${templateId}_template`);
    const container = document.importNode(template.content, true);
    // this.clean(templateId);
    this._panel.appendChild(container);
    return container;
  }

  _onDocumentationInputChanged() {
    if (this.element) {
      if (this.element.documentation) {
        const actual = this.element.documentation;
        if (actual !== this.documentationInput) {
          this.element.documentation = { text: this.documentationInput };
        }
      } else {
        // console.log('CREATE new documentation');
        const id = this.editor.idIndex++;
        this.element.documentation = { id, text: this.documentationInput };
      }
    }
  }

  _setTimeoutOnPropertyChanged(
    timeout = undefined,
    executeFn,
    el,
    evt,
    waitTime = 100,
  ) {
    el.addEventListener(evt, () => {
      clearTimeout(timeout);
      timeout = setTimeout(() => {
        executeFn();
        clearTimeout(timeout);
      }, waitTime);
    });
  }

  _includeComponent(type, wrap, container, position, options) {
    this._includeTemplate(type);
    wrap = this.panel.querySelector(`#wrapper_${type}`);
    let component;
    switch (position) {
      case 'afterend':
      case 'beforebegin': {
        container.insertAdjacentElement(position, wrap);
        break;
      }
      default: {
        container.appendChild(wrap);
        break;
      }
    }
    const { nameId, label } = options;
    switch (type) {
      case 'expander': {
        const { expandId } = options;
        component = new ExpanderComponent(nameId, this.panel, expandId, label);
        const expandZone = this.panel.querySelector(`#${expandId}`);
        wrap.insertAdjacentElement('afterend', expandZone);
        break;
      }
      case 'search': {
        const {
          add, responseTime, url, hasLabel,
        } = options;
        component = new SearchComponent(
          nameId,
          this.panel,
          label,
          add,
          responseTime,
          url,
          hasLabel,
        );
        break;
      }
      case 'list': {
        const { arrayList, add } = options;
        component = new ListComponent(
          nameId,
          this.panel,
          label,
          arrayList,
          add,
        );
        break;
      }
      case 'date': {
        component = new DateComponent(nameId, this.panel, label);
        break;
      }
      case 'tabpanel': {
        const { tabs } = options;
        component = new TabComponent(nameId, this.panel, tabs);
        break;
      }
      case 'dropdown': {
        const {
          add, url, hasLabel, canReload, baseUrl,
        } = options;
        component = new DropdownListComponent(
          nameId,
          this.panel,
          label,
          add,
          url,
          baseUrl,
          hasLabel,
          canReload,
        );
        break;
      }
      default: {
        component = new ConditionComponent(nameId, this.panel, label);
        break;
      }
    }
    return component;
  }
}
