import * as Snap from 'snapsvg-cjs';
import Config from 'config';
import Element from './element';
import { getElementsOfType } from '../../utils/util';

export default class Gateway extends Element {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(Config.GATEWAY, id, viewport, x, y, properties, isMovable, [
      Config.DELETE,
      Config.TASK,
      Config.SUBPROCESS,
      Config.END_EVENT,
      Config.INTERMEDIATE_EVENT,
      Config.GATEWAY,
      Config.TEXT,
      Config.CONNECTOR,
    ]);

    if (!properties) {
      this._properties.type = 'exclusive';
      this._properties.executionType = 'automatic';
      this._properties.activity = {
        id,
        name: this._properties.name,
      };
    }
    this.drawElement();
    this.initElement();
  }

  get type() {
    return this._properties.type;
  }

  set type(value) {
    this.emit('element_onpropertychange');
    if (this._properties.type === 'exclusive' && value !== 'exclusive') {
      this.emit('delete_exclusive');
    }
    this._properties.type = value;
    if (value === 'exclusive' || value === 'inclusive') {
     // this._properties.executionType = 'automatic';
    } else {
      delete this.properties.executionType;
    }
    if (value === 'parallel' || value === 'event') {
      this.emit('change_parallel_event');
    }
    this._updateTypeIcon();
  }

  get name() {
    return this._properties.activity.name;
  }

  set name(value) {
    this.emit('element_onpropertychange');
    this._properties.activity.name = value;
    this._properties.name = value;
    this.updateTextLabel('gateway__group__text', value);
  }

  get executionType() {
    return this._properties.executionType;
  }

  set executionType(value) {
    this.emit('element_onpropertychange');
    this._properties.executionType = value;
  }

  getAllConnectorAssociated(elements) {
    const connectors = getElementsOfType(Config.CONNECTOR, elements);
    const transitions = connectors.filter((conn) => {
      return (
        conn.type === Config.CONNECTOR_TYPE_TRANSITION
        && parseInt(this.id, 10) === parseInt(conn.from, 10)
      );
    });
    return transitions;
  }

  deleteAllExecutionOrderConnector(elements) {
    const transitions = this.getAllConnectorAssociated(elements);
    transitions.forEach((transition) => {
      transition.deletePropertyByName('executionOrder');
    });
  }

  changeAllConnectorsKind(elements) {
    const transitions = this.getAllConnectorAssociated(elements);
    transitions.forEach((transition) => {
      transition.properties.kind = 'none';
    });
  }

  drawElement() {
    const rhombo = this._viewport
      .polygon(
        20,
        0,
        0,
        20,
        20,
        Config.GATEWAY_SIZE.width,
        Config.GATEWAY_SIZE.height,
        20,
      )
      .attr({
        class: 'gateway__bg',
      });

    const gateway = rhombo.appendTo(this._viewport);
    const bounds = gateway.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);
    const icon = this._drawGatewayTypeIcon();

    const label = this.drawForeignObjLabel(
      -40,
      48,
      120,
      80,
      this.name,
      'gateway__group__text',
      `text_${this.id}`,
    );
    label.select('g').attr({
      class: 'gateway__group',
    });

    this._graphic = this._viewport
      .g(selectionBox, gateway, icon, label)
      .mousedown(this.onElementMouseDown)
      .attr({
        class: 'gateway',
        id: `${Config.GATEWAY}_${this._properties.id}`,
      });

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }
  }

  _drawGatewayTypeIcon() {
    let typeIcon;
    let typeIconGroup;
    switch (this.type.toUpperCase()) {
      case 'EXCLUSIVE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="gateway__typeicon--exclusive" d="M38,12.8L35.2,10L24,21.2L12.8,10L10,12.8L21.2,24L10,35.2l2.8,2.8L24,26.8L35.2,38l2.8-2.8L26.8,24L38,12.8z"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.5, T${8},${8}`);
        break;
      case 'INCLUSIVE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="gateway__typeicon--inclusive" d="M 25, 50 a 25,25 0 1,1 50,0 a 25,25 0 1,1 -50,0"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.4, T${0},${0}`);
        break;
      case 'PARALLEL':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="gateway__typeicon--parallel" d="M38,26H26v12h-4V26H10v-4h12V10h4v12h12V26z"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.6, T${5.5},${5.5}`);
        break;
      case 'EVENT':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="gateway__typeicon--event" transform="scale(1, -1) translate(0, -100)" d="M499 802c-220 4-425-177-448-396-24-185 78-380 247-461 160-80 368-56 501 66 134 118 190 318 128 486-57 167-217 293-393 303-12 1-23 2-35 2z m-5-49c202 7 391-161 407-362 19-178-92-361-262-420-166-61-368-2-468 145-103 144-99 356 18 490 74 90 188 146 305 147z m8-44c-186 6-358-158-361-344-9-169 116-334 283-367 155-33 328 46 397 190 71 140 39 327-83 430-64 57-150 91-236 91z m-2-48c164 4 314-144 311-308 3-152-119-297-272-312-143-19-295 73-336 213-43 134 14 295 138 364 48 28 103 43 159 43z m0-87c-72-52-143-104-215-156 27-84 55-169 82-253 89 0 177 0 266 0 28 84 55 169 82 253-71 52-143 104-215 156z m0-30c62-45 124-90 187-135-24-74-48-147-72-220-77 0-153 0-230 0-24 73-48 146-72 220 63 45 125 90 187 135z"/></g>',
        );
        typeIconGroup = typeIcon.select('g');
        typeIconGroup.transform(`s0.03, T${5},${27}`);
        break;
      case 'NONE':
        typeIcon = Snap.parse('<g id="typeicon"></g>');
        typeIconGroup = typeIcon.select('g');
        break;
      default:
        break;
    }
    return typeIconGroup;
  }

  _updateTypeIcon() {
    // delete old icon
    const oldIcon = this._graphic.select('#typeicon');
    oldIcon.remove();
    // append new icon
    const typeIconGroup = this._drawGatewayTypeIcon();
    typeIconGroup.appendTo(this._graphic);
  }
}
