import Config from 'config';
import TemplatePanel from '../templatePanel';
import { normalizeText } from './validator';

export default class GatewayFacilis extends TemplatePanel {
  constructor(editor) {
    super(editor, 'gateway');
    this._init();
  }

  _init() {
    // Variables Initializations
    this._typeSelect = this._panel.querySelector('#gateway_type_select');
    this._nameInput = this._panel.querySelector('#gateway_name_input');

    // Events Initializations
    this._typeSelect.addEventListener('input', () => {
      this._onTypeSelectChanged();
    });
    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onNameInputChanged();
      },
      this._nameInput,
      'input',
    );
  }

  get typeSelect() {
    return this._typeSelect.value;
  }

  get nameInput() {
    return this._nameInput.value;
  }

  set typeSelect(value) {
    this._typeSelect.value = value;
  }

  set nameInput(value) {
    this._nameInput.value = value;
  }

  show(el) {
    // console.log(el.properties);
    this.typeSelect = el.type;
    this.nameInput = el.name;
    this.documentationInput = el.documentation;
    this._changeVisibility(this._panel, true);

    el.on('delete_exclusive', () => {
      el.deleteAllExecutionOrderConnector(this.editor.elements);
    });

    el.on('change_parallel_event', () => {
      el.changeAllConnectorsKind(this.editor.elements);
    });
  }

  _onTypeSelectChanged() {
    if (this.element) {
      const oldType = this.element.type.toUpperCase();
      const newType = this.typeSelect.toUpperCase();
      if (oldType !== newType) {
        this.element.type = this.typeSelect;
      }
    }
  }

  _onNameInputChanged() {
    if (this.element) {
      const actualName = this.element.elementType === Config.GATEWAY
        ? this.element.name
        : undefined;
      this.nameInput = normalizeText(this.nameInput, /["]/g);
      if (
        actualName !== this.nameInput
        && this.element.elementType === Config.GATEWAY
      ) {
        this.element.name = this.nameInput;
      }
    }
  }

  _onDocumentationInputChanged() {
    this.documentationInput = normalizeText(this.documentationInput);
    super._onDocumentationInputChanged();
  }
}
