import Config from 'config';
import EventEmitter from 'eventemitter3';
import ShapesInteraction from 'shapes-interaction';
import { getProcessElementByID } from '../utils/util';

export default class BoundaryEventsManager extends EventEmitter {
  constructor(editor) {
    super();
    this._editor = editor;
    this._boundaryEventsToMove = [];
  }

  moveStart(element) {
    // attached events to task or subprocess
    if (element.elementType === Config.TASK) {
      // remove event elements attached to task if not in intersection
      this._removeBoundaryEventsNotIntersecting(element);
      // check if event is attached to task or subprocess to move along
      for (let a = 0, len = this._editor.elements.length; a < len; a++) {
        const el = this._editor.elements[a];
        if (el.elementType === Config.INTERMEDIATE_EVENT) {
          if (el.isAttached && el.target === element.id) {
            if (!element.boundaryEvents.some((item) => item === el.id)) {
              element.addBoundaryEvents(el.id);
            }
            // bring boundary event to front
            el.toFront();
            this._boundaryEventsToMove.push(el);
            // intersecting elements
          } else if (this._checkIntersection(el, element)) {
            // shouldnt be connected
            if (!this._editor._connectorManager.areConnected(element, el)) {
              if (!element.boundaryEvents.some((item) => item === el.id)) {
                this._attached(el, element.id, true);
                element.addBoundaryEvents(el.id);
              }
              // bring attached intermediate event to front
              el.toFront();
              this._boundaryEventsToMove.push(el);
            }
          }
        }
      }
    } else if (element.elementType === Config.INTERMEDIATE_EVENT) {
      // check if event el is intersecting with task or subprocess to give visual clue
      this._checkParentHover(element, false);
    }
  }

  moving(element) {
    // move attached intermediate events
    if (element.elementType === Config.TASK) {
      for (let i = 0, len = this._boundaryEventsToMove.length; i < len; i++) {
        const el = this._boundaryEventsToMove[i];
        el.moveElement(
          Math.round(element.mx + el.x),
          Math.round(element.my + el.y),
        );
        this._editor.updateElementConnectors(el);
      }
    } else if (element.elementType === Config.INTERMEDIATE_EVENT) {
      // check if event el is intersecting with task or subprocess to give visual clue
      this._checkParentHover(element, false);
    }
  }

  moveEnd(element) {
    // move attached intermediate events
    if (element.elementType === Config.TASK) {
      for (let i = 0, len = this._boundaryEventsToMove.length; i < len; i++) {
        const el = this._boundaryEventsToMove[i];
        el.moveElementEnd(
          Math.round(element.mx + el.x),
          Math.round(element.my + el.y),
        );
        el.toFront();
      }
    } else if (element.elementType === Config.INTERMEDIATE_EVENT) {
      // check if event el is intersecting with task or subprocess to give visual clue
      this._checkParentHover(element, true);
      // check still attached
      this._checkIfAttached(element);
    }
    this._boundaryEventsToMove = [];
  }

  noMoveEnd(element) {
    this._boundaryEventsToMove = [];
    if (element.elementType === Config.INTERMEDIATE_EVENT) {
      // check if event el is intersecting with task or subprocess to give visual clue
      this._checkParentHover(element, true);
    }
  }

  _removeBoundaryEventsNotIntersecting(element) {
    let i = element.boundaryEvents.length;
    while (i--) {
      const el = getProcessElementByID(
        element.boundaryEvents[i],
        this._editor.elements,
      );
      if (el) {
        if (!this._checkIntersection(el, element)) {
          this._attached(el, null, false);
          element.removeBoundaryEvents(i);
        }
      } else {
        element.removeBoundaryEvents(i);
      }
    }
  }

  _checkIntersection(el, element) {
    const eventBounds = el.selectionBox.node.getBoundingClientRect();
    const cx = eventBounds.left + eventBounds.width / 2;
    const cy = eventBounds.top + eventBounds.height / 2;
    const eventCircle = [[cx, cy], el.radius];
    const elementBounds = element.box.node.getBoundingClientRect();
    const elementBox = [
      [elementBounds.x, elementBounds.y],
      elementBounds.width,
      elementBounds.height,
    ];
    return ShapesInteraction.intersection(elementBox, eventCircle);
  }

  _checkParentHover(element, isMoveEnd) {
    for (let i = 0, len = this._editor.elements.length; i < len; i++) {
      const elHover = this._editor.elements[i];
      if (elHover.elementType === Config.TASK) {
        if (!isMoveEnd) {
          if (this._checkIntersection(element, elHover)) {
            if (
              !this._editor._connectorManager.areConnected(element, elHover)
            ) {
              elHover.graphic.addClass('element--boundary-event-hover');
            }
          } else {
            elHover.graphic.removeClass('element--boundary-event-hover');
          }
        } else {
          elHover.graphic.removeClass('element--boundary-event-hover');
          if (this._checkIntersection(element, elHover)) {
            if (
              !this._editor._connectorManager.areConnected(element, elHover)
            ) {
              this._attached(element, elHover.id, true);
            }
          }
        }
      }
    }
  }

  _checkIfAttached(el) {
    // check not attached, to remove props
    if (el.target) {
      if (
        !this._checkIntersection(
          el,
          getProcessElementByID(el.target, this._editor.elements),
        )
      ) {
        this._attached(el, null, false);
      }
    }
  }

  _attached(event, id, isAttached) {
    if (isAttached) {
      event.target = id;
      event.isAttached = isAttached;
      if (event.eventType === 'none') {
        event.type = 'timer';
      }
      const taskContainer = this._editor.elements.find((element) => {
        return (
          element.elementType === Config.TASK && element.id === event.target
        );
      });
      if (taskContainer) {
        const { taskType } = taskContainer;
        if (
          (taskType === 'receive'
            || taskType === 'send'
            || taskType === 'service'
            || taskType === 'script')
          && event.eventType === 'message'
        ) {
          event.type = 'timer';
        }
      }
      taskContainer.boundaryEvents.push(event.id);
      this.emit('boundary_attached', event);
    } else {
      event.target = null;
      event.isAttached = false;
      if (event.eventType === 'multiple' || event.eventType === 'error') {
        event.type = 'none';
      }
    }
  }
}
