import axios from 'axios';
import isEmpty from 'lodash/isEmpty';
import Config from 'config';
import i18next from 'i18next';
import TemplatePanel from './templatePanel';
import Converter from '../../utils/convertToJson';

export default class SubProcessPanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'subprocess');
    this._init();
    this._initComponents();
    this._initEventsComponents();
  }

  _init() {
    this.SUBPROCESS_PANEL = {
      SUBPROCESS_NAME_SEARCH: {
        nameId: 'subprocess_name_input',
        label: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelNameSearchLabel'),
        add: true,
        url: Config.SUBP_NAME_URL,
        hasLabel: false,
        canReload: true,
      },
      STANDARD_LOOP_CONDITION: {
        nameId: 'subp_standard_loop_cond',
        label: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelLoopConditionLabel'),
      },
      REUSABLE: {
        nameId: 'subp_reusable_type',
        label: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelReusableLabel'),
        expandId: 'expand_reusable_zone',
      },
      REUSABLE_ENTITY_SEARCH: {
        nameId: 'subp_reusable_entity',
        label: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelReusableEntitySearchLabel'),
        add: false,
        url: Config.SUBP_REUSABLE_ENT_URL,
        hasLabel: true,
        canReload: true,
      },
      REUSABLE_FORMS_SEARCH: {
        nameId: 'subp_reusable_form',
        label: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelReusableFormsSearchLabel'),
        add: false,
        url: Config.SUBP_REUSABLE_ENT_FORMS_URL,
        hasLabel: false,
        canReload: true,
      },
      REUSABLE_FORMS_LIST: {
        nameId: 'subp_reusable_forms_list',
        label: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelReusableFormsListLabel'),
        add: true,
      },
      MULTIINSTANCE_LOOPTYPE_SEARCH: {
        nameId: 'subp_multiinstance_looptype',
        label: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelMultiInstanceLoopTypeLabel'),
        add: true,
        responseTime: 1000,
        url: Config.SUBP_MULTIINSTANCE_URL,
        hasLabel: false,
        canReload: true,
      },
    };

    this.OPTIONS_TOAST = {
      type: 'info',
      title: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastTitle'),
      duration: 3000,
      position: 'bottom-left',
    };
    // subp = subprocess
    // this._subpNameInput = this._panel.querySelector('#subprocess_name_input');
    this._subpTypeSelect = this._panel.querySelector('#subprocess_type_select');
    this._subpLoopTypeSelect = this._panel.querySelector('#subprocess_loop_type_select');
    this._subpColorInput = this._panel.querySelector('#subprocess_color_input');
    this._subpColorInputName = this._panel.querySelector('#subprocess_color_name_input');

    this._subpStandardLoop = this._panel.querySelector('#subp_standard_loop_switch');
    this._subpConditionDoc = this._panel.querySelector('#subp_condition_documentation_input');

    this._subpReusableFormType = this._panel.querySelector('#subp_reusable_form_type');
    this._subpReusableExecution = this._panel.querySelector('#subp_reusable_execution');
    this._subpReusableSkipFirstTask = this._panel.querySelector('#subp_reusable_skip_first_task');
    this._subpTypeSelect.addEventListener('input', () => {
      this._onSubpTypeSelectChanged();
    });
    this._subpLoopTypeSelect.addEventListener('input', () => {
      this._onSubpLoopTypeSelectChanged();
    });
    this._subpColorInput.addEventListener('change', () => {
      this._onSubpColorChanged();
    });
    this._subpReusableFormType.addEventListener('input', () => {
      this._onSubpReusableFormTypeChanged();
    });
  }

  _initComponents() {
    this.wrapStandardLoopSwitch = this._panel.querySelector('#wrapper_subprocess_standard_loop_switch');
    this.wrapSubpConditionDoc = this._subpConditionDoc.parentNode.parentNode;
    this.wrapReusableExpandZone = this._panel.querySelector('#expand_reusable_zone');
    this.wrapReusableFormType = this._subpReusableFormType.parentNode.parentNode;
    this.wrapSubpLoopType = this._subpLoopTypeSelect.parentNode.parentNode;
    this.wrapSubpType = this._subpTypeSelect.parentNode.parentNode;
    this._initSubpNameComponent();
    this._initStandardLoopComponents();
    this._initReusableTypeComponents();
    this._initMultiInstanceComponent();
  }

  _initSubpNameComponent() {
    const { SUBPROCESS_NAME_SEARCH } = this.SUBPROCESS_PANEL;
    this._subpNameInput = this._includeComponent(
      'dropdown',
      this.wrapSubpName,
      this.wrapSubpType,
      'beforebegin',
      SUBPROCESS_NAME_SEARCH,
    );
    this._subpNameInput.placeholder = 'Insert a Subprocess';
    this._subpNameInput.toggleDisabledButton(true);
  }

  _initStandardLoopComponents() {
    const { STANDARD_LOOP_CONDITION } = this.SUBPROCESS_PANEL;

    this._subpStandardLoopCond = this._includeComponent(
      'condition',
      this.wrapStandardLoopCond,
      this.wrapSubpConditionDoc,
      'beforebegin',
      STANDARD_LOOP_CONDITION,
    );

    this._subpStandardLoopCond.placeholder = i18next.t(
      'subprocessPropsPanel.txtProDesSubprocessPanelPlaceholderCondition',
    );
    this._subpConditionDoc.placeholder = i18next.t(
      'subprocessPropsPanel.txtProDesSubprocessPanelPlaceholderDocumentation',
    );

    this._changeVisibility(this._subpStandardLoopCond.wrapper, false);
    this._changeVisibility(this.wrapSubpConditionDoc, false);
  }

  _initReusableTypeComponents() {
    const {
      REUSABLE, REUSABLE_ENTITY_SEARCH, REUSABLE_FORMS_SEARCH, REUSABLE_FORMS_LIST,
    } = this.SUBPROCESS_PANEL;
    REUSABLE_FORMS_LIST.arrayList = this._createReusableFormsArrayList();

    this._subpReusableExp = this._includeComponent(
      'expander',
      this.wrapReusableExpandZone,
      this.wrapReusableExpandZone,
      'beforebegin',
      REUSABLE,
    );
    this._subpReusableEntitySearch = this._includeComponent(
      'dropdown',
      this.wrapReusableEntSearch,
      this.wrapReusableFormType,
      'beforebegin',
      REUSABLE_ENTITY_SEARCH,
    );
    this._subpReusableFormSearch = this._includeComponent(
      'dropdown',
      this.wrapReusableFormSearch,
      this.wrapReusableFormType,
      'afterend',
      REUSABLE_FORMS_SEARCH,
    );
    this._subpReusableFormsList = this._includeComponent(
      'list',
      this.wrapReusableFormsList,
      this._subpReusableFormSearch.wrapper,
      'afterend',
      REUSABLE_FORMS_LIST,
    );

    this._subpReusableEntitySearch.placeholder = i18next.t(
      'subprocessPropsPanel.txtProDesSubprocessPanelPlaceholderEntitySelect',
    );
    this._subpReusableFormSearch.placeholder = i18next.t(
      'subprocessPropsPanel.txtProDesSubprocessPanelPlaceholderAddForm',
    );
  }

  _initMultiInstanceComponent() {
    const { MULTIINSTANCE_LOOPTYPE_SEARCH } = this.SUBPROCESS_PANEL;

    this._subpMultiInstanceType = this._panel.querySelector('#subp_multiinstance_type_select');
    this.wrapMultiInstanceType = this._panel.querySelector('#wrap_subp_multiinstance_type_select');

    this._subpMultiplicationSearch = this._includeComponent(
      'dropdown',
      this.wrapMultipSearch,
      this.wrapMultiInstanceType,
      'afterend',
      MULTIINSTANCE_LOOPTYPE_SEARCH,
    );
    this._subpMultiplicationNumber = this._panel.querySelector('#subp_multiinstance_number_type');
    this.wrapMultiplicationNumber = this._subpMultiplicationNumber.parentNode.parentNode;
    this._subpMultiplicationSearch.placeholder = 'Select an Attribute';
    this._subpMultiplicationSearch.toggleDisabledButton(true);
  }

  _initEventsComponents() {
    this._subpStandardLoop.addEventListener('input', () => {
      this._onSubpStandardLoopChanged();
    });

    this._subpStandardLoopCond.on('change_condition', (condition) => {
      this._onSubpStandardLoopCondChanged(condition);
    });

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onSubpStandardLoopDocumentationChanged();
      },
      this._subpConditionDoc,
      'input',
    );

    this._subpNameInput.on('select_item', (item) => {
      this._onSubpNameInputChanged(item);
    });

    this._subpNameInput.on('add_item', () => {
      this._onAddNewSubprocess();
    });

    this._subpMultiplicationSearch.on('select_item', (item) => {
      this._onSelectMultiplicationSearch(item);
    });

    this._subpMultiplicationSearch.on('add_item', () => {
      this._onAddNewMultiplicationAttribute();
    });

    this._subpReusableEntitySearch.on('select_item', (item) => {
      this._onSelectReusableEntitySearch(item);
    });

    this._subpReusableEntitySearch.on('clear_item', () => {
      this._onDeleteReusableEntitySearch();
    });

    this._subpReusableFormsList.on('add_item', () => {
      this._onAddReusableFormsList();
    });

    this._subpReusableFormSearch.on('select_item', (item) => {
      this._onSelectReusableForm(item);
    });

    this._subpReusableFormsList.on('edit_item', (status) => {
      this._onEditSubpReusableFormsList(status);
    });

    this._subpReusableFormsList.on('selected_item', (item) => {
      this._onSelectSubpReusableFormsList(item);
    });

    this._subpReusableFormsList.on('delete_item', (item) => {
      this._onDeleteSubpReusableFormsList(item);
    });

    this._subpReusableFormsList.on('delete_all_items', () => {
      this._onDeleteAllSubpReusableFormsList();
    });

    this._subpReusableFormsList.on('move', (actualPos, oldPos, actual, old) => {
      this._onOrderFormChanged(actualPos, oldPos, actual, old);
    });

    this._subpReusableExecution.addEventListener('input', () => {
      this._onSelectSubpReusableExecutionChanged();
    });

    this._subpReusableSkipFirstTask.addEventListener('input', () => {
      this._onSubpReusableSkipFirtsTaskChanged();
    });

    this._subpMultiInstanceType.addEventListener('input', () => {
      this._onSubMultiInstanceTypeChanged();
    });

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onValidateMultiplicationNumber();
        this._onSubpMultiplicationNumberChanged();
      },
      this._subpMultiplicationNumber,
      'input',
      100,
    );
  }

  set subpNameInput(newValue) {
    this._subpNameInput.valueEl = newValue;
  }

  set subpTypeSelect(newValue) {
    this._subpTypeSelect.value = newValue || '';
  }

  set subpLoopTypeSelect(newValue) {
    this._subpLoopTypeSelect.value = newValue || '';
  }

  set subpColorInput(value) {
    this._subpColorInput.value = value;
  }

  set subpColorInputName(value) {
    this._subpColorInputName.value = value;
  }

  get subpNameInput() {
    return this._subpNameInput.valueEl;
  }

  get subpTypeSelect() {
    return this._subpTypeSelect.value;
  }

  get subpLoopTypeSelect() {
    return this._subpLoopTypeSelect.value;
  }

  get subpMultiInstanceType() {
    return this._subpMultiInstanceType.checked;
  }

  set subpMultiInstanceType(value) {
    this._subpMultiInstanceType.checked = value;
    const label = this.wrapMultiInstanceType.querySelector('.highlighted');
    label.innerText = value ? 'Value' : 'Attribute';
  }

  get subpMultiplicationNumber() {
    const { value } = this._subpMultiplicationNumber;
    return value !== '' ? parseInt(value, 10) : value;
  }

  set subpMultiplicationNumber(value) {
    this._subpMultiplicationNumber.value = value;
  }

  get subpColorInput() {
    return this._subpColorInput.value;
  }

  get subpStandardLoop() {
    return this._subpStandardLoop.checked;
  }

  get subpStandardLoopCond() {
    return this._subpStandardLoopCond.conditionArea;
  }

  get subpStandardLoopCondDoc() {
    return this._subpConditionDoc.value;
  }

  set subpStandardLoopCond(value) {
    this._subpStandardLoopCond.conditionArea = value;
  }

  set subpStandardLoopCondDoc(value) {
    this._subpConditionDoc.value = value;
  }

  set subpStandardLoop(value) {
    this._subpStandardLoop.checked = value;
  }

  set subpReusableEntitySearch(value) {
    this._subpReusableEntitySearch.valueEl = value;
  }

  get subpReusableFormType() {
    return this._subpReusableFormType.value;
  }

  set subpReusableFormSearch(value) {
    this._subpReusableFormSearch.valueEl = value;
  }

  set subpMultiplicationSearch(value) {
    this._subpMultiplicationSearch.valueEl = value;
  }

  get subpMultiplicationSearch() {
    return this._subpMultiplicationSearch.valueEl.trim();
  }

  get subpReusableExecution() {
    return this._subpReusableExecution.value;
  }

  get subpReusableSkipFirstTask() {
    return this._subpReusableSkipFirstTask.checked;
  }

  set subpReusableExecution(value) {
    this._subpReusableExecution.value = value;
  }

  set subpReusableSkipFirstTask(value) {
    this._subpReusableSkipFirstTask.checked = value;
  }

  show(el) {
    this.showGenericComponents(el);
    this.showStandardLoop(el);
    this.showReusable(el);
    this.showMultiInstance(el);
    this._changeVisibility(this._panel, true);
  }

  showGenericComponents(el) {
    this._subpNameInput.selectedItem = { id: el.subprocessId, name: el.name };
    this.subpTypeSelect = el.type;
    this.subpLoopTypeSelect = el.loopType;
    this.subpColorInput = el.color;
    this.subpColorInputName = el.color;
    this.documentationInput = el.documentation;
  }

  showStandardLoop(el) {
    const loopType = this.subpLoopTypeSelect.toUpperCase();
    this.subpStandardLoop = false;
    this._changeVisibility(this.wrapStandardLoopSwitch, loopType === 'STANDARD');
    const isVisibleCond = this._elementVisibility(this._subpStandardLoopCond.wrapper);
    const isVisibleCondDoc = this._elementVisibility(this.wrapSubpConditionDoc);
    if (isVisibleCond && loopType !== 'STANDARD') {
      this._changeVisibility(this._subpStandardLoopCond.wrapper, false);
    }
    if (isVisibleCondDoc && loopType !== 'STANDARD') {
      this._changeVisibility(this.wrapSubpConditionDoc, false);
    }

    this.subpStandardLoopCond = el.standard && el.standardCondition ? el.standardCondition : '';
    this.subpStandardLoopCondDoc = el.standard && el.standardDocumentation ? el.standardDocumentation : '';
  }

  showReusable(el) {
    const reusable = this.subpTypeSelect.toUpperCase();
    this._changeVisibility(this._subpReusableExp.wrapper, reusable === 'REUSABLE');
    const { isExpand } = this._subpReusableExp;
    if (isExpand && reusable !== 'REUSABLE') this._subpReusableExp.expand();
    this._subpReusableEntitySearch.selectedItem = !isEmpty(el.reusableEntity)
      ? { id: el.reusableEntity.id, name: el.reusableEntity.name }
      : undefined;
    const formType = this.subpReusableFormType === 'entity' ? 'E' : 'P';
    this._subpReusableExp.showValue(this._getReusableExpShowValue(el));
    this._subpReusableFormsList.arrayList.data = this._getReusableFormByType(formType, el);
    this._subpReusableFormSearch.selectedItem = undefined;
    if (this._subpReusableFormsList.editStatus) {
      this._subpReusableFormsList.editStatus = false;
      this._setReusableEditionMode(false);
    }
    this._subpReusableFormsList.refreshList();
    this.subpReusableExecution = el.execution || 'synchronized';
    this.subpReusableSkipFirstTask = el.skipFirst || false;
  }

  showMultiInstance(el) {
    const multiInstance = this.subpLoopTypeSelect.toUpperCase() === 'MULTIINSTANCE';
    const isMultiplicationNumber = el.multiInstanceType === 'value';
    this._changeVisibility(this.wrapMultiInstanceType, multiInstance);
    this._changeVisibility(this._subpMultiplicationSearch.wrapper, multiInstance && !isMultiplicationNumber);
    this._changeVisibility(this.wrapMultiplicationNumber, multiInstance && isMultiplicationNumber);
    const { id, name } = el.multiInstanceAttribute;
    const condition = el.multiInstance && id && name;
    this._subpMultiplicationSearch.selectedItem = condition ? { id, name } : undefined;
    this.subpMultiInstanceType = el.multiInstanceType === 'value';
    this.subpMultiplicationNumber = el.multiInstanceValue || '';
    if (!this._subpMultiplicationSearch.load && multiInstance) {
      this._subpMultiplicationSearch.load = true;
    }
  }

  _getReusableExpShowValue(el) {
    const valueToShow = [];
    if (el.reusable) {
      const forms = el.reusableForms || [];
      forms.forEach((value) => valueToShow.push(value.name));
    }
    return valueToShow;
  }

  _getReusableFormByType(type, el) {
    let formsByType = [];
    if (el.reusable) {
      const forms = el.reusableForms || [];
      formsByType = forms.filter((value) => {
        return value.formType === type;
      });
    }
    return formsByType;
  }

  _createReusableFormsArrayList() {
    const arrayList = {
      header: [
        i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelHeaderName'),
        i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelHeaderEdit'),
        i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelHeaderDeleteAll'),
      ],
      deleteAction: true,
      deleteActionAll: true,
      editAction: true,
      data: [],
      notShow: ['id', 'order', 'step', 'formType'],
      actions: [],
    };
    return arrayList;
  }

  _onSubpNameInputChanged(item) {
    const condition = !item && this.subpNameInput !== '';
    this._subpNameInput.toggleDisabledButton(!condition);
    if (item) {
      this.element.name = item;
    }
  }

  async _onAddNewSubprocess() {
    const selected = this._subpNameInput.selectedItem;
    if (!selected && Config.ENV === 'Production' && this.element) {
      const nameExp = new RegExp(/^[a-zA-Z][\w]+[a-zA-Z0-9]$/gi);
      const nameValue = this.subpNameInput;
      if (nameExp.test(nameValue)) {
        const valueUri = encodeURI(nameValue);
        const url = `${Config.NEW_SUBP_URL}&name=${valueUri}&title=${valueUri}`;
        try {
          const response = await axios.post(url);
          if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
            this.showInvalidSession();
          }
          if (response) {
            const convert = new Converter();
            const [subpCreated] = convert.convertToJson(response.data);
            this.element.name = subpCreated;
            this._subpNameInput.selectedItem = subpCreated;
            this._subpNameInput.reloadData();
            this._subpNameInput.toggleDisabledButton(true);
            // message
            const options = this.OPTIONS_TOAST;
            options.type = 'success';
            options.title = i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastSuccessTitle');
            options.message = i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastSuccessMessage');
            this.toastMessage.showMessage(options);
          }
        } catch (error) {
          throw Error(i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelErrorMessage'));
        }
      } else {
        const options = this.OPTIONS_TOAST;
        options.type = 'danger';
        options.title = i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastErrorTitle');
        options.message = `${i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelErrorToastErrorMessageIsValid')} <b>"${nameValue}"</b> ${i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastMessageIsValid')}`;
        this.toastMessage.showMessage(options);
      }
    }
  }

  _onSubpTypeSelectChanged() {
    if (this.element) {
      const typeSelect = this.subpTypeSelect;
      if (this.element.type !== typeSelect) {
        this.element.type = typeSelect;
        if (typeSelect.toUpperCase() !== 'REUSABLE') {
          this.element.deletePropertyByName('reusable');
        } else {
          const execution = this.subpReusableExecution;
          const skipFirst = this.subpReusableSkipFirstTask;
          this.element.reusable = { execution, skipFirst };
        }
        this.showReusable(this.element);
      }
    }
  }

  _onSubpLoopTypeSelectChanged() {
    const loopType = this.subpLoopTypeSelect;
    if (loopType !== this.element.loopType) {
      this.element.loopType = loopType;
      const propToDelete = loopType === 'multiinstance' ? 'standard' : 'multiInstance';
      if (
        (loopType.toUpperCase() === 'MULTIINSTANCE' && this.element.standard)
        || (loopType.toUpperCase() === 'STANDARD' && this.element.multiInstance)
      ) {
        this.element.deletePropertyByName(propToDelete);
      }
      if (loopType.toUpperCase() === 'MULTIINSTANCE') {
        const multiInstanceType = this.subpMultiInstanceType ? 'value' : 'attribute';
        this.element.multiInstance = {
          type: multiInstanceType,
        };
      }
      if (
        (loopType.toUpperCase() === 'NONE' && this.element.multiInstance)
        || (loopType.toUpperCase() === 'NONE' && this.element.standard)
      ) {
        this.element.deletePropertyByName(this.element.multiInstance ? 'multiInstance' : 'standard');
        this.subpMultiInstanceType = false;
        this.subpMultiplicationNumber = '';
      }
      this.showMultiInstance(this.element);
      this.showStandardLoop(this.element);
    }
  }

  _onSubpColorChanged() {
    this.element.color = this.subpColorInput;
    this.subpColorInputName = this.subpColorInput;
  }

  _onSubpStandardLoopChanged() {
    const standardLoop = this.subpStandardLoop;
    this._changeVisibility(this._subpStandardLoopCond.wrapper, standardLoop);
    this._changeVisibility(this.wrapSubpConditionDoc, standardLoop);
  }

  _onSubpReusableFormTypeChanged() {
    const formType = this.subpReusableFormType === 'entity' ? 'E' : 'P';
    const url = formType === 'E' ? Config.SUBP_REUSABLE_ENT_FORMS_URL : Config.SUBP_REUSABLE_PROC_FORMS_URL;
    this._subpReusableFormSearch.url = url; // Automatic reload data !!!
    this._subpReusableFormsList.arrayList.data = this._getReusableFormByType(formType, this.element);
    this._subpReusableFormsList.refreshList();
  }

  _onSubpStandardLoopCondChanged(condition) {
    if (this.element) {
      this.element.standardCondition = condition;
    }
  }

  _onSubpStandardLoopDocumentationChanged() {
    const documentation = this.subpStandardLoopCondDoc;
    if (this.element) {
      this.element.standardDocumentation = documentation;
    }
  }

  _onSelectMultiplicationSearch(item) {
    if (item && this.element) {
      const multiInstance = this.element.multiInstanceAttribute;
      const multiSelect = item || {};
      if (multiInstance.name !== multiSelect.name) {
        this.element.multiInstanceAttribute = multiSelect;
      }
    }
    const condition = !item && this.subpMultiplicationSearch !== '';
    this._subpMultiplicationSearch.toggleDisabledButton(!condition);
  }

  async _onAddNewMultiplicationAttribute() {
    const regex = /^[a-zA-Z][a-zA-Z_0-9]+[a-zA-Z0-9]$/gi;
    const name = this.subpMultiplicationSearch;
    if (regex.test(name)) {
      try {
        const url = `${Config.NEW_NUMERIC_ATTR_URL}${name}`;
        const response = await axios.post(url);
        if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
          this.showInvalidSession();
        }
        if (response) {
          const convert = new Converter();
          const [attrCreated] = convert.convertToJson(response.data);
          this.element.multiInstanceAttribute = { ...attrCreated };
          this._subpMultiplicationSearch.selectedItem = attrCreated;
          this._subpMultiplicationSearch.reloadData();
          this._subpMultiplicationSearch.toggleDisabledButton(true);
          // message
          const options = { ...this.OPTIONS_TOAST };
          options.type = 'success';
          options.title = i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastSuccessTitle');
          options.message = i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastSuccessMessageAttr');
          this.toastMessage.showMessage(options);
        }
      } catch (error) {
        throw Error(i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelErrorMessageAttr'));
      }
    } else {
      const options = { ...this.OPTIONS_TOAST };
      options.type = 'danger';
      options.title = i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastErrorTitle');
      options.message = `${i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastErrorMessageSubprocessNameAttr')} <b>"${name}"</b> ${i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastMessageIsValid')}`;
      this.toastMessage.showMessage(options);
    }
  }

  _onSelectReusableEntitySearch(item) {
    if (this.element) {
      const entitySelect = item || {};
      const entity = this.element.reusableEntity;
      if (entity.name !== entitySelect.name) {
        this.element.reusableEntity = entitySelect;
      }
    }
  }

  _onDeleteReusableEntitySearch() {
    if (this.element) {
      this.element.reusableEntity = null;
    }
  }

  _onSelectReusableForm(item) {
    const formType = this.subpReusableFormType.toUpperCase().charAt(0);
    const { editStatus } = this._subpReusableFormsList;
    if (item && editStatus && this.element) {
      const editForm = this._subpReusableFormsList.rowEditableSelected;
      const index = this.element.reusableForms.findIndex((value) => {
        return parseInt(value.id, 10) === parseInt(editForm.id, 10) && value.formType === formType;
      });
      const existForm = this.element.reusableForms.findIndex((rform) => {
        return parseInt(rform.id, 10) === parseInt(item.id, 10) && rform.formType === formType;
      });
      if (index !== -1 && existForm === -1) {
        const newForm = { ...editForm };
        newForm.id = parseInt(item.id, 10);
        newForm.name = item.name;
        this.element.editReusableForm(index, newForm);

        const formType = this.subpReusableFormType.toUpperCase().charAt(0);
        this._subpReusableFormsList.arrayList.data = this._getReusableFormByType(formType, this.element);
        this._subpReusableFormsList.rowEditableSelected = newForm;
        this._subpReusableFormsList.refreshList();
        const showValue = this._getReusableExpShowValue(this.element);
        this._subpReusableExp.showValue(showValue);
      }
      if (existForm !== -1) {
        this._subpReusableFormSearch.selectedItem = { ...editForm };
        const options = {
          ...this.OPTIONS_TOAST,
          type: 'danger',
          title: i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastErrorTitle'),
          message: `${i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastErrorMessageForm')} "${item.name}" ${i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastErrorMessageExist')}`,
        };
        this.toastMessage.showMessage(options);
      }
    }
  }

  _onAddReusableFormsList() {
    const formToAdd = this._subpReusableFormSearch.selectedItem;
    const formType = this.subpReusableFormType.toUpperCase().charAt(0);
    if (formToAdd) {
      const forms = this.element.reusableForms || [];
      const findForm = forms.find((value) => {
        return parseInt(value.id, 10) === parseInt(formToAdd.id, 10) && value.formType === formType;
      });
      if (!findForm) {
        const add = {
          id: parseInt(formToAdd.id, 10),
          name: formToAdd.name,
          order: forms.length,
          step: 1,
          formType,
        };
        this.element.addReusableForm(add);
        this._subpReusableFormsList.arrayList.data = this._getReusableFormByType(formType, this.element);
        this._subpReusableFormsList.refreshList();
        this._subpReusableFormsList._scrollToRow(add, () => this._onSelectSubpReusableFormsList(add));
        this._subpReusableFormSearch.selectedItem = undefined;
        const valueShow = this._getReusableExpShowValue(this.element);
        this._subpReusableExp.showValue(valueShow);
      }
    }
  }

  _setReusableEditionMode(status) {
    this._subpReusableEntitySearch.disabled = status;
    this._subpReusableFormType.disabled = status;
    this._subpReusableExecution.disabled = status;
    this._subpReusableSkipFirstTask.disabled = status;
  }

  _onEditSubpReusableFormsList(status) {
    // console.log(status ? 'Editing ON' : 'Editing OFF');
    this._setReusableEditionMode(status);
    if (status) {
      const formEdit = this._subpReusableFormsList.rowEditableSelected;
      this._subpReusableFormSearch.selectedItem = {
        id: formEdit.id,
        name: formEdit.name,
      };
    } else {
      this._subpReusableFormSearch.selectedItem = undefined;
    }
  }

  _onSelectSubpReusableFormsList(item) {
    if (item) {
      const options = this.OPTIONS_TOAST;
      options.message = `<b>${i18next.t('subprocessPropsPanel.txtProDesSubprocessPanelToastMessageFormName')}</b> ${item.name}`;
      this.toastMessage.showMessage(options);
    }
  }

  _onDeleteSubpReusableFormsList(item) {
    const formType = this.subpReusableFormType.toUpperCase().charAt(0);
    if (this.element && item) {
      const index = this.element.reusableForms.findIndex((value) => {
        return parseInt(value.id, 10) === parseInt(item.id, 10) && value.formType === formType;
      });
      if (index !== -1) {
        this.element.deleteReusableForm(index);
        // this._subpReusableFormsList.arrayList.data = this.element.reusableForms;
        this._subpReusableFormsList.arrayList.data = this.element.reusableForms.filter((x) => x.formType === formType);

        this._subpReusableFormsList.refreshList();
        this._subpReusableExp.showValue(this._getReusableExpShowValue(this.element));
      }
    }
  }

  _onDeleteAllSubpReusableFormsList() {
    if (this.element) {
      this.element.cleanReusableForm(this._subpReusableFormType.value === 'entity' ? 'E' : 'P');
      this._subpReusableExp.showValue(this._getReusableExpShowValue(this.element));
    }
  }

  _onSelectSubpReusableExecutionChanged() {
    if (this.element) {
      this.element.execution = this.subpReusableExecution;
    }
  }

  _onSubpReusableSkipFirtsTaskChanged() {
    if (this.element) {
      this.element.skipFirst = this.subpReusableSkipFirstTask;
    }
  }

  _onOrderFormChanged(actualPos, oldPos, actual, old) {
    const formType = this.subpReusableFormType.toUpperCase().charAt(0);
    if (this.element) {
      // this.element.changeReusableFormPosition(actualPos, actual,formType);
      // this.element.changeReusableFormPosition(oldPos, old,formType);

      this.element.changeReusableFormPosition(actualPos, oldPos, actual, old, formType);
    }
  }

  _onSubMultiInstanceTypeChanged() {
    const isNumberMult = this.subpMultiInstanceType;
    const labelEl = this.wrapMultiInstanceType.querySelector('.highlighted');
    this._changeVisibility(this._subpMultiplicationSearch.wrapper, !isNumberMult);
    this._changeVisibility(this.wrapMultiplicationNumber, isNumberMult);
    if (isNumberMult && this.element) {
      this._subpMultiplicationSearch.selectedItem = undefined;
    } else {
      this.subpMultiplicationNumber = '';
    }
    const value = isNumberMult ? 'Value' : 'Attribute';
    labelEl.innerText = value;
    if (this.element) this.element.multiInstanceType = value.toLowerCase();
  }

  _onValidateMultiplicationNumber() {
    const value = this.subpMultiplicationNumber;
    if (value < 1 || isNaN(value)) {
      this.subpMultiplicationNumber = 1;
    }
    if (value === '') {
      this.subpMultiplicationNumber = '';
    } else {
      this.subpMultiplicationNumber = parseInt(value, 10);
    }
  }

  _onSubpMultiplicationNumberChanged() {
    if (this.element) {
      const multiplication = this.subpMultiplicationNumber;
      const actual = this.element.multiInstanceValue;
      if (actual !== multiplication) {
        this.element.multiInstanceValue = multiplication;
      }
    }
  }
}
