import * as Snap from 'snapsvg-cjs';
import Config from 'config';
import isEmpty from 'lodash/isEmpty';
import Element from './element';

export default class Task extends Element {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(Config.TASK, id, viewport, x, y, properties, isMovable, [
      Config.DELETE,
      Config.TASK,
      Config.SUBPROCESS,
      Config.END_EVENT,
      Config.INTERMEDIATE_EVENT,
      Config.GATEWAY,
      Config.TEXT,
      Config.CONNECTOR,
    ]);
    this._WIDTH = Config.TASK_SIZE.width;
    this._HEIGHT = Config.TASK_SIZE.height;

    if (!properties) {
      this._properties.taskType = 'user';
      this._properties.loopType = 'none';
      this._properties.name = 'Default_Task';
      this._properties.title = 'Default Task';
      this._properties.taskId = 2;
      this._properties.condition = '';
      this._properties.color = '#ffffff';
      this._properties.activity = {
        id,
        name: 'Default_Task',
      };
    }

    this.drawElement();
    this.initElement();
  }

  get taskType() {
    return this._properties.taskType;
  }

  set taskType(value) {
    this.emit('element_onpropertychange');
    this._properties.taskType = value;
    this._updateTaskTypeIcon();
  }

  get loopType() {
    return this._properties.loopType;
  }

  set loopType(value) {
    this.emit('element_onpropertychange');
    this._properties.loopType = value;
    this._updateLoopTypeIcon();
  }

  set taskName(value) {
    this.emit('element_onpropertychange');
    this._properties.activity.name = value.name;
    this._properties.name = value.name;
    if (value.id) this._properties.taskId = parseInt(value.id, 10);
    if (value.label) this.title = value.label;
  }

  get taskName() {
    return this._properties.name;
  }

  get title() {
    return this._properties.title || '';
  }

  set title(value) {
    this._properties.title = value;
    this._updateTitleLabel();
  }

  get comments() {
    return this._properties.comments;
  }

  set comments(value) {
    this._properties.comments = value;
  }

  get condition() {
    return this._properties.condition;
  }

  set condition(value) {
    this.emit('element_onpropertychange');
    this._properties.condition = value;
  }

  get role() {
    return this._properties.role;
  }

  get roleId() {
    return this._properties.role.id;
  }

  get roleName() {
    return this._properties.role.name;
  }

  set role(value) {
    this.emit('element_onpropertychange');
    if (!this._properties.role) this._properties.role = {};
    this._properties.role.id = parseInt(value.id, 10);
    this._properties.role.name = value.name;
  }

  get groups() {
    return this._properties.groups;
  }

  set groups(value) {
    this.emit('element_onpropertychange');
    this._properties.groups = value;
  }

  // Only for facilis purpose
  get multiplyCount() {
    return this._properties.multiplyCount;
  }

  // Only for facilis purpose
  set multiplyCount(value) {
    this.emit('element_onpropertychange');
    this._properties.multiplyCount = value;
  }

  createDefaultGroup() {
    if (!this._properties.groups) {
      this.groups = [];
    }
  }

  objectIndex(obj, prop, idCompare = 'id') {
    const copyArray = Array.isArray(prop) ? prop : this._properties[prop] || [];
    const index = copyArray.findIndex((value) => {
      return parseInt(value[idCompare], 10) === parseInt(obj[idCompare], 10);
    });
    return index;
  }

  setPropertyObject(object, propArray, propToChange, value) {
    const index = this.objectIndex(object, propArray);
    if (index !== -1 && this._properties[propArray]) {
      this.emit('element_onpropertychange');
      if (Array.isArray(propToChange) && Array.isArray(value)) {
        let i = 0;
        propToChange.forEach((prop) => {
          this._properties[propArray][index][prop] = value[i];
          i++;
        });
      } else {
        this._properties[propArray][index][propToChange] = value;
      }
    }
  }

  deleteBindingInIndex(indexEvt, indexBin) {
    this.emit('element_onpropertychange');
    this._properties.events[indexEvt].bindings.splice(indexBin, 1);
  }

  createDefaultSchedule() {
    if (!this.schedule) {
      this.schedule = {};
    }
  }

  get forms() {
    return this._properties.forms;
  }

  set forms(value) {
    this.emit('element_onpropertychange');
    this._properties.forms = value;
  }

  setSpecificFormInStepByIndex(
    indexStep,
    type,
    newForm = undefined,
    indexForm,
    property,
    value,
  ) {
    this.emit('element_onpropertychange');
    if (newForm) {
      this._properties.forms[indexStep][type] = newForm;
    } else {
      this._properties.forms[indexStep][type][indexForm][property] = value;
    }
  }

  get multiInstanceAttributeName() {
    return this.multiInstance ? this.multiInstance.attribute : undefined;
  }

  insertForm(newForm) {
    if (!this.forms) {
      this._properties.forms = [];
    }
    this.forms.push(newForm);
  }

  get states() {
    return this._properties.states;
  }

  set states(value) {
    this.emit('element_onpropertychange');
    this._properties.states = value;
  }

  createDefaultStates() {
    if (!this.states) this._properties.states = [];
  }

  insertState(newState) {
    if (!this.states) {
      this._properties.states = [];
    }
    this.states.push(newState);
  }

  get events() {
    return this._properties.events;
  }

  set events(value) {
    this.emit('element_onpropertychange');
    this._properties.events = value;
  }

  setPropertyWithoutSave(property, value) {
    this._properties[property] = value;
    if (property === 'loopType') {
      this._updateLoopTypeIcon();
    }
    if (property === 'taskType') {
      this._updateTaskTypeIcon();
    }
  }

  setBindingsEventByIndex(index, value) {
    this.emit('element_onpropertychange');
    this._properties.events[index].bindings = value;
  }

  setBindingEvent(indexEvt, indexBinding, value) {
    if (indexBinding === -1) {
      this.events[indexEvt].bindings.push(value);
    } else {
      this._properties.events[indexEvt].bindings[indexBinding] = value;
    }
  }

  setSpecificBindingAttribute(event, attr, type, value = null) {
    const eventIndex = this.objectIndex(event, 'events');
    if (eventIndex !== -1) {
      const attrs = this.events[eventIndex].bindings || [];
      const attrIndex = attrs.findIndex((bin) => {
        return parseInt(bin.busClaParId, 10) === parseInt(attr.id, 10);
      });
      if (attrIndex !== -1) {
        this.emit('element_onpropertychange');
        this.events[eventIndex].bindings[attrIndex].busClaParBndType = type;
        if (type === 'V' && value) {
          this.events[eventIndex].bindings[attrIndex].busClaParBndValue = value;
        }
        if ((type === 'E' || type === 'P') && value) {
          this.events[eventIndex].bindings[attrIndex].attId = value.attId;
          this.events[eventIndex].bindings[attrIndex].attTooltip = value.attTooltip;
          this.events[eventIndex].bindings[attrIndex].attName = value.attName;
        }
      }
    }
  }

  createDefaultEvents() {
    if (!this._properties.events) this._properties.events = [];
  }

  insertEvent(newEvent) {
    if (!this.events) {
      this._properties.events = [];
    }
    this.events.push(newEvent);
  }

  get multiInstance() {
    return this._properties.multiInstance;
  }

  set multiInstance(value) {
    this.emit('element_onpropertychange');
    this._properties.multiInstance = value;
  }

  get multiInstanceType() {
    return this.multiInstance ? this.multiInstance.type : undefined;
  }

  set multiInstanceType(value) {
    this.emit('element_onpropertychange');
    if (value === 'value' && !isEmpty(this.multiInstanceAttribute || {})) {
      delete this._properties.multiInstance.attributeId;
      delete this._properties.multiInstance.attribute;
    }
    if (value === 'attribute' && this.multiInstanceValue) {
      delete this._properties.multiInstance.value;
    }
    this._properties.multiInstance.type = value;
  }

  get multiInstanceValue() {
    return this.multiInstance ? this.multiInstance.value : undefined;
  }

  set multiInstanceValue(value) {
    this.emit('element_onpropertychange');
    this._properties.multiInstance.value = value;
  }

  get multiInstanceAttribute() {
    const attribute = {};
    const multiInstance = this.multiInstance || {};
    if (multiInstance.attributeId && multiInstance.attribute) {
      attribute.id = this.multiInstance.attributeId;
      attribute.name = this.multiInstance.attribute;
    }
    return attribute;
  }

  get schedule() {
    return this._properties.schedule;
  }

  set schedule(value) {
    this._properties.schedule = value;
  }

  set scheduleId(value) {
    this.emit('element_onpropertychange');
    this._properties.schedule.id = value;
  }

  get scheduleId() {
    return this.schedule ? this.schedule.id : undefined;
  }

  get scheduleName() {
    return this.schedule ? this.schedule.scheduleName : undefined;
  }

  set scheduleName(value) {
    this._properties.schedule.scheduleName = value;
  }

  set scheduleType(value) {
    this.emit('element_onpropertychange');
    this._properties.schedule.type = value;
  }

  get scheduleType() {
    return this.schedule ? this.schedule.type : undefined;
  }

  set scheduleSubprocess(value) {
    this.emit('element_onpropertychange');
    this._properties.schedule.subprocess = value;
    this._properties.schedule.task = '';
    this._properties.schedule.taskName = '';
  }

  set scheduleSubprocessName(value) {
    this._properties.schedule.subprocessName = value;
  }

  get scheduleSubprocessName() {
    return this.schedule ? this.schedule.subprocessName : undefined;
  }

  get scheduleTask() {
    return this.schedule ? this.schedule.task : undefined;
  }

  set scheduleTask(value) {
    this.emit('element_onpropertychange');
    this._properties.schedule.task = value;
  }

  get scheduleTaskName() {
    return this.schedule ? this._properties.schedule.taskName : undefined;
  }

  set scheduleTaskName(value) {
    this._properties.schedule.taskName = value;
  }

  deleteScheduleProperties(props) {
    this.emit('element_onpropertychange');
    props.forEach((prop) => {
      delete this._properties.schedule[prop];
    });
    if (this._properties.schedule === {}) {
      delete this._properties.schedule;
    }
  }

  get taskId() {
    return this._properties.taskId || '';
  }

  set taskId(value) {
    this._properties.taskId = value;
  }

  get color() {
    return this._properties.color || '#FFFFFF';
  }

  set color(value) {
    this.emit('element_onpropertychange');
    this._properties.color = value;
    this._setColors(this.color, '.task__bg', '.task__group__text', [
      '.task__looptypeicon',
      '.task__typeicon',
    ]);
  }

  get inputMessageRef() {
    return this._properties.inputMessageRef;
  }

  get wsMethodName() {
    return this.inputMessageRef ? this.inputMessageRef.wsMethodName : undefined;
  }

  get imrAttrProc() {
    return this.inputMessageRef ? this.inputMessageRef.attrProc : undefined;
  }

  get imrAttrEnt() {
    return this.inputMessageRef ? this.inputMessageRef.attrEnt : undefined;
  }

  set inputMessageRef(value) {
    this._properties.inputMessageRef = value;
  }

  set wsMethodName(value) {
    this.emit('element_onpropertychange');
    if (!this.inputMessageRef) this.inputMessageRef = {};
    this._properties.inputMessageRef.wsMethodName = value;
  }

  insertAttributeInputMsgRef(arrayType, value) {
    this.emit('element_onpropertychange');
    if (!this.inputMessageRef) this._properties.inputMessageRef = {};
    if (!this.inputMessageRef[arrayType]) {
      this._properties.inputMessageRef[arrayType] = [];
    }
    this.inputMessageRef[arrayType].push(value);
  }

  cleanAttributesInputMsgRef(arrayType) {
    this.emit('element_onpropertychange');
    this._properties.inputMessageRef[arrayType] = [];
  }

  editAttributesInputMsgRef(arrayType, index, value) {
    if (this.inputMessageRef[arrayType][index]) {
      this.emit('element_onpropertychange');
      this.inputMessageRef[arrayType][index] = value;
    }
  }

  deleteAttributeInputMsgRef(arrayType, index) {
    if (this.inputMessageRef[arrayType][index]) {
      this.emit('element_onpropertychange');
      this.inputMessageRef[arrayType].splice(index, 1);
    }
  }

  get outputMessageRef() {
    return this._properties.outputMessageRef;
  }

  set outputMessageRef(value) {
    this._properties.outputMessageRef = value;
  }

  createDefaultBPMNAutoComplete() {
    if (!this.outputMessageRef) this.outputMessageRef = [];
    const defaultWs = {
      id: 1,
      eventId: 11,
      busClaId: 102,
      busClaName: 'BPMNAutoComplete',
      name: 'ONREADY',
      order: 0,
      WS: true,
    };
    this._properties.outputMessageRef.push(defaultWs);
  }

  addWebServiceOutputMsgRef(webService) {
    this.emit('element_onpropertychange');
    this.outputMessageRef.push(webService);
  }

  editWebServiceOutputMsgRef(index, value) {
    if (this.outputMessageRef[index]) {
      this.emit('element_onpropertychange');
      this.outputMessageRef[index] = value;
    }
  }

  deleteWebServiceOutputMsgRef(index) {
    if (this.outputMessageRef[index]) {
      this.emit('element_onpropertychange');
      this.outputMessageRef.splice(index, 1);
    }
  }

  cleanWebServicesOutputMsgRef() {
    if (this.outputMessageRef) {
      this.emit('element_onpropertychange');
      this.outputMessageRef = [];
    }
  }

  deleteBindingWebServiceOutputMsgRef(indexWs, indexBin) {
    if (this.outputMessageRef[indexWs].bindings[indexBin]) {
      this.emit('element_onpropertychange');
      this.outputMessageRef[indexWs].bindings.splice(indexBin, 1);
    }
  }

  setWebServicePropertyOutputMsgRef(indexWs, indexBin, property, value) {
    if (this.outputMessageRef[indexWs].bindings[indexBin]) {
      const webService = this.outputMessageRef[indexWs];
      this.emit('element_onpropertychange');
      if (Array.isArray(property) && Array.isArray(value)) {
        for (let i = 0; i < value.length; i++) {
          webService.bindings[indexBin][property[i]] = value[i];
        }
      } else {
        webService.bindings[indexBin][property] = value;
      }
    }
  }

  deleteValueWebServiceProperty(indexWs, indexBin) {
    if (this.outputMessageRef[indexWs].bindings[indexBin]) {
      delete this.outputMessageRef[indexWs].bindings[indexBin]
        .busClaParBndValue;
    }
  }

  deleteAttributeWebServicesProperties(indexWs, indexBin) {
    if (this.outputMessageRef[indexWs].bindings[indexBin]) {
      delete this.outputMessageRef[indexWs].bindings[indexBin].attId;
      delete this.outputMessageRef[indexWs].bindings[indexBin].attTooltip;
      delete this.outputMessageRef[indexWs].bindings[indexBin].attName;
    }
  }

  addBindingWebServiceOutputMsgRef(wsIndex, binding) {
    if (this.outputMessageRef[wsIndex]) {
      this.emit('element_onpropertychange');
      if (!this.outputMessageRef[wsIndex].bindings) this.outputMessageRef[wsIndex].bindings = [];
      this.outputMessageRef[wsIndex].bindings.push(binding);
    }
  }

  drawElement() {
    this._box = this._viewport
      .rect(0, 0, this._WIDTH, this._HEIGHT, 8)
      .attr({ class: 'task__bg', fill: this.color });

    this._label = this.drawForeignObjLabel(
      0,
      0,
      this._WIDTH,
      this._HEIGHT,
      this.title,
      'task__group__text',
      `text_${this.id}`,
    );
    this._label.select('g').attr({
      class: 'task__group',
    });

    const taskBox = this._box.appendTo(this._viewport);
    const bounds = taskBox.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    const typeIconGroup = this._drawTaskTypeIcon();
    const loopTypeIconGroup = this._drawLoopTypeIcon();

    this._graphic = this._viewport
      .g(selectionBox, taskBox, this._label, typeIconGroup, loopTypeIconGroup)
      .mousedown(this.onElementMouseDown)
      .attr({ class: 'task', id: `${Config.TASK}_${this._properties.id}` });

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }

    this._setColors(this.color, '.task__bg', '.task__group__text', [
      '.task__looptypeicon',
      '.task__typeicon',
    ]);
  }

  _drawTaskTypeIcon() {
    let typeIcon;
    switch (this.taskType.toUpperCase()) {
      case 'USER':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="task__typeicon" transform="scale(1, -1) translate(0, -100)" d="M496 668c-89 0-152-69-152-146l0 0 0 0c0-24 6-48 16-71 7-16 16-31 27-43-65-22-140-58-178-130l-2-4 0-231 578 0 0 231-2 4c-37 71-111 107-175 129 31 32 40 73 40 115l0 0 0 0c0 77-63 146-152 146z m-61-83c5 0 9 0 14-1 39-2 53-9 63-16 10-6 17-13 44-14l0 0 0 0c21 1 32 5 39 9 3 2 5 4 8 5 7-14 10-30 10-46 0-47-8-80-51-106l4-32c9-3 19-6 28-9 1-5 3-13 4-20 1-8 1-16 1-23-1-6-3-9-3-10-22-21-61-34-100-34-39 0-78 13-100 34 0 1-2 4-3 10 0 7 0 15 1 23 1 7 3 15 4 20 9 3 19 6 27 9l3 34c-2 2-4 4-7 6-9 7-21 23-29 41-8 19-13 40-13 57 0 20 5 39 15 56 2 1 4 1 6 2 7 3 18 5 35 5z m-75-225c0 0 0 0 0-1-2-9-2-20-1-30 1-11 4-22 13-31 31-31 78-45 124-45 46 0 93 14 124 45 10 9 12 20 13 31 2 10 1 21-1 30 0 1 0 1 0 1 48-21 93-50 118-95l0-187-83 0 0 132-35 0 0-132-272 0 0 132-35 0 0-132-83 0 0 187c25 45 70 74 118 95z" /></g>',
        );
        break;
      case 'SERVICE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="task__typeicon" transform="scale(1, -1) translate(0, -100)" d="M413 631c0-19 0-37 0-56-16-4-30-10-44-18l-40 39-74-74 40-40c-8-13-14-28-18-43l-56 0 0-106 56 0c6-20 17-39 28-55l0 91-49-1 0 36 49 0 3 14c4 20 12 40 23 57l8 12-34 35 25 25 34-34 12 8c18 11 38 19 58 24l14 3 0 48 36 0 0-48 14 0 80 0c-9 10-46 24-59 28l0 55c-39 0-70 0-106 0z m100-101l0-55c-16-5-31-11-44-19l-40 40-74-75 39-40c-7-13-13-28-17-43l-57 0 0-106 57 1c5-16 11-30 18-44l-40-40 75-74 41 40c13-8 28-14 44-18l0-57c36 0 74 0 105 0l0 58c16 4 31 10 44 18l41-40 74 75-41 40c8 14 14 28 18 44l56 0 0 105-56 0c-5 16-11 30-19 43l39 39-75 74-39-39c-14 8-28 14-44 18l1 55-106 0z m35-35l36 0-1-48 15-3c20-4 40-12 58-23l11-8 34 34 25-25-33-34 8-12c12-18 20-37 24-57l3-14 49 0 0-35-49-1-3-14c-4-20-12-40-23-57l-8-12 36-35-26-26-35 36-12-8c-18-12-38-20-58-24l-14-3 0-51c-9 0-20 0-35 0l0 50-15 3c-20 4-40 12-58 24l-11 8-36-36-26 25 36 35-8 12c-12 18-20 38-24 58l-3 14-50-1 0 36 50 0 3 14c4 21 12 40 23 58l8 12-35 34 25 25 35-34 12 8c18 12 37 19 58 24l14 3 0 48z m18-128c-43 0-79-36-79-79 0-44 36-79 79-79 43 0 79 35 79 79 0 43-36 79-79 79z m0-35c25 0 44-20 44-44 0-25-19-44-44-44-24 0-44 19-44 44 0 24 20 44 44 44z" /></g>',
        );
        break;
      case 'RECEIVE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="task__typeicon" transform="scale(1, -1) translate(0, -100)" d="M95 618l0-535 811 0 0 535-811 0z m83-35l644 0-322-212-322 212z m-48-10l370-244 371 244 0-455-741 0 0 455z" /></g>',
        );
        break;
      case 'MANUAL':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="task__typeicon" transform="scale(1, -1) translate(0, -100)" d="M460 659c-11 0-22-5-31-10l0 0 0 0c-47-31-209-143-245-168l0 0 0 0c-32-22-54-55-66-95l0 0 0 0c-13-41-11-86-11-124l0 0 0 0c0-28 1-53 8-83 0 0 0 0 0 0 11-44 31-75 61-95 29-19 65-26 105-26 147-1 293-1 439 0l0 0 0 0c16 0 31 6 41 18 10 12 14 28 14 45 0 11-2 22-5 32l16 0c16 0 30 4 40 14 11 9 16 22 19 35l0 0c4 19 2 38-6 53 17 2 31 8 41 19 10 13 13 29 13 47 0 18-4 35-14 49-10 13-27 23-46 23l0 0 0 0c-7 0-13 0-21 0 4 9 6 19 6 31l0 0 0 0c0 17-5 35-15 48-10 14-27 23-46 23-112 0-223-1-317-1 5 5 9 10 15 15 16 17 31 32 39 42 20 25 23 59 6 83-8 13-21 22-34 24-2 0-4 1-5 1l0 0c-1 0-1 0-1 0z m0-35c0 0 1 0 1-1 3 0 6-1 10-8l0 0 0 0c6-9 6-29-4-42l0 0 0 0c-4-5-21-23-37-39-16-16-32-31-39-40-8-9-5-16-3-21 2-5 4-10 14-12 3-1 4-1 5-1 0 0 1 0 3 0 2 0 5 0 9 0 113-1 225 0 338 0l0 0 0 0c9 0 13-3 18-9 4-6 8-16 8-28 0-12-3-19-7-24-3-4-8-6-19-6l-242 0 0-35 242 0 0 0c34 0 51 0 76 0 9 0 13-3 18-9 4-6 7-16 7-28l0 0c0-13-2-21-5-24-3-4-8-7-20-7-4 0-34 0-47 0 0 0 0 0-1 0-90 0-180 0-270 0l0 0 0-35 0 0c0 0 191 0 271 0 10 0 15-4 20-12 5-8 7-20 5-34l0 0 0 0c-2-9-5-14-8-17-3-2-7-4-17-4-90 0-181 0-271 0l0 0 0-35 0 0 205 0 0 0c7 0 10-2 14-7 3-4 6-13 6-25l0 0 0 0c0-11-3-19-6-23-3-4-6-5-14-5-146-1-292-1-438 0l0 0 0 0c-37 0-66 6-87 20-21 14-37 37-46 74l0 1 0 0c-6 26-7 47-7 74l0 0c0 39-1 80 9 114 11 34 28 59 53 76l0 0c36 25 199 138 244 167l0 0c5 3 8 4 11 4 0 1 1 1 1 1z" /></g>',
        );
        break;
      case 'SCRIPT':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="task__typeicon" transform="scale(1, -1) translate(0, -100)" d="M380 661l-4-2-2-1c-58-35-98-67-125-98-26-31-39-62-40-92 0-60 44-105 83-145 39-40 74-78 78-109 1-16-3-32-20-54-17-22-49-48-99-78l-74-43 458 0 5 3 1 0c53 31 89 60 112 89 23 29 32 59 29 88-6 56-52 97-90 137-39 39-70 76-70 112 0 17 7 37 29 62 21 25 57 55 113 88l0 0 73 43-457 0z m13-46l282 0c-25-19-45-37-60-55-26-31-39-62-39-92-1-60 43-105 82-145 39-40 74-78 78-109 1-16-2-32-20-54-16-21-47-46-94-74l-283 0c20 15 36 30 47 45 23 29 33 59 30 88-6 56-52 97-91 137-38 39-70 76-69 112 0 17 7 37 28 62 21 25 56 53 109 85z m-66-80l0-21 220 0 0 21-220 0z m-9-115l0-22 229 0 0 22-229 0z m104-116l0-21 221 0 0 21-221 0z m31-115l0-22 229 0 0 22-229 0z" /></g>',
        );
        break;
      case 'SEND':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="task__typeicon" transform="scale(1, -1) translate(0, -100)" d="M99 608l401-227 401 227-802 0z m1-84l0-448 802 0 0 448-402-224-400 224z" /></g>',
        );
        break;
      case 'BUSINESSRULE':
        typeIcon = Snap.parse(
          '<g id="typeicon"><path class="task__typeicon" transform="scale(1, -1) translate(0, -100)" d="M89 659l0-617 822 0 0 617-822 0z m35-209l159 0 0-173-159 0 0 173z m194 0l558 0 0-173-558 0 0 173z m-194-208l159 0 0-165-159 0 0 165z m194 0l558 0 0-165-558 0 0 165z" /></g>',
        );
        break;
      case 'NONE':
        typeIcon = Snap.parse('<g id="typeicon"></g>');
        break;
      default:
        break;
    }
    const typeIconGroup = typeIcon.select('g');
    typeIconGroup.transform(`s0.03, T${4},${23}`);
    const taskTypeIcon = typeIcon.select('.task__typeicon');
    if (taskTypeIcon) {
      taskTypeIcon.attr({
        fill: this._foregroundColor,
      });
    }
    return typeIconGroup;
  }

  _updateTaskTypeIcon() {
    // delete old icon
    const oldIcon = this._graphic.select('#typeicon');
    oldIcon.remove();
    // append new icon
    const typeIconGroup = this._drawTaskTypeIcon();
    typeIconGroup.appendTo(this._graphic);
  }

  _drawLoopTypeIcon() {
    let looptypeIcon;
    switch (this.loopType.toUpperCase()) {
      case 'MULTIINSTANCE':
        looptypeIcon = Snap.parse(
          '<g id="looptypeicon"><path class="task__looptypeicon" transform="scale(1, -1) translate(0, -100)" d="M250 650l0-600 100 0 0 600-100 0z m200 0l0-600 100 0 0 600-100 0z m200 0l0-600 100 0 0 600-100 0z" /></g>',
        );
        break;
      case 'NONE':
        looptypeIcon = Snap.parse('<g id="looptypeicon"></g>');
        break;
      default:
        break;
    }

    const loopTypeIconGroup = looptypeIcon.select('g');
    loopTypeIconGroup.transform(
      `s0.022, T${this._WIDTH / 2 - 10},${this._HEIGHT - 8}`,
    );

    const taskLoopTypeIcon = looptypeIcon.select('.task__looptypeicon');
    if (taskLoopTypeIcon) {
      taskLoopTypeIcon.attr({
        fill: this._foregroundColor,
      });
    }

    return loopTypeIconGroup;
  }

  _updateLoopTypeIcon() {
    // delete old icon
    const oldIcon = this._graphic.select('#looptypeicon');
    oldIcon.remove();
    // append new icon
    const loopTypeIconGroup = this._drawLoopTypeIcon();
    loopTypeIconGroup.appendTo(this._graphic);
  }

  _updateTitleLabel() {
    const text = this._graphic.select('.task__group__text');
    text.node.innerText = this.title;
  }

  // Only for Facilis purpose
  deleteFormIfExist(form) {
    let deleteFlag = false;
    if (this.forms) {
      this.forms.forEach((step) => {
        const {
          EForms, PForms, entity, process,
        } = step;
        const indexEForm = EForms.findIndex((frm) => frm.id === form.id);
        const indexPForm = PForms.findIndex((frm) => frm.id === form.id);
        if (indexEForm !== -1) {
          EForms.splice(indexEForm, 1);
          const updEnt = Number.parseInt(entity, 10) - 1;
          step.entity = updEnt.toString();
          deleteFlag = true;
        }
        if (indexPForm !== -1) {
          PForms.splice(indexPForm, 1);
          const updProc = Number.parseInt(process, 10) - 1;
          step.process = updProc.toString();
          deleteFlag = true;
        }
      });
    }
    return deleteFlag;
  }
}
