import Config from 'config';
import {
  getMinX,
  getMaxX,
  getMinY,
  getMaxY,
  getProcessElementByID,
  getClosestMultiple,
} from '../utils/util';

export default class AlignManager {
  constructor(editor, history) {
    this._editor = editor;
    this._historyManager = history;
  }

  alignLeft() {
    const selectedArray = this._getSelected(this._editor.elements);
    if (selectedArray.length > 1) {
      const minXElement = getMinX(selectedArray);
      const isAligned = [];
      const isValid = [];
      for (let i = 0, len = selectedArray.length - 1; i < len; i++) {
        const el = selectedArray[i];
        if (
          el.selectionBox.node.getBoundingClientRect().x
          === selectedArray[i + 1].selectionBox.node.getBoundingClientRect().x
        ) {
          isAligned[i] = true;
        } else {
          isAligned[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isAligned.includes(false) && !isValid.includes(false)) {
        this._historyManager.save();
        for (let i = 0, len = selectedArray.length; i < len; i++) {
          const el = selectedArray[i];
          // update selected elements position
          const oldX = el.x;
          el.x = minXElement.x;
          this._editor.updateElementConnectors(el);
          // update boundary events position
          this._updateHorizontalBoundaryEventsPos(el, oldX);
        }
      }
    }
  }

  alignRight() {
    const selectedArray = this._getSelected(this._editor.elements);
    if (selectedArray.length > 1) {
      const maxXElement = getMaxX(selectedArray);
      const isAligned = [];
      const isValid = [];
      for (let i = 0, len = selectedArray.length - 1; i < len; i++) {
        const el = selectedArray[i];
        if (
          el.selectionBox.node.getBoundingClientRect().right
          === selectedArray[i + 1].selectionBox.node.getBoundingClientRect().right
        ) {
          isAligned[i] = true;
        } else {
          isAligned[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isAligned.includes(false) && !isValid.includes(false)) {
        this._historyManager.save();
        for (let i = 0, len = selectedArray.length; i < len; i++) {
          const el = selectedArray[i];
          if (maxXElement !== el) {
            // update elements position
            const oldX = el.x;
            el.x = maxXElement.x + (maxXElement.width - el.width);
            this._editor.updateElementConnectors(el);
            // update boundary events position
            this._updateHorizontalBoundaryEventsPos(el, oldX);
          }
        }
      }
    }
  }

  alignTop() {
    const selectedArray = this._getSelected(this._editor.elements);
    if (selectedArray.length > 1) {
      const minYElement = getMinY(selectedArray);
      const isAligned = [];
      const isValid = [];
      for (let i = 0, len = selectedArray.length - 1; i < len; i++) {
        const el = selectedArray[i];
        if (
          el.selectionBox.node.getBoundingClientRect().top
          === selectedArray[i + 1].selectionBox.node.getBoundingClientRect().top
        ) {
          isAligned[i] = true;
        } else {
          isAligned[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isAligned.includes(false) && !isValid.includes(false)) {
        this._historyManager.save();
        for (let i = 0, len = selectedArray.length; i < len; i++) {
          const el = selectedArray[i];
          // update elements position position
          const oldY = el.y;
          el.y = minYElement.y;
          this._editor.updateElementConnectors(el);
          // update boundary events position
          this._updateVerticalBoundaryEventsPos(el, oldY);
        }
      }
    }
  }

  alignBottom() {
    const selectedArray = this._getSelected(this._editor.elements);
    if (selectedArray.length > 1) {
      const maxYElement = getMaxY(selectedArray);
      const isValid = [];
      const isAligned = [];
      for (let i = 0, len = selectedArray.length - 1; i < len; i++) {
        const el = selectedArray[i];
        if (
          el.selectionBox.node.getBoundingClientRect().bottom
          === selectedArray[i + 1].selectionBox.node.getBoundingClientRect().bottom
        ) {
          isAligned[i] = true;
        } else {
          isAligned[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isAligned.includes(false) && !isValid.includes(false)) {
        // if min/max different save history,
        // this to avoid multiple align histories in same position
        this._historyManager.save();
        for (let i = 0, len = selectedArray.length; i < len; i++) {
          const el = selectedArray[i];
          if (maxYElement !== el) {
            const oldY = el.y;
            const newY = maxYElement.y + (maxYElement.height - el.height);
            // update elements position
            el.y = newY;
            this._editor.updateElementConnectors(el);
            // update boundary events position
            this._updateVerticalBoundaryEventsPos(el, oldY);
          }
        }
      }
    }
  }

  alignHorizontalCenter(elementsArray, saveHistory) {
    const isCentered = [];
    const isValid = [];
    // remove connectors from selected array
    const selectedArray = elementsArray.filter((obj) => {
      return obj.elementType !== Config.CONNECTOR;
    });

    if (selectedArray.length > 1) {
      const maxYElement = getMaxY(selectedArray);
      const minYElement = getMinY(selectedArray);
      const yMax = maxYElement.y + maxYElement.height;
      const yMin = minYElement.y;
      const centerY = (yMin + yMax) / 2;
      for (let i = 0, len = selectedArray.length; i < len; i++) {
        const el = selectedArray[i];
        if (el.y === Math.round(centerY - el.height / 2)) {
          isCentered[i] = true;
        } else {
          isCentered[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isCentered.includes(false) && !isValid.includes(false)) {
        if (saveHistory) {
          this._historyManager.save();
        }
        for (let i = 0, len = selectedArray.length; i < len; i++) {
          const el = selectedArray[i];
          // update element position
          const newY = Math.round(centerY - el.height / 2);
          const oldY = el.y;
          if (this._editor._isSnapToGridOn) {
            el.y = getClosestMultiple(newY, Config.GRID_SIZE)
              + Config.ELEMENT_SELECT_SIZE / 2;
          } else {
            el.y = newY;
          }
          this._editor.updateElementConnectors(el);
          // update boundary events position
          this._updateVerticalBoundaryEventsPos(el, oldY);
        }
      }
    }
  }

  alignVerticalCenter() {
    const isCentered = [];
    const isValid = [];
    // remove connectors from selected array
    const selectedArray = this._getSelected(this._editor.elements).filter(
      (obj) => {
        return obj.elementType !== Config.CONNECTOR;
      },
    );
    if (selectedArray.length > 1) {
      const maxXElement = getMaxX(selectedArray);
      const minXElement = getMinX(selectedArray);
      const xMax = maxXElement.x + maxXElement.width;
      const xMin = minXElement.x;
      const centerX = (xMin + xMax) / 2;
      for (let i = 0, len = selectedArray.length; i < len; i++) {
        const el = selectedArray[i];
        if (el.x === Math.round(centerX - el.width / 2)) {
          isCentered[i] = true;
        } else {
          isCentered[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isCentered.includes(false) && !isValid.includes(false)) {
        this._historyManager.save();
        for (let i = 0, len = selectedArray.length; i < len; i++) {
          const el = selectedArray[i];
          const newX = Math.round(centerX - el.width / 2);
          const oldX = el.x;
          // update elements positions
          if (this._editor._isSnapToGridOn) {
            el.x = getClosestMultiple(newX, Config.GRID_SIZE)
              + Config.ELEMENT_SELECT_SIZE / 2;
          } else {
            el.x = newX;
          }
          this._editor.updateElementConnectors(el);
          // update boundary events position
          this._updateHorizontalBoundaryEventsPos(el, oldX);
        }
      }
    }
  }

  distVertical() {
    const isDistributed = [];
    const selectedArray = this._getSelected(this._editor.elements);
    selectedArray.sort((a, b) => a.y - b.y);
    if (selectedArray.length > 2) {
      const maxYElement = selectedArray[selectedArray.length - 1];
      const minYElement = selectedArray[0];
      const yMin = minYElement.y - Config.ELEMENT_SELECT_SIZE / 2;
      const yMax = maxYElement.y - Config.ELEMENT_SELECT_SIZE / 2;
      // this._editor._paper.circle(maxYElement.x - (Config.ELEMENT_SELECT_SIZE / 2), yMax, 5);
      // this._editor._paper.circle(minYElement.x - (Config.ELEMENT_SELECT_SIZE / 2), yMin, 5);

      const freeSpace = yMax - yMin - minYElement.height;
      let usedSpace = 0;
      for (let i = 1; i < selectedArray.length - 1; i++) usedSpace += selectedArray[i].height;
      let delta = (freeSpace - usedSpace) / (selectedArray.length - 1);

      const useHeight = delta >= 0;
      if (delta < 0) {
        delta = (yMax - yMin) / (selectedArray.length - 1);
      }
      // console.log(delta);
      // check if already distributed
      const isValid = [];
      for (let i = 1; i < selectedArray.length - 1; i++) {
        const el = selectedArray[i];
        const diff = el.y - (selectedArray[i - 1].y + selectedArray[i - 1].height);
        // console.log(`${selectedArray[i].elementType} diff:`, diff);
        if (diff === delta) {
          isDistributed[i] = true;
        } else {
          isDistributed[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isDistributed.includes(false) && !isValid.includes(false)) {
        this._historyManager.save();
        for (let i = 1; i < selectedArray.length - 1; i++) {
          const el = selectedArray[i];
          const oldY = el.y;
          // update element position
          const newY = selectedArray[i - 1].y
            + (useHeight ? selectedArray[i - 1].height : 0)
            + delta;
          if (this._editor._isSnapToGridOn) {
            el.y = getClosestMultiple(newY, Config.GRID_SIZE)
              + Config.ELEMENT_SELECT_SIZE / 2;
          } else {
            el.y = newY;
          }
          this._editor.updateElementConnectors(el);
          // update boundary events position
          this._updateVerticalBoundaryEventsPos(el, oldY);
        }
      }
    }
  }

  distHorizontal() {
    const isDistributed = [];
    const selectedArray = this._getSelected(this._editor.elements);
    selectedArray.sort((a, b) => a.x - b.x);
    if (selectedArray.length > 2) {
      const maxXElement = selectedArray[selectedArray.length - 1];
      const minXElement = selectedArray[0];
      const xMin = minXElement.x - Config.ELEMENT_SELECT_SIZE / 2;
      const xMax = maxXElement.x - Config.ELEMENT_SELECT_SIZE / 2;
      // this._editor._paper.circle(maxXElement.x - (Config.ELEMENT_SELECT_SIZE / 2), xMax, 5);
      // this._editor._paper.circle(minXElement.x - (Config.ELEMENT_SELECT_SIZE / 2), xMin, 5);

      const freeSpace = xMax - xMin - minXElement.width;
      let usedSpace = 0;
      for (let i = 1; i < selectedArray.length - 1; i++) usedSpace += selectedArray[i].width;
      let delta = (freeSpace - usedSpace) / (selectedArray.length - 1);

      const useWidth = delta >= 0;
      if (delta < 0) {
        delta = (xMax - xMin) / (selectedArray.length - 1);
      }
      // console.log(delta);
      // check if already distributed
      const isValid = [];
      for (let i = 1; i < selectedArray.length - 1; i++) {
        const el = selectedArray[i];
        const diff = el.x - (selectedArray[i - 1].x + selectedArray[i - 1].width);
        // console.log(`${selectedArray[i].elementType} diff:`, diff);
        if (diff === delta) {
          isDistributed[i] = true;
        } else {
          isDistributed[i] = false;
        }
        this._checkIsValid(isValid, i, el);
      }
      if (isDistributed.includes(false) && !isValid.includes(false)) {
        this._historyManager.save();
        for (let i = 1; i < selectedArray.length - 1; i++) {
          const el = selectedArray[i];
          const oldX = el.x;
          // update elements position
          const newX = selectedArray[i - 1].x
            + (useWidth ? selectedArray[i - 1].width : 0)
            + delta;
          if (this._editor._isSnapToGridOn) {
            el.x = getClosestMultiple(newX, Config.GRID_SIZE)
              + Config.ELEMENT_SELECT_SIZE / 2;
          } else {
            el.x = newX;
          }
          this._editor.updateElementConnectors(el);
          // update boundary events position
          this._updateHorizontalBoundaryEventsPos(el, oldX);
        }
      }
    }
  }

  _checkIsValid(isValid, i, el) {
    if (
      el.elementType === Config.POOL
      || el.elementType === Config.LANE
      || el.elementType === Config.CONNECTOR
    ) {
      isValid[i] = false;
    } else {
      isValid[i] = true;
    }
  }

  _updateHorizontalBoundaryEventsPos(el, oldX) {
    if (
      el.elementType === Config.TASK
      || el.elementType === Config.SUBPROCESS
    ) {
      for (let i = 0, len = el.boundaryEvents.length; i < len; i++) {
        const elB = getProcessElementByID(
          el.boundaryEvents[i],
          this._editor.elements,
        );
        if (!elB.selected) {
          const dx = oldX - elB.x;
          elB.x = el.x - dx;
          this._editor.updateElementConnectors(elB);
        }
      }
    }
  }

  _updateVerticalBoundaryEventsPos(el, oldY) {
    if (
      el.elementType === Config.TASK
      || el.elementType === Config.SUBPROCESS
    ) {
      for (let i = 0, len = el.boundaryEvents.length; i < len; i++) {
        const elB = getProcessElementByID(
          el.boundaryEvents[i],
          this._editor.elements,
        );
        if (!elB.selected) {
          const dy = oldY - elB.y;
          elB.y = el.y - dy;
          this._editor.updateElementConnectors(elB);
        }
      }
    }
  }

  _getSelected(elArray) {
    const selectedArray = [];
    let el;
    for (let i = 0, len = elArray.length; i < len; i++) {
      el = elArray[i];
      if (
        el.selected
        && el.elementType !== Config.POOL
        && el.elementType !== Config.LANE
        && el.elementType !== Config.CONNECTOR
      ) {
        selectedArray.push(el);
      }
    }
    return selectedArray;
  }
}
