import Config from 'config';
import TemplatePanel from './templatePanel';
import { normalizeText } from './facilis/validator';

export default class PoolLanePanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'pool_lane');
    this._init();
  }

  _init() {
    this._nameInput = this._panel.querySelector('#pool_lane_name_input');

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onNameInputChanged();
      },
      this._nameInput,
      'input',
      100,
    );
  }

  get nameInput() {
    return this._nameInput.value;
  }

  set nameInput(value) {
    this._nameInput.value = value;
  }

  show(el) {
    this.nameInput = el.name;
    this.documentationInput = el.documentation;
    this._changeVisibility(this._panel, true);
  }

  _onNameInputChanged() {
    if (this.element) {
      const { elementType, name } = this.element;
      const isPoolOrLane = elementType === Config.POOL || elementType === Config.LANE;
      const actual = isPoolOrLane ? name : undefined;
      this.nameInput = normalizeText(this.nameInput, /["]/g);
      if (actual !== this.nameInput && isPoolOrLane) {
        this.element.name = this.nameInput;
      }
    }
  }
}
