import axios from 'axios';
import Component from './Component';
import Converter from '../../utils/convertToJson';

export default class SearchComponent extends Component {
  constructor(nameId, panel, label, add = true, responseTime = 800, url, hasLabel = false) {
    super('search', nameId, panel, label);
    this._init(add, responseTime, nameId, url, hasLabel);
  }

  set placeholder(value) {
    this._selectEl.placeholder = value;
  }

  get data() {
    return this._dataList;
  }

  get valueEl() {
    return this._selectEl.value;
  }

  set valueEl(value) {
    this._selectEl.value = value;
  }

  get selectEl() {
    return this._selectEl;
  }

  set selectEl(value) {
    this._selectEl.value = value;
  }

  set disabled(value) {
    this._selectEl.disabled = value;
  }

  get selectedItem() {
    const valueToFind = this.valueEl.toLowerCase().trim();
    const findEl = this._dataList.find((value) => value.name.toLowerCase().trim() === valueToFind);
    return findEl;
  }

  set url(value) {
    this._url = value;
  }

  get load() {
    return this._load;
  }

  set load(value) {
    this._load = value;
    this.showData();
  }

  get alwaysEnableBtn() {
    return this._alwaysEnableBtn;
  }

  set alwaysEnableBtn(value) {
    this._alwaysEnableBtn = value;
    this.addBtn.disabled = !value;
  }

  get dataList() {
    return this._dataList;
  }

  set add(value) {
    this.addBtn.setAttribute('data-visible', value);
  }

  _init(add, responseTime, nameId, url, hasLabel) {
    this.addBtn = this.wrapper.querySelector('.btn');
    this.addBtn.title += ` ${this.label}`;
    this._selectEl = this.wrapper.querySelector('.input_control');
    this.listItems = this.wrapper.querySelector('.items_list');
    this.listItems.setAttribute('id', `${this.listItems.id}_${nameId}`);
    this._selectEl.setAttribute('list', this.listItems.id);
    this._dataList = [];
    this._url = url;
    this.hasLabel = hasLabel;
    this.valueToFind = this.valueEl.toLowerCase();
    this._responseTime = responseTime;
    this._load = false;
    this._alwaysEnableBtn = false;
    this._searchAction = this.wrapper.querySelector('a.search_action');

    this.addBtn.addEventListener('click', () => {
      this.addAction();
    });
    if (!add) {
      this.addBtn.setAttribute('data-visible', add);
    } else {
      this.addBtn.disabled = !this._alwaysEnableBtn;
    }

    this.selectEl.addEventListener('click', () => {
      if (this.valueEl !== '') {
        this.valueEl = '';
        this.alwaysEnableBtn = false;
      }
    });

    this.panel.parentNode.addEventListener('wheel', () => {
      this._selectEl.blur();
    });

    this.panel.parentNode.addEventListener('scroll', () => {
      this._selectEl.blur();
    });

    this._searchAction.addEventListener('click', async () => {
      if (this._responseTime !== 0) {
        this.valueEl = '';
        await this._getResultData();
        this._selectEl.focus();
      }
    });
  }

  addAction() {
    const selected = this.selected();
    this.emit('add_item', selected);
  }

  selected() {
    const options = Array.from(this.listItems.options);
    const el = options.find((opt) => opt.value.toLowerCase().trim() === this.valueEl.toLowerCase());
    let selected = {};
    if (el !== undefined) {
      selected = { id: el.getAttribute('data-key'), name: el.value };
      if (el.label) {
        selected.label = el.label;
      }
      if (!this._alwaysEnableBtn) this._enableBtn(false);
      this.emit('select_item', selected);
    } else {
      if (!this._alwaysEnableBtn) this._enableBtn(true);
      this.emit('select_item', undefined);
    }
    return selected;
  }

  _enableBtn(enable) {
    if (this.addBtn !== null) {
      this.addBtn.disabled = enable;
    }
  }

  async loadData() {
    try {
      const convert = new Converter();
      const response = await axios.get(this._url);
      if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
        this.showInvalidSession();
      }

      this._dataList = convert.convertToJson(response.data);
      const options = this.listItems.querySelectorAll('option');
      if (options.length === 0) {
        this._loadDataList(this._dataList, this.hasLabel);
      }
    } catch (error) {
      console.warn(error);
    }
  }

  async _getResultData() {
    this._cleanData();
    await this.loadData();
  }

  async showData() {
    if (this._responseTime === 0 && this.load && this._dataList.length === 0) {
      await this._getResultData();
    }
    let timeout;
    this._selectEl.addEventListener('input', () => {
      clearTimeout(timeout);
      if (this._responseTime !== 0) {
        timeout = setTimeout(async () => {
          const currentValue = this._selectEl.value;
          const exist = this._dataList.findIndex((value) => value.name === currentValue) !== -1;
          if (this.valueToFind !== currentValue && currentValue !== '' && !exist) {
            await this._getResultData();
            this.valueToFind = currentValue;
          }
          clearTimeout(timeout);
        }, this._responseTime);
      }
      this.selected();
    });
  }

  _cleanData() {
    const options = this.listItems.querySelectorAll('option');
    if (options.length > 0) {
      for (let i = 0; i < options.length; i++) {
        options[i].remove();
      }
    }
  }

  _loadDataList(dataList, label) {
    dataList.forEach((option) => {
      const dataValue = option.name;
      const dataKey = option.id;
      const optionEl = document.createElement('option');
      optionEl.setAttribute('value', dataValue);
      optionEl.setAttribute('data-key', dataKey);
      optionEl.addEventListener('mouseover', () => {});
      if (label) optionEl.setAttribute('label', option.label);
      this.listItems.appendChild(optionEl);
    });
  }
}
