import EventEmitter from 'eventemitter3';
import * as Snap from 'snapsvg-cjs';

export default class ContextButton extends EventEmitter {
  constructor(paper, icon, parent, pos) {
    super();
    this._paper = paper;
    this._btn = null;
    this._bg = null;
    this._iconGroup = null;
    this._onButtonClicked = this._onButtonClicked.bind(this);
    this._ICON_OFFSET_X = 20;
    this._ICON_OFFSET_Y = 25;
    this._BG_OFFSET_X = 15;
    this._drawMenu(parent, pos, icon);
  }

  _drawMenu(parent, pos, icon) {
    this._bg = this._paper
      .rect(pos.x + this._BG_OFFSET_X, pos.y, 40, 36)
      .attr({ class: 'context_menu_btn__bg' });

    const iconShape = Snap.parse(icon);
    this._iconGroup = iconShape.select('g');
    this._iconGroup.transform(
      `s0.03, T${pos.x + this._ICON_OFFSET_X},${pos.y + this._ICON_OFFSET_Y}`,
    );

    this._btn = this._paper
      .g(this._bg, this._iconGroup)
      .mousedown(this._onButtonClicked)
      .attr({ class: 'context_menu_btn' });

    this._btn.appendTo(parent);
  }

  _onButtonClicked(event) {
    event.stopPropagation();
    this.emit('on_context_button_clicked', event);
  }

  updatePosition(pos) {
    this._bg.attr({ x: pos.x + this._BG_OFFSET_X, y: pos.y });
    const newMatrix = new Snap.Matrix();
    newMatrix.translate(
      pos.x + this._ICON_OFFSET_X,
      pos.y + this._ICON_OFFSET_Y,
    );
    newMatrix.scale(0.03);
    this._iconGroup.attr({ transform: newMatrix });
  }
}
