import Config from 'config';

export default class GridManager {
  constructor(editor, size) {
    this._editor = editor;
    this._gridSize = size;
    this._grid = null;
    this._isGridOn = false;
  }

  init() {
    if (!this._grid) {
      this._drawGrid();
    }
    if (this.isGridOn) {
      // when opening new documents previous grid state needs to be accounted for
      if (Config.ENV === 'Offline') {
        this.show();
      }
    }
  }

  get gridSize() {
    return this._gridSize;
  }

  get isGridOn() {
    return this._isGridOn;
  }

  _drawGrid() {
    const viewport = this._editor._viewport;
    const bounds = document.getElementById('app').getBoundingClientRect();
    const wv = bounds.width * 32;
    const hv = bounds.height * 32;
    const sq = viewport.rect(-wv / 2, -hv / 2, wv, hv);

    this._grid = sq.prependTo(viewport);
    const pLine1 = viewport
      .line(0, 0, this._gridSize, 0)
      .attr({ class: 'grid_bg__line' });
    const pLine2 = viewport
      .line(0, 0, 0, this._gridSize)
      .attr({ class: 'grid_bg__line' });
    const pattern = viewport
      .g(pLine1, pLine2)
      .pattern(0, 0, this._gridSize, this._gridSize);

    // apply pattern
    this._grid.attr({
      fill: pattern,
      class: 'grid_bg',
      'data-visible': 'off',
    });
  }

  show() {
    this._grid.node.setAttribute('data-visible', 'on');
    this._isGridOn = true;
  }

  hide() {
    this._grid.node.setAttribute('data-visible', 'off');
    this._isGridOn = false;
  }
}
