import EventEmitter from 'eventemitter3';
import i18next from 'i18next';
import ToastMessage from './ToastMessage';

export default class Component extends EventEmitter {
  constructor(type, nameId, panel, label = undefined) {
    super();
    this._nameId = nameId;
    this._panel = panel;
    const modified = this.panel.querySelector(`#wrapper_${type}`);
    modified.setAttribute('id', `${modified.id}_${this.nameId}`);
    this._wrapper = modified;
    this._label = label;
    this._labelEl = this._wrapper.querySelector('.main__sidepanel__form__label');
    if (this._labelEl) this.labelEl = `${label}:`;
  }

  get nameId() {
    return this._nameId;
  }

  get panel() {
    return this._panel;
  }

  get wrapper() {
    return this._wrapper;
  }

  set nameID(value) {
    this._nameId = value;
  }

  set panel(value) {
    this._panel = value;
  }

  set wrapper(value) {
    this._wrapper = value;
  }

  /**
   * @param {string} value
   */
  set labelEl(value) {
    this._labelEl.innerText = value;
  }

  get label() {
    return this._label;
  }

  clean() {
    this.wrapper.remove();
  }

  disabled(el, flag) {
    el.setAttribute('disabled', flag);
  }

  showInvalidSession() {
    const toast = new ToastMessage();
    const option = {
      duration: 15000,
      position: 'bottom-left',
    };
    toast.showMessage({
      type: 'danger',
      title: i18next.t('component.txtComponentToastTitleError'),
      message: i18next.t('component.txtComponentToastMessage'),
      ...option,
    });
  }
}
