import Config from 'config';
import { intersectRect, getAllLanes } from '../utils/util';

export default class PoolManager {
  constructor(editor) {
    this._editor = editor;
    this._poolElements = [];
    this._laneElements = [];
    this._LANE_MIN_HEIGHT = 160;
    this._isFirstMove = true;
  }

  moveStart(pool) {
    if (pool.elementType === Config.POOL) {
      this._poolElements = [];
      const len = this._editor.elements.length;
      for (let i = 0; i < len; i++) {
        const el = this._editor.elements[i];
        const elGraphic = el.selectionBox;
        if (el.elementType !== Config.POOL) {
          if (elGraphic) {
            const isContained = intersectRect(
              pool.selectionBox.node,
              elGraphic.node,
            );
            if (isContained) {
              this._poolElements.push(el);
            }
          }
        }
      }
    }
  }

  moving(pool) {
    if (pool.elementType === Config.POOL) {
      let el;
      for (let i = 0, len = this._poolElements.length; i < len; i++) {
        el = this._poolElements[i];
        if (el.elementType !== Config.CONNECTOR) {
          el.moveElement(
            Math.round(pool.mx + el.x),
            Math.round(pool.my + el.y),
          );
        }
      }
      if (this._isFirstMove) {
        this._editor.toggleElementConnectorsVisibility(false);
        this._isFirstMove = false;
      }
    }
  }

  moveEnd(pool) {
    if (pool.elementType === Config.POOL) {
      let el;
      for (let i = 0, len = this._poolElements.length; i < len; i++) {
        el = this._poolElements[i];
        if (el.elementType !== Config.CONNECTOR) {
          el.moveElementEnd(
            Math.round(pool.mx + el.x),
            Math.round(pool.my + el.y),
          );
          // update connections
          this._editor.updateElementConnectors(
            el,
            { x: pool.mx, y: pool.my },
            false,
            true,
          );

          // hack to fix
          this._editor.updateElementConnectors(el, { x: 0, y: 0 }, false, true);
        }

        if (el.elementType === Config.CONNECTOR) {
          // update points segments for undo
          el.resetMovingPointSegment();
        }
      }
      this._editor.toggleElementConnectorsVisibility(true);
      this._isFirstMove = true;
    }
  }

  noMoveEnd(pool) {
    if (pool.elementType === Config.POOL) {
      this._editor.toggleElementConnectorsVisibility(true);
      this._isFirstMove = true;
    }
  }

  onElementDelete() {
    let i = this._editor.elements.length;
    while (i--) {
      if (this._editor.elements[i].elementType === Config.LANE) {
        this._editor.elements[i].delete();
        this._editor.elements.splice(i, 1);
      }
    }
  }

  onResizeStart() {
    this._laneElements = getAllLanes(this._editor.elements);
  }

  onResize(pool, width, height) {
    let totalHeightLanes = 0;
    let lastLaneHeight = height;
    let laneNameBoxWidth;
    const len = this._laneElements.length;
    for (let i = 0; i < len; i++) {
      const lane = this._laneElements[i];
      laneNameBoxWidth = lane._nameBoxWidth;
      lane.resizeWidth(width - laneNameBoxWidth);
      if (i < len - 1) {
        lastLaneHeight -= lane.height;
      }
      totalHeightLanes += lane.height;
    }
    // stop resizing if last lane smaller than this._LANE_MIN_HEIGHT
    if (len >= 1) {
      if (lastLaneHeight >= this._LANE_MIN_HEIGHT) {
        pool.minHeight = 0;
        this._laneElements[len - 1].resizeHeight(lastLaneHeight);
        pool.resize(width, height);
      } else {
        pool.minHeight = height;
        pool.resize(
          this._laneElements[0].width + laneNameBoxWidth,
          totalHeightLanes,
        );
      }
    } else {
      // no lanes just the pool
      pool.minHeight = 0;
      pool.resize(width, height);
    }
  }

  onResizeEnd() {
    for (let i = 0; i < this._laneElements.length; i++) {
      const lane = this._laneElements[i];
      // props for history
      lane.properties.size = {
        width: lane.width,
        height: lane.height,
      };
    }
    this._laneElements = [];
  }
}
