import Component from './Component';

export default class ExpanderComponent extends Component {
  constructor(nameId, panel, expandId, label) {
    super('expander', nameId, panel, label);
    this.expandId = expandId;

    this._init();
  }

  get isExpand() {
    return JSON.parse(this._expandZone.getAttribute('data-visible'));
  }

  get inputEl() {
    return this.showInput;
  }

  set inputEl(value) {
    this.showInput.placeholder = value;
  }

  get expandZone() {
    return this._expandZone;
  }

  _init() {
    this.expandBtn = this.wrapper.querySelector('.btn');
    this.showInput = this.wrapper.querySelector('input[type=text]');
    this._expandZone = this.panel.querySelector(`#${this.expandId}`);
    this._expandZone.setAttribute('data-visible', false);

    // Add Events
    this.expandBtn.addEventListener('click', () => {
      this.expand();
    });
  }

  expand() {
    const iconUp = 'fa-arrow-up';
    const iconDown = 'fa-arrow-down';
    const iconEl = this.expandBtn.querySelector('svg');
    if (this.isExpand) {
      iconEl.classList.replace(iconUp, iconDown);
      this._expandZone.setAttribute('data-visible', false);
    } else {
      iconEl.classList.replace(iconDown, iconUp);
      this._expandZone.setAttribute('data-visible', true);
      this.emit('expand');
    }
  }

  showValue(data) {
    if (data.length > 0) {
      let newData = `(${data.length}) - `;
      let index = 0;
      const quantity = 2;
      while (index < quantity && index < data.length) {
        const element = data[index];
        newData = newData.concat(`${element}`);
        if (index !== quantity - 1 && index !== data.length - 1) {
          newData = newData.concat(', ');
        }
        index++;
      }
      if (data.length > quantity) {
        newData = newData.concat(' ... ');
      }
      this.showInput.setAttribute('placeholder', newData);
    } else {
      this.showInput.setAttribute('placeholder', '');
    }
  }
}
