import Config from 'config';
import axios from 'axios';
import i18next from 'i18next';
import Component from './Component';
import ToastMessage from './ToastMessage';
import Converter from '../../utils/convertToJson';

export default class ConditionComponent extends Component {
  constructor(nameId, panel, label, placeholder = '') {
    super('condition', nameId, panel, label);
    this._conditionArea = this.wrapper.querySelector('.condition_area');
    this._conditionArea.value = '';
    this.help = this.wrapper.querySelector('a.condition_question');
    this.validate = this.wrapper.querySelector('a.condition_validate');
    this.syntaxRules = this.wrapper.querySelector('.condition_syntax_rules');
    this._isValid = false;
    let timeout;
    this.placeholder = placeholder;
    this._hideValidate(true);

    this.help.addEventListener('click', () => {
      this.syntaxRules.hidden = !this.syntaxRules.hidden;
    });

    this.validate.addEventListener('click', async () => {
      await this._onConditionAreaValidated();
    });

    this._conditionArea.addEventListener('blur', async () => {
      await this._onConditionAreaValidated();
    });

    this._conditionArea.addEventListener('input', () => {
      if (Config.ENV !== 'Production') {
        clearTimeout(timeout);
        timeout = setTimeout(() => {
          this._onConditionAreaChanged();
          clearTimeout(timeout);
        }, 800);
      }
      // if (this.conditionArea !== '' && Config.ENV === 'Production') {
      if (Config.ENV === 'Production') {
        this.isValid = false;
        this._hideValidate(false);
      }
    });
  }

  get isValid() {
    return this._isValid;
  }

  set isValid(value) {
    this._isValid = value;
  }

  get conditionAreaEl() {
    return this._conditionArea;
  }

  get conditionArea() {
    return this._conditionArea.value.trim();
  }

  set conditionArea(value) {
    this._conditionArea.value = value;
  }

  set placeholder(value) {
    this._conditionArea.placeholder = value;
  }

  set disabled(value) {
    this.conditionAreaEl.disabled = value;
  }

  get disabled() {
    return this.conditionAreaEl.disabled;
  }

  _hideValidate(value) {
    if (value) {
      this.validate.classList.add('invisible');
    } else {
      this.validate.classList.remove('invisible');
    }
  }

  resetValidate() {
    this.isValid = false;
    this.validate.removeAttribute('data-valid');
  }

  _onConditionAreaChanged() {
    this.emit('change_condition', this.conditionArea);
  }

  async _validate() {
    const condition = this.conditionArea;
    const url = `${Config.VALIDATE_COND_URL}${encodeURI(condition)}`;
    if (
      Config.ENV === 'Production'
      // && this.conditionArea !== ''
      && !this.disabled
    ) {
      try {
        const response = await axios.post(url);
        if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
          this.showInvalidSession();
        }
        const converter = new Converter();
        const [data] = converter.convertToJson(response.data);
        this.isValid = data.message === 'OK';
        this._hideValidate(true);
        return data;
      } catch (error) {
        throw Error(
          `${i18next.t('conditionalComponent.txtConditionalComponentErrorMessage')}\n ${error}`,
        );
      }
    }
    return true;
  }

  async _onConditionAreaValidated() {
    if (Config.ENV === 'Production') {
      const toast = new ToastMessage();
      const data = await this._validate();
      const option = {
        duration: 3000,
        position: 'bottom-left',
      };
      if (!this.isValid) {
        if (data.message) {
          const message = data.message.split('.  Algunos')[0];
          toast.showMessage({
            type: 'danger',
            title: i18next.t('conditionalComponent.txtConditionalComponentToastTitle'),
            message,
            ...option,
          });
        }
      } else {
        this._onConditionAreaChanged();

        if (this.conditionArea !== '') {
          toast.showMessage({
            type: 'success',
            title: i18next.t('conditionalComponent.txtConditionalComponentToastTitleSuccess'),
            message: i18next.t(
              'conditionalComponent.txtConditionalComponentToastTitleSuccessMessage',
            ),
            ...option,
          });
        }
      }
    }
  }
}
