import Config from 'config';

import TaskPanel from './taskPanel';
import GatewayPanel from './gatewayPanel';
import GroupPanel from './groupPanel';
import TextPanel from './textPanel';
import PoolLanePanel from './poolLanePanel';
import EventPanel from './eventPanel';
import DataPanel from './dataPanel';
import SubProcessPanel from './subprocessPanel';
import WorkflowPanel from './workflowPanel';
import ConnectorPanel from './connectorPanel';
import PropertiesPanel from './propertiesPanel';

export default class Panels extends PropertiesPanel {
  constructor(editor) {
    super();
    this._taskPanel = new TaskPanel(editor);
    this._subprocessPanel = new SubProcessPanel(editor);
    this._gatewayPanel = new GatewayPanel(editor);
    this._eventPanel = new EventPanel(editor);
    this._groupPanel = new GroupPanel(editor);
    this._textPanel = new TextPanel(editor);
    this._dataPanel = new DataPanel(editor);
    this._poolLanePanel = new PoolLanePanel(editor);
    this._workflowPanel = new WorkflowPanel(editor);
    this._connectorPanel = new ConnectorPanel(editor);
  }

  show(panelName, element, undo = false) {
    switch (panelName) {
      case Config.TASK:
        this._taskPanel.show(element);
        break;
      case Config.SUBPROCESS:
        this._subprocessPanel.show(element);
        break;
      case Config.GATEWAY:
        this._gatewayPanel.show(element);
        break;
      case Config.START_EVENT:
        this._eventPanel.show(element);
        break;
      case Config.INTERMEDIATE_EVENT:
        this._eventPanel.show(element);
        break;
      case Config.END_EVENT:
        this._eventPanel.show(element);
        break;
      case Config.GROUP:
        this._groupPanel.show(element);
        break;
      case Config.TEXT:
        this._textPanel.show(element);
        break;
      case Config.DATA_STORE:
        this._dataPanel.show(element);
        break;
      case Config.DATA_OBJECT:
        this._dataPanel.show(element);
        break;
      case Config.DATA_INPUT:
        this._dataPanel.show(element);
        break;
      case Config.DATA_OUTPUT:
        this._dataPanel.show(element);
        break;
      case Config.POOL:
        this._poolLanePanel.show(element);
        break;
      case Config.LANE:
        this._poolLanePanel.show(element);
        break;
      case Config.CONNECTOR:
        this._connectorPanel.show(element);
        break;
      case Config.WORKFLOW:
        /* console.log({
          NOT_VISIBLE: !this._workflowPanel.isVisible,
          UNDO_ACTION: undo,
        }); */
        if (!this._workflowPanel.isVisible || undo) {
          this._workflowPanel.show(element);
        }
        break;
      default:
        break;
    }
  }

  hide(panelName) {
    switch (panelName) {
      case Config.TASK:
        this._taskPanel.hide();
        break;
      case Config.SUBPROCESS:
        this._subprocessPanel.hide();
        break;
      case Config.GATEWAY:
        this._gatewayPanel.hide();
        break;
      case Config.START_EVENT:
        this._eventPanel.hide();
        break;
      case Config.INTERMEDIATE_EVENT:
        this._eventPanel.hide();
        break;
      case Config.END_EVENT:
        this._eventPanel.hide();
        break;
      case Config.GROUP:
        this._groupPanel.hide();
        break;
      case Config.TEXT:
        this._textPanel.hide();
        break;
      case Config.DATA_STORE:
        this._dataPanel.hide();
        break;
      case Config.DATA_INPUT:
        this._dataPanel.hide();
        break;
      case Config.DATA_OUTPUT:
        this._dataPanel.hide();
        break;
      case Config.DATA_OBJECT:
        this._dataPanel.hide();
        break;
      case Config.POOL:
        this._poolLanePanel.hide();
        break;
      case Config.LANE:
        this._poolLanePanel.hide();
        break;
      case Config.CONNECTOR:
        this._connectorPanel.hide();
        break;
      case Config.WORKFLOW:
        this._workflowPanel.hide();
        break;
      default:
        break;
    }
  }
}
