import axios from 'axios';
import Config from 'config';
import i18next from 'i18next';
import Converter from '../../utils/convertToJson';
import TemplatePanel from './templatePanel';
import { indexByProperty, mapInOutParameters } from '../../utils/util';

export default class EventPanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'event');
    this._init();
    this._initMessageComponents();
    this._initTimerComponents();
    this._initEventOfComponents();
  }

  _init() {
    this.EVENT_PANEL = {
      MESSAGE: {
        nameId: 'event_message',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelMessageLabel'),
        expandId: 'expand_message_zone',
      },
      MSG_ATTR_SEARCH: {
        nameId: 'event_message_attrs',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelAttributeSearchLabel'),
        add: true,
        url: Config.EVT_MSG_ATTRS_URL,
        hasLabel: true,
        canReload: true,
      },
      MSG_ATTRS_LIST: {
        nameId: 'event_message_attrs_list',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelAttributeListLabel'),
        add: true,
      },
      MSG_WS_SEARCH: {
        nameId: 'event_message_webservice',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelWSSearchLabel'),
        add: false,
        url: Config.EVT_MSG_WS_URL,
        hasLabel: false,
        canReload: true,
      },
      MSG_WS_LIST: {
        nameId: 'event_message_webservices_list',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelWSListLabel'),
        add: true,
      },
      MSG_BINDINGS_LIST: {
        nameId: 'event_message_bindings_list',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelBindingListLabel'),
        add: false,
      },
      MSG_WS_ATTR_SEARCH: {
        nameId: 'event_message_ws_attribute',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelWSAttributeSearchLabel'),
        add: false,
        url: Config.EVT_MSG_WS_ATTR_URL,
        baseUrl: Config.EVT_MSG_WS_ATTR_URL,
        hasLabel: true,
        canReload: true,
      },
      TIMER: {
        nameId: 'event_timer',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelTimerLabel'),
        expandId: 'expand_timer_zone',
      },
      TIMER_ATTRIBUTE_SEARCH: {
        nameId: 'event_timer_attribute',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelTimerAttributeSearchLabel'),
        add: false,
        url: Config.EVT_TIMER_ATTR_URL,
        hasLabel: true,
        canReload: true,
      },
      TIMER_START_DATE: {
        nameId: 'event_timer_start_date',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelTimerStartDateLabel'),
      },
      TIMER_END_DATE: {
        nameId: 'event_timer_end_date',
        label: i18next.t('eventPropsPanel.txtProDesEventPanelTimerStartDateLabel'),
      },
    };

    this.OPTIONS_TOAST = {
      type: 'info',
      title: i18next.t('eventPropsPanel.txtProDesEventPanelToastTitleInformation'),
      duration: 3000,
      position: 'bottom-left',
    };
    this._eventNameType = this._panel.querySelector('#event_name_type');
    this._detailTypeSelect = this._panel.querySelector('#event_detail_type_select');
    this._nameInput = this._panel.querySelector('#event_name_input');

    this._eventMsgCatchThrow = this._panel.querySelector('#event_message_catch_throw');
    this._eventMsgWebServiceInput = this._panel.querySelector('#event_message_webservice_input');
    this._eventMsgAttributesType = this._panel.querySelector('#event_message_attr_type');
    this._eventMsgBindingType = this._panel.querySelector('#event_message_binding_type');
    this._eventMsgBindingValue = this._panel.querySelector('#event_message_binding_value');

    this._eventTimerType = this._panel.querySelector('#event_timer_type_select');
    this._eventTimerStartDate = this._panel.querySelector('#event_timer_start_date');
    this._eventTimerEndDate = this._panel.querySelector('#event_timer_end_date');
    this._eventTimerUnit = this._panel.querySelector('#event_timer_unit');
    this._eventTimerValue = this._panel.querySelector('#event_timer_value');
    this._eventTimerAttrType = this._panel.querySelector('#event_timer_attribute_type');
    // Wrappers
    this.wrapDetailTypeSelect = this._detailTypeSelect.parentNode.parentNode;
    this.wrapMsgCatchThrow = this._eventMsgCatchThrow.parentNode.parentNode;
    this.wrapAttrsType = this._eventMsgAttributesType.parentNode.parentNode;
    this.wrapWebserviceInput = this._eventMsgWebServiceInput.parentNode.parentNode;
    this.wrapBindingType = this._eventMsgBindingType.parentNode.parentNode;
    this.wrapBindingValue = this._eventMsgBindingValue.parentNode.parentNode;
    this.wrapBindingAttrType = this._panel.querySelector('#wrapper_event_message_binding_attr_type');
    this.wrapTimerAttrType = this._eventTimerAttrType.parentNode.parentNode;
    // Events
    this._detailTypeSelect.addEventListener('input', () => {
      this._onDetailTypeSelectChanged();
    });
    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onNameInputChanged();
      },
      this._nameInput,
      'input',
      500,
    );

    this._eventTimerType.addEventListener('input', () => {
      this._onTimerTypeChanged();
    });
  }

  get nameInput() {
    return this._nameInput.value;
  }

  get detailTypeSelect() {
    return this._detailTypeSelect.value.toLowerCase();
  }

  set nameInput(value) {
    this._nameInput.value = value;
  }

  set detailTypeSelect(value) {
    this._detailTypeSelect.value = value;
  }

  set eventNameType(value) {
    this._eventNameType.value = value;
  }

  get eventTimerType() {
    return this._eventTimerType.value;
  }

  set eventTimerType(value) {
    this._eventTimerType.value = value;
  }

  get eventTimerStartDate() {
    return this._eventTimerStartDate.date;
  }

  set eventTimerStartDate(value) {
    this._eventTimerStartDate.date = value;
  }

  get eventTimerEndDate() {
    return this._eventTimerEndDate.date;
  }

  set eventTimerEndDate(value) {
    this._eventTimerEndDate.date = value;
  }

  get eventTimerUnit() {
    return this._eventTimerUnit.value;
  }

  set eventTimerUnit(value) {
    this._eventTimerUnit.value = value;
  }

  get eventTimerValue() {
    const { value } = this._eventTimerValue;
    return value !== '' ? parseInt(value, 10) : value;
  }

  set eventTimerValue(value) {
    this._eventTimerValue.value = value;
  }

  get eventTimerAttribute() {
    return this._eventTimerAttributeSearch.valueEl;
  }

  set eventTimerAttribute(value) {
    this._eventTimerAttributeSearch.selectedItem = value;
  }

  set eventTimerAttrType(value) {
    this._eventTimerAttrType.value = value;
  }

  get eventTimerAttrType() {
    return this._eventTimerAttrType.value;
  }

  get eventMsgWebServiceInput() {
    return this._eventMsgWebServiceInput.value;
  }

  set eventMsgWebServiceInput(value) {
    this._eventMsgWebServiceInput.value = value;
  }

  set eventMsgAttributeSearch(value) {
    this._eventMsgAttributeSearch.valueEl = value;
  }

  get eventMsgAttributeSearch() {
    return this._eventMsgAttributeSearch.valueEl;
  }

  get eventMsgAttributeType() {
    return this._eventMsgAttributesType.value;
  }

  set eventMsgAttributeType(value) {
    this._eventMsgAttributesType.value = value;
  }

  get eventMsgCatchThrow() {
    return this._eventMsgCatchThrow.value;
  }

  set eventMsgCatchThrow(value) {
    this._eventMsgCatchThrow.value = value;
  }

  set eventMsgWebServiceSearch(value) {
    this._eventMsgWebServiceSearch.valueEl = value;
  }

  get eventMsgWebServiceSearch() {
    return this._eventMsgWebServiceSearch.valueEl;
  }

  get eventMsgBindingType() {
    return this._eventMsgBindingType.value;
  }

  set eventMsgBindingType(value) {
    this._eventMsgBindingType.value = value;
  }

  get eventMsgBindingValue() {
    return this._eventMsgBindingValue.value;
  }

  set eventMsgBindingValue(value) {
    this._eventMsgBindingValue.value = value;
  }

  set eventMsgWSAttributeSearch(value) {
    this._eventMsgWSAttributeSearch.valueEl = value;
  }

  get bindingAttrTypes() {
    return this.wrapBindingAttrType.querySelectorAll('input[type=radio]');
  }

  get bindingAttrTypeChecked() {
    const checked = this.wrapBindingAttrType.querySelector('input[type=radio]:checked');
    return checked.value;
  }

  set bindingAttrTypeToCheck(value) {
    const radio = this.wrapBindingAttrType.querySelector(`#binding_attr_type_${value}`);
    radio.checked = true;
  }

  get wsSelected() {
    return this._wsSelected;
  }

  set wsSelected(value) {
    this._wsSelected = value;
  }

  _initTimerComponents() {
    const { TIMER, TIMER_ATTRIBUTE_SEARCH } = this.EVENT_PANEL;
    const { TIMER_START_DATE, TIMER_END_DATE } = this.EVENT_PANEL;

    this._eventTimerExp = this._includeComponent(
      'expander',
      this.wrapTimer,
      this._eventMessageExp.expandZone,
      'afterend',
      TIMER,
    );
    this._eventTimerAttributeSearch = this._includeComponent(
      'dropdown',
      this.wrapEvtTimerAttrSearch,
      this.wrapTimerAttrType,
      'beforebegin',
      TIMER_ATTRIBUTE_SEARCH,
    );
    const dateWrapper = this._getTimerGroup('date');
    this._eventTimerStartDate = this._includeComponent(
      'date',
      this.wrapStartDate,
      dateWrapper,
      'inner',
      TIMER_START_DATE,
    );
    this._eventTimerEndDate = this._includeComponent('date', this.wrapEndDate, dateWrapper, 'inner', TIMER_END_DATE);
  }

  _initMessageComponents() {
    const { MESSAGE, MSG_ATTR_SEARCH, MSG_ATTRS_LIST } = this.EVENT_PANEL;
    const {
      MSG_WS_SEARCH, MSG_WS_LIST, MSG_BINDINGS_LIST, MSG_WS_ATTR_SEARCH,
    } = this.EVENT_PANEL;
    MSG_ATTRS_LIST.arrayList = this._createMessageAttributesArrayList();
    MSG_WS_LIST.arrayList = this._createMessageWebServicesArrayList();
    MSG_BINDINGS_LIST.arrayList = this._createMessageBindingsArrayList();

    const isProduction = Config.ENV === 'Production';
    MSG_ATTR_SEARCH.add = isProduction;

    // Message Comps Start-Intermediate
    this._eventMessageExp = this._includeComponent(
      'expander',
      this.wrapMessage,
      this.wrapDetailTypeSelect,
      'afterend',
      MESSAGE,
    );
    this._eventMsgAttributeSearch = this._includeComponent(
      'dropdown',
      this.wrapEvtMsgAttrSearch,
      this.wrapWebserviceInput,
      'afterend',
      MSG_ATTR_SEARCH,
    );
    this._eventMsgAttributesList = this._includeComponent(
      'list',
      this.wrapEvtMsgAttrsList,
      this.wrapAttrsType,
      'afterend',
      MSG_ATTRS_LIST,
    );
    // Message Comps End
    this._eventMsgWebServiceSearch = this._includeComponent(
      'dropdown',
      this.wrapEvtMsgWSSearch,
      this._eventMsgAttributesList.wrapper,
      'afterend',
      MSG_WS_SEARCH,
    );
    this._eventMsgWebServicesList = this._includeComponent(
      'list',
      this.wrapEvtMsgWSList,
      this._eventMsgWebServiceSearch.wrapper,
      'afterend',
      MSG_WS_LIST,
    );
    this._eventMsgWSBindingsList = this._includeComponent(
      'list',
      this.wrapEvtMsgWSBindingsList,
      this.wrapBindingType,
      'beforebegin',
      MSG_BINDINGS_LIST,
    );
    this._eventMsgWSAttributeSearch = this._includeComponent(
      'dropdown',
      this.wrapEvtMsgWSAttrSearch,
      this.wrapBindingAttrType,
      'afterend',
      MSG_WS_ATTR_SEARCH,
    );
    this._wsSelected = null;
    this._eventMsgAttributeSearch.toggleDisabledButton(true);
  }

  _initEventOfComponents() {
    this._eventTimerStartDate.on('change_date', (date) => {
      this._onEventTimerDateChanged(date, 'start');
    });

    this._eventTimerEndDate.on('change_date', (date) => {
      this._onEventTimerDateChanged(date, 'end');
    });

    this._eventTimerUnit.addEventListener('input', () => {
      this._onEventTimerFrequencyChanged();
    });

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onValidateFrequencyValue();
        this._onEventTimerFrequencyChanged();
      },
      this._eventTimerValue,
      'input',
      100,
    );

    this._eventTimerAttrType.addEventListener('input', () => {
      this._onEventTimerAttributeChanged();
    });

    this._eventTimerAttributeSearch.on('select_item', () => {
      this._onEventTimerAttributeChanged();
    });

    this._eventMessageExp.on('expand', () => {
      this._onEventMessageExpand();
    });

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onEventMessageWebServiceChanged();
      },
      this._eventMsgWebServiceInput,
      'input',
    );

    this._eventMsgAttributeSearch.on('add_item', () => {
      this._onAddNewMessageAttribute();
    });

    this._eventMsgAttributeSearch.on('select_item', (item) => {
      this._onSelectToEditEventMsgAttributeSeacrh(item);
    });

    this._eventMsgAttributesList.on('delete_item', (item) => {
      this._onDeleteEventMsgAttributesList(item);
    });

    this._eventMsgAttributesList.on('delete_all_items', () => {
      this._onDeleteAllEventMsgAttributesList();
    });

    this._eventMsgAttributesList.on('add_item', () => {
      this._onAddEventMessageAttribute();
    });

    this._eventMsgAttributesList.on('selected_item', (item) => {
      this._onSelectEventMsgAttributesList(item);
    });

    this._eventMsgAttributesList.on('edit_item', (status) => {
      this._onEditEventMsgAttributesList(status);
    });

    this._eventMsgAttributesList.on('checked_item', (param) => {
      this._onCheckedEventMsgAttributesList(param);
    });

    this._eventMsgAttributesType.addEventListener('input', () => {
      this._onEventMsgAttributesTypeChanged();
    });

    this._eventMsgCatchThrow.addEventListener('input', () => {
      this._onEventMsgCatchThrowChanged();
    });

    this._eventMsgWebServicesList.on('add_item', () => {
      this._onAddEventMsgWebServicesList();
    });

    this._eventMsgWebServicesList.on('selected_item', (item) => {
      this._onSelectMsgWebServicesList(item);
    });

    this._eventMsgWebServicesList.on('edit_item', (status) => {
      this._onEditMsgWebServicesList(status);
    });

    this._eventMsgWebServicesList.on('delete_item', (item) => {
      this._onDeleteEventMsgWebServicesList(item);
    });

    this._eventMsgWebServicesList.on('delete_all_items', () => {
      this._onDeleteAllEventMsgWebServices();
    });

    this._eventMsgWebServiceSearch.on('select_item', (item) => {
      this._onSelectToEditEventMsgWebServiceSearch(item);
    });

    this._eventMsgWSBindingsList.on('selected_item', (item) => {
      this._onSelectEventMsgWSBindings(item);
    });

    this._eventMsgBindingType.addEventListener('input', () => {
      this._onEventMsgWSBindingTypeChanged();
    });

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onEventMsgWSBindingValueChanged();
      },
      this._eventMsgBindingValue,
      'input',
    );

    this.bindingAttrTypes.forEach((radio) => {
      radio.addEventListener('click', () => {
        this._onEventMsgWSBindingAttributeChanged('attrType');
      });
    });

    this._eventMsgWSAttributeSearch.on('select_item', (item) => {
      this._onEventMsgWSBindingAttributeChanged('attribute', item);
    });
  }

  _setDetailTypeSelect(el) {
    let optionsValues = ['None', 'Message', 'Signal', 'Timer']; // Is very Important this order !!!
    this._cleanTypeSelect();
    if (el.elementType === Config.END_EVENT) {
      optionsValues.pop();
      optionsValues = optionsValues.concat(['Terminate', 'Error']);
    }
    if (el.elementType === Config.INTERMEDIATE_EVENT && el.isAttached) {
      optionsValues.shift();
      optionsValues = [...optionsValues, 'Multiple', 'Error'];
      const taskContainer = this.editor.elements.find((element) => {
        return element.elementType === Config.TASK && element.id === el.target;
      });
      if (taskContainer) {
        const { taskType } = taskContainer;
        if (taskType === 'receive' || taskType === 'send' || taskType === 'service' || taskType === 'script') {
          optionsValues.shift();
        }
      }
    }
    optionsValues.forEach((option) => this._createOptionElement(option));
  }

  _getTypeToShow(el) {
    const { elementType } = el;
    const splitType = elementType.split('Event');
    const [type] = splitType;
    return `${type.toUpperCase()} ${i18next.t('eventPropsPanel.txtProDesEventPanelTypeToShow')}`;
  }

  show(el) {
    this._setDetailTypeSelect(el);
    this.eventNameType = this._getTypeToShow(el);
    this.detailTypeSelect = el.eventType;
    this.nameInput = el.eventName;
    this.documentationInput = el.documentation;
    this._showTimerComponentsAccordingToType(el);
    this._showMessageComponentsAccordingToType(el);
    this._changeVisibility(this._panel, true);
  }

  _getDateObject(date) {
    const startDate = {
      date: {
        year: date.getFullYear(),
        month: date.getMonth() + 1,
        day: date.getDate(),
      },
      time: { hour: date.getHours(), minutes: date.getMinutes() },
    };
    return startDate;
  }

  _showTimerComponentsAccordingToType(el) {
    const isIntermediate = el.elementType !== Config.INTERMEDIATE_EVENT;
    const timerValue = this._getTimerShowValue(el);

    this._changeVisibility(this._eventTimerExp.wrapper, el.eventType === 'timer' || el.eventType === 'multiple');
    this._changeVisibility(this._eventTimerEndDate.wrapper, isIntermediate);
    this._eventTimerExp.showValue(timerValue);

    if (this._eventTimerExp.isExpand && el.eventType !== 'timer') {
      this._eventTimerExp.expand();
    }

    this._cleanTimerDateComponents();
    this._cleanTimerFrequencyComponents();
    this._cleanTimerAttributeComponents();

    if (el.eventType === 'timer' && el.timer) {
      const date = el.dateTimer || {};
      if (date) this.eventTimerType = 'date';
      if (date.start) {
        this.eventTimerStartDate = this._getDateObject(date.start);
      }
      if (date.end) this.eventTimerEndDate = this._getDateObject(date.end);

      const frequency = el.frequencyTimer;
      if (frequency) {
        this.eventTimerUnit = frequency.unit;
        this.eventTimerValue = frequency.value;
        this.eventTimerType = 'frequency';
      }

      const attribute = el.attributeTimer;
      if (attribute) {
        this.eventTimerType = 'attribute';
        this.eventTimerAttrType = attribute.type;
        const { id, name } = attribute;
        this.eventTimerAttribute = { id, name };
      }
      this._onTimerTypeChanged();
    }
    this._changeVisibility(this._eventMessageExp.wrapper, el.eventType === 'message' || el.eventType === 'multiple');
  }

  _showMessageComponentsAccordingToType(el) {
    const { elementType, eventType } = el;
    const isStart = elementType === Config.START_EVENT;
    const isItermediate = elementType === Config.INTERMEDIATE_EVENT;
    const isEnd = elementType === Config.END_EVENT;
    const isMessage = eventType === 'message' || eventType === 'multiple';
    const isThrow = el.catchThrowMessage === 'throw';
    const isIntermediateThrow = isItermediate && isThrow;
    const showValue = this._getMessageShowValue(el);

    this._toggleEventMessageComponents(el);
    if (!isMessage && this._eventMessageExp.isExpand) {
      this._eventMessageExp.expand();
    }
    this._eventMessageExp.showValue(showValue);

    if (isMessage && (isStart || (isItermediate && !isIntermediateThrow))) {
      this._fillStartOrItermEventMessageComponents(el, isItermediate);
    }
    if (isMessage && (isEnd || isIntermediateThrow)) {
      this._fillEndEventMessageComponents(el);
    }
  }

  _cleanStartItermEventMessageCmp() {
    this.eventMsgCatchThrow = 'catch';
    this.eventMsgWebServiceInput = '';
    this.eventMsgAttributeType = 'entity';
    this._eventMsgAttributeSearch.selectedItem = undefined;
    this._eventMsgAttributesList.arrayListData = [];
  }

  _cleanEndEventMessageCmp() {
    this._eventMsgWebServiceSearch.selectedItem = undefined;
    this._eventMsgWebServicesList.arrayListData = [];
    this._eventMsgWSBindingsList.arrayListData = [];
    this.eventMsgBindingType = 'none';
    this._eventMsgBindingType.disabled = true;
    this.eventMsgBindingValue = '';
    this._eventMsgWSAttributeSearch.selectedItem = undefined;
    this.bindingAttrTypeToCheck = 'entity';
    this.wsSelected = null;
  }

  _fillStartOrItermEventMessageComponents(el, isIntermediate) {
    this._cleanStartItermEventMessageCmp();
    const attrType = this.eventMsgAttributeType;
    if (isIntermediate) {
      this.eventMsgCatchThrow = el.catchThrowMessage || 'catch';
    }
    this.eventMsgWebServiceInput = el.webServiceMessage || '';
    if (this._eventMsgAttributesList.editStatus) {
      this._eventMsgAttributesList.editStatus = false;
    }
    this._eventMsgAttributesList.arrayListData = attrType === 'process' ? el.processAttributesMessage || [] : el.entityAttributesMessage || [];
  }

  _fillEndEventMessageComponents(el) {
    this._cleanEndEventMessageCmp();
    const webServices = this._getFilteredWebServicesArray(el.webServices || []);
    this._eventMsgWebServicesList.arrayListData = webServices;
    this._eventMsgWSBindingsList.arrayListData = [];
    this.eventMsgCatchThrow = el.catchThrowMessage || 'throw';
    if (this._eventMsgWebServicesList.editStatus) {
      this._eventMsgWebServicesList.editStatus = false;
    }
  }

  _getFilteredWebServicesArray(webServices) {
    const ignore = 'BPMNAutoComplete';
    let filtered = [];
    if (this.element.webServices) {
      filtered = webServices.filter((ws) => ws.busClaName !== ignore);
    }
    return filtered;
  }

  async _getBindings(classId) {
    let bindings = [];
    let url = Config.EVT_MSG_WS_BINDINGS_URL;
    if (Config.ENV !== 'Development') url = `${url}${classId}`;
    try {
      const response = await axios.get(url);
      if (response && response.data.toUpperCase().indexOf('HTML') > -1) {
        this.showInvalidSession();
      }
      const convert = new Converter();
      bindings = convert.convertToJson(response.data);
    } catch (error) {
      throw Error(`${i18next.t('eventPropsPanel.txtProDesEventPanelErrorObtainParameter')} \n${error}`);
    }
    const parameters = this._customBindingsParameters(bindings);
    return mapInOutParameters(parameters);
  }

  _getTimerShowValue(el) {
    const valueToShow = [];
    if (el.timer) {
      if (el.dateTimer) valueToShow.push('DATE');
      if (el.frequencyTimer) valueToShow.push('FREQUENCY');
      if (el.attributeTimer) valueToShow.push('ATTRIBUTE');
    }
    return valueToShow;
  }

  _updateMessageExpValue(el) {
    const value = this._getMessageShowValue(el);
    this._eventMessageExp.showValue(value);
  }

  _customBindingsParameters(parameters = []) {
    if (this.wsSelected) {
      const { bindings } = this.wsSelected;
      const indexBindings = indexByProperty(bindings || [], 'busClaParId');
      return parameters.map((parameter) => {
        const existBinding = indexBindings[parameter.id];
        return {
          ...parameter,
          binding: existBinding ? this._getEspecificationBinding(existBinding) : 'None',
        };
      });
    }
    return parameters;
  }

  _getEspecificationBinding(binding) {
    const isValue = binding.busClaParBndType === 'V' && binding.busClaParBndValue !== '';
    const isProcess = binding.busClaParBndType === 'P';
    const isEntity = binding.busClaParBndType === 'E';
    const isAttribute = (isProcess || isEntity) && binding.attId !== -1 && binding.attId;
    if (isValue) return 'Value';
    if (isAttribute) return 'Attribute';
    return 'None';
  }

  _getMessageShowValue(el) {
    const valueToShow = [];
    const { elementType } = el;
    const isStart = elementType === Config.START_EVENT;
    const isIntermediate = elementType === Config.INTERMEDIATE_EVENT;
    const isEnd = elementType === Config.END_EVENT;
    const isThrow = el.catchThrowMessage === 'throw';
    const isIntermediateThrow = isIntermediate && isThrow;
    if (el.message && (isStart || (isIntermediate && !isIntermediateThrow)) && !isEnd) {
      valueToShow.push(el.webServiceMessage || '');
    }
    if (el.message && (isEnd || isIntermediateThrow) && !isStart && el.webServices) {
      const ignore = 'BPMNAutoComplete';
      el.webServices.forEach((value) => {
        if (value.busClaName !== ignore) valueToShow.push(value.busClaName);
      });
    }
    return valueToShow;
  }

  _getTimerGroup(name) {
    const timerGroup = this._eventTimerExp.expandZone.querySelector(`#wrapper_timer_${name}`);
    return timerGroup;
  }

  _getActiveTimerGroup() {
    const activeGroup = this._eventTimerExp.expandZone.querySelector('.timer_group[data-visible=true]');
    return activeGroup;
  }

  _toggleEventMessageComponents(el) {
    this._cleanEndEventMessageCmp();
    this._cleanAndHideBindingsComponents();
    const isStart = el.elementType === Config.START_EVENT;
    const isIntermediate = el.elementType === Config.INTERMEDIATE_EVENT;
    const isEnd = el.elementType === Config.END_EVENT;
    const catchThrow = el.catchThrowMessage || 'catch';
    const isCatch = isStart || (isIntermediate && catchThrow === 'catch');
    const { eventType, isAttached } = el;
    const isThrow = isEnd || (isIntermediate && catchThrow === 'throw');
    const hideCatchThrow = isIntermediate && eventType !== 'multiple' && eventType === 'message' && !isAttached;
    this._changeVisibility(this.wrapMsgCatchThrow, hideCatchThrow);
    this._toggleCatchEventMessageComponents(isCatch);
    this._toggleThrowEventMessageComponents(isThrow);
  }

  _toggleCatchEventMessageComponents(isCatch) {
    this._changeVisibility(this.wrapWebserviceInput, isCatch);
    this._changeVisibility(this.wrapAttrsType, isCatch);
    this._changeVisibility(this._eventMsgAttributeSearch.wrapper, isCatch);
    this._changeVisibility(this._eventMsgAttributesList.wrapper, isCatch);
  }

  _toggleThrowEventMessageComponents(isThrow) {
    this._changeVisibility(this._eventMsgWebServiceSearch.wrapper, isThrow);
    this._changeVisibility(this._eventMsgWebServicesList.wrapper, isThrow);
    this._changeVisibility(this._eventMsgWSBindingsList.wrapper, isThrow);
    this._changeVisibility(this.wrapBindingType, isThrow);
    this._changeVisibility(this.wrapBindingValue, false);
    this._changeVisibility(this.wrapBindingAttrType, false);
    this._changeVisibility(this._eventMsgWSAttributeSearch.wrapper, false);
  }

  _isActiveTimerGroup(name) {
    const active = this._getActiveTimerGroup();
    const idActive = active.id;
    const nameActive = idActive.split('wrapper_timer_')[1];
    return nameActive === name;
  }

  _onTimerTypeChanged() {
    const timerType = this.eventTimerType;
    const active = this._getActiveTimerGroup();
    const timerGroup = this._getTimerGroup(timerType);

    if (this.element) {
      const actualName = active.getAttribute('data-timer');
      if (actualName !== timerType) {
        this._onDeleteTimerProperty(actualName);

        if (!this._isActiveTimerGroup(timerType)) {
          this._changeVisibility(active, false);
          this._changeVisibility(timerGroup, true);
        }
      }
    }
  }

  _getFilteredAttributesByType(type, attributes) {
    const filterBy = type.toUpperCase().charAt(0);
    const filtered = attributes.filter((attr) => attr.type === filterBy);
    return filtered;
  }

  _createMessageAttributesArrayList() {
    const arrayList = {
      header: [
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgAttributeHeaderName'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgAttributeHeaderMulti'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgAttributeHeaderUk'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgAttributeHeaderEdit'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgAttributeHeaderDeleteAll'),
      ],
      deleteAction: true,
      deleteActionAll: true,
      editAction: true,
      data: [],
      notShow: ['id', 'type'],
      actions: [
        { key: 'multivalued', type: 'checkbox' },
        { key: 'uk', type: 'checkbox' },
      ],
    };
    return arrayList;
  }

  _createMessageWebServicesArrayList() {
    const arrayList = {
      header: [
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgWsHeaderName'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgWsHeaderEdit'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgWsHeaderDeleteAll'),
      ],
      deleteAction: true,
      deleteActionAll: true,
      editAction: true,
      data: [],
      notShow: ['id', 'eventId', 'busClaId', 'name', 'WS', 'order', 'bindings', 'haveCond', 'condition'],
      actions: [],
    };
    return arrayList;
  }

  _createMessageBindingsArrayList() {
    const arrayList = {
      header: [
        i18next.t(
          'eventPropsPanel.txtProDesEventPanelMsgBindingHeaderParameter',
        ),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgBindingHeaderType'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgBindingHeaderInOut'),
        i18next.t('eventPropsPanel.txtProDesEventPanelMsgBindingHeaderBinding'),
      ],
      deleteAction: false,
      deleteActionAll: false,
      editAction: false,
      data: [],
      notShow: ['id'],
      actions: [],
    };
    return arrayList;
  }

  _createOptionElement(value) {
    const option = document.createElement('option');
    option.setAttribute('value', value.toLowerCase());
    option.innerText = value;
    if (this._detailTypeSelect) {
      this._detailTypeSelect.appendChild(option);
    }
  }

  _onDetailTypeSelectChanged() {
    const type = this.detailTypeSelect;
    if (this.element) {
      const actual = this.element.eventType;
      if (actual !== type) {
        if (actual === 'message' && type === 'none') {
          this.element.webServices = this.element.webServices.filter((ws) => ws.busClaName === 'BPMNAutoComplete');
        } else {
          this.element.webServices = [];
        }
        this.element.deletePropertyByName((type !== 'timer' || type !== 'multiple') && 'timer');
        this.element.deletePropertyByName((type !== 'message' || type !== 'multiple') && 'message');
        this.element.type = type;
        this._showTimerComponentsAccordingToType(this.element);
        this._showMessageComponentsAccordingToType(this.element);
      }
    }
  }

  _onNameInputChanged() {
    this.element.name = this.nameInput;
  }

  _cleanTypeSelect() {
    const options = this._detailTypeSelect.querySelectorAll('option');
    if (options.length !== 0) {
      options.forEach((option) => option.remove());
    }
  }

  _cleanTimerDateComponents() {
    this._eventTimerStartDate.clean();
    this._eventTimerEndDate.clean();
  }

  _cleanTimerFrequencyComponents() {
    this.eventTimerUnit = 'Minutes';
    this.eventTimerValue = '';
  }

  _cleanTimerAttributeComponents() {
    this.eventTimerAttrType = 'entity';
    this.eventTimerAttribute = undefined;
  }

  _onDeleteTimerProperty(timerType) {
    if (this.element) {
      const timer = this.element.timer || {};
      if (timer[timerType]) this.element.deleteTimerProperty(timerType);
      switch (timerType.toUpperCase()) {
        case 'DATE':
          this._cleanTimerDateComponents();
          break;
        case 'FREQUENCY':
          this._cleanTimerFrequencyComponents();
          break;
        case 'ATTRIBUTE':
          this._cleanTimerAttributeComponents();
          break;
        default:
          break;
      }
      const showValue = this._getTimerShowValue(this.element);
      this._eventTimerExp.showValue(showValue);
    }
  }

  _onEventTimerDateChanged(date, type) {
    if (date && this.element) {
      this.element.setDateTimerByType(date, type);
      const showData = this._getTimerShowValue(this.element);
      this._eventTimerExp.showValue(showData);
    }
  }

  _onValidateFrequencyValue() {
    const value = this.eventTimerValue;
    if (value < 1 || isNaN(value)) {
      this.eventTimerValue = 1;
    }
    if (value === '') this.eventTimerValue = '';
  }

  _onEventTimerFrequencyChanged() {
    const frequency = {
      unit: this.eventTimerUnit,
      value: this.eventTimerValue,
    };
    const actualFreq = this.element.frequencyTimer || {};
    if (actualFreq.unit !== frequency.unit) {
      frequency.value = '';
      this.element.frequencyTimer = frequency;
      this.eventTimerValue = frequency.value;
    }
    if (this.element && this.eventTimerValue !== '') {
      const freqTimer = this.element.frequencyTimer || {};
      if (frequency.unit !== freqTimer.value || frequency.value !== freqTimer.value) {
        this.element.frequencyTimer = frequency;
        const showData = this._getTimerShowValue(this.element);
        this._eventTimerExp.showValue(showData);
        this.eventTimerValue = frequency.value;
      }
    }
  }

  _onEventTimerAttributeChanged() {
    const selected = this._eventTimerAttributeSearch.selectedItem || {};
    let value = { type: this.eventTimerAttrType };
    if (this.element) {
      const { id, name } = selected;
      value = { ...value, id: parseInt(id || 0, 10), name: name || '' };
      const actualValue = this.element.attributeTimer || {};
      if (value.type !== actualValue.type || value.id !== actualValue.id || value.name !== actualValue.name) {
        if (value.type !== actualValue.type) {
          this.eventTimerAttribute = undefined;
          value = { ...value, id: 0, name: '' };
        }
        this.element.attributeTimer = value;

        const showData = this._getTimerShowValue(this.element);
        this._eventTimerExp.showValue(showData);
      }
    }
  }

  _onEventMessageExpand() {
    if (!this.element.message) {
      this._cleanStartItermEventMessageCmp();
      this._cleanEndEventMessageCmp();
    }
  }

  _onEventMessageWebServiceChanged() {
    if (this.element) {
      const actualWS = this.element.webServiceMessage;
      const webService = this.eventMsgWebServiceInput;
      if (actualWS !== webService) {
        this.element.webServiceMessage = webService;

        const showValue = this._getMessageShowValue(this.element);
        this._eventMessageExp.showValue(showValue);
      }
    }
  }

  async _onAddNewMessageAttribute() {
    const attribute = this._eventMsgAttributeSearch.selectedItem;
    const attrName = this.eventMsgAttributeSearch;
    if (!attribute && Config.ENV === 'Production' && this.element) {
      const nameExp = new RegExp(/^[a-zA-Z][\w]+[a-zA-Z0-9]$/gi);
      if (nameExp.test(attrName)) {
        const url = `${Config.EVT_MSG_NEW_ATTRS_URL}${encodeURI(attrName)}`;
        try {
          const response = await axios.post(url);

          const converter = new Converter();
          if (response && response) {
            if (response.data.toUpperCase().indexOf('HTML') > -1) {
              this.showInvalidSession();
            }
            const [newAttr] = converter.convertToJson(response.data);
            this._eventMsgAttributeSearch.selectedItem = newAttr;
            this._eventMsgAttributeSearch.reloadData();
            this._eventMsgAttributeSearch.toggleDisabledButton(true);

            // message
            const options = { ...this.OPTIONS_TOAST };
            options.type = 'success';
            options.title = i18next.t('eventPropsPanel.txtProDesEventPanelToastTitleSuccess');
            options.message = i18next.t('eventPropsPanel.txtProDesEventPanelToastTitleSuccessMessage');
            this.toastMessage.showMessage(options);
          }
        } catch (error) {
          throw Error(`${i18next.t('eventPropsPanel.txtProDesEventPanelErrorInsertAttribute')}: \n ${error}`);
        }
      } else {
        const options = { ...this.OPTIONS_TOAST };
        options.type = 'danger';
        options.title = i18next.t('eventPropsPanel.txtProDesEventPanelToastTitleError');
        options.message = `${i18next.t(
          'eventPropsPanel.txtProDesEventPanelErrorMessageAttrName',
        )} <b>"${attrName}"</b> ${i18next.t('eventPropsPanel.txtProDesEventPanelErrorMessageNotValid')}`;
        this.toastMessage.showMessage(options);
      }
    }
  }

  _onAddEventMessageAttribute() {
    const attribute = this._eventMsgAttributeSearch.selectedItem;
    const attrType = this.eventMsgAttributeType;
    if (attribute && this.element) {
      const newAttribute = {
        id: parseInt(attribute.id, 10),
        name: attribute.name,
        multivalued: false,
        uk: false,
        type: attrType.toUpperCase().charAt(0),
      };
      const arrayType = attrType === 'process' ? 'attrProc' : 'attrEnt';
      const attrFind = this.element.existMessageAttribute(attribute.id, arrayType);
      if (!attrFind) {
        this.element.addMessageAttribute(newAttribute, attrType);
        const showValue = this._getMessageShowValue(this.element);
        const attributes = attrType === 'process' ? this.element.processAttributesMessage : this.element.entityAttributesMessage;
        this._eventMsgAttributesList.arrayList.data = attributes;
        this._eventMsgAttributesList.refreshList();
        this._eventMessageExp.showValue(showValue);
        // this._eventMsgAttributeSearch.selectedItem = undefined;
      }
      this._eventMsgAttributesList._scrollToRow(newAttribute, () => this._onSelectEventMsgAttributesList(newAttribute));
    }
  }

  _onEventMsgAttributesTypeChanged() {
    const attrType = this.eventMsgAttributeType;
    if (this.element) {
      const attributes = attrType === 'process' ? this.element.processAttributesMessage : this.element.entityAttributesMessage;
      this._eventMsgAttributesList.arrayList.data = attributes || [];
      this._eventMsgAttributesList.refreshList();
    }
  }

  _onEventMsgCatchThrowChanged() {
    const catchThrow = this.eventMsgCatchThrow;
    const el = this.element || {};
    if (el.catchThrowMessage !== catchThrow) {
      el.catchThrowMessage = catchThrow;
      this._toggleEventMessageComponents(el);
      this._updateMessageExpValue(el);
    }
  }

  _onSelectEventMsgAttributesList(item) {
    if (item) {
      const options = { ...this.OPTIONS_TOAST };
      options.message = `
        <b>${i18next.t('eventPropsPanel.txtProDesEventPanelToastMessageAttrName')} </b>${item.name}<br>
        <b>${i18next.t('eventPropsPanel.txtProDesEventPanelToastMessageMultivalued')} </b>${
  item.multivalued ? 'True' : 'False'
}<br>
        <b>${i18next.t('eventPropsPanel.txtProDesEventPanelToastMessageUniqueKey')} </b>${item.uk ? 'True' : 'False'}
      `;
      this.toastMessage.showMessage(options);
    }
  }

  _onEditEventMsgAttributesList(status) {
    const editAttr = this._eventMsgAttributesList.rowEditableSelected;
    const selected = this._eventMsgAttributeSearch.selectedItem;
    const editAlreadyOnlist = selected ? this._onCheckedEventMsgAttributesList(selected) : false;
    const condition = status && editAttr && !editAlreadyOnlist;
    this._eventMsgAttributeSearch.selectedItem = condition ? { id: editAttr.id, name: editAttr.name } : undefined;
    this._eventMsgWebServiceInput.disabled = condition;
    this._eventMsgAttributesType.disabled = condition;
  }

  _onCheckedEventMsgAttributesList(param) {
    const { value, position, keyName } = param;
    const { data } = this._eventMsgAttributesList.arrayList;
    const oppositeProp = keyName === 'multivalued' ? 'uk' : 'multivalued';
    if (value) data[position][oppositeProp] = !value;
    this._eventMsgAttributesList.refreshList();
  }

  _onCheckEditEventMsgAttributeOnList(param) {
    // CHECK IF 'param' ALREADY ASSIGNED ON LIST
    return this._eventMsgAttributesList._arrayList.data.find((item) => {
      return Number(param.id) === Number(item.id);
    });
  }

  _onSelectToEditEventMsgAttributeSeacrh(attribute) {
    const attrType = this.eventMsgAttributeType;
    const selected = this._eventMsgAttributesList.rowEditableSelected;
    const alreadyOnList = this._onCheckEditEventMsgAttributeOnList(attribute);
    const { editStatus } = this._eventMsgAttributesList;
    if (this.element && attribute && selected && editStatus && !alreadyOnList) {
      const valueAttr = {
        id: parseInt(attribute.id, 10),
        name: attribute.name,
        multivalued: false,
        uk: false,
        type: attrType.toUpperCase().charAt(0),
      };
      this.element.editMessageAttribute(selected.id, attrType, valueAttr);
      const attributes = attrType === 'process' ? this.element.processAttributesMessage : this.element.entityAttributesMessage;
      this._eventMsgAttributesList.rowEditableSelected = valueAttr;
      this._eventMsgAttributesList.arrayList.data = attributes;
      this._eventMsgAttributesList.refreshList();
    }
    const condition = !attribute && this.eventMsgAttributeSearch !== '' && !editStatus;
    this._eventMsgAttributeSearch.toggleDisabledButton(!condition);
  }

  _onDeleteEventMsgAttributesList(attribute) {
    const attrType = this.eventMsgAttributeType;
    if (this.element && attribute) {
      this.element.deleteMessageAttribute(attribute.id, attrType);

      const attributes = attrType === 'process' ? this.element.processAttributesMessage : this.element.entityAttributesMessage;
      this._eventMsgAttributesList.arrayList.data = attributes;
      this._eventMsgAttributesList.refreshList();
    }
  }

  _onDeleteAllEventMsgAttributesList() {
    const attrType = this.eventMsgAttributeType;
    if (this.element) {
      this.element.deleteAllMessageAttributes(attrType);
    }
  }

  _refreshWebServicesList(el) {
    this._eventMsgWebServicesList.arrayList.data = this._getFilteredWebServicesArray(el.webServices);
    this._eventMsgWebServicesList.refreshList();
    const showData = this._getMessageShowValue(el);
    this._eventMessageExp.showValue(showData);
  }

  _onAddEventMsgWebServicesList() {
    const wsSelected = this._eventMsgWebServiceSearch.selectedItem;
    if (wsSelected && this.element) {
      const ONCOMPLETE = {
        eventId: 10,
        name: 'ONCOMPLETE',
      };
      const wsFind = this.element.existWebService(wsSelected.id);
      if (!wsFind) {
        const wsToAdd = {
          busClaId: parseInt(wsSelected.id, 10),
          busClaName: wsSelected.name,
          ...ONCOMPLETE,
        };
        const defaultWebService = {
          WS: true,
          busClaId: 102,
          busClaName: 'BPMNAutoComplete',
          eventId: 11,
          id: 1,
          name: 'ONREADY',
          order: 0,
        };
        if (this.element.webServices && this.element.webServices.length === 0 && !this.element.webServices.includes(defaultWebService)) {
          this.element.webServices.push(defaultWebService);
          this._refreshWebServicesList(this.element);
        }
        this.element.addWebService(wsToAdd);
        this._refreshWebServicesList(this.element);
        this._eventMsgWebServicesList._scrollToRow({ id: this.element.webServices.length, name: wsToAdd.busClaName }, () => { this._onSelectMsgWebServicesList(wsToAdd); });
        this._eventMsgWebServiceSearch.selectedItem = undefined;
      }
    }
  }

  async _onSelectMsgWebServicesList(webService) {
    if (webService && this.element) {
      if (webService !== this.wsSelected) {
        this.wsSelected = this._eventMsgWebServicesList.rowSelected;
        const bindings = await this._getBindings(webService.busClaId);
        this._eventMsgWSBindingsList.arrayListData = bindings;
        this._cleanAndHideBindingsComponents();
      }

      const options = {
        ...this.OPTIONS_TOAST,
        message: `<b>${i18next.t('eventPropsPanel.txtProDesEventPanelToastMessageName')}</b> ${webService.busClaName}`,
      };
      this.toastMessage.showMessage(options);
    }
  }

  _cleanAndHideBindingsComponents() {
    this.eventMsgBindingType = 'none';
    this._eventMsgBindingType.disabled = true;
    this.eventMsgBindingValue = '';
    this.bindingAttrTypeToCheck = 'entity';
    this._eventMsgWSAttributeSearch.selectedItem = undefined;
    this._changeVisibility(this.wrapBindingValue, false);
    this._changeVisibility(this.wrapBindingAttrType, false);
    this._changeVisibility(this._eventMsgWSAttributeSearch.wrapper, false);
  }

  async _onEditMsgWebServicesList(status) {
    const condition = this.element && status;
    const isVisibleAttr = this._elementVisibility(this._eventMsgWSAttributeSearch.wrapper);
    const isVisibleAttrType = this._elementVisibility(this.wrapBindingAttrType);
    const isVisibleValue = this._elementVisibility(this.wrapBindingValue);
    // const wsSelected = this._eventMsgWebServicesList.rowSelected;
    const wsEdit = this._eventMsgWebServicesList.rowEditableSelected;
    if (condition && (isVisibleAttr || isVisibleAttrType || isVisibleValue)) {
      this._cleanAndHideBindingsComponents();
    }
    if (status) {
      this._eventMsgBindingType.disabled = true;
      this._eventMsgWSBindingsList.unSelectAll();
      this._eventMsgWSBindingsList.disabled = true;
    } else {
      this._eventMsgWSBindingsList.disabled = false;
    }
    this.eventMsgBindingType = 'none';
    this._eventMsgWebServiceSearch.selectedItem = wsEdit ? { id: wsEdit.busClaId, name: wsEdit.busClaName } : undefined;
  }

  async _onSelectToEditEventMsgWebServiceSearch(webService) {
    const wsToEdit = this._eventMsgWebServicesList.rowEditableSelected;
    const { editStatus } = this._eventMsgWebServicesList;
    if (webService && this.element && editStatus && wsToEdit) {
      const ws = this.element.editWebService(wsToEdit.id, webService);
      this._eventMsgWebServicesList.rowEditableSelected = ws;
      const bindings = await this._getBindings(webService.id);
      this._eventMsgWSBindingsList.arrayListData = bindings;
      this._eventMsgWSBindingsList.refreshList();
      this._refreshWebServicesList(this.element);
    }
  }

  _onDeleteEventMsgWebServicesList(webService) {
    if (webService && this.element) {
      this.element.deleteWebService(webService.id);
      const defaultWebService = {
        WS: true,
        busClaId: 102,
        busClaName: 'BPMNAutoComplete',
        eventId: 11,
        id: 1,
        name: 'ONREADY',
        order: 0,
      };
      if (this.element.webServices.length === 0 && !this.element.webServices.includes(defaultWebService)) {
        this.element.webServices.push(defaultWebService);
        this._refreshWebServicesList(this.element);
      }
      this._eventMsgWSBindingsList.arrayListData = [];
      this._eventMsgWSBindingsList.refreshList();
    }
  }

  _onDeleteAllEventMsgWebServices() {
    if (this.element) {
      this.element.webServices = [];
      this._refreshWebServicesList(this.element);
    }
  }

  _onSelectEventMsgWSBindings(parameter) {
    if (parameter && this.wsSelected && this.element) {
      this._cleanAndHideBindingsComponents();
      // const editStatus = this._eventMsgWebServicesList._editStatus;
      this._eventMsgBindingType.disabled = false;
      const binding = this.element.existWSBindingParameter(this.wsSelected.id, parameter.id);
      if (binding) {
        const type = binding.busClaParBndType;
        if (type === 'V' && parameter.binding !== 'None') {
          this.eventMsgBindingType = 'value';
          this._changeVisibility(this.wrapBindingValue, true);
          this.setFieldValueEachType();
          this.eventMsgBindingValue = binding.busClaParBndValue || '';
        }
        if ((type === 'E' || type === 'P') && parameter.binding !== 'None') {
          this.eventMsgBindingType = 'attribute';
          this._changeVisibility(this.wrapBindingAttrType, true);
          this._changeVisibility(this._eventMsgWSAttributeSearch.wrapper, true);
          this.bindingAttrTypeToCheck = type === 'E' ? 'entity' : 'process';
          this._eventMsgWSAttributeSearch.selectedItem = {
            id: binding.attId,
            name: binding.attTooltip || binding.attName,
          };
          this.setUrlAttributeEachType();
        }
      } else {
        if (parameter.binding === 'None' && Config.ENV === 'Production') {
          const parType = parameter.type;
          this._eventMsgWSAttributeSearch.url = `${this._eventMsgWSAttributeSearch.baseUrl}&attType=${parType}`;
        }
        this._cleanAndHideBindingsComponents();
        this._eventMsgBindingType.disabled = false;
      }
      if (
        Config.ENV === 'Production'
        && (this.eventMsgBindingType === 'attribute' || parameter.binding === undefined)
      ) {
        const parType = parameter.type;
        this._eventMsgWSAttributeSearch.url = `${this._eventMsgWSAttributeSearch.baseUrl}&attType=${parType}`;
      }

      let type;
      switch (parameter.type) {
        case 'S':
          type = 'String';
          break;
        case 'D':
          type = 'Date';
          break;
        default:
          type = 'Number';
          break;
      }
      const message = `
      <b>${i18next.t('eventPropsPanel.txtProDesEventPanelToastMessageParameter')}</b> ${parameter.param}<br>
      <b>${i18next.t('eventPropsPanel.txtProDesEventPanelToastMessageType')}</b> ${type}<br>
      <b>${i18next.t('eventPropsPanel.txtProDesEventPanelToastMessageInOut')}</b> ${parameter.inout}<br>
    `;
      this.toastMessage.showMessage({ ...this.OPTIONS_TOAST, message });
    }
  }

  setUrlAttributeEachType(flag = true) {
    const bindingSelected = this._eventMsgWSBindingsList.rowSelected;
    if (bindingSelected && Config.ENV === 'Production' && flag) {
      const { type } = bindingSelected;
      this._eventMsgWSAttributeSearch.url = `${Config.DEFAULT_ATTRS_URL}&attType=${type}&name=`;
    }
  }

  changeWhenTypeIsValue(webService, binding) {
    this.element.editBindingParameter(webService.id, binding.busClaParId, {
      busClaParBndType: 'V',
      busClaParBndValue: '',
    });
    this.element.deletePropertyOfBindingParameter(webService.id, binding.busClaParId, [
      'attId',
      'attName',
      'attTooltip',
    ]);
  }


  changeWhenTypeIsAttribute(webService, binding) {
    this.element.editBindingParameter(webService.id, binding.busClaParId, {
      busClaParBndType: 'E',
    });
    this.element.deletePropertyOfBindingParameter(webService.id, binding.busClaParId, ['busClaParBndValue']);
  }

  _updateBindingsParametersList(webService) {
    this._getBindings(webService.busClaId).then((bindings) => {
      this._eventMsgWSBindingsList.arrayList.data = bindings;
      this._eventMsgWSBindingsList.refreshList();
    });
  }

  _onEventMsgWSBindingTypeChanged() {
    const bindingParam = this._eventMsgWSBindingsList.rowSelected;
    const bindingType = this.eventMsgBindingType;
    let type;
    if (bindingType === 'value') type = 'V';
    if (bindingType === 'attribute') type = 'E';

    if (this.wsSelected && bindingParam && this.element) {
      const binding = this.element.existWSBindingParameter(this.wsSelected.id, bindingParam.id);
      const isDistinct = binding ? binding.busClaParBndType !== type : false;
      if (type === 'V') {
        if (binding && isDistinct) {
          this.changeWhenTypeIsValue(this.wsSelected, binding);
        }
        this.setFieldValueEachType();
      }
      if (binding && type === 'P' && isDistinct) {
        this.changeWhenTypeIsAttribute(this.wsSelected, binding);
        this.setUrlAttributeEachType();
      }

      //!
      //   if (binding && !type && isDistinct && bindingParam.binding === 'None') {
      if (binding && !type) {
        this.element.deleteBindingParameter(this.wsSelected.id, binding.busClaParId);
        this._cleanAndHideBindingsComponents();
        this._eventMsgBindingType.disabled = false;
      }
      this._changeVisibility(this.wrapBindingValue, bindingType === 'value');
      this._changeVisibility(this.wrapBindingAttrType, bindingType === 'attribute');
      this._changeVisibility(this._eventMsgWSAttributeSearch.wrapper, bindingType === 'attribute');
      this._updateBindingsParametersList(this.wsSelected);
    }
  }

  getNewBindingToAdd(parameter, newProps, type) {
    const newBinding = {
      busClaParId: parseInt(parameter.id, 10),
      busClaParName: parameter.param,
      busClaParType: parameter.type,
      busClaParBndType: type,
      ...newProps,
    };
    return newBinding;
  }

  setFieldValueEachType(flag = true) {
    const bindingSelected = this._eventMsgWSBindingsList.rowSelected;
    if (bindingSelected && flag) {
      const { type } = bindingSelected;
      switch (type) {
        case 'N': {
          this._eventMsgBindingValue.setAttribute('type', 'number');
          break;
        }
        case 'D': {
          this._eventMsgBindingValue.setAttribute('type', 'date');
          break;
        }
        default: {
          this._eventMsgBindingValue.setAttribute('type', 'text');
          break;
        }
      }
      this._toggleNumberFieldAttributes(type === 'N');
    }
  }

  _toggleNumberFieldAttributes(add) {
    const hasMinAttribute = this._eventMsgBindingValue.hasAttribute('min');
    const hasStepAttribute = this._eventMsgBindingValue.hasAttribute('step');
    if (add && !hasMinAttribute && !hasStepAttribute) {
      this._eventMsgBindingValue.setAttribute('min', '0');
      this._eventMsgBindingValue.setAttribute('max', Number.MAX_SAFE_INTEGER);
      this._eventMsgBindingValue.setAttribute('step', '1');
    } else {
      this._eventMsgBindingValue.removeAttribute('min');
      this._eventMsgBindingValue.removeAttribute('step');
      this._eventMsgBindingValue.removeAttribute('max');
    }
  }

  _editOrAddBinding(binding, props, type) {
    if (binding) {
      this.element.editBindingParameter(this.wsSelected.id, binding.busClaParId, props);
    }
    const parameter = this._eventMsgWSBindingsList.rowSelected;
    if (!binding && parameter) {
      const newBinding = this.getNewBindingToAdd(parameter, props, type);
      this.element.addBindingParameter(this.wsSelected.id, newBinding);
    }
  }

  _onEventMsgWSBindingValueChanged() {
    const paramSelected = this._eventMsgWSBindingsList.rowSelected;
    const bindingValue = this.eventMsgBindingValue.trim();
    if (this.element && paramSelected && this.wsSelected) {
      const binding = this.element.existWSBindingParameter(this.wsSelected.id, paramSelected.id);
      if (bindingValue !== '') {
        const newProp = { busClaParBndValue: bindingValue };
        this._editOrAddBinding(binding, newProp, 'V');
        this._updateBindingsParametersList(this.wsSelected);
      }
      const param = binding || {};
      this._fillValidBindingValue(param.busClaParBndValue);
    }
  }

  _fillValidBindingValue(oldValue = '') {
    const { type } = this._eventMsgBindingValue;
    if (type === 'number' && this.eventMsgBindingValue === '') {
      this.eventMsgBindingValue = oldValue;
    }
  }

  _onEventMsgWSBindingAttributeChanged(type, attribute = undefined) {
    const paramSelected = this._eventMsgWSBindingsList.rowSelected;
    if (this.element && paramSelected && this.wsSelected) {
      const binding = this.element.existWSBindingParameter(this.wsSelected.id, paramSelected.id);
      let newProps;
      const attrType = this.bindingAttrTypeChecked;
      let isDistinct;
      if (type === 'attrType') {
        newProps = { busClaParBndType: attrType };
        isDistinct = binding ? binding.busClaParBndType !== attrType : true;
      }
      if (type === 'attribute' && attribute) {
        isDistinct = binding ? parseInt(binding.attId, 10) !== parseInt(attribute.id, 10) : true;
        newProps = {
          attId: parseInt(attribute.id, 10),
          attTooltip: attribute.name,
        };
        if (attribute.label) newProps.attName = attribute.label;
      }
      if (((type === 'attribute' && attribute) || type === 'attrType') && isDistinct) {
        this._editOrAddBinding(binding, newProps, attrType);
      }
      this._updateBindingsParametersList(this.wsSelected);
    }
  }
}
