/* eslint-disable no-undef */
/* eslint-disable no-unused-vars */

import './style.css';
import Config from 'config';
import MicroModal from 'micromodal';
import { library, dom } from '@fortawesome/fontawesome-svg-core';

import i18next from 'i18next';
import locI18next from 'loc-i18next';
import Backend from 'i18next-http-backend';

// Free Solid
import { faUndo } from '@fortawesome/free-solid-svg-icons/faUndo';
import { faRedo } from '@fortawesome/free-solid-svg-icons/faRedo';
import { faTh } from '@fortawesome/free-solid-svg-icons/faTh';
import { faVectorSquare } from '@fortawesome/free-solid-svg-icons/faVectorSquare';
import { faSearchPlus } from '@fortawesome/free-solid-svg-icons/faSearchPlus';
import { faSearchMinus } from '@fortawesome/free-solid-svg-icons/faSearchMinus';
import { faSearch } from '@fortawesome/free-solid-svg-icons/faSearch';
import { faPlus } from '@fortawesome/free-solid-svg-icons/faPlus';
import { faArrowDown } from '@fortawesome/free-solid-svg-icons/faArrowDown';
import { faArrowUp } from '@fortawesome/free-solid-svg-icons/faArrowUp';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons/faInfoCircle';
import { faTimes } from '@fortawesome/free-solid-svg-icons/faTimes';
import { faQuestion } from '@fortawesome/free-solid-svg-icons/faQuestion';
import { faPen } from '@fortawesome/free-solid-svg-icons/faPen';
import { faCheckCircle } from '@fortawesome/free-solid-svg-icons/faCheckCircle';
import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons/faExclamationTriangle';
import { faExclamationCircle } from '@fortawesome/free-solid-svg-icons/faExclamationCircle';
import { faTasks } from '@fortawesome/free-solid-svg-icons/faTasks';
import { faGripLinesVertical } from '@fortawesome/free-solid-svg-icons/faGripLinesVertical';
import { faSave } from '@fortawesome/free-solid-svg-icons/faSave';
import { faCrosshairs } from '@fortawesome/free-solid-svg-icons/faCrosshairs';

// Free Regular
import { faCheckSquare } from '@fortawesome/free-regular-svg-icons/faCheckSquare';
import { faSquare } from '@fortawesome/free-regular-svg-icons/faSquare';
import { faKeyboard } from '@fortawesome/free-regular-svg-icons/faKeyboard';
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons/faTrashAlt';
import { faClock } from '@fortawesome/free-regular-svg-icons/faClock';
import { faCalendarAlt } from '@fortawesome/free-regular-svg-icons/faCalendarAlt';
import { faHandPointer } from '@fortawesome/free-regular-svg-icons/faHandPointer';

import axios from 'axios';
import Editor from './js/editor';
import ButtonBar from './js/ui/controls/buttonBar';
import ToolBar from './js/ui/controls/toolBar';
import supportedBrowsers from './js/utils/supportedBrowsers';

library.add(
  faUndo,
  faRedo,
  faTh,
  faVectorSquare,
  faHandPointer,
  faSearchPlus,
  faSearchMinus,
  faSearch,
  faPlus,
  faArrowUp,
  faArrowDown,
  faTasks,
  faTimes,
  faCheckSquare,
  faSquare,
  faQuestion,
  faKeyboard,
  faPen,
  faInfoCircle,
  faCheckCircle,
  faExclamationTriangle,
  faExclamationCircle,
  faGripLinesVertical,
  faTrashAlt,
  faCalendarAlt,
  faClock,
  faSave,
  faCrosshairs,
);
dom.watch();

document.addEventListener('DOMContentLoaded', () => {
  let editor;
  let toolBar;
  let buttonBar;

  const localize = locI18next.init(i18next);
  i18next.use(Backend).init(
    {
      backend: {
        loadPath: Config.LOCALIZATION_URL,
        crossDomain: true,
      },
      lng: 'en',
      fallbackLng: 'en',
      debug: Config.LOCALIZATION_DEBUG,
    },
    (t) => {
      // localizing all html data tags
      localize('[data-i18n]');
      editor = new Editor();
      editor.on('editor_on_open_subprocess', (props) => {
        // subprocess clicked
        console.log(props);
      });
      toolBar = new ToolBar(editor);
      buttonBar = new ButtonBar(editor);
      MicroModal.init();
      // Load XPDL if on Dev mode
      if (Config.ENV === 'Development') {
        // -- For test the XPDL Parser -- //
        const url = process.env.MY_IP || 'localhost';
        const xpdl = process.env.DEV_XPDL || 'xpdl';
        axios.get(`http://${url}:3000/${xpdl}`).then((resp) => {
          // call editor with response
          editor.buildProcess(resp.data);
        });
        // Empty Process
        // editor.buildEmptyProcess();
      } else {
        // for apia integration when built for prod.
        const txtMap = window.parent.document.getElementById('txtMap').innerText;
        if (txtMap === '') {
          // Get value of action combo for the process ['C' || 'A' || 'Z']
          const action = window.parent.document.getElementById('cmbAction');
          const proAction = action ? action.value : 'C';
          editor.buildEmptyProcess(null, null, proAction);
        } else {
          editor.buildProcess(txtMap);
        }
      }
    },
  );

  // ------------------------------ //
  // global functions for apia
  // ------------------------------ //
  const map = {
    loadXPDL(xml) {
      editor.buildProcess(xml);
    },
    newXPDL() {
      editor.buildEmptyProcess();
    },
    getXPDL() {
      return editor.buildXMLProcess();
    },
    getPDF() {
      editor.saveToPDF();
    },
    getPNG() {
      editor.saveToImage();
    },
    getElements() {
      return editor.elements;
    },
    getElementsProperties() {
      return editor.elements.map((value) => value.properties);
    },
    getSelectedElement() {
      for (let i = 0, len = editor.elements.length; i < len; i++) {
        const el = editor.elements[i];
        if (el.selected) {
          return el;
        }
      }
      return false;
    },
    getSelectedElementProperties() {
      for (let i = 0, len = editor.elements.length; i < len; i++) {
        const el = editor.elements[i];
        if (el.selected) {
          return el.properties;
        }
      }
      return false;
    },
    getVersion() {
      return {
        version: VERSION,
        commit: COMMITHASH,
        branch: BRANCH,
        env: ENVIRONMENT,
        app_target: APP_TARGET,
        timestamp: TIMESTAMP,
        package: PACKAGE_VERSION,
      };
    },
  };
  window.top.map = map;

  const confirmBtn = window.parent.document.querySelector('#btnConf button');
  const genDocBtn = window.parent.document.querySelector('#btnGenDoc button');
  // const frmData = window.parent.document.querySelector('#frmData');
  if (confirmBtn && Config.ENV === 'Production') {
    const txtMap = window.parent.document.getElementById('txtMap');
    confirmBtn.addEventListener('click', () => {
      txtMap.value = map.getXPDL();
    });
    genDocBtn.addEventListener('click', () => {
      map.getPDF();
    });
  }

  // write version to keyboard shorcuts modal
  const modalFooterDiv = document.querySelector('.modal__footer');
  modalFooterDiv.innerText = `Build: ${VERSION.substr(-8)}`;
});

window.addEventListener('load', () => {
  const mainDiv = document.querySelector('.main');
  const spinner = document.querySelector('.spinner');
  spinner.style.display = 'none';
  if (supportedBrowsers.test(navigator.userAgent)) {
    // to combat FOUC, make main div visible
    mainDiv.setAttribute('data-state', 'on');
  } else {
    // eslint-disable-next-line no-alert
    alert(i18next.t('messages.proDesBrowserNotSupportedMsg'));
  }
});
