import Config from 'config';
import EventEmitter from 'eventemitter3';
import ContextButton from './contextButton';

export default class ContextMenu extends EventEmitter {
  constructor(element, buttonNames) {
    super();
    this._element = element;
    this._paper = element.paper;
    this._buttonNames = buttonNames;
    this._buttonBar = null;
    this._graphic = element.graphic;
    this._selectionBox = element.selectionBox;
    this._menuItems = [];
    this._MENU_OFFSET_Y = 35;
    this._initButtons();
  }

  show() {
    this._updateMenuItemsPosition();
    this._buttonBar.removeClass('context_menu--hidden');
  }

  hide() {
    this._buttonBar.addClass('context_menu--hidden');
  }

  _initButtons() {
    const bounds = this._selectionBox.node.getBoundingClientRect();
    let offsetY = 0;
    let offsetY2 = 0;
    const menuGroup = this._paper.g().attr({
      class: 'context_menu',
      id: `ctx_${this._element.id}`,
    });
    // position individual buttons
    for (let i = 0, len = this._buttonNames.length; i < len; i++) {
      let pos = null;
      if (i < 4) {
        pos = { x: bounds.right - 5, y: bounds.y + offsetY };
        offsetY += this._MENU_OFFSET_Y;
      } else {
        pos = { x: bounds.right + 35, y: bounds.y + offsetY2 };
        offsetY2 += this._MENU_OFFSET_Y;
      }
      let btn = null;
      switch (this._buttonNames[i]) {
        case Config.DELETE:
          btn = new ContextButton(
            this._paper,
            '<g id="remove_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M429 743c-58-4-70 11-71-52l0-9-172-56c-20-5-20-29 0-30l624 0c19 0 19 25 0 30l-168 55 0 10c0 62-7 48-71 52l-71 0z m-215-204c-14 0-25-12-24-26l50-555c1-12 11-22 24-22l457 0c13 0 23 9 25 22l64 554c2 15-9 27-24 27l-143 0-143 0z m27-49l259 0 259 0-59-505-414 0z m120-73c-1 0-2 0-3 0-16-1-28-15-25-31l44-320c2-15 16-26 31-24 15 2 25 17 22 32l-44 319c-2 13-12 23-25 24z m280 0c-13 0-25-10-27-24l-44-319c-3-15 7-30 22-32 16-2 29 9 31 24l44 320c3 16-9 30-26 31z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_remove_button_clicked');
          });
          break;
        case Config.TASK:
          btn = new ContextButton(
            this._paper,
            '<g id="append_task_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M215 745c-107 0-195-87-195-194l0-402c0-107 88-194 195-194l570 0c108 0 195 87 195 194l0 402c0 107-87 194-195 194l-570 0z m0-60l570 0c75 0 135-59 135-134l0-402c0-75-60-134-135-134l-570 0c-75 0-135 59-135 134l0 402c0 75 60 134 135 134z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.TASK);
          });
          break;
        case Config.SUBPROCESS:
          btn = new ContextButton(
            this._paper,
            '<g id="append_subprocess_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)"  d="M216 716c-86 0-155-69-155-154l0-424c0-85 69-154 155-154l569 0c85 0 154 69 154 154l0 424c0 85-69 154-154 154l-569 0z m0-48l569 0c59 0 106-47 106-106l0-424c0-59-47-106-106-106l-116 0 0 308-338 0 0-15 0-293-115 0c-60 0-106 47-106 106l0 424c0 59 46 106 106 106z m146-359l276 0 0-276-276 0 0 276z m115-39l0-23 0-54-54 0-23 0 0-46 23 0 54 0 0-54 0-23 46 0 0 23 0 54 54 0 23 0 0 46-23 0-54 0 0 54 0 23-46 0z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.SUBPROCESS);
          });
          break;
        case Config.GATEWAY:
          btn = new ContextButton(
            this._paper,
            '<g id="append_gateway_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M59 376c-14-14-14-38 0-52l415-415c14-14 38-14 52 0l415 415c14 14 14 38 0 52l-415 415c-14 14-38 14-52 0l-415-415z m51-26l390 390 390-390-390-390-390 390z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.GATEWAY);
          });
          break;
        case Config.START_EVENT:
          btn = new ContextButton(
            this._paper,
            '<g id="append_start_event_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M496 802c-176 0-345-113-412-276-70-161-34-362 89-487 119-128 314-175 477-115 169 58 294 224 301 403 12 176-92 351-250 428-62 31-132 47-201 47-1 0-3 0-4 0z m12-49c173-1 335-126 380-293 47-159-17-344-155-439-143-105-354-97-489 18-136 109-185 309-115 468 60 147 212 248 371 246 3 0 6 0 8 0z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.START_EVENT);
          });
          break;
        case Config.INTERMEDIATE_EVENT:
          btn = new ContextButton(
            this._paper,
            '<g id="append_intermediate_event_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M496 802c-219 2-422-178-445-396-24-185 78-380 247-461 160-80 368-56 501 66 134 118 190 318 128 486-57 165-214 291-388 303-15 1-29 2-43 2z m8-49c201 3 386-169 398-370 16-178-100-360-271-415-164-56-362 4-460 148-103 144-99 357 19 491 76 92 194 149 314 146z m-12-44c-185 0-352-166-352-352-4-168 124-331 291-360 153-30 322 49 390 191 71 141 39 329-84 431-67 59-156 92-245 90z m14-48c165 1 312-150 305-315 0-150-121-290-272-305-143-19-295 73-336 213-43 134 14 296 139 364 50 29 107 44 164 43z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.INTERMEDIATE_EVENT);
          });
          break;
        case Config.END_EVENT:
          btn = new ContextButton(
            this._paper,
            '<g id="append_end_event_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M496 802c-203 1-394-153-437-351-41-174 33-368 181-470 143-103 348-111 497-15 150 91 238 275 210 449-26 181-170 339-350 376-33 7-67 11-101 11z m10-142c150 1 287-123 302-271 19-142-72-291-210-334-134-45-296 13-366 138-77 129-45 313 78 403 56 43 126 66 196 64z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.END_EVENT);
          });
          break;
        case Config.TEXT:
          btn = new ContextButton(
            this._paper,
            '<g id="append_text_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M677 795l0-25 0-411 0-412 0-24 24 0 224 0 25 0 0 49-25 0-199 0 0 387 0 386 199 0 25 0 0 50-25 0-224 0-24 0z m-81-386l-75-60 60-75 75 60-60 75z m-151-119l-75-60 60-75 75 59-60 76z m-150-120l-76-59 60-76 75 60-59 75z m-151-119l-76-60 60-75 76 59-60 76z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.TEXT);
          });
          break;
        case Config.OPEN_SUBPROCESS:
          btn = new ContextButton(
            this._paper,
            '<g id="append_text_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M150 700l0-25 0-675 700 0 0 700z m44-44l612 0 0-612-612 0z m261-106l0-155-155 0 0-90 155 0 0-155 90 0 0 155 155 0 0 90-155 0 0 155z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_open_subprocess');
          });
          break;
        case Config.CONNECTOR:
          btn = new ContextButton(
            this._paper,
            '<g id="append_text_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M911 749c0 0-285-146-431-214 31-28 60-59 90-89-147-149-295-299-443-448 11-11 22-21 33-31 148 149 295 299 443 448 30-29 60-60 90-90 77 153 218 424 218 424z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_connect_button', Config.CONNECTOR);
          });
          break;
        case Config.LANE:
          btn = new ContextButton(
            this._paper,
            '<g id="append_text_icon"><path class="context_menu_btn__icon" transform="scale(1, -1) translate(0, -100)" d="M46-50l0 89 49 0 0-40 16 0 0-49-65 0z m114 0l0 49 97 0 0-49-97 0z m146 0l0 49 98 0 0-49-98 0z m147 0l0 49 97 0 0-49-97 0z m146 0l0 49 98 0 0-49-98 0z m147 0l0 49 97 0 0-49-97 0z m145 0l0 49 13 0 0 40 49 0 0-64 0-25-25 0-37 0z m-845 138l0 92 49 0 0-92-49 0z m858 0l0 92 49 0 0-92-49 0z m-857 139l0 527 906 0 0-527-906 0z m857 48l0 435-810 0 0-435 810 0z" /></g>',
            this._buttonBar,
            pos,
          );
          btn.on('on_context_button_clicked', () => {
            this.emit('element_on_append', Config.LANE);
          });
          break;
        default:
          break;
      }
      menuGroup.add(btn._btn);
      this._menuItems.push(btn);
    }
    this._buttonBar = menuGroup.appendTo(
      document.getElementById('ctxMenuLayer'),
    );
  }

  _updateMenuItemsPosition() {
    const bounds = this._selectionBox.node.getBoundingClientRect();
    // this is to account for the toolbar height
    const svgTop = document.getElementById('app').getBoundingClientRect().top;
    let offsetY = -svgTop;
    let offsetY2 = -svgTop;
    for (let i = 0, len = this._menuItems.length; i < len; i++) {
      let pos = null;
      if (i < 4) {
        pos = { x: bounds.right - 5, y: bounds.y + offsetY };
        offsetY += this._MENU_OFFSET_Y;
      } else {
        pos = { x: bounds.right + 35, y: bounds.y + offsetY2 };
        offsetY2 += this._MENU_OFFSET_Y;
      }
      this._menuItems[i].updatePosition(pos);
    }
  }

  delete() {
    this._buttonBar.remove();
  }
}
