import Config from 'config';
import Element from './element';

export default class EventElement extends Element {
  constructor(
    event,
    id,
    x,
    y,
    viewport,
    properties,
    isMovable,
    contextMenuItems,
  ) {
    super(event, id, viewport, x, y, properties, isMovable, contextMenuItems);
    this._radius = Config.EVENT_SIZE.radius;
    if (!properties) {
      this._properties.type = 'none';
      const nameEvt = this.elementType.split('Event');
      this._properties.activity = { id, name: `${nameEvt[0]} Event` };
    }
    this.drawElement();
  }

  get isAttached() {
    return this._properties.isAttached;
  }

  get radius() {
    return this._radius;
  }

  get eventName() {
    return this._properties.activity.name;
  }

  set name(value) {
    this._properties.activity.name = value;
  }

  get eventType() {
    return this._properties.type;
  }

  get message() {
    return this._properties.message;
  }

  get timer() {
    return this._properties.timer;
  }

  set timer(value) {
    this._properties.timer = value;
  }

  get trasitions() {
    return this._properties.trasitions || [];
  }

  set message(value) {
    this._properties.message = value;
  }

  get webServiceMessage() {
    return this.message ? this.message.webService : undefined;
  }

  get catchThrowMessage() {
    return this.message ? this.message.catchThrow : undefined;
  }

  set catchThrowMessage(value) {
    this.emit('element_onpropertychange');
    this._createDefaultMessage();
    this._properties.message.catchThrow = value;
  }

  getFactorFrequency(unit) {
    switch (unit) {
      case 'MINUTES':
        return 1000;
      case 'HOURS':
        return 60000;
      case 'DAYS':
        return 1440000;
      case 'WEEKS':
        return 10080000;
      default:
        break;
    }
    return 0;
  }

  addTransition(transition) {
    if (this.trasitions.length === 0) {
      this._properties.trasitions = [];
    }
    this.trasitions.push(transition);
  }

  get processAttributesMessage() {
    return this.message ? this.message.attrProc : [];
  }

  get entityAttributesMessage() {
    return this.message ? this.message.attrEnt : [];
  }

  set processAttributesMessage(value) {
    this._properties.message.attrProc = value;
  }

  set entityAttributesMessage(value) {
    this._properties.message.attrEnt = value;
  }

  set webServiceMessage(value) {
    this.emit('element_onpropertychange');
    this._createDefaultMessage();
    this._properties.message.webService = value;
  }

  get webServices() {
    return this._properties.events;
  }

  set webServices(value) {
    this.emit('element_onpropertychange');
    this._properties.events = value;
  }

  _createDefaultMessage() {
    if (!this.message) {
      const type = this.elementType;
      const catchThrow = type === Config.START_EVENT || type === Config.INTERMEDIATE_EVENT
        ? 'catch'
        : 'throw';
      this._properties.message = { catchThrow };
    }
  }

  get dateTimer() {
    return this.timer.date;
  }

  set dateTimer(value) {
    this._properties.timer.date = value;
  }

  get frequencyTimer() {
    return this.timer ? this.timer.frequency : undefined;
  }

  set frequencyTimer(value) {
    this.emit('element_onpropertychange');
    this._createDefaultFrequencyTimer();
    this._properties.timer.frequency = value;
  }

  get attributeTimer() {
    return this.timer ? this.timer.attribute : undefined;
  }

  set attributeTimer(value) {
    this.emit('element_onpropertychange');
    this._createDefaultAttributeTimer();
    this._properties.timer.attribute = value;
  }

  _createDefaultDateTimer() {
    if (!this.timer) this.timer = {};
    if (!this.dateTimer) this.dateTimer = {};
  }

  _createDefaultFrequencyTimer() {
    if (!this.timer) this.timer = {};
    if (!this.frequencyTimer) this._properties.timer.frequency = {};
  }

  _createDefaultAttributeTimer() {
    if (!this.timer) this.timer = {};
    if (!this.attributeTimer) this._properties.timer.attribute = {};
  }

  setDateTimerByType(value, type) {
    this.emit('element_onpropertychange');
    this._createDefaultDateTimer();
    this._properties.timer.date[type] = value;
  }

  deleteTimerProperty(prop) {
    if (this.timer[prop]) delete this._properties.timer[prop];
  }

  _defaultAttrsMessageProperties(flag) {
    this._createDefaultMessage();
    if (!this.message[flag]) this._properties.message[flag] = [];
  }

  existMessageAttribute(id, arrayType) {
    const message = this._properties.message || {};
    const join = message[arrayType] || [];
    return join.find((attr) => parseInt(attr.id, 10) === parseInt(id, 10));
  }

  findMessageAttributeIndex(id, arrayType) {
    const attributes = this.message[arrayType] || [];
    for (let index = 0; index < attributes.length; index++) {
      const attr = attributes[index];
      if (parseInt(attr.id, 10) === parseInt(id, 10)) {
        return index;
      }
    }
    return -1;
  }

  addMessageAttribute(attribute, flag) {
    const arrayType = flag === 'entity' ? 'attrEnt' : 'attrProc';
    this.emit('element_onpropertychange');
    this._defaultAttrsMessageProperties(arrayType);
    this._properties.message[arrayType].push(attribute);
  }

  editMessageAttribute(id, type, value) {
    const arrayType = type === 'entity' ? 'attrEnt' : 'attrProc';
    const position = this.findMessageAttributeIndex(id, arrayType);
    if (position !== -1) {
      this.emit('element_onpropertychange');
      this._properties.message[arrayType][position] = value;
    }
  }

  deleteMessageAttribute(id, type) {
    const arrayType = type === 'entity' ? 'attrEnt' : 'attrProc';
    const position = this.findMessageAttributeIndex(id, arrayType);
    if (position !== -1) {
      this.emit('element_onpropertychange');
      this._properties.message[arrayType].splice(position, 1);
    }
  }

  deleteAllMessageAttributes(type) {
    const arrayType = type === 'entity' ? 'attrEnt' : 'attrProc';
    this.emit('element_onpropertychange');
    this._properties.message[arrayType] = [];
  }

  _defaultWebServices() {
    if (!this.message) this._createDefaultMessage();
    this._properties.events = [
      {
        WS: true,
        busClaId: 102,
        busClaName: 'BPMNAutoComplete',
        eventId: 11,
        id: 1,
        name: 'ONREADY',
        order: 0,
      },
    ];
  }

  maxId(data) {
    let max = 0;
    data.forEach((item) => {
      if (parseInt(item.id, 10) > max) max = parseInt(item.id, 10);
    });
    return max;
  }

  existWebService(id) {
    const wsList = this.webServices || [];
    const wsFind = wsList.find((ws) => {
      return parseInt(ws.id, 10) === parseInt(id, 10);
    });
    return wsFind;
  }

  existWSBindingParameter(wsId, bindingId) {
    const wsIndex = this.getWebServiceIndexById(wsId);
    let binding;
    if (wsIndex !== -1) {
      const bindings = this.webServices[wsIndex].bindings || [];
      binding = bindings.find((param) => {
        return parseInt(param.busClaParId, 10) === parseInt(bindingId, 10);
      });
    }
    return binding;
  }

  getWebServiceIndexById(id) {
    const index = this.webServices.findIndex((ws) => {
      return parseInt(ws.id, 10) === parseInt(id, 10);
    });
    return index;
  }

  getBindingIndexById(wsIndex, id) {
    const bindings = this.webServices[wsIndex].bindings || [];
    const index = bindings.findIndex((bin) => {
      return parseInt(bin.busClaParId, 10) === parseInt(id, 10);
    });
    return index;
  }

  addWebService(webService) {
    this.emit('element_onpropertychange');
    if (!this.webServices) this._defaultWebServices();
    const newWS = {
      id: this.maxId(this.webServices) + 1,
      order: this.webServices.length,
      WS: true,
      bindings: [],
      ...webService,
    };
    this._properties.events.push(newWS);
  }

  editWebService(id, value) {
    const index = this.getWebServiceIndexById(id);
    let webService;
    if (index !== -1) {
      this.emit('element_onpropertychange');
      webService = this.webServices[index];
      webService.busClaId = parseInt(value.id, 10);
      webService.busClaName = value.name;
      webService.bindings = [];
    }
    return webService;
  }

  deleteWebService(id) {
    const index = this.getWebServiceIndexById(id);
    if (index !== -1) {
      this.emit('element_onpropertychange');
      if (this.webServices.length === 2) {
        this._properties.events = [];
      } else {
        this._properties.events.splice(index, 1);
      }
    }
  }

  editBindingParameter(wsId, bindingId, values) {
    const wsIndex = this.getWebServiceIndexById(wsId);
    if (wsIndex !== -1) {
      const bindIndex = this.getBindingIndexById(wsIndex, bindingId);
      if (bindIndex !== -1) {
        const entries = Object.entries(values);
        this.emit('element_onpropertychange');
        entries.forEach((ent) => {
          const [prop, value] = ent;
          this.webServices[wsIndex].bindings[bindIndex][prop] = value;
        });
      }
    }
  }

  deletePropertyOfBindingParameter(wsId, bindingId, props) {
    const wsIndex = this.getWebServiceIndexById(wsId);
    if (wsIndex !== -1) {
      const bindIndex = this.getBindingIndexById(wsIndex, bindingId);
      if (bindIndex !== -1) {
        props.forEach((prop) => {
          if (this.webServices[wsIndex].bindings[bindIndex][prop]) {
            delete this.webServices[wsIndex].bindings[bindIndex][prop];
          }
        });
      }
    }
  }

  deleteBindingParameter(wsId, bindingId) {
    const wsIndex = this.getWebServiceIndexById(wsId);
    if (wsIndex !== -1) {
      const bindIndex = this.getBindingIndexById(wsIndex, bindingId);
      if (bindIndex !== -1) {
        this.emit('element_onpropertychange');
        this.webServices[wsIndex].bindings.splice(bindIndex, 1);
        if (this.webServices[wsIndex].bindings.length === 0) {
          delete this.webServices[wsIndex].bindings;
        }
      }
    }
  }

  addBindingParameter(wsId, binding) {
    const wsIndex = this.getWebServiceIndexById(wsId);
    if (wsIndex !== -1) {
      this.emit('element_onpropertychange');
      if (!this.webServices[wsIndex].bindings) this.webServices[wsIndex].bindings = [];
      this.webServices[wsIndex].bindings.push(binding);
    }
  }
}
