import i18next from 'i18next';
import Component from './Component';
import ToastMessage from './ToastMessage';

export default class ListComponent extends Component {
  constructor(nameId, panel, label, arrayList, add) {
    super('list', nameId, panel, label);
    this._arrayList = arrayList;
    this._idItem = 'id';
    this._editStatus = false;
    this._disabledStatus = false;
    this._rowEditableSelected = null;
    this.MAX_ROWS = 6;
    this._initComponent();
    this._initEvents(add);
    this._initListTable();
    this._load = 0;
  }

  get editStatus() {
    return this._editStatus;
  }

  set editStatus(value) {
    this._editStatus = value;
    this.addAction.disabled = this._editStatus;
  }

  get idItem() {
    return this._idItem;
  }

  set idItem(value) {
    this._idItem = value;
  }

  get arrayList() {
    return this._arrayList;
  }

  set arrayList(value) {
    this._arrayList = value;
    this._initListTable();
  }

  set arrayListData(value) {
    this._cleanElementsList();
    this._arrayList.data = value;
    if (value.length > 0) {
      const list = this.wrapper.querySelector('.list');
      this._cleanElementsList();
      this._createListElement(list, this._arrayList.data);
    }
    if (this.selected()) {
      this.selected().classList.toggle('select');
    }
    this._toggleBlankRow();
    this.moveUpAction.disabled = true;
    this.moveDownAction.disabled = true;
  }

  get rowsItem() {
    return this.wrapper.querySelectorAll('.list .row');
  }

  get rowEditableSelected() {
    return this._rowEditableSelected;
  }

  set rowEditableSelected(value) {
    this._rowEditableSelected = value;
  }

  rowById(id) {
    return this.wrapper.querySelector(`ul[data-key='${id}']`);
  }

  get rowSelected() {
    let rowSelected;
    if (this.selected()) {
      const key = this.selected().getAttribute('data-key');
      rowSelected = this.arrayList.data.find((value) => {
        return parseInt(value[this.idItem], 10) === parseInt(key, 10);
      });
    }
    return rowSelected;
  }

  set rowSelected(value) {
    this.unSelectAll();
    // IF "rowEditableSelected = true" addAction button set to disable
    this.addAction.disabled = this.rowEditableSelected && true;
    const rowElement = this.rowById(value[this.idItem]);
    if (rowElement) {
      rowElement.classList.add('select');
    }
  }

  get isOverflowing() {
    return this._arrayList.data.length > this.MAX_ROWS;
  }

  updateActionsButtons() {
    if (this.rowSelected) {
      const index = this.arrayList.data.findIndex((item) => {
        return parseInt(item[this.idItem], 10) === parseInt(this.rowSelected[this.idItem], 10);
      });
      if (index !== -1) {
        const count = this.arrayList.data.length - 1;
        this.moveUpAction.disabled = index === 0;
        this.moveDownAction.disabled = index === count;
      }
    }
  }

  get disabled() {
    return this._disabledStatus;
  }

  set disabled(value) {
    this._disabledStatus = value;
    this.addAction.disabled = value;
    this.moveUpAction.disabled = true;
    this.moveDownAction.disabled = true;
    if (this.rowSelected && !value) {
      const index = this.arrayList.data.findIndex((item) => {
        return parseInt(item[this.idItem], 10) === parseInt(this.rowSelected[this.idItem], 10);
      });
      if (index !== -1) {
        const count = this.arrayList.data.length - 1;
        this.moveUpAction.disabled = index < count;
        this.moveDownAction.disabled = index > count;
      }
    }
  }

  unSelectAll(disabled = false) {
    if (this.selected()) this.selected().classList.remove('select');
    this.disabled = disabled;
  }

  _cleanElementsList() {
    const items = this.wrapper.querySelectorAll('.list .row');
    items.forEach((element) => element.remove());
  }

  refreshList() {
    const selected = this.editStatus ? this.rowEditableSelected : this.rowSelected;
    const list = this.wrapper.querySelector('.list');
    this._cleanElementsList();
    this._createListElement(list, this._arrayList.data);
    if (selected) {
      let elSelected;
      this.rowsItem.forEach((value) => {
        if (parseInt(value.getAttribute('data-key'), 10) === parseInt(selected[this._idItem], 10)) {
          elSelected = value;
        }
      });
      elSelected.classList.toggle(this.editStatus ? 'edit-status' : 'select');
    }
    this.updateActionsButtons();
    this._toggleBlankRow();
  }

  _initComponent() {
    const buttonList = this.wrapper.querySelectorAll('.actions .btn');
    this._elementsList = this.wrapper.querySelector('.list');
    this._createdDeleteAll = false;
    [this.addAction, this.moveUpAction, this.moveDownAction] = buttonList;
    this._setDefaultStateAction();
  }

  _initEvents(add) {
    this.moveDownAction.addEventListener('click', () => {
      this._moveDownItem();
    });
    this.moveUpAction.addEventListener('click', () => {
      this._moveUpItem();
    });
    if (add) {
      this.addAction.addEventListener('click', () => {
        this.emit('add_item');
      });
    } else {
      this.addAction.remove();
      this.moveDownAction.classList.add('btn-col-2');
      this.moveUpAction.classList.add('btn-col-2');
    }
  }

  _setDefaultStateAction() {
    this.moveUpAction.disabled = true;
    this.moveDownAction.disabled = true;
  }

  _initListTable() {
    const list = this.wrapper.querySelector('.list');
    const children = list.querySelectorAll('ul');
    if (children.length > 0) {
      children.forEach((element) => element.remove());
    }
    const header = this._createRowElement(this.arrayList.header, true);
    list.appendChild(header);
    this._createListElement(list, this.arrayList.data);
    this._toggleBlankRow();
  }

  _toggleBlankRow() {
    const list = this.wrapper.querySelector('.list');
    let emptyRow = list.querySelector('ul[data-empty]');
    if (emptyRow !== null) {
      emptyRow.remove();
      emptyRow = null;
    }

    if (this.arrayList.data.length === 0 && emptyRow === null && list.children.length === 1) {
      const row = document.createElement('ul');
      row.classList.add('row');
      row.setAttribute('data-empty', true);
      this.arrayList.header.forEach(() => {
        const cell = this._createCellElement('', false);
        row.appendChild(cell);
      });
      list.appendChild(row);
    }
  }

  _createDeleteActionElement() {
    const del = document.createElement('a');
    del.setAttribute('href', '#');
    const icon = document.createElement('i');
    icon.setAttribute('class', 'fas fa-times');
    del.appendChild(icon);
    del.addEventListener('click', () => this.deleteItem());
    return del;
  }

  _createCheckAllActionElement(value) {
    const checkAll = document.createElement('a');
    const icon = document.createElement('i');
    icon.setAttribute('class', 'fas fa-tasks');
    checkAll.appendChild(icon);
    checkAll.setAttribute('class', 'checkbox');
    checkAll.style.marginLeft = '0.4em';
    checkAll.addEventListener('click', () => {
      this.checkAllItems(value);
    });
    return checkAll;
  }

  _createDeleteAllActionElement() {
    const deleteAll = document.createElement('a');
    deleteAll.classList.add('delete-all');
    const icon = document.createElement('i');
    icon.setAttribute('class', 'fas fa-times');
    deleteAll.appendChild(icon);
    deleteAll.addEventListener('click', () => {
      this._deleteAllItems();
    });
    return deleteAll;
  }

  _createEditActionElement() {
    const edit = document.createElement('a');
    edit.classList.add('edit');
    const icon = document.createElement('i');
    icon.setAttribute('class', 'fas fa-pen');
    edit.appendChild(icon);
    edit.addEventListener('click', () => this.fireEdition(this.target()));
    return edit;
  }

  _createCheckActionElement(value, action) {
    const checkbox = document.createElement('a');
    checkbox.setAttribute('href', '#');
    checkbox.setAttribute('name', action.key);
    checkbox.setAttribute('class', 'checkbox');
    const icon = document.createElement('i');
    if (value) {
      icon.setAttribute('class', 'far fa-check-square fa-lg');
    } else {
      icon.setAttribute('class', 'far fa-square fa-lg');
    }
    checkbox.appendChild(icon);
    checkbox.addEventListener('click', () => {
      this.checkItem();
    });
    return checkbox;
  }

  _createAccordingTheActionType(action, value = '') {
    let actionEl;
    const type = action.type || action;
    switch (type) {
      case 'checkbox': {
        actionEl = this._createCheckActionElement(value, action);
        break;
      }
      case 'delete': {
        actionEl = this._createDeleteActionElement();
        break;
      }
      case 'edit': {
        actionEl = this._createEditActionElement();
        break;
      }
      default:
        break;
    }
    actionEl.title = type.toUpperCase();
    return actionEl;
  }

  _createCellElement(data, th) {
    const cell = document.createElement('li');
    if (th) {
      if (data !== 'delete-all' && data !== 'edit') cell.innerText = data;
      if (typeof data === 'string') {
        const value = data.toLowerCase();
        if (
          value === i18next.t('listComponent.txtListComponentHeaderCond')
          || value === i18next.t('listComponent.txtListComponentHeaderInOut')
          || value === i18next.t('listComponent.txtListComponentHeaderType')
          || value === i18next.t('listComponent.txtListComponentHeaderEntity')
          || value === i18next.t('listComponent.txtListComponentHeaderProcess')
        ) {
          cell.classList.add('condition-cell');
        }
        if (value === 'event') {
          cell.classList.add('medium-cell');
        }
        if (value === 'binding') {
          cell.classList.add('binding-cell');
        }
      }
      if (data.type !== undefined) {
        cell.innerText = data.title;
        cell.classList.add('cell-center');
        cell.classList.add('action-cell');
      }
      if (data === 'delete-all' || data === 'edit') {
        cell.classList.add('action-cell');
      }
    } else {
      const find = this.arrayList.actions.find((value) => data[0] === value.key);
      if (find) {
        cell.setAttribute('class', 'cell-center');
        const actionEl = this._createAccordingTheActionType(find || 'delete', data[1]);
        cell.appendChild(actionEl);
        cell.classList.add('action-cell');
      } else {
        cell.setAttribute('title', data[1] || '');
        cell.innerText = data[1] || '';
      }
    }
    return cell;
  }

  _createRowElement(data, thead) {
    const row = document.createElement('ul');
    data.forEach((cell) => {
      const cellNode = this._createCellElement(cell, thead);
      if ((cell === 'delete-all' || cell === 'edit') && thead) {
        cellNode.classList.add('cell-center');
        if (this.arrayList.deleteActionAll && cell === 'delete-all') {
          const deleteAll = this._createDeleteAllActionElement();
          this._createdDeleteAll = true;
          cellNode.appendChild(deleteAll);
        }
      }
      const notShow = this.arrayList.notShow.find((value) => cell[0] === value);
      if (!notShow) {
        row.appendChild(cellNode);
      }
    });

    if (!thead) {
      row.setAttribute('class', 'row');
      const dataKey = data.find((value) => {
        return value[0] === this._idItem;
      });
      if (dataKey) row.setAttribute('data-key', dataKey[1]);
      if (this.arrayList.editAction) {
        const editCell = this._createCellElement('', false);
        editCell.classList.add('cell-center');
        const actionEl = this._createAccordingTheActionType('edit');
        editCell.appendChild(actionEl);
        editCell.classList.add('action-cell');
        row.appendChild(editCell);
      }
      if (this.arrayList.deleteAction) {
        const deleteCell = this._createCellElement('', false);
        deleteCell.classList.add('cell-center');
        const actionEl = this._createAccordingTheActionType('delete');
        deleteCell.appendChild(actionEl);
        deleteCell.classList.add('action-cell');
        row.appendChild(deleteCell);
      }
      row.addEventListener('click', () => {
        this.selectElement();
      });
    }
    return row;
  }

  _createListElement(nodeList, data) {
    if (data) {
      data.forEach((row) => {
        const entries = Object.entries(row);
        const rowNode = this._createRowElement(entries, false);
        nodeList.appendChild(rowNode);
      });
    }
  }

  target() {
    let select;
    if (event.target !== undefined) {
      select = event.target;
      while (select.nodeName.toLowerCase() !== 'ul') {
        select = select.parentNode;
      }
    }
    return select;
  }

  selected() {
    const selected = this.wrapper.querySelector('ul.row.select');
    return selected;
  }

  selectElement() {
    const selected = this.selected();
    if (selected !== null) {
      const isDifferent = selected.getAttribute('data-key') !== this.target().getAttribute('data-key');
      if (isDifferent) {
        selected.classList.remove('select');
      }
    }
    if (!this.editStatus && !this.disabled) {
      if (!this.target().classList.contains('select')) {
        this.target().classList.add('select');
      }
      const actual = this.selected();
      const rowSelected = this.arrayList.data.find((value) => {
        return parseInt(value[this._idItem], 10) === parseInt(actual.getAttribute('data-key'), 10);
      });
      this.emit('selected_item', rowSelected);
      this._enableOrDisableActionBtn();
    }
  }

  _enableOrDisableActionBtn() {
    if (this.selected() !== null && this.selected() !== undefined) {
      const header = this.wrapper.querySelector('.list').firstElementChild;
      this.moveUpAction.disabled = header === this.selected().previousElementSibling;
      this.moveDownAction.disabled = this.selected().nextElementSibling === null;
    } else {
      this._setDefaultStateAction();
    }
  }

  _check(el, value) {
    const className = value ? 'far fa-square fa-lg' : 'far fa-check-square fa-lg';
    el.setAttribute('class', className);
    return el;
  }

  checkItem() {
    event.stopPropagation();
    const checkbox = event.target;
    const idEdit = this.rowEditableSelected && this.rowEditableSelected.id;
    let id;
    if (checkbox.parentNode.getAttribute('class') === 'checkbox') {
      id = this.target().getAttribute('data-key');
      const position = this.arrayList.data.findIndex((value) => {
        return parseInt(value[this._idItem], 10) === parseInt(id, 10);
      });
      const keyName = event.target.parentNode.getAttribute('name');
      const row = this.arrayList.data[position];
      this._check(checkbox, row[keyName]);
      row[keyName] = !row[keyName];
      this.emit('checked_item', { value: row[keyName], position, keyName });
      if (this.rowEditableSelected && +id === idEdit) {
        this.rowEditableSelected = row;
      } else {
        this.rowSelected = row;
      }
      // IF EDIT-STATUS 'true' ENABLE OR DISABLE BUTTON
      if (!this._editStatus) {
        this._enableOrDisableActionBtn();
      }
    }
  }

  checkAllItems(value) {
    this.arrayList.data.forEach((el) => {
      el[value.key] = value.all;
    });
    const checkboxs = this.wrapper.querySelectorAll('.checkbox i');
    checkboxs.forEach((checkbox) => {
      if (checkbox.parentNode.name === value.key) {
        this._check(checkbox, value.all);
      }
    });
    value.all = !value.all;
  }

  addItem(item) {
    if (item) {
      const exist = this.arrayList.data.find((value) => {
        return parseInt(value[this._idItem], 10) === parseInt(item.id, 10);
      });
      if (!exist) {
        this.arrayList.data.push(item);
        const insertEl = Object.entries(item);
        const row = this._createRowElement(insertEl, false);
        this._elementsList.appendChild(row);
        this.updateActionsButtons();
      }
    }
    this._toggleBlankRow();
  }

  _deleteInArray(key) {
    const position = this.arrayList.data.findIndex((value) => {
      return parseInt(value[this._idItem], 10) === parseInt(key, 10);
    });
    if (position !== -1) {
      this.arrayList.data.splice(position, 1);
    }
  }

  fireEdition(target) {
    event.stopPropagation();
    this.selectElement();
    const existEdit = this.wrapper.querySelector('.row.edit-status');
    let sameRowEdited = false;
    if (existEdit) {
      const idEdit = parseInt(existEdit.getAttribute('data-key'), 10);
      const idTarget = parseInt(target.getAttribute('data-key'), 10);
      sameRowEdited = idEdit === idTarget;
      if (this.rowEditableSelected) {
        this.moveUpAction.disabled = true;
        this.moveDownAction.disabled = true;
      } else {
        // Enable buttons
        this.moveUpAction.disabled = false;
        this.moveDownAction.disabled = false;
      }
    } else {
      // State return
      this._setDefaultStateAction();
    }

    if (
      ((!existEdit && !this.editStatus) || (this.editStatus && sameRowEdited))
      && !this.disabled
    ) {
      this.editStatus = !this.editStatus;
      target.classList.toggle('edit-status');
      if (target && this.editStatus) {
        const targetIndex = target.getAttribute('data-key');
        this.rowsItem.forEach((row) => row.classList.remove('select'));
        this.rowEditableSelected = this.arrayList.data.find((value) => {
          return parseInt(value[this._idItem], 10) === parseInt(targetIndex, 10);
        });
      } else {
        this.selectElement();
        this.rowEditableSelected = null;
      }
      this.emit('edit_item', this.editStatus);
    }
  }

  deleteItem() {
    event.stopPropagation();
    if (this.editStatus) return;
    if (!this.disabled) {
      const deleteItem = this.target();
      const key = parseInt(deleteItem.getAttribute('data-key'), 10);
      const item = this.arrayList.data.find((value) => {
        return parseInt(value[this._idItem], 10) === parseInt(key, 10);
      });
      deleteItem.remove();
      this._toggleBlankRow();
      this.emit('delete_item', item);
      this._enableOrDisableActionBtn();
      this.showDeleteItem(item);
    }
  }

  showDeleteItem(item) {
    const toast = new ToastMessage();
    const label = this._label.match(/(^\w+)/g) || 'Delete Item';
    const option = {
      duration: 15000,
      position: 'bottom-left',
    };
    const message = `${
      item.busClaName
        ? `<b>${i18next.t('listComponent.txtListComponentHeaderToastMessageBusinessClass')}:</b> ${
          item.busClaName
        }<br>`
        : ''
    }
     ${
  item.name
    ? `<b>${i18next.t('listComponent.txtListComponentHeaderToastMessageName')}</b> ${
      item.name
    }<br>`
    : ''
}
     ${
  item.haveCond
    ? `<b>${i18next.t('listComponent.txtListComponentHeaderToastMessageCondition')}</b> ${
      item.haveCond
    }`
    : ''
}`;
    toast.showMessage({
      type: 'info',
      title: `${i18next.t('listComponent.txtListComponentHeaderToastMessageTitle')} ${label}`,
      message,
      ...option,
    });
  }

  _deleteAllItems() {
    if (this.editStatus) return;
    if (this.arrayList.data.length !== 0 && !this.disabled) {
      // eslint-disable-next-line no-alert
      const accept = confirm(i18next.t('listComponent.txtListComponentHeaderConfirmMessage'));
      if (accept) {
        this.arrayList.data = [];
        this.rowsItem.forEach((element) => element.remove());
        this._toggleBlankRow();
        this.emit('delete_all_items');
      }
    }
  }

  _scrollToRow(row, executeFn, select = true) {
    // if (this.isOverflowing) {
    let element = row;
    if (!(row instanceof Node)) {
      element = this.rowById(row[this.idItem]);
      if (select) {
        this.rowSelected = row;
      }
    }
    element.scrollIntoView({
      behavior: 'smooth',
      block: 'nearest',
    });
    //  }
    if (select) {
      if (executeFn) executeFn();
      this.updateActionsButtons();
    }
  }

  _move(direction) {
    let actualPosition = 0;
    let oldPosition = 0;
    let actual;
    let old;
    // eslint-disable-next-line consistent-return
    this.rowsItem.forEach((row, index) => {
      if (this.selected().getAttribute('data-key') === row.getAttribute('data-key')) {
        actualPosition = direction === 'up' ? index - 1 : index + 1;
        // Set in the original array list
        const temp = this.arrayList.data[actualPosition];
        this.arrayList.data[actualPosition] = this.arrayList.data[index];
        this.arrayList.data[index] = temp;
        oldPosition = index;

        // Set in the actual nodelist
        const before = this.rowsItem[actualPosition];
        const actionParam = direction === 'up' ? 'beforebegin' : 'afterend';
        before.insertAdjacentElement(actionParam, row);

        actual = this.arrayList.data[actualPosition];
        old = this.arrayList.data[index];
      }
    });
    this._scrollToRow(this.selected());
    this.emit('move', actualPosition, oldPosition, actual, old, this.arrayList.data);
  }

  _moveUpItem() {
    if (this.editStatus) return;
    if (this.selected().previousElementSibling !== null) {
      this._move('up');
      this._enableOrDisableActionBtn();
    }
  }

  _moveDownItem() {
    if (this.editStatus) return;
    if (this.selected().nextElementSibling !== null) {
      this._move('down');
      this._enableOrDisableActionBtn();
    }
  }
}
