import Config from 'config';
import i18next from 'i18next';
import TemplatePanel from './templatePanel';
import { getProcessElementByID, getElementsOfType } from '../../utils/util';
import { validatePositiveInteger } from './facilis/validator';

export default class ConnectorPanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'connector');
    this._init();
    this._initEvents();
  }

  // getters methods
  get connectorNameInput() {
    return this._connectorNameInput.value.trim();
  }

  get connectorTypeSelect() {
    return this._connectorTypeSelect.value;
  }

  get connectorApiaSelect() {
    return this._connectorApiaSelect.value.toUpperCase();
  }

  get connectorExpCondition() {
    return this._connectorExpCondition.conditionArea;
  }

  get connectorDirectionSelect() {
    return this._connectorDirectionSelect.value;
  }

  get connectorExecOrder() {
    return this._connectorExecOrder.value;
  }

  // setters methods
  set connectorNameInput(value) {
    this._connectorNameInput.value = value;
  }

  set connectorTypeSelect(value) {
    this._connectorTypeSelect.value = value;
  }

  set connectorApiaSelect(value) {
    this._connectorApiaSelect.value = value;
  }

  set connectorExpCondition(value) {
    this._connectorExpCondition.conditionArea = value;
  }

  set connectorDirectionSelect(value) {
    this._connectorDirectionSelect.value = value;
  }

  set connectorExecOrder(value) {
    this._connectorExecOrder.value = value;
  }

  _init() {
    this.CONNECTOR_PANEL = {
      EXPRESSION: {
        nameId: 'connector_exp_condition',
        label: i18next.t('connectorPropsPanel.txtProDesConnectorPanelExpressionLabel'),
      },
    };
    const { EXPRESSION } = this.CONNECTOR_PANEL;
    this._connectorNameInput = this.panel.querySelector('#connector_name_input');
    this._connectorDirectionSelect = this.panel.querySelector('#connector_direction_select');
    this._connectorTypeSelect = this.panel.querySelector('#connector_type_select');
    this._connectorApiaSelect = this.panel.querySelector('#connector_apia_select');
    this._wrapConnectorDoc = this._documentationInput.parentNode.parentNode;
    this._wrapConnectorType = this._connectorTypeSelect.parentNode.parentNode;
    this._wrapConnectorDirection = this._connectorDirectionSelect.parentNode.parentNode;
    this._wrapConnectorApia = this._connectorApiaSelect.parentNode.parentNode;
    this._connectorExpCondition = this._includeComponent(
      'condition',
      this._wrapCod,
      this._wrapConnectorType,
      'afterend',
      EXPRESSION,
    );
    this._connectorExecOrder = this.panel.querySelector('#connector_execution_order');
    this._wrapConnectorExecOrder = this._connectorExecOrder.parentNode.parentNode;
    this._changeVisibility(this._connectorExpCondition.wrapper, false);
    this._changeVisibility(this._wrapConnectorExecOrder, false);
  }

  _initEvents() {
    this._connectorTypeSelect.addEventListener('input', () => {
      this._onConnectorTypeSelectChanged();
    });

    this._connectorDirectionSelect.addEventListener('input', () => {
      this._onConnectorDirectionSelectChanged();
    });

    this._connectorApiaSelect.addEventListener('input', () => {
      this._onConnectorApiaSelectChanged();
    });

    this._connectorExpCondition.on('change_condition', (condition) => {
      this._onConnectorExpressionCondChanged(condition);
    });

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onConnectorNameChanged();
      },
      this._connectorNameInput,
      'input',
      200,
    );

    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onConnectorExecutionOrderChanged();
      },
      this._connectorExecOrder,
      'input',
      200,
    );
  }

  _transitionComponentsAccordingElement(from, to) {
    let module = '';
    const typeFrom = from.elementType;
    switch (typeFrom) {
      case Config.START_EVENT:
      case Config.INTERMEDIATE_EVENT: {
        module = 2;
        break;
      }
      case Config.TASK:
      case Config.SUBPROCESS: {
        module = 1;
        if (to.elementType === Config.INTERMEDIATE_EVENT) module = 2;
        break;
      }
      case Config.GATEWAY: {
        module = 2;
        if (from.type === 'exclusive' || from.type === 'inclusive') module = 3;
        break;
      }
      default:
        break;
    }
    return module;
  }

  _haveGatewayMultipleConnectors(id) {
    const connectors = getElementsOfType(Config.CONNECTOR, this.editor.elements);
    const transitions = connectors.filter((conn) => {
      return conn.type === Config.CONNECTOR_TYPE_TRANSITION;
    });
    let count = 0;
    transitions.forEach((transition) => {
      if (parseInt(transition.from, 10) === parseInt(id, 10)) count++;
    });
    return count > 1;
  }

  _showModuleTransition(module, from) {
    this._changeVisibility(this._wrapConnectorType, true);
    this._changeVisibility(this._wrapConnectorApia, true);
    this._changeVisibility(this._wrapConnectorDoc, true);
    this._changeVisibility(this._connectorExpCondition.wrapper, false);
    this._changeVisibility(this._wrapConnectorDirection, false);
    this._changeVisibility(this._wrapConnectorExecOrder, false);
    switch (module) {
      case 1: {
        this._connectorTypeSelect.disabled = false;
        this._connectorApiaSelect.disabled = false;
        this.connectorTypeSelect = 'none';
        this.connectorApiaSelect = 'N';
        break;
      }
      case 2: {
        this._connectorTypeSelect.disabled = true;
        this._connectorApiaSelect.disabled = false;
        this.connectorTypeSelect = 'none';
        this.connectorApiaSelect = 'N';
        this._connectorTypeSelect.disabled = true;
        break;
      }
      case 3: {
        this._connectorTypeSelect.disabled = true;
        this._connectorApiaSelect.disabled = false;
        this.connectorApiaSelect = 'N';
        this._changeVisibility(this._connectorExpCondition.wrapper, true);
        if (
          from.type === 'exclusive'
          && this._haveGatewayMultipleConnectors(from.id)
          && Config.ENV !== 'Offline'
        ) {
          this._changeVisibility(this._wrapConnectorExecOrder, true);
        }
        break;
      }
      default:
        break;
    }
  }

  show(el) {
    const elementFrom = getProcessElementByID(el.from, this.editor.elements);
    const elementTo = getProcessElementByID(el.to, this.editor.elements);

    this.connectorNameInput = el.connectorName || '';
    this.documentationInput = el.documentation;

    if (el.type === Config.CONNECTOR_TYPE_TRANSITION) {
      const module = this._transitionComponentsAccordingElement(elementFrom, elementTo);
      this._showModuleTransition(module, elementFrom);
      this.connectorTypeSelect = module !== 3 ? el.kind || 'none' : 'expression';
      this.connectorExpCondition = el.expression || '';
      this.connectorApiaSelect = el.connectorClass || 'N';
      const executionOrder = (el.executionOrder !== undefined) ? el.executionOrder : '';
      this.connectorExecOrder = executionOrder;
      if (module === 1 && el.kind === 'expression') {
        this._changeVisibility(this._connectorExpCondition.wrapper, true);
      }
    } else {
      this.connectorDirectionSelect = el.direction || '';
      const isInputOrOutput = this._isDataInputOutput(el.from, el.to);
      this._connectorDirectionSelect.disabled = isInputOrOutput;
      this._changeVisibility(this._wrapConnectorType, false);
      this._changeVisibility(this._wrapConnectorApia, false);
      this._changeVisibility(this._wrapConnectorDirection, true);
      this._changeVisibility(this._wrapConnectorDoc, true);
      this._changeVisibility(this._connectorExpCondition.wrapper, false);
      this._changeVisibility(this._wrapConnectorExecOrder, false);
    }
    this._changeVisibility(this._panel, true);
  }

  _isDataInputOutput(fromId, toId) {
    const from = getProcessElementByID(fromId, this.editor.elements);
    const to = getProcessElementByID(toId, this.editor.elements);
    return (
      from.elementType === Config.DATA_INPUT
      || from.elementType === Config.DATA_OUTPUT
      || to.elementType === Config.DATA_INPUT
      || to.elementType === Config.DATA_OUTPUT
    );
  }

  _onConnectorNameChanged() {
    if (this.element) {
      const { elementType, connectorName } = this.element;
      const actualName = elementType === Config.CONNECTOR ? connectorName : undefined;
      if (actualName !== this.connectorNameInput && elementType === Config.CONNECTOR) {
        this.element.connectorName = this.connectorNameInput;
      }
    }
  }

  _onConnectorTypeSelectChanged() {
    const condition = this.connectorTypeSelect === 'expression';
    this.element.kind = this.connectorTypeSelect;
    if (!condition && this.element.expression) delete this.element.properties.expression;
    this._changeVisibility(this._connectorExpCondition.wrapper, condition);
    this._changeVisibility(this._wrapConnectorDoc, condition);
  }

  _onConnectorExpressionCondChanged(condition) {
    this.element.expression = condition;
  }

  _onConnectorApiaSelectChanged() {
    this.element.connectorClass = this.connectorApiaSelect;
  }

  _onConnectorDirectionSelectChanged() {
    this.element.direction = this.connectorDirectionSelect;
  }

  _onConnectorExecutionOrderChanged() {
    if (this.element) {
      const actual = this.element.executionOrder;
      if (this.connectorExecOrder !== '') {
        const isValid = validatePositiveInteger(this.connectorExecOrder, 0);
        this.connectorExecOrder = !isValid ? '0' : parseInt(this.connectorExecOrder, 10);
      } else {
        this.connectorExecOrder = '';
      }
      if (actual !== this.connectorExecOrder) {
        this.element.executionOrder = parseInt(this.connectorExecOrder, 10);
      }
    }
  }
}
