import Config from 'config';
import Element from './element';

export default class Group extends Element {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(Config.GROUP, id, viewport, x, y, properties, isMovable, [
      Config.DELETE,
      Config.TEXT,
    ]);

    this.resizable = true;

    if (!properties) {
      this._properties.size = {
        width: Config.GROUP_SIZE.width,
        height: Config.GROUP_SIZE.height,
      };
      this._properties.name = '';
    }
    this.drawElement();
    this.initElement();
  }

  get name() {
    return this._properties.name;
  }

  set name(value) {
    this.emit('element_onpropertychange');
    this._properties.name = value;
    this._updateNameLabel();
  }

  drawElement() {
    const groupBox = this._viewport
      .rect(0, 0, this._properties.size.width, this._properties.size.height, 8)
      .attr({ class: 'group__box' });

    const groupHotSpot = this._viewport
      .rect(
        5,
        5,
        this._properties.size.width - 20,
        this._properties.size.height - 20,
        8,
      )
      .attr({ class: 'group__box__hotspot' });

    const group = groupBox.appendTo(this._viewport);
    const bounds = group.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    const label = this.drawLabel(this.name, 'group__text', -20, bounds);

    this._graphic = this._viewport
      .g(selectionBox, group, groupHotSpot, label)
      .attr({ class: 'group', id: `${Config.GROUP}_${this._properties.id}` })
      .mousedown(this.onElementMouseDown);

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }
  }

  onResizingStart() {
    this._contextMenu.hide();
  }

  onResizing(width, height) {
    this._width = width;
    this._height = height;

    this._selectionBox.attr({
      width: this._width + this.SELECTSIZE,
      height: this._height + this.SELECTSIZE,
    });
    const el = this._graphic.select('.group__box');
    el.attr({
      width: this._width,
      height: this._height,
    });
    const hotspot = this._graphic.select('.group__box__hotspot');
    hotspot.attr({
      width: Math.abs(this._width - 20),
      height: Math.abs(this._height - 20),
    });

    const label = this._graphic.select('.group__text');
    label.attr({ x: this._width / 2 });
  }

  onResizeEnd() {
    // on element resize end
  }

  _updateNameLabel() {
    const label = this._graphic.select('.group__text');
    // set text
    label.attr('text', this.name);
    // reposition label
    label.attr('x', Math.round(this._properties.size.width / 2));
  }
}
