import Component from './Component';

export default class DateComponent extends Component {
  constructor(nameId, panel, label, fullDate = undefined) {
    super('date', nameId, panel, label);
    this._init(fullDate);
  }

  _init(fullDate) {
    const dateElements = this.wrapper.querySelectorAll('.date input');
    const timeElements = this.wrapper.querySelectorAll('.time input');
    [this._year, this._month, this._day] = dateElements;
    [this._hour, this._minute] = timeElements;
    this._actual = new Date();

    if (fullDate) {
      const { date, time } = fullDate;
      this.year = date.year;
      this.month = date.month;
      this.day = date.day;

      this.hour = time.hour;
      this.minutes = time.minutes;
    }

    dateElements.forEach((element) => {
      element.addEventListener('change', () => {
        this._onValidateDateTime(element);
        if (this._isNotEmpty()) this.emit('change_date', this.date);
      });
      // element.addEventListener('input', () => {
      // });
    });

    timeElements.forEach((element) => {
      element.addEventListener('change', () => {
        this._onValidateDateTime(element);
        if (this._isNotEmpty()) this.emit('change_date', this.date);
      });
      // element.addEventListener('input', () => {
      //   if (this._isNotEmpty()) this.emit('change_date', this.date);
      // });
    });
  }

  set date(value) {
    const { date, time } = value;
    this.year = date.year;
    this.month = date.month;
    this.day = date.day;

    this.hour = time.hour;
    this.minutes = time.minutes;
  }

  get actualYear() {
    return this._actual.getFullYear();
  }

  get actualMonth() {
    return this._actual.getMonth() + 1;
  }

  get actualDay() {
    return this._actual.getDate();
  }

  get actualHour() {
    return this._actual.getHours();
  }

  get actualMinutes() {
    return this._actual.getMinutes();
  }

  get year() {
    const year = this._year.value;
    return year === '' ? -1 : parseInt(year, 10);
  }

  get month() {
    const month = this._month.value;
    return month === '' ? -1 : parseInt(month, 10);
  }

  get day() {
    const day = this._day.value;
    return day === '' ? -1 : parseInt(day, 10);
  }

  get hour() {
    const hour = this._hour.value;
    return hour === '' ? -1 : parseInt(hour, 10);
  }

  get minutes() {
    const minutes = this._minute.value;
    return minutes === '' ? -1 : parseInt(minutes, 10);
  }

  get date() {
    if (!this._isNotEmpty()) {
      this._onValidateDateTime(undefined);
    }
    return new Date(
      this.year,
      this.month - 1,
      this.day,
      this.hour,
      this.minutes,
    );
  }

  set year(value) {
    this._year.value = value;
  }

  set month(value) {
    this._month.value = this.format(value);
  }

  set day(value) {
    this._day.value = this.format(value);
  }

  set hour(value) {
    this._hour.value = this.format(value);
  }

  set minutes(value) {
    this._minute.value = this.format(value);
  }

  format(value) {
    if (value !== '') {
      value = parseInt(value, 10);
      if (value < 10) return `0${value}`;
    }
    return value;
  }

  clean() {
    this.year = '';
    this.month = '';
    this.day = '';
    this.hour = '';
    this.minutes = '';
  }

  _getNumberOfDay(month, year) {
    switch (month) {
      case 1:
      case 3:
      case 5:
      case 7:
      case 8:
      case 10:
      case 12:
        return 31;
      case 2: {
        if (this._isLeapYear(year)) return 29;
        return 28;
      }
      default:
        return 30;
    }
  }

  _isLeapYear(year) {
    return (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0;
  }

  _isNotEmpty() {
    return (
      this.year !== -1
      && this.month !== -1
      && this.day !== -1
      && this.hour !== -1
      && this.minutes !== -1
    );
  }

  _validateYear() {
    if (this.year < this.actualYear) {
      this.year = this.actualYear;
    }
  }

  _validateMonth() {
    if (
      this.year === this.actualYear
      && (this.month < this.actualMonth || this.month > 12)
    ) {
      this.month = this.actualMonth;
    }
    if (this.month > 12 || this.month < 1) this.month = 1;
    this.month = this.format(this.month);
  }

  _validateDay() {
    const topDay = this._getNumberOfDay(this.month, this.year);
    const minDay = 1;
    if (
      this.year === this.actualYear
      && this.month === this.actualMonth
      && (this.day < this.actualDay || this.day > topDay)
    ) {
      this.day = this.actualDay;
    }
    if (this.day > topDay) this.day = topDay;
    if (this.day < minDay) this.day = minDay;
    this.day = this.format(this.day);
  }

  _validateHour() {
    if (
      this.year === this.actualYear
      && this.month === this.actualMonth
      && this.day === this.actualDay
      && (this.hour < this.actualHour || this.hour > 23)
    ) {
      this.hour = this.actualHour;
    }
    this.hour = this.format(this.hour);
  }

  _validateMinutes() {
    if (
      this.year === this.actualYear
      && this.month === this.actualMonth
      && this.day === this.actualDay
      && this.hour === this.actualHour
      && (this.minutes < this.actualMinutes || this.minutes > 59)
    ) {
      this.minutes = this.actualMinutes;
    }
    this.minutes = this.format(this.minutes);
  }

  _onValidateDateTime(element) {
    const value = element ? element.value : '';
    if (value !== '') {
      this._validateYear();
      this._validateMonth();
      this._validateDay();
      this._validateHour();
      this._validateMinutes();
    }
  }
}
