import Config from 'config';
import TemplatePanel from './templatePanel';
import { normalizeText } from './facilis/validator';

export default class GroupPanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'group');
    this._init();
  }

  _init() {
    // Variables Initilizations
    this._nameInput = this._panel.querySelector('#group_name_input');
    // Events Initializations
    this._setTimeoutOnPropertyChanged(
      undefined,
      () => {
        this._onNameInputChanged();
      },
      this._nameInput,
      'input',
    );
  }

  get nameInput() {
    return this._nameInput.value;
  }

  set nameInput(value) {
    this._nameInput.value = value;
  }

  show(el) {
    this.nameInput = el.name;
    this.documentationInput = el.documentation;
    this._changeVisibility(this._panel, true);
  }

  _onNameInputChanged() {
    if (this.element) {
      const actual = this.element.elementType === Config.GROUP
        ? this.element.name
        : undefined;
      this.nameInput = normalizeText(this.nameInput, /["]/g);
      if (actual !== this.nameInput) this.element.name = this.nameInput;
    }
  }
}
