import EventEmitter from 'eventemitter3';

export default class ButtonBar extends EventEmitter {
  constructor(editor) {
    super();
    this._editor = editor;
    // Undo/Redo Bar
    document.getElementById('undo_btn').addEventListener(
      'click',
      () => {
        editor.undoAction();
      },
      false,
    );

    document.getElementById('redo_btn').addEventListener(
      'click',
      () => {
        editor.redoAction();
      },
      false,
    );

    // Alignment
    document.getElementById('align_left_btn').addEventListener(
      'click',
      () => {
        editor.alignSelectionLeft();
      },
      false,
    );

    document.getElementById('align_right_btn').addEventListener(
      'click',
      () => {
        editor.alignSelectionRight();
      },
      false,
    );

    document.getElementById('align_top_btn').addEventListener(
      'click',
      () => {
        editor.alignSelectionTop();
      },
      false,
    );

    document.getElementById('align_bottom_btn').addEventListener(
      'click',
      () => {
        editor.alignSelectionBottom();
      },
      false,
    );

    document.getElementById('align_horizontal_btn').addEventListener(
      'click',
      () => {
        editor.alignSelectionHorizontal();
      },
      false,
    );

    document.getElementById('align_vertical_btn').addEventListener(
      'click',
      () => {
        editor.alignSelectionVertical();
      },
      false,
    );

    document.getElementById('dist_horizontal_btn').addEventListener(
      'click',
      () => {
        editor.distSelectionHorizontal();
      },
      false,
    );

    document.getElementById('dist_vertical_btn').addEventListener(
      'click',
      () => {
        editor.distSelectionVertical();
      },
      false,
    );

    document.getElementById('grid_btn').addEventListener('click', (event) => {
      const element = event.currentTarget;
      element.setAttribute(
        'data-state',
        element.getAttribute('data-state') === 'on' ? 'off' : 'on',
      );
      if (element.getAttribute('data-state') === 'on') {
        editor.showGrid(true);
      } else {
        editor.showGrid(false);
      }
    });
  }
}
