import Config from 'config';
import {
  getPool,
  getAllLanes,
  getPreviousLaneByYPos,
  getNextLaneByYPos,
} from '../utils/util';

export default class LaneManager {
  constructor(editor) {
    this._editor = editor;
    this._laneElements = [];
    this._pool = null;
    this._nextLane = null;
    this._DEFAULT_LANE_HEIGHT = 160;
    this._LANE_MIN_HEIGHT = 160;
    this._LANE_NAME_BAR_WIDTH = 30;
  }

  createLane(pool) {
    // id is correct otherwise pool and first lane share id, need diff id for undo
    const id = ++this._editor.idIndex;
    const totalLanes = getAllLanes(this._editor.elements).length;
    let poolHeight = pool.height;
    let newElement = null;
    if (totalLanes > 0) {
      newElement = this._editor.createElement(
        Config.LANE,
        pool.x,
        pool.y + poolHeight,
        {
          id,
          size: {
            width: pool.width - this._LANE_NAME_BAR_WIDTH,
            height: this._DEFAULT_LANE_HEIGHT,
          },
        },
        false,
      );
      // set pool height + new lane
      poolHeight += this._DEFAULT_LANE_HEIGHT;
      pool.resize(pool.width, poolHeight);
    } else {
      // create lane when no other lanes present
      newElement = this._editor.createElement(
        Config.LANE,
        pool.x,
        pool.y,
        {
          id,
          size: {
            width: pool.width - this._LANE_NAME_BAR_WIDTH,
            height: pool.height,
          },
        },
        false,
      );
    }
    return newElement;
  }

  onLaneDeleted(deletedLane) {
    const allLanes = getAllLanes(this._editor.elements);
    const previousLane = getPreviousLaneByYPos(allLanes, deletedLane.y);
    // resize previous lane in pool
    if (previousLane) {
      previousLane.resizeHeight(previousLane.height + deletedLane.height);
      // for history
      previousLane.properties.size = {
        width: previousLane.width,
        height: previousLane.height,
      };
    }
    // reposition lanes in pool again using pool's y.
    const pool = getPool(this._editor.elements);
    for (let i = 0; i < allLanes.length; i++) {
      if (i > 0) {
        allLanes[i].y = allLanes[i - 1].y + allLanes[i - 1].height;
      } else {
        allLanes[i].y = pool.y;
      }
    }
  }

  resizeStart(lane) {
    this._laneElements = getAllLanes(this._editor.elements);
    this._pool = getPool(this._editor.elements);
    this._nextLane = getNextLaneByYPos(this._laneElements, lane.y);
  }

  resize(lane, height) {
    lane.resizeHeight(height);
    let lanesHeight = 0;
    let otherLanesHeight = 0;
    const len = this._laneElements.length;
    for (let i = 0; i < len; i++) {
      const el = this._laneElements[i];
      const h = el.height;
      lanesHeight += h;
      if (el !== lane && el !== this._nextLane) {
        otherLanesHeight += h;
      }
    }
    const nextLaneHeight = this._nextLane.height + (this._pool.height - lanesHeight);
    if (len === 1 || lane === this._laneElements[len - 1]) {
      // one lane and last lane
      lane.maxHeight = Number.POSITIVE_INFINITY;
      this._pool.resize(lane.width + lane._nameBoxWidth, lanesHeight);
    } else if (len > 1) {
      // more than one except last and first ones
      lane.maxHeight = this._pool.height - otherLanesHeight - this._LANE_MIN_HEIGHT;
      this._nextLane.y = lane.y + lane.height;
      this._nextLane.resizeHeight(nextLaneHeight);
    }
  }

  resizeEnd() {
    // set props for history
    const len = this._laneElements.length;
    for (let i = 0; i < len; i++) {
      this._laneElements[i].properties.size = {
        height: this._laneElements[i].height,
        width: this._laneElements[i].width,
      };
    }
    this._laneElements = [];
    this._pool = null;
    this._nextLane = null;
  }
}
