/* eslint-disable no-loop-func */
/* eslint-disable no-restricted-syntax */
/* eslint-disable new-cap */
import Config from 'config';
import axios from 'axios';
import sanitize from 'sanitize-filename';
import i18next from 'i18next';
import Documentation from './documentation';
import { getElementsOfType, downloadPDF, getElementsByConnectedType } from '../utils/util';
import 'jspdf-autotable';

export default class DocManager extends Documentation {
  constructor(editor) {
    super();
    this._editor = editor;
    this._h1 = 22;
    this._h2 = 18;
    this._PAGE_SIZE = 'a4';
  }

  async generateProcessDocumentation() {
    // downloads PDF directly to browser with png attached

    // lazy loading webpack import
    const module = await import(/* webpackChunkName: "jspdf" */ 'jspdf');
    const doc = new module.default('p', 'pt', this._PAGE_SIZE, true);

    const parser = new DOMParser();
    // get event info data from apia
    const eventData = await axios.get(Config.CLASSES_DESCRIPTION_URL);
    const xmlEventsDoc = parser.parseFromString(eventData.data, 'text/xml');

    // Get Process Name from Apia
    let proName;
    let proDesc;
    if (Config.ENV === 'Production') {
      const iframe = window.top.document.querySelectorAll('iframe.active')[0];
      const iframeDoc = iframe.contentDocument || iframe.contentWindow.document;
      proName = iframeDoc.querySelector('#dataGenProTitle').innerText || 'Apia Process';
      proDesc = iframeDoc.querySelector('#txtDesc').innerText || '';
    } else {
      proName = 'Apia Process';
      proDesc = 'Process description text from Apia';
    }

    // Page position and margins
    const margins = 40;
    let offsetY = 0;
    let taskCounter = 0;
    let taskDetailCounter = 0;
    let formCounter = 0;

    doc.setFontSize(9);
    doc.text(i18next.t('doc.proDesDocHeader'), margins, margins);

    // Process Title - split text if needed
    let line = doc.internal.pageSize.getHeight() / 2; // Line height to start text at
    const lineHeight = 32;
    const wrapWidth = 380;
    doc.setFontSize(32);
    const splitText = doc.splitTextToSize(proName, wrapWidth);
    for (let i = 0; i < splitText.length; i++) {
      // loop thru each line and increase
      const xOffset = doc.internal.pageSize.width / 2
        - doc.getStringUnitWidth(splitText[i]) * (doc.internal.getFontSize() / 2);
      doc.text(splitText[i], xOffset, line);
      line = lineHeight + line;
    }

    // Process Description
    doc.autoTable({
      startY: line,
      theme: 'grid',
      headStyles: {
        fontSize: 16,
        fontStyle: 'normal',
        halign: 'center',
        fillColor: '#ffffff',
        textColor: '#000000',
      },
      margin: {
        top: margins,
        right: margins,
        bottom: margins,
        left: margins,
      },
      head: [[proDesc]],
      body: [],
    });

    // Add Map IMAGE
    // canvas w/ map image
    const canvas = await this.getPNGCanvas();
    doc.addPage(this._PAGE_SIZE, 'l');
    const width = doc.internal.pageSize.getWidth();
    const height = doc.internal.pageSize.getHeight();
    const widthRatio = width / canvas.width;
    const heightRatio = height / canvas.height;
    const ratio = widthRatio > heightRatio ? heightRatio : widthRatio;

    doc.addImage(
      canvas.toDataURL('image/png'),
      'png',
      (width - canvas.width * ratio) / 2,
      0,
      canvas.width * ratio,
      canvas.height * ratio,
      undefined,
      'FAST',
    );

    // WORKFLOW Documentation
    doc.addPage(this._PAGE_SIZE, 'p');

    // Process Events
    doc.setFontSize(this._h1);
    doc.setFontStyle('bold');
    doc.text(i18next.t('doc.proDesWorkProLbl'), margins, 80);

    offsetY = 80 + 20;

    if (this._editor.workflow.properties.documentation.text) {
      doc.setFontSize(10);
      doc.setFontStyle('normal');
      doc.autoTable({
        startY: offsetY,
        margin: { left: margins },
        body: [
          [
            {
              content: this._editor.workflow.properties.documentation.text,
              theme: 'plain',
              styles: { fontSize: 10, halign: 'left', fillColor: '#ffffff' },
            },
          ],
        ],
      });
      offsetY = doc.previousAutoTable.finalY + 30;
    }

    // Process Events
    doc.setFontSize(12);
    doc.setFontStyle('bold');
    const procEvents = this._getEvents(this._editor.workflow.events, xmlEventsDoc);
    if (procEvents.length > 0) {
      doc.text(i18next.t('doc.proDesProEvtLbl'), margins, offsetY, {
        align: 'left',
      });

      offsetY += 18;

      doc.autoTable({
        startY: offsetY,
        theme: 'grid',
        headStyles: { fillColor: '#7FDBFF', textColor: '#000000' },
        margin: {
          top: 100,
          right: margins,
          bottom: 0,
          left: margins,
        },
        head: [
          [
            i18next.t('doc.proDesProEvtEvtHeaderLbl'),
            i18next.t('doc.proDesProBizClsHeaderLbl'),
            i18next.t('doc.proDesProDescHeaderLbl'),
          ],
        ],
        body: this._getEvents(this._editor.workflow.events, xmlEventsDoc),
      });

      offsetY = doc.previousAutoTable.finalY + 60;
    }

    offsetY += 30;
    // ---------------------------------------
    // Pool Documentation
    // ---------------------------------------
    const poolArray = getElementsOfType(Config.POOL, this._editor.elements);
    if (poolArray.length > 0) {
      doc.setFontSize(this._h1);
      doc.setFontStyle('bold');
      // Pool Title
      doc.text(i18next.t('doc.proDesPoolLbl'), margins, offsetY);
      offsetY += 10;

      // Pool details
      doc.autoTable({
        startY: offsetY,
        theme: 'grid',
        margin: {
          top: margins,
          right: margins,
          bottom: margins,
          left: margins,
        },
        body: [
          [i18next.t('doc.proDesPoolDetailsNameLbl'), poolArray[0].properties.name],
          [
            i18next.t('doc.proDesPoolDetailsDocLbl'),
            poolArray[0].properties.documentation ? poolArray[0].properties.documentation.text : '',
          ],
        ],
        columnStyles: {
          0: {
            cellWidth: 160,
            fillColor: '#DDDDDD',
            textColor: '#000000',
            fontStyle: 'bold',
          },
        },
      });
      offsetY = doc.previousAutoTable.finalY + 60;
    }

    // ---------------------------------------
    // Lanes Documentation
    // ---------------------------------------
    const lanesArray = getElementsOfType(Config.LANE, this._editor.elements);
    if (lanesArray.length > 0) {
      doc.setFontSize(this._h1);
      doc.setFontStyle('bold');
      // lanes Title
      doc.text(i18next.t('doc.proDesLanesLbl'), margins, offsetY);
      offsetY += 10;
      // Lanes details
      doc.autoTable({
        startY: offsetY,
        headStyles: { fillColor: '#DDDDDD', textColor: '#000000' },
        margin: {
          top: margins,
          right: margins,
          bottom: margins,
          left: margins,
        },
        head: [
          [i18next.t('doc.proDesLanesDetailsNameLbl'), i18next.t('doc.proDesLanesDetailsDocLbl')],
        ],
        body: this._getLanes(lanesArray),
      });
      offsetY = doc.previousAutoTable.finalY + 60;
    }

    // ---------------------------------------
    // TASKS Documentation
    // ---------------------------------------
    doc.setFontSize(this._h1);
    doc.setFontStyle('bold');
    // Task Title
    doc.text(i18next.t('doc.proDesTasksLbl'), margins, offsetY);
    offsetY += 30;

    const taskArray = getElementsOfType(Config.TASK, this._editor.elements);
    // sort task array alphabetically by title
    taskArray.sort((a, b) => (a.properties.title > b.properties.title ? 1 : -1));

    // LOOP all TASKS
    for await (const task of taskArray) {
      if (taskCounter > 0) {
        doc.addPage(this._PAGE_SIZE, 'p');
        offsetY = margins + 20;
      }
      formCounter = 1;
      taskDetailCounter = 1;
      const { title } = task;
      const taskGroupArray = task.properties.groups;
      const taskEventsArray = task.properties.events;
      const taskStatesArray = task.properties.states;
      const taskBoundaryEvents = task.properties.boundaryEvents;

      let role;
      if (task.properties.role) {
        role = task.properties.role.name;
      } else {
        role = '';
      }

      // Task Title Name
      doc.autoTable({
        startY: offsetY,
        theme: 'grid',
        headStyles: {
          fontSize: this._h2,
          fontWeight: 'bold',
          fillColor: '#ffffff',
          textColor: '#000000',
        },
        margin: {
          top: margins,
          right: margins,
          bottom: margins,
          left: margins,
        },
        head: [[`${++taskCounter}. ${title}`]],
        body: [],
      });

      offsetY = doc.previousAutoTable.finalY + 10;

      // task documentation from map props
      if (task.properties.documentation) {
        doc.autoTable({
          startY: offsetY,
          margin: { left: margins },
          body: [
            [
              {
                content: task.properties.documentation.text,
                theme: 'plain',
                styles: { fontSize: 10, halign: 'left', fillColor: '#ffffff' },
              },
            ],
          ],
        });
        offsetY = doc.previousAutoTable.finalY + 30;
      }

      // Task Details Table
      offsetY += 18;
      doc.setFontSize(12);
      doc.setFontStyle('bold');
      doc.text(
        `${taskCounter}.${taskDetailCounter} ${i18next.t('doc.proDesTaskDetailsLbl')}:`,
        margins,
        offsetY,
        {
          align: 'left',
        },
      );
      offsetY += 10;
      doc.autoTable({
        startY: offsetY,
        theme: 'grid',
        margin: {
          top: margins,
          right: margins,
          bottom: margins,
          left: margins,
        },
        body: [
          [i18next.t('doc.proDesTaskDetailsNameLbl'), task.properties.name],
          [i18next.t('doc.proDesTaskDetailsTypeLbl'), task.properties.taskType],
          [i18next.t('doc.proDesTaskDetailsConLbl'), task.properties.condition],
          [i18next.t('doc.proDesTaskDetailsRoleLbl'), role],
        ],
        columnStyles: {
          0: {
            cellWidth: 160,
            fillColor: '#DDDDDD',
            textColor: '#000000',
            fontStyle: 'bold',
          },
        },
      });

      offsetY = doc.previousAutoTable.finalY + 18;

      // TASK GROUPS
      if (taskGroupArray) {
        offsetY += 18;
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        // Title
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesTaskGrpLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );

        offsetY += 14;
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          headStyles: { fillColor: '#DDDDDD', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [i18next.t('doc.proDesTaskGrpNameHeader'), i18next.t('doc.proDesTaskGrpCondHeader')],
          ],
          body: this._getTaskGroups(taskGroupArray),
        });
      }

      offsetY = doc.previousAutoTable.finalY + 30;

      // TASK ANNOTATIONS AND DATA OBJECTS
      //  Annotations
      const annotationsArr = getElementsByConnectedType(task, Config.TEXT, this._editor.elements);

      const dataObjectsArr = getElementsByConnectedType(
        task,
        Config.DATA_OBJECT,
        this._editor.elements,
      );

      const dataStoreArr = getElementsByConnectedType(
        task,
        Config.DATA_STORE,
        this._editor.elements,
      );

      const dataInputArr = getElementsByConnectedType(
        task,
        Config.DATA_INPUT,
        this._editor.elements,
      );

      const dataOutputArr = getElementsByConnectedType(
        task,
        Config.DATA_OUTPUT,
        this._editor.elements,
      );

      const doArr = [...dataObjectsArr, ...dataInputArr, ...dataOutputArr];

      if (annotationsArr.length > 0) {
        offsetY += 18;
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        // Title
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesTaskAnnotationLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );

        offsetY += 14;
        // Anotation details
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          body: [
            [i18next.t('doc.proDesPoolDetailsNameLbl'), annotationsArr[0].properties.text || ''],
            [
              i18next.t('doc.proDesPoolDetailsDocLbl'),
              annotationsArr[0].properties.documentation
                ? annotationsArr[0].properties.documentation.text
                : '',
            ],
          ],
          columnStyles: {
            0: {
              cellWidth: 160,
              fillColor: '#DDDDDD',
              textColor: '#000000',
              fontStyle: 'bold',
            },
          },
        });
        offsetY = doc.previousAutoTable.finalY + 30;
      }

      // Data Objects
      if (doArr.length > 0) {
        offsetY += 18;
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        // Title
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesDoLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );
        offsetY += 14;
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          headStyles: { fillColor: '#DDDDDD', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [
              i18next.t('doc.proDesTaskDOTypeHeader'),
              i18next.t('doc.proDesTaskDONameHeader'),
              i18next.t('doc.proDesTaskDOStateHeader'),
              i18next.t('doc.proDesTaskDOCollHeader'),
              i18next.t('doc.proDesTaskDODocsHeader'),
            ],
          ],
          body: this._getTaskDo(doArr),
        });
        offsetY = doc.previousAutoTable.finalY + 30;
      }

      // Data Store
      if (dataStoreArr.length > 0) {
        offsetY += 18;
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        // Title
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesDtaStoreLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );
        offsetY += 14;
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          headStyles: { fillColor: '#DDDDDD', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [
              i18next.t('doc.proDesTaskDOStoreNameHeader'),
              i18next.t('doc.proDesTaskDOStoreStateHeader'),
              i18next.t('doc.proDesTaskDOStoreCapacityHeader'),
              i18next.t('doc.proDesTaskDOStoreLimitHeader'),
              i18next.t('doc.proDesTaskDOStoreDocsHeader'),
            ],
          ],
          body: this._getTaskDtaStore(dataStoreArr),
        });
        offsetY = doc.previousAutoTable.finalY + 60;
      }

      // TASK STATES
      if (taskStatesArray) {
        offsetY += 14;
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesTaskStateLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );

        offsetY += 14;
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          headStyles: { fillColor: '#DDDDDD', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [
              i18next.t('doc.proDesTaskStateEvtHeader'),
              i18next.t('doc.proDesTaskStateNameHeader'),
              i18next.t('doc.proDesTaskStateDescHeader'),
            ],
          ],
          body: this._getTaskStates(taskStatesArray),
        });
      }

      offsetY = doc.previousAutoTable.finalY + 30;

      // Get forms data XML by task id
      const xmlDoc = await this._getFormDataByTaskId(task.properties.id);
      const forms = xmlDoc.getElementsByTagName('form');

      // TASK FORMS
      if (forms.length > 0) {
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        // Title
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesTaskFormsLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );
      }

      // Iterate all task form elements
      for (let i = 0; i < forms.length; i++) {
        offsetY += 18;
        const taskFormData = this._getTaskFormData(forms[i]);

        // Task Form Name Title
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          headStyles: { fillColor: '#ffffff', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [
              `${taskCounter}.${taskDetailCounter}.${formCounter++} ${forms[i].getAttribute(
                'frmTitle',
              )}`,
            ],
          ],
          body: [],
        });

        // Task Forms Table
        doc.autoTable({
          startY: doc.previousAutoTable.finalY,
          theme: 'grid',
          headStyles: { fillColor: '#DDDDDD', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [
              i18next.t('doc.proDesTaskFormsNameHeader'),
              i18next.t('doc.proDesTaskFormsDescHeader'),
            ],
          ],
          body: [[forms[i].getAttribute('frmName'), forms[i].getAttribute('frmDesc')]],
          pageBreak: 'avoid',
        });

        // Form Events Table
        if (taskFormData.taskFormEvents.length > 0) {
          doc.autoTable({
            startY: doc.previousAutoTable.finalY,
            theme: 'grid',
            headStyles: { fillColor: '#7FDBFF', textColor: '#000000' },
            margin: {
              top: margins,
              right: margins,
              bottom: margins,
              left: margins,
            },
            head: [
              [
                i18next.t('doc.proDesTaskFormsEvtNameHeader'),
                i18next.t('doc.proDesTaskFormsEvtTypeHeader'),
                i18next.t('doc.proDesTaskFormsEvtDescHeader'),
              ],
            ],
            body: taskFormData.taskFormEvents,
            pageBreak: 'avoid',
          });
        }

        // Task Fields Table
        if (taskFormData.formFieldAttributes.length > 0) {
          doc.autoTable({
            startY: doc.previousAutoTable.finalY,
            theme: 'grid',
            headStyles: { fillColor: '#DDDDDD', textColor: '#000000' },
            margin: {
              top: margins,
              right: margins,
              bottom: margins,
              left: margins,
            },
            head: [
              [
                i18next.t('doc.proDesTaskFormsFldsNameHeader'),
                i18next.t('doc.proDesTaskFormsFldsFieldHeader'),
                i18next.t('doc.proDesTaskFormsFldsTypeHeader'),
                i18next.t('doc.proDesTaskFormsFldsEditHeader'),
                i18next.t('doc.proDesTaskFormsFldsReqHeader'),
                i18next.t('doc.proDesTaskFormsFldsGridHeader'),
                i18next.t('doc.proDesTaskFormsFldsDescHeader'),
              ],
            ],
            body: taskFormData.formFieldAttributes,
            pageBreak: 'avoid',
          });
        }

        // Task Fields Events Table
        if (taskFormData.taskFieldsEvents.length > 0) {
          doc.autoTable({
            startY: doc.previousAutoTable.finalY,
            theme: 'grid',
            headStyles: { fillColor: '#7FDBFF', textColor: '#000000' },
            margin: {
              top: margins,
              right: margins,
              bottom: margins,
              left: margins,
            },
            head: [['Field', 'Event Name', 'Event Type', 'Description']],
            body: taskFormData.taskFieldsEvents,
            pageBreak: 'avoid',
          });
        }
        offsetY = doc.previousAutoTable.finalY + 30;
      }

      // TASK EVENTS
      if (taskEventsArray) {
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        // Title
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesTaskEventsLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );
        offsetY += 14;
        // Task Events Table
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          headStyles: { fillColor: '#7FDBFF', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [
              i18next.t('doc.proDesTaskEventsEvtHeader'),
              i18next.t('doc.proDesTaskEventsBizHeader'),
              i18next.t('doc.proDesTaskEventsDescHeader'),
            ],
          ],
          body: this._getEvents(taskEventsArray, xmlEventsDoc),
          pageBreak: 'avoid',
        });
        offsetY = doc.previousAutoTable.finalY + 30;
      }

      // TASK BOUNDARY EVENTS
      if (taskBoundaryEvents.length > 0) {
        doc.setFontSize(12);
        doc.setFontStyle('bold');
        // Title
        doc.text(
          `${taskCounter}.${++taskDetailCounter} ${i18next.t('doc.proDesTaskBoundLbl')}:`,
          margins,
          offsetY,
          {
            align: 'left',
          },
        );
        offsetY += 14;
        // Task Events Table
        doc.autoTable({
          startY: offsetY,
          theme: 'grid',
          headStyles: { fillColor: '#7FDBFF', textColor: '#000000' },
          margin: {
            top: margins,
            right: margins,
            bottom: margins,
            left: margins,
          },
          head: [
            [
              i18next.t('doc.proDesTaskBoundEvtHeader'),
              i18next.t('doc.proDesTaskBoundTypeHeader'),
              i18next.t('doc.proDesTaskBoundDtlHeader'),
            ],
          ],
          body: this._getBoundaryEvents(taskBoundaryEvents),
          pageBreak: 'avoid',
        });
        offsetY = doc.previousAutoTable.finalY + 30;
      }
    }

    offsetY += 30;

    // ---------------------------------------
    // Gateway Documentation
    // ---------------------------------------
    const gatewayArray = getElementsOfType(Config.GATEWAY, this._editor.elements);
    if (gatewayArray.length > 0) {
      doc.setFontSize(this._h1);
      doc.setFontStyle('bold');
      // Gateway Title
      doc.text(i18next.t('doc.proDesGatewayLbl'), margins, offsetY);
      offsetY += 10;
      // LOOP all TASKS
      doc.autoTable({
        startY: offsetY,
        theme: 'grid',
        headStyles: { fillColor: '#7FDBFF', textColor: '#000000' },
        margin: {
          top: margins,
          right: margins,
          bottom: margins,
          left: margins,
        },
        head: [
          [
            i18next.t('doc.proDesGatewaysNameHeader'),
            i18next.t('doc.proDesGatewaysTypeHeader'),
            i18next.t('doc.proDesGatewaysExecTypeHeader'),
            i18next.t('doc.proDesGatewaysDocumentationHeader'),
          ],
        ],
        body: this._getGatewayDetails(gatewayArray),
      });
      offsetY = doc.previousAutoTable.finalY + 60;
    }

    // -----------------------------------------------------
    // save pdf to browser
    // -----------------------------------------------------
    if (Config.ENV === 'Production') {
      downloadPDF(doc.output('arraybuffer'), `${sanitize(proName)}.pdf`);
    } else {
      doc.save(sanitize(proName));
    }
  }

  _getLanes(lanesArray) {
    const lanesArr = [];
    lanesArray.forEach((lane, index) => {
      lanesArr[index] = [
        lane.properties.name || '',
        lane.properties.documentation ? lane.properties.documentation.text : '',
      ];
    });
    return lanesArr;
  }

  _getTaskDtaStore(dtaStore) {
    const dtaStoreArr = [];
    dtaStore.forEach((dtaObj, index) => {
      dtaStoreArr[index] = [
        dtaObj.properties.name || '',
        dtaObj.properties.state || '',
        dtaObj.properties.capacity || '',
        dtaObj.properties.unlimit || '',
        dtaObj.properties.documentation ? dtaObj.properties.documentation.text : '',
      ];
    });
    return dtaStoreArr;
  }

  _getTaskDo(doArray) {
    const dtaObjectArr = [];
    doArray.forEach((dtaObj, index) => {
      dtaObjectArr[index] = [
        dtaObj.elementType || '',
        dtaObj.properties.name || '',
        dtaObj.properties.state || '',
        dtaObj.properties.collection || 'false',
        dtaObj.properties.documentation ? dtaObj.properties.documentation.text : '',
      ];
    });
    return dtaObjectArr;
  }

  _getGatewayDetails(gatewaysArray) {
    const gatewaysDetails = [];
    gatewaysArray.forEach((gateway, index) => {
      gatewaysDetails[index] = [
        gateway.properties.name || '---',
        gateway.properties.type || '',
        gateway.properties.executionType || '',
        gateway.documentation || '',
      ];
    });
    return gatewaysDetails;
  }

  _getEvents(eventsArray, xmlDoc) {
    const tskEvents = [];
    eventsArray.forEach((event, index) => {
      // console.log(event.busClaId);
      tskEvents[index] = [
        event.name,
        xmlDoc.querySelector(`[*|id='${event.busClaId}']`).getAttribute('name'),
        xmlDoc.querySelector(`[*|id='${event.busClaId}']`).getAttribute('desc'),
      ];
    });
    return tskEvents;
  }

  _getTaskGroups(taskGroupArray) {
    const tskGroups = [];
    taskGroupArray.forEach((group, index) => {
      tskGroups[index] = [group.name, group.condition];
    });
    return tskGroups;
  }

  _getTaskStates(taskStatesArray) {
    const tskStates = [];
    taskStatesArray.forEach((state, index) => {
      tskStates[index] = [state.event, state.name, state.conditionDoc];
    });
    return tskStates;
  }

  _getBoundaryEvents(taskBoundaryEventsArray) {
    const tskBoundEvts = [];
    // let evtDetail;
    taskBoundaryEventsArray.forEach((evt, index) => {
      switch (evt.eventType) {
        case 'timer':
          // evtDetail = evt.dateTimer
          break;
        case 'message':
          // evtDetail = evt.dateTimer
          break;
        default:
          break;
      }
      tskBoundEvts[index] = [evt.eventName, evt.eventType, 'ToDo'];
    });
    return tskBoundEvts;
  }

  async _getFormDataByTaskId(taskId) {
    const parser = new DOMParser();
    let formData;
    if (Config.ENV === 'Development') {
      formData = await axios.get(`${Config.TASK_FORM_DOC}/${taskId}`);
    } else if (Config.ENV === 'Production') {
      formData = await axios.get(`${Config.TASK_FORM_DOC}&taskid=${taskId}`);
    }
    return parser.parseFromString(formData.data, 'text/xml');
  }

  _getTaskFormData(formTag) {
    // get form events
    const taskFormEvents = [];
    // get form field attributes
    const formFieldAttributes = [];
    // get form field events for each field
    const taskFieldsEvents = [];
    let tskFldEvtCounter = 0;
    for (let f = 0; f < formTag.children.length; f++) {
      if (formTag.children[f].nodeName === 'attribute') {
        const attributeField = formTag.children[f];
        formFieldAttributes[f] = [
          attributeField.getAttribute('attLabel'),
          attributeField.getAttribute('fieldtype'),
          attributeField.getAttribute('datatype'),
          attributeField.getAttribute('editable'),
          attributeField.getAttribute('required'),
          attributeField.getAttribute('grid'),
          attributeField.getAttribute('attTooltip'),
        ];
        // Get Task Field Events
        for (let e = 0; e < attributeField.children.length; e++) {
          if (attributeField.children[e].nodeName === 'events') {
            for (let v = 0; v < attributeField.children[e].children.length; v++) {
              taskFieldsEvents[tskFldEvtCounter++] = [
                attributeField.getAttribute('attLabel'),
                attributeField.children[e].children[v].getAttribute('busClaName'),
                attributeField.children[e].children[v].getAttribute('evtName'),
                attributeField.children[e].children[v].getAttribute('busClaDesc'),
              ];
            }
          }
        }
      } else if (formTag.children[f].nodeName === 'events') {
        // Get Form Events
        const events = formTag.children[f];
        for (let c = 0; c < events.children.length; c++) {
          taskFormEvents[c] = [
            events.children[c].getAttribute('busClaName'),
            events.children[c].getAttribute('evtName'),
            events.children[c].getAttribute('busClaDesc'),
          ];
        }
      }
    }
    return {
      taskFormEvents,
      formFieldAttributes,
      taskFieldsEvents,
    };
  }
}
