import Config from 'config';
import TemplatePanel from './templatePanel';
import EventManager from './managers/eventManager';

export default class WorkflowPanel extends TemplatePanel {
  constructor(editor) {
    super(editor, 'workflow');
    this._initElements();
    this._initEventsHandlers();
  }

  _initElements() {
    const wrapper = this._panel.querySelector('#manager_workflow_events');
    this._eventsManager = new EventManager('workflow', wrapper, {
      eventsUrl: Config.WORKFLOW_EVENTS_URL,
      busClassUrl: Config.WORKFLOW_ACTIONS_URL,
      attributesUrl: Config.WORKFLOW_ATTRS_URL,
      bindingsUrl: Config.WORKFLOW_BINDINGS_URL,
    });
  }

  _initEventsHandlers() {
    this._eventsManager.on('change', (data) => this._setEventsWhenManagerChanged(data));
  }

  show(el) {
    const { events } = el;
    this._eventsManager.reset();
    this._eventsManager.eventsList = events;
    this.documentationInput = el.documentation || '';
    this._changeVisibility(this._panel, true);
  }

  _setEventsWhenManagerChanged(events) {
    if (this.element) {
      this.element.events = events;
    }
  }
}
