import History from 'immutable-undo';
import Config from 'config';
import EventEmitter from 'eventemitter3';
import cloneDeep from 'lodash/cloneDeep';
import Diff from 'diff-arrays-of-objects';
import { getProcessElementByID } from '../utils/util';

export default class HistoryManager extends EventEmitter {
  constructor(editor) {
    super();
    this._editor = editor;
    this._history = null;
  }

  init() {
    this._history = History.create({
      maxUndos: 100,
    });
    this._checkUndoSize();
    this._checkRedoSize();
  }

  save() {
    this._history = this._history.push(this._getElementsData());
    // console.log('SAVE::::::', this._history);
    this._checkUndoSize();
    this._checkRedoSize();
  }

  undo() {
    if (this._history.canUndo) {
      const data = this._history.previous;
      this._history = this._history.undo(this._getElementsData());
      this._updateElementsFromHistory(data.elements);
    }
    this._checkUndoSize();
    this._checkRedoSize();
  }

  redo() {
    this._history.redo(this._getElementsData());
    if (this._history.canRedo) {
      const data = this._history.next;
      this._history = this._history.redo(this._getElementsData());
      this._updateElementsFromHistory(data.elements);
    }
    this._checkUndoSize();
    this._checkRedoSize();
  }

  _checkUndoSize() {
    // console.log('UNDO SIZE:', this._history.undos.size);
    if (this._history.undos.size === 0) {
      this.emit('undo', false);
    } else {
      this.emit('undo', true);
    }
  }

  _checkRedoSize() {
    // console.log('REDO SIZE:', this._history.redos.size);
    if (this._history.redos.size === 0) {
      this.emit('redo', false);
    } else {
      this.emit('redo', true);
    }
  }

  _getElementsData() {
    const elements = [];
    this._editor.elements.forEach((element) => {
      elements.push({
        id: element.id,
        type: element.elementType,
        props: cloneDeep(element.properties),
        x: element.x,
        y: element.y,
      });
    });
    return { elements };
  }

  _updateElementsFromHistory(elements) {
    const diffs = Diff(elements, this._getElementsData().elements, 'id', {
      updatedValues: Diff.updatedValues.first,
    });

    // console.log(diffs);

    // create element
    if (diffs.removed) {
      const connectorsArray = [];
      diffs.removed.forEach((element) => {
        if (element.type !== Config.CONNECTOR) {
          this._createEl(element);
        } else {
          connectorsArray.push(element);
        }
      });
      // create connectors after all elements created
      connectorsArray.forEach((connector) => {
        this._createEl(connector);
      });
    }

    // update element [delete, create]
    if (diffs.updated) {
      diffs.updated.forEach((element) => {
        const elToDel = this._editor._viewport.select(
          `#${element.type}_${element.id}`,
        );
        const elToDelCtxMenu = this._editor._paper.select(`#ctx_${element.id}`);
        if (element.type !== Config.WORKFLOW) {
          elToDel.remove();
          elToDelCtxMenu.remove();
        }
        this._removeFromElementsArray(element);
        const newEl = this._createEl(element);
        // update new element reference in connectors
        if (element.type !== Config.CONNECTOR) {
          this._editor._connections.forEach((connector) => {
            if (connector._toEl.id === newEl.id) {
              connector._toEl = newEl;
            }
            if (connector._fromEl.id === newEl.id) {
              connector._fromEl = newEl;
            }
          });
          // update connectors positions
          this._editor.updateElementConnectors(newEl);
        }
      });
    }

    // remove element
    if (diffs.added) {
      diffs.added.forEach((element) => {
        const elToDel = this._editor._viewport.select(
          `#${element.type}_${element.id}`,
        );
        elToDel.remove();
        const elToDelCtxMenu = this._editor._paper.select(`#ctx_${element.id}`);
        elToDelCtxMenu.remove();
        this._removeFromElementsArray(element);
      });
    }
    this.emit('history_done');
  }

  _createEl(element) {
    return this._editor.createElement(
      element.type,
      element.x,
      element.y,
      element.props,
      false,
    );
  }

  _removeFromElementsArray(element) {
    const el = getProcessElementByID(element.id, this._editor.elements);
    // remove element form editors elements array
    for (let i = 0, len = this._editor.elements.length; i < len; i++) {
      if (this._editor.elements[i] === el) {
        this._editor.elements.splice(i, 1);
      }
    }
    // if connector also delete from connectors array
    if (el.elementType === Config.CONNECTOR) {
      for (let i = 0, len = this._editor._connections.length; i < len; i++) {
        if (this._editor._connections[i] === el) {
          this._editor._connections.splice(i, 1);
        }
      }
    }
  }
}
