import Config from 'config';
import key from 'mousetrap';
import { getElementsOfType, getViewPortBBox } from '../../utils/util';

export default class ToolBar {
  constructor(editor) {
    this._editor = editor;
    this._isDraggingElement = false;
    this._toolBarClone = null;

    this._onToolBarElMouseMove = this._onToolBarElMouseMove.bind(this);
    this._onToolBarElMouseUp = this._onToolBarElMouseUp.bind(this);
    this._onToolBarElMouseDown = this._onToolBarElMouseDown.bind(this);

    document.addEventListener(
      'dragstart',
      (event) => {
        this._onToolDragStart(event);
      },
      false,
    );

    document.addEventListener('drop', (event) => {
      this._onToolDrop(event);
    });

    document.addEventListener(
      'dragover',
      (event) => {
        event.preventDefault();
        this._onToolDragOver(event);
      },
      false,
    );

    document.addEventListener('dragover', (event) => {
      // needed for drop event to fire
      event.preventDefault();
    });

    document.getElementById('task_btn').addEventListener(
      'click',
      (event) => {
        this._clickToolbarElement(event, Config.TASK);
      },
      false,
    );

    document.getElementById('task_btn').draggable = true;

    document.getElementById('start_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.START_EVENT);
      },
      false,
    );

    document.getElementById('start_btn').draggable = true;

    document.getElementById('end_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.END_EVENT);
      },
      false,
    );

    document.getElementById('end_btn').draggable = true;

    document.getElementById('intermediate_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.INTERMEDIATE_EVENT);
      },
      false,
    );

    document.getElementById('intermediate_btn').draggable = true;

    document.getElementById('gateway_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.GATEWAY);
      },
      false,
    );

    document.getElementById('gateway_btn').draggable = true;

    document.getElementById('subprocess_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.SUBPROCESS);
      },
      false,
    );

    document.getElementById('subprocess_btn').draggable = true;

    document.getElementById('pool_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.POOL);
      },
      false,
    );

    document.getElementById('pool_btn').draggable = true;

    document.getElementById('group_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.GROUP);
      },
      false,
    );

    document.getElementById('group_btn').draggable = true;

    document.getElementById('data_obj_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.DATA_OBJECT);
      },
      false,
    );

    document.getElementById('data_obj_btn').draggable = true;

    document.getElementById('data_store_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.DATA_STORE);
      },
      false,
    );

    document.getElementById('data_store_btn').draggable = true;

    document.getElementById('data_input_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.DATA_INPUT);
      },
      false,
    );

    document.getElementById('data_input_btn').draggable = true;

    document.getElementById('data_output_btn').addEventListener(
      'click',
      () => {
        this._clickToolbarElement(event, Config.DATA_OUTPUT);
      },
      false,
    );

    document.getElementById('data_output_btn').draggable = true;

    document.getElementById('hand_btn').addEventListener(
      'click',
      (event) => {
        const element = event.currentTarget;
        const connectorBtn = document.querySelector('#connector_btn');
        const lassoBtn = document.querySelector('#lasso_btn');
        lassoBtn.setAttribute('data-state', 'off');
        connectorBtn.setAttribute('data-state', 'off');
        element.setAttribute('data-state', 'on');
        editor.mode = Config.HAND_MODE;
      },
      false,
    );

    document.getElementById('lasso_btn').addEventListener(
      'click',
      (event) => {
        const element = event.currentTarget;
        const handBtn = document.querySelector('#hand_btn');
        const connectorBtn = document.querySelector('#connector_btn');
        handBtn.setAttribute('data-state', 'off');
        connectorBtn.setAttribute('data-state', 'off');
        element.setAttribute('data-state', 'on');
        editor.mode = Config.LASSO_MODE;
      },
      false,
    );

    document.getElementById('connector_btn').addEventListener(
      'click',
      (event) => {
        const element = event.currentTarget;
        const handBtn = document.querySelector('#hand_btn');
        const lassoBtn = document.querySelector('#lasso_btn');
        lassoBtn.setAttribute('data-state', 'off');
        handBtn.setAttribute('data-state', 'off');
        element.setAttribute('data-state', 'on');
        editor.mode = Config.CONNECTOR_MODE;
      },
      false,
    );

    // Zoom Bar
    document.getElementById('zoom_in').addEventListener(
      'click',
      () => {
        editor.zoomIn();
      },
      false,
    );

    document.getElementById('zoom_out').addEventListener(
      'click',
      () => {
        editor.zoomOut();
      },
      false,
    );

    document.getElementById('reset_view').addEventListener(
      'click',
      () => {
        editor.resetView();
      },
      false,
    );
  }

  _clickToolbarElement(event, elementType) {
    if (!this._isDraggingElement) {
      this._isDraggingElement = true;
      event.target.blur();
      // change cloned icon color to blue
      // const style = window.getComputedStyle(document.documentElement);
      // change color before cloning el
      // event.target.style.color = style.getPropertyValue(
      //   '--icon-dragging-color',
      // );
      event.target.style = 'opacity: 0.7';
      this._toolBarClone = document.body.appendChild(
        this._cloneWithStyles(event.target),
      );
      this._toolBarClone.style.position = 'absolute';
      this._toolBarClone.style.left = `${
        event.clientX - event.target.offsetWidth / 2
      }px`;
      this._toolBarClone.style.top = `${
        event.clientY - event.target.offsetHeight / 2
      }px`;
      document.elementType = elementType;
      // restore color back to normal
      // event.target.style.color = style.getPropertyValue('--icons-color');
      event.target.style = 'opacity: 1';
      // set bg color of editor depending if element allowed
      this._giveVisualClueIfDropNotOK(elementType);

      document.addEventListener('mouseup', this._onToolBarElMouseUp);
      document.addEventListener('mousedown', this._onToolBarElMouseDown);
      document.addEventListener('mousemove', this._onToolBarElMouseMove);
      // bind esc key to cancel
      key.bind('esc', (evt) => {
        if (evt.target.tagName === 'BODY' || evt.target.tagName === 'A') {
          this._toolbarDraggingDone();
          evt.preventDefault();
        }
      });
    }
  }

  _onToolDragStart(evt) {
    let type = null;
    evt.target.style = 'opacity: 0.7;';
    switch (evt.target.id) {
      case 'task_btn':
        type = Config.TASK;
        break;
      case 'end_btn':
        type = Config.END_EVENT;
        break;
      case 'start_btn':
        type = Config.START_EVENT;
        break;
      case 'intermediate_btn':
        type = Config.INTERMEDIATE_EVENT;
        break;
      case 'gateway_btn':
        type = Config.GATEWAY;
        break;
      case 'subprocess_btn':
        type = Config.SUBPROCESS;
        break;
      case 'pool_btn':
        type = Config.POOL;
        break;
      case 'group_btn':
        type = Config.GROUP;
        break;
      case 'data_obj_btn':
        type = Config.DATA_OBJECT;
        break;
      case 'data_store_btn':
        type = Config.DATA_STORE;
        break;
      case 'data_input_btn':
        type = Config.DATA_INPUT;
        break;
      case 'data_output_btn':
        type = Config.DATA_OUTPUT;
        break;
      default:
        break;
    }
    this._dataType = type;
    this._dragBtnId = evt.target.id;
  }

  _onToolDragOver() {
    this._giveVisualClueIfDropNotOK(this._dataType);
  }

  _onToolDrop(evt) {
    evt.preventDefault();
    const btn = document.getElementById(this._dragBtnId);
    btn.style = 'opacity: 1;';
    const p = this._editor._viewport.getInverseScreenPoint(
      evt.clientX,
      evt.clientY,
    );
    const pos = this._getElementPosition(p, this._dataType);
    if (
      this._validateCreate(this._dataType)
      && this._isDropZoneOk(
        document.elementFromPoint(evt.clientX, evt.clientY),
        evt.currentTarget.elementType,
      )
    ) {
      this._editor.createElement(
        this._dataType,
        pos.x,
        pos.y,
        null,
        true,
        true,
      );
    }
    this._editor._paper.removeClass('dragging_element');
    this._editor._paper.removeClass('dragging_element_not_allowed');
  }

  _onToolBarElMouseDown() {}

  _onToolBarElMouseMove(event) {
    this._toolBarClone.style.left = `${
      event.clientX - parseInt(this._toolBarClone.style.width, 10) / 2
    }px`;
    this._toolBarClone.style.top = `${
      event.clientY - parseInt(this._toolBarClone.style.height, 10) / 2
    }px`;
  }

  _onToolBarElMouseUp() {
    const p = this._editor._viewport.getInverseScreenPoint(
      event.clientX,
      event.clientY,
    );
    const pos = this._getElementPosition(p, event.currentTarget.elementType);
    this._toolbarDraggingDone();

    if (
      this._validateCreate(event.currentTarget.elementType)
      && this._isDropZoneOk(
        document.elementFromPoint(event.clientX, event.clientY),
        event.currentTarget.elementType,
      )
    ) {
      this._editor.createElement(
        event.currentTarget.elementType,
        pos.x,
        pos.y,
        null,
        true,
        true,
      );
    }
  }

  _toolbarDraggingDone() {
    this._isDraggingElement = false;
    this._toolBarClone.remove();
    document.removeEventListener('mousemove', this._onToolBarElMouseMove);
    document.removeEventListener('mouseup', this._onToolBarElMouseUp);
    document.removeEventListener('mousedown', this._onToolBarElMouseDown);
    key.unbind('esc');
    this._editor._paper.removeClass('dragging_element');
    this._editor._paper.removeClass('dragging_element_not_allowed');
  }

  _giveVisualClueIfDropNotOK(elementType) {
    switch (elementType) {
      case Config.START_EVENT:
        if (
          getElementsOfType(Config.START_EVENT, this._editor.elements).length
          > 0
        ) {
          this._editor._paper.addClass('dragging_element_not_allowed');
        } else {
          this._editor._paper.addClass('dragging_element');
        }
        break;
      case Config.POOL:
        if (getElementsOfType(Config.POOL, this._editor.elements).length > 0) {
          this._editor._paper.addClass('dragging_element_not_allowed');
        } else {
          this._editor._paper.addClass('dragging_element');
        }
        break;
      default:
        this._editor._paper.addClass('dragging_element');
        break;
    }
  }

  _getElementPosition(p, type) {
    let x;
    let y;
    switch (type) {
      case Config.TASK:
      case Config.SUBPROCESS:
        x = p.x - Config.TASK_SIZE.width / 2;
        y = p.y - Config.TASK_SIZE.height / 2;
        break;
      case Config.GATEWAY:
        x = p.x - Config.GATEWAY_SIZE.width / 2;
        y = p.y - Config.GATEWAY_SIZE.height / 2;
        break;
      case Config.START_EVENT:
      case Config.INTERMEDIATE_EVENT:
      case Config.END_EVENT:
        x = p.x - Config.EVENT_SIZE.radius;
        y = p.y - Config.EVENT_SIZE.radius;
        break;
      case Config.DATA_OBJECT:
      case Config.DATA_INPUT:
      case Config.DATA_OUTPUT:
      case Config.DATA_STORE:
        x = p.x - Config.DATA_SIZE.width / 2;
        y = p.y - Config.DATA_SIZE.height / 2;
        break;
      case Config.POOL:
        const vx = getViewPortBBox(this._editor._viewport);
        if (vx.x === Number.POSITIVE_INFINITY) {
          x = 200;
          y = 200;
        } else {
          x = vx.x - 80;
          y = vx.y - 40;
        }
        break;
      case Config.GROUP:
        x = p.x - Config.GROUP_SIZE.width / 2;
        y = p.y - Config.GROUP_SIZE.height / 2;
        break;
      case Config.TEXT:
        x = p.x - Config.TEXT_SIZE.width / 2;
        y = p.y - Config.TEXT_SIZE.height / 2;
        break;
      default:
        break;
    }
    return { x, y };
  }

  _cloneWithStyles(node) {
    const clone = node.cloneNode(true);
    const styles = window.getComputedStyle(node);
    if (styles.cssText !== '') {
      clone.style.cssText = styles.cssText;
    } else {
      const cssText = Object.values(styles).reduce(
        (css, propertyName) => `${css}${propertyName}:${styles.getPropertyValue(propertyName)};`,
      );
      clone.style.cssText = cssText;
    }
    return clone;
  }

  _isDropZoneOk(target, src) {
    if (target.id === 'app') {
      return true;
    }
    if (src === Config.INTERMEDIATE_EVENT && target.closest('.task')) {
      return true;
    }
    return false;
  }

  _validateCreate(type) {
    let canCreateElement = false;
    // validate element can be created
    switch (type) {
      case Config.START_EVENT:
        if (
          getElementsOfType(Config.START_EVENT, this._editor.elements).length
          > 0
        ) {
          canCreateElement = false;
        } else {
          canCreateElement = true;
        }
        break;
      case Config.POOL:
        if (getElementsOfType(Config.POOL, this._editor.elements).length > 0) {
          canCreateElement = false;
        } else {
          canCreateElement = true;
        }
        break;
      default:
        canCreateElement = true;
        break;
    }
    return canCreateElement;
  }
}
