import * as Snap from 'snapsvg-cjs';
import Config from 'config';
import Element from './element';

export default class Subprocess extends Element {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(Config.SUBPROCESS, id, viewport, x, y, properties, isMovable, [
      Config.DELETE,
      Config.TASK,
      Config.SUBPROCESS,
      Config.END_EVENT,
      Config.INTERMEDIATE_EVENT,
      Config.GATEWAY,
      Config.TEXT,
      Config.CONNECTOR,
      Config.OPEN_SUBPROCESS,
    ]);
    this._WIDTH = Config.TASK_SIZE.width;
    this._HEIGHT = Config.TASK_SIZE.height;

    if (!properties) {
      this._properties.activity = {
        id,
        name: 'Default_Process',
      };
      this._properties.name = 'Default_Process';
      if (Config.ENV !== 'Offline') this._properties.activitySetId = 1;
      this._properties.type = 'embedded';
      this._properties.loopType = 'none';
      this._properties.color = this._foregroundColor;
    }
    this.drawElement();
    this.initElement();
  }

  get name() {
    return this._properties.name;
  }

  set name(value) {
    this.emit('element_onpropertychange');
    this._properties.activity.name = value.name;
    this._properties.name = value.name;
    const propToChange = this.type === 'reusable' ? 'subprocessId' : 'activitySetId';
    this._properties[propToChange] = parseInt(value.id, 10);
    this._updateNameLabel();
  }

  get type() {
    return this._properties.type;
  }

  get subprocessId() {
    return this._properties.activitySetId || this._properties.subprocessId;
  }

  set type(value) {
    this.emit('element_onpropertychange');
    const idToChange = this.type === 'reusable' ? 'subprocessId' : 'activitySetId';
    const idValue = this._properties[idToChange];
    this.deletePropertyByName(idToChange);
    if (value === 'reusable') {
      this._properties.subprocessId = idValue;
    } else {
      this._properties.activitySetId = idValue;
    }
    this._properties.type = value;
    this._updateType();
  }

  get loopType() {
    return this._properties.loopType;
  }

  set loopType(value) {
    this.emit('element_onpropertychange');
    this._properties.loopType = value;
    this._updateLoopTypeIcon();
  }

  // only for facilis purpose
  get multiplyCount() {
    return this._properties.multiplyCount;
  }

  set multiplyCount(value) {
    this.emit('element_onpropertychange');
    this._properties.multiplyCount = value;
  }

  get color() {
    return this._properties.color || '#D6F6FF';
  }

  set color(value) {
    this.emit('element_onpropertychange');
    this._properties.color = value;
    this._setColors(this.color, '.subprocess__bg', '.subprocess__group__text', [
      '.subprocess__subprocess_icon',
      '.subprocess__looptypeicon',
    ]);
  }

  get standard() {
    return this._properties.standard;
  }

  set standard(value) {
    this._properties.standard = value;
  }

  get standardDocumentation() {
    return this._properties.standard.conditionDoc;
  }

  get standardCondition() {
    return this._properties.standard
      ? this._properties.standard.condition
      : undefined;
  }

  set standardDocumentation(value) {
    this.emit('element_onpropertychange');
    if (!this.standard) this.standard = {};
    this._properties.standard.conditionDoc = value;
  }

  set standardCondition(value) {
    this.emit('element_onpropertychange');
    if (!this.standard) this.standard = {};
    this._properties.standard.condition = value;
  }

  get reusable() {
    return this._properties.reusable;
  }

  set reusable(value) {
    this._properties.reusable = value;
  }

  get reusableEntity() {
    let entity = {};
    const reusable = this.reusable || {};
    if (reusable.entityId) {
      entity = {
        id: this._properties.reusable.entityId,
        name: this._properties.reusable.entityAttrName,
      };
    }
    return entity;
  }

  get reusableForms() {
    return this.reusable ? this._properties.reusable.forms : undefined;
  }

  set reusableEntity(value) {
    this.emit('element_onpropertychange');
    if (!this.reusable) this._properties.reusable = {};
    if (value === null) {
      delete this._properties.reusable.entityId;
      delete this._properties.reusable.entityAttrName;
    } else {
      this._properties.reusable.entityId = parseInt(value.id, 10);
      this._properties.reusable.entityAttrName = value.name;
    }
  }

  get execution() {
    return this.reusable ? this._properties.reusable.execution : undefined;
  }

  get skipFirst() {
    return this.reusable ? this._properties.reusable.skipFirst : undefined;
  }

  set execution(value) {
    this.emit('element_onpropertychange');
    this._properties.reusable.execution = value;
  }

  set skipFirst(value) {
    this.emit('element_onpropertychange');
    this._properties.reusable.skipFirst = value;
  }

  get multiInstance() {
    return this._properties.multiInstance;
  }

  get multiInstanceType() {
    return this.multiInstance ? this.multiInstance.type : undefined;
  }

  set multiInstanceType(value) {
    this.emit('element_onpropertychange');
    this._properties.multiInstance.type = value;
    if (value === 'attribute' && this.multiInstanceValue !== undefined) {
      delete this._properties.multiInstance.value;
    }
    if (value === 'value' && this.multiInstanceAttribute !== {}) {
      delete this._properties.multiInstance.attribute;
      delete this._properties.multiInstance.attributeId;
    }
  }

  get multiInstanceValue() {
    return this.multiInstance ? this.multiInstance.value : undefined;
  }

  set multiInstanceValue(value) {
    this.emit('element_onpropertychange');
    this._properties.multiInstance.value = value;
  }

  get multiInstanceAttribute() {
    let attribute = {};
    if (this.multiInstance) {
      attribute = {
        id: this.multiInstance.attributeId,
        name: this.multiInstance.attribute,
      };
    }
    return attribute;
  }

  set multiInstanceAttribute(value) {
    this.emit('element_onpropertychange');
    this._properties.multiInstance.attributeId = parseInt(value.id, 10);
    this._properties.multiInstance.attribute = value.name;
  }

  set multiInstance(value) {
    this.emit('element_onpropertychange');
    this._properties.multiInstance = value;
  }

  addReusableForm(form) {
    this.emit('element_onpropertychange');
    if (!this.reusable) this._properties.reusable = {};
    if (!this.reusableForms) this.properties.reusable.forms = [];
    this.reusableForms.push(form);
  }

  deleteReusableForm(index) {
    const form = this.properties.reusable.forms[index];
    if (form) {
      this.emit('element_onpropertychange');
      this.properties.reusable.forms.splice(index, 1);
    }
  }

  editReusableForm(index, value) {
    const form = this._properties.reusable.forms[index];
    if (form) {
      this.emit('element_onpropertychange');
      this._properties.reusable.forms[index] = value;
    }
  }

 /* changeReusableFormPosition(position, form,frmType) {
    let forms = this._properties.reusable.forms.filter( x => x.formType === frmType);
    let formsOther  = this._properties.reusable.forms.filter( x => x.formType !== frmType);
    if (forms[position]) {
      
      forms[position] = form;
      forms[position].order = position;
      
      this._properties.reusable.forms[position] = forms.concat(formsOther);
      //this._properties.reusable.forms[position].order = position;
    }
  }*/

  changeReusableFormPosition(actualPos, oldPos, actual, old, formType){
    let forms = this._properties.reusable.forms.filter( x => x.formType === formType);
    let formsOther  = this._properties.reusable.forms.filter( x => x.formType !== formType);
    
    if (forms[oldPos]  && forms[actualPos]) {
      
      forms[actualPos] = actual;
      forms[actualPos].order = actualPos;

      forms[oldPos] = old;
      forms[oldPos].order = oldPos;
    }

    this._properties.reusable.forms = forms.concat(formsOther);

  }

  cleanReusableForm(frmType) {
    this.emit('element_onpropertychange');
    //this.properties.reusable.forms = [];
    this.properties.reusable.forms = this.properties.reusable.forms.filter( x => x.formType !== frmType);
  }

  drawElement() {
    const svgSubprocessIcon = Snap.parse(
      '<g id="icon"><path d="M56,0H8C3.6,0,0,3.6,0,8v48c0,4.4,3.6,8,8,8h48c4.4,0,8-3.6,8-8V8C64,3.6,60.4,0,56,0z M58.7,56c0,1.5-1.2,2.7-2.7,2.7H8 c-1.5,0-2.7-1.2-2.7-2.7V8c0-1.5,1.2-2.7,2.7-2.7h48c1.5,0,2.7,1.2,2.7,2.7V56z" /><path d="M50.7,29.3h-16v-16c0-1.5-1.2-2.7-2.7-2.7c-1.5,0-2.7,1.2-2.7,2.7v16h-16c-1.5,0-2.7,1.2-2.7,2.7c0,1.5,1.2,2.7,2.7,2.7 h16v16c0,1.5,1.2,2.7,2.7,2.7c1.5,0,2.7-1.2,2.7-2.7v-16h16c1.5,0,2.7-1.2,2.7-2.7C53.3,30.5,52.1,29.3,50.7,29.3z" /></g>',
    );
    const subprocessIcon = svgSubprocessIcon.select('g').attr({
      class: 'subprocess__subprocess_icon',
    });

    subprocessIcon.transform(
      `s0.2, T${Math.floor(this._WIDTH / 2) - 8},${Math.floor(
        this._HEIGHT - 20,
      )}`,
    );

    this._box = this._viewport
      .rect(0, 0, this._WIDTH, this._HEIGHT, 8)
      .attr({ class: 'subprocess__bg', fill: this.color });

    this._label = this.drawForeignObjLabel(
      0,
      0,
      this._WIDTH,
      this._HEIGHT,
      this.name,
      'subprocess__group__text',
      `text_${this.id}`,
    );
    this._label.select('g').attr({
      class: 'subprocess__group',
    });

    const subProcess = this._box.appendTo(this._viewport);
    const bounds = subProcess.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    const loopTypeIconGroup = this._drawLoopTypeIcon();

    this._graphic = this._viewport
      .g(
        selectionBox,
        subProcess,
        this._label,
        subprocessIcon,
        loopTypeIconGroup,
      )
      .mousedown(this.onElementMouseDown)
      .attr({
        class: 'subprocess',
        id: `${Config.SUBPROCESS}_${this._properties.id}`,
      });

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }

    this._updateType();
    this._setColors(this.color, '.subprocess__bg', '.subprocess__group__text', [
      '.subprocess__subprocess_icon',
      '.subprocess__looptypeicon',
    ]);
  }

  _drawLoopTypeIcon() {
    let looptypeIcon;
    switch (this.loopType.toUpperCase()) {
      case 'STANDARD':
        looptypeIcon = Snap.parse(
          '<g id="looptypeicon"><path class="subprocess__looptypeicon" transform="scale(1, -1) translate(0, -100)" d="M529 645c-126 1-247-86-283-207-28-87-12-187 42-260-44 8-89 17-134 26-4-20-8-40-12-59 81-16 162-32 242-47 16 80 32 160 47 240-19 4-39 8-59 11-9-48-19-97-28-145-69 82-68 214 2 295 69 86 200 111 295 57 92-47 143-159 118-259-22-104-124-184-230-182-28 2-42-38-19-53 20-13 45-4 67-3 124 19 229 125 245 250 19 119-46 244-152 300-43 23-92 36-141 36z" /></g>',
        );
        break;
      case 'MULTIINSTANCE':
        looptypeIcon = Snap.parse(
          '<g id="looptypeicon"><path class="subprocess__looptypeicon" transform="scale(1, -1) translate(0, -100)" d="M250 650l0-600 100 0 0 600-100 0z m200 0l0-600 100 0 0 600-100 0z m200 0l0-600 100 0 0 600-100 0z" /></g>',
        );
        break;
      case 'NONE':
        looptypeIcon = Snap.parse('<g id="looptypeicon"></g>');
        break;
      default:
        break;
    }

    const loopTypeIconGroup = looptypeIcon.select('g');
    loopTypeIconGroup.transform(
      `s0.025, T${24},${Math.floor(this._HEIGHT - 8)}`,
    );

    const subprocessLoopTypeIcon = looptypeIcon.select(
      '.subprocess__looptypeicon',
    );
    if (subprocessLoopTypeIcon) {
      subprocessLoopTypeIcon.attr({
        fill: this._foregroundColor,
      });
    }

    return loopTypeIconGroup;
  }

  _updateLoopTypeIcon() {
    // delete old icon
    const oldIcon = this._graphic.select('#looptypeicon');
    oldIcon.remove();
    // append new icon
    const loopTypeIconGroup = this._drawLoopTypeIcon();
    loopTypeIconGroup.appendTo(this._graphic);
  }

  _updateNameLabel() {
    const text = this._graphic.select('.subprocess__group__text');
    text.node.innerText = this.name;
  }

  _updateType() {
    if (this._properties.type.toUpperCase() === 'REUSABLE') {
      this._graphic.addClass('subprocess--reusable');
    } else {
      this._graphic.removeClass('subprocess--reusable');
    }
  }
}
