/* eslint-disable no-unused-expressions */
import * as Snap from 'snapsvg-cjs';
import EventEmitter from 'eventemitter3';
import { isReadable, mostReadable } from '@ctrl/tinycolor';
import throttle from 'lodash/throttle';
import Config from 'config';
import {} from '../../utils/snaptoolkit';
import ContextMenu from '../controls/contextMenu';
import { getClosestMultiple } from '../../utils/util';

export default class Element extends EventEmitter {
  constructor(type, id, viewport, x, y, properties, isMovable, contextMenuItems) {
    super();
    this._elementType = type;
    this._box = null;
    this._label = null;
    this._graphic = null;
    this._dx = x;
    this._dy = y;
    this._tdx = 0;
    this._tdy = 0;
    this._mx = x;
    this._my = y;
    this._width = 0;
    this._height = 0;
    this._viewport = viewport;
    this._hSize = 12;
    this._handles = [];
    this._selectionBox = null;
    this._contextMenuItems = contextMenuItems;
    this._isMovable = isMovable;

    const style = window.getComputedStyle(document.documentElement);
    this._foregroundColor = style.getPropertyValue('--subprocess-fill-color');
    this._stroke_color = style.getPropertyValue('--stroke-color');
    this._CONTRAST_COLORS_SELECTION = ['#fffb00', '#0433ff', '#00fdff', '00f900'];

    if (properties) {
      this._properties = properties;
      if (this._properties.size) {
        this._width = this._properties.size.width;
        this._height = this._properties.size.height;
      }
      // boundary props for undo/redo
    } else {
      this._properties = {
        id,
      };
      if (
        this.elementType !== Config.TEXT
        && this.elementType !== Config.START_EVENT
        && this.elementType !== Config.INTERMEDIATE_EVENT
        && this.elementType !== Config.END_EVENT
      ) this._properties.name = `${this.elementType}`;
    }
    if (!this._properties.boundaryEvents) this._properties.boundaryEvents = [];

    this._selected = false;
    this.SELECTSIZE = Config.ELEMENT_SELECT_SIZE;

    this.onElementMouseDown = this.onElementMouseDown.bind(this);
    this.onElementMoveEnd = this.onElementMoveEnd.bind(this);
    // throttle to help when multiple moving
    this.onElementMove = throttle(this.onElementMove.bind(this), 80);
    this.onElementMoveStart = this.onElementMoveStart.bind(this);
    this.onHandleDrag = this.onHandleDrag.bind(this);
    this.onHandleDragStart = this.onHandleDragStart.bind(this);
    this.onHandleDragEnd = this.onHandleDragEnd.bind(this);
  }

  initElement() {
    // init position
    if (this.x !== undefined && this.y !== undefined) {
      this._graphic.transform(`T${this.x},${this.y}`);
    }

    // init context menu
    this._contextMenu = new ContextMenu(this, this._contextMenuItems);
    this._contextMenu.on('element_on_remove_button_clicked', () => {
      this.emit('element_on_remove_button_clicked');
    });
    this._contextMenu.on('element_on_append', (elementName) => {
      this.emit('element_on_append', elementName);
    });
    this._contextMenu.on('element_on_open_subprocess', () => {
      this.emit('element_on_open_subprocess');
    });
    this._contextMenu.on('element_on_connect_button', (elementName) => {
      this.emit('element_on_connect_button', elementName);
    });
    // selected property for history undo/red
    this.selected = this._properties.selected;
    // hide menu from history when recreated
    this._contextMenu.hide();
  }

  get x() {
    return this._dx;
  }

  set x(value) {
    this._dx = value;
    this._graphic.transform(`t${this._dx},${this._dy}`);
  }

  get y() {
    return this._dy;
  }

  set y(value) {
    this._dy = value;
    this._graphic.transform(`t${this._dx},${this._dy}`);
  }

  get mx() {
    return this._mx;
  }

  get my() {
    return this._my;
  }

  get properties() {
    return this._properties;
  }

  get elementType() {
    return this._elementType;
  }

  get graphic() {
    return this._graphic;
  }

  get box() {
    return this._box;
  }

  get paper() {
    return this._viewport;
  }

  get selected() {
    return this._selected;
  }

  get selectionBox() {
    return this._selectionBox;
  }

  get width() {
    return this.selectionBox.getBBox().width;
  }

  get height() {
    return this.selectionBox.getBBox().height;
  }

  get boundaryEvents() {
    return this._properties.boundaryEvents;
  }

  addBoundaryEvents(value) {
    this._properties.boundaryEvents.push(value);
  }

  removeBoundaryEvents(value) {
    this._properties.boundaryEvents.splice(value, 1);
  }

  set selected(state) {
    this._selected = state;
    this._properties.selected = state;
    if (state) {
      this._graphic.select('#selectionBox').addClass('element--selected');
      if (this.resizable) {
        this.drawResizeHandles();
      }
      this._onSelected();
    } else {
      this._graphic.select('#selectionBox').removeClass('element--selected');
      if (this.resizable) {
        this.removeResizeHandles();
      }
      this._onDeselected();
    }
  }

  get id() {
    return this._properties.id;
  }

  set id(value) {
    this._properties.id = value;
    if (this._properties.activity) {
      this._properties.activity.id = value;
    }
  }

  set deleteProperty(value) {
    delete this._properties[value];
  }

  addProperty(object, name, value) {
    object[name] = value;
  }

  _onSelected() {
    this.emit('element_selected');
  }

  _onDeselected() {
    this.emit('element_unselected');
  }

  drawSelectionBox(bounds, className = 'selectionBox') {
    this._selectionBox = this._viewport
      .rect(
        Math.round(bounds.x - this.SELECTSIZE / 2),
        Math.round(bounds.y - this.SELECTSIZE / 2),
        Math.round(bounds.width + this.SELECTSIZE),
        Math.round(bounds.height + this.SELECTSIZE),
      )
      .attr({ class: className, id: 'selectionBox' });
    return this._selectionBox;
  }

  drawResizeHandles() {
    const bounds = this._selectionBox.getBBox();
    this._handles = [
      this._viewport
        .rect(bounds.x2 - this._hSize / 2, bounds.y2 - this._hSize / 2, this._hSize, this._hSize)
        .attr({ class: 'resize__handle--se', id: 'resize__handle' }),
    ];

    const handlesGroup = this._viewport
      .g(this._handles[0])
      .drag(this.onHandleDrag, this.onHandleDragStart, this.onHandleDragEnd)
      .attr({ class: 'resize', id: 'resize_group' });
    this._graphic.append(handlesGroup);
  }

  removeResizeHandles() {
    if (this._graphic.select('#resize_group')) {
      this._graphic.select('#resize_group').remove();
    }
  }

  onHandleDragStart() {
    // stop dragging object while resizing
    this._graphic.undrag();
    // reposition handle
    this.removeResizeHandles();
    this.drawResizeHandles();
    this.emit('element_resize_start');
  }

  onHandleDrag(dx, dy, x, y, e) {
    const selBox = this._selectionBox;
    const [se] = this._handles;

    const snapInvMatrix = selBox.transform().diffMatrix.invert();
    snapInvMatrix.e = 0;
    snapInvMatrix.f = 0;

    const tdx = snapInvMatrix.x(dx, dy) + this.SELECTSIZE / 2;
    const tdy = snapInvMatrix.y(dx, dy) + this.SELECTSIZE / 2;

    const ep = this._viewport.getMousePoint(e, 0, 0);

    const offX = Math.round(ep.x - this._graphic.matrix.e);
    const offY = Math.round(ep.y - this._graphic.matrix.f);

    if (offX > 0 && offY > 0) {
      se.transform(`t${[tdx, tdy]}`);
      this.emit('element_resizing', offX, offY);
    }
  }

  onHandleDragEnd() {
    this.emit('element_resize_end');
    this._properties.size = { width: this._width, height: this._height };
    // restore dragging
    this._graphic.drag(this.onElementMove, this.onElementMoveStart, this.onElementMoveEnd);
  }

  toFront() {
    this._graphic.appendTo(this._viewport);
  }

  toBack() {
    // insert after grid
    const grid = this._viewport.select('.grid_bg');
    this._graphic.insertAfter(grid);
  }

  snap(isSnapOn, snapSize) {
    this._isSnapOn = isSnapOn;
    this._snapSize = snapSize;
    if (
      isSnapOn
      && this.elementType !== Config.POOL
      && this.elementType !== Config.LANE
      && this.elementType !== Config.CONNECTOR
    ) {
      // reposition element in closest grid square
      this.x = getClosestMultiple(this.x, snapSize) + this.SELECTSIZE / 2;
      this.y = getClosestMultiple(this.y, snapSize) + this.SELECTSIZE / 2;
      // emit so connectors can update
      this.emit('element_snap_to_grid');
    }
  }

  onElementMoveStart(x, y, event) {
    this.toFront();
    this._graphic.addClass('draggable');
    this._graphic.data('ot', this._graphic.transform().local);
    this.emit('element_move_start', event);
  }

  onElementMove(dx, dy, x, y, event) {
    const snapInvMatrix = this._graphic.transform().diffMatrix.invert();
    snapInvMatrix.e = 0;
    snapInvMatrix.f = 0;

    if (!this._isSnapOn) {
      this._tdx = Math.round(snapInvMatrix.x(dx, dy));
      this._tdy = Math.round(snapInvMatrix.y(dx, dy));
    } else {
      this._tdx = Snap.snapTo(this._snapSize, Math.round(snapInvMatrix.x(dx, dy)), 25);
      this._tdy = Snap.snapTo(this._snapSize, Math.round(snapInvMatrix.y(dx, dy)), 25);
    }

    this._graphic.transform(`t${[this._tdx, this._tdy]}${this._graphic.data('ot')}`);

    this._mx = this._tdx;
    this._my = this._tdy;
    this.emit('element_moving', event);
  }

  onElementMoveEnd(event) {
    this._graphic.removeClass('draggable');
    const newx = this._graphic.matrix.e;
    const newy = this._graphic.matrix.f;
    if (this._dx !== newx || this._dy !== newy) {
      this.emit('element_move_end', event);
      this._dx = newx;
      this._dy = newy;
    } else {
      this.emit('element_no_move_end');
    }
  }

  onElementMouseDown(e) {
    this.toFront();
    this.emit('element_mouse_down', e);
  }

  moveElement(x, y) {
    this._graphic.transform(`t${[x, y]}`);
  }

  moveElementEnd(x, y) {
    this._dx = x;
    this._dy = y;
  }

  deleteEl(useAnime = true) {
    this._contextMenu.delete();
    if (
      useAnime
      && this.elementType !== Config.CONNECTOR
      && this.elementType !== Config.POOL
      && this.elementType !== Config.LANE
    ) {
      const keyframes = [
        {
          transform: `scale(0.8) translate(${this._dx}px, ${this._dy}px)`,
          easing: 'ease-in',
          transformOrigin: `${this._graphic.getBBox().cx}px ${this._graphic.getBBox().cy}px`,
        },
        {
          transform: `scale(1.4) translate(${this._dx}px, ${this._dy}px)`,
          easing: 'cubic-bezier(.75,-0.5,0,1.75)',
          transformOrigin: `${this._graphic.getBBox().cx}px ${this._graphic.getBBox().cy}px`,
        },
      ];
      const anime = this._graphic.node.animate(keyframes, {
        duration: 200,
        delay: 100,
      });
      anime.onfinish = () => {
        this._graphic.remove();
        this.emit('element_deleted');
      };
    } else {
      this._graphic.remove();
      this.emit('element_deleted');
    }
  }

  delete() {
    // to delete without emitting event
    this._contextMenu.delete();
    this._graphic.remove();
  }

  _updateNameLabel(label, bg, text) {
    // set text value
    label.attr('text', text);
    // reposition label
    const bounds = bg.getBBox();
    label.attr('x', Math.round(bounds.x + bounds.width / 2));
  }

  drawLabel(value, classname, offsetY, bounds) {
    const label = this._viewport.text(0, offsetY, value).attr({
      class: classname,
      dominantBaseline: 'middle',
      textAnchor: 'middle',
    });
    label.attr('x', Math.round(bounds.x + bounds.width / 2));
    return label;
  }

  drawForeignObjLabel(x, y, width, height, title, classname, id) {
    const fobjectSVG = `<g><foreignObject 
      x="${x}"
      y="${y}"
      width="${width}" 
      height="${height}"
      id="${id}">
    <p class="${classname}">${title}</p></foreignObject></g>`;
    return Snap.parse(fobjectSVG);
  }

  updateTextLabel(className, value) {
    const text = this._graphic.select(`.${className}`);
    text.node.innerText = value;
  }

  _setColors(color, bg, text, iconsArray) {
    if (color !== '#ffffff') {
      if (!isReadable(color, this._stroke_color, { level: 'AA', size: 'small' })) {
        this._foregroundColor = mostReadable(color, this._CONTRAST_COLORS_SELECTION, {
          includeFallbackColors: true,
          level: 'AA',
          size: 'small',
        }).toHexString();
      } else {
        this._foregroundColor = this._stroke_color;
      }
    } else {
      this._foregroundColor = this._stroke_color;
    }
    // bg
    this.graphic.select(bg).attr({
      fill: color,
      stroke: this._foregroundColor,
    });
    // text
    const taskText = this.graphic.select(text);
    if (taskText) {
      taskText.attr({
        color: this._foregroundColor,
      });
    }
    // icons
    for (let i = 0; i < iconsArray.length; i++) {
      const icon = this.graphic.select(iconsArray[i]);
      if (icon) {
        icon.attr({
          fill: this._foregroundColor,
          stroke: this._foregroundColor,
        });
      }
    }
  }

  set documentation(value) {
    this.emit('element_onpropertychange');
    if (this.documentation) {
      this._properties.documentation.text = value.text;
    } else {
      this._properties.documentation = {
        id: value.id,
        text: value.text,
      };
    }
  }

  get documentation() {
    const doc = this._properties.documentation || {};
    return doc.text || '';
  }

  deletePropertyByName(name) {
    if (this._properties[name]) {
      delete this._properties[name];
    }
  }
}
