import Config from 'config';
import Element from './element';

export default class TextAnnotation extends Element {
  constructor(id, x, y, viewport, properties, isMovable) {
    super(Config.TEXT, id, viewport, x, y, properties, isMovable, [
      Config.DELETE,
    ]);

    this.resizable = true;

    if (!properties) {
      this._properties.size = {
        width: Config.TEXT_SIZE.width,
        height: Config.TEXT_SIZE.height,
      };
      this._properties.text = '';
    }

    this.drawElement();
    this.initElement();
  }

  get text() {
    return this._properties.text;
  }

  set text(value) {
    this.emit('element_onpropertychange');
    this._properties.text = value;
    this.updateTextLabel('annotation__box__text', this._properties.text);
  }

  drawElement() {
    const annotationBox = this._viewport
      .rect(0, 0, this._properties.size.width, this._properties.size.height)
      .attr({ class: 'annotation__bg' });

    const label = this.drawForeignObjLabel(
      0,
      0,
      this._properties.size.width,
      this._properties.size.height,
      this._properties.text,
      'annotation__box__text',
      'text_box',
    );

    const annotationText = annotationBox.appendTo(this._viewport);
    const bounds = annotationText.getBBox();
    const selectionBox = this.drawSelectionBox(bounds);

    const c = this._drawTextBorder(bounds);

    this._graphic = this._viewport
      .g(selectionBox, annotationText, label, c)
      .attr({
        class: 'annotation',
        id: `${Config.TEXT}_${this._properties.id}`,
      })
      .mousedown(this.onElementMouseDown);

    if (this._isMovable) {
      this._graphic.drag(
        this.onElementMove,
        this.onElementMoveStart,
        this.onElementMoveEnd,
      );
    }
  }

  onResizingStart() {
    this._contextMenu.hide();
  }

  onResizing(width, height) {
    this._width = width;
    this._height = height;
    const elBg = this._graphic.select('.annotation__bg');
    const elText = this._graphic.select('#text_box');

    const annotationBorder = this._graphic.select('.annotation__border');
    if (annotationBorder) annotationBorder.remove();

    this._selectionBox.attr({
      width: this._width + this.SELECTSIZE,
      height: this._height + this.SELECTSIZE,
    });

    elBg.attr({
      width: this._width,
      height: this._height,
    });

    elText.attr({
      width: this._width,
      height: this._height,
    });

    const bounds = elBg.getBBox();
    this.graphic.add(this._drawTextBorder(bounds));
  }

  onResizeEnd() {
    // on element resize
  }

  _drawTextBorder(bounds) {
    const depth = 10;
    const c = this._viewport
      .polyline([
        bounds.x + depth,
        bounds.y,
        bounds.x,
        bounds.y,
        bounds.x,
        bounds.y,
        bounds.x,
        bounds.y2,
        bounds.x,
        bounds.y2,
        bounds.x + depth,
        bounds.y2,
      ])
      .attr({ class: 'annotation__border' });
    return c;
  }
}
