import Config from 'config';
import cloneDeep from 'lodash/cloneDeep';
import { getSelected } from '../utils/util';

export default class ClipboardManager {
  constructor(editor, history) {
    this._editor = editor;
    this._historyManager = history;
    this._clipboard = [];
    this._clipboardElements = [];
    this._pasteOffset = 0;
    this._PASTE_POSITION_OFFSET = 30;
  }

  copy() {
    const selected = getSelected(this._editor.elements);
    this._clipboardElements = selected.filter((obj) => {
      return (
        obj.elementType !== Config.CONNECTOR
        && obj.elementType !== Config.START_EVENT
        && obj.elementType !== Config.POOL
        && obj.elementType !== Config.LANE
        && !obj.properties.isAttached
      );
    });
    this._clipboard = cloneDeep(
      this._clipboardElements.map((el) => el.properties),
    );
    this._pasteOffset = 1;
  }

  paste() {
    // deselect previous elements if anything on clipboard
    if (this._clipboard.length > 0) {
      this._editor.unSelectAllElements();
      // paste
      this._historyManager.save();
      for (let i = 0, len = this._clipboard.length; i < len; i++) {
        const el = this._clipboardElements[i];
        const elProps = this._clipboard[i];
        // guess next id, it gets assigned in createElement method
        const nextID = this._editor.idIndex + 1;
        elProps.id = nextID;
        if (elProps.activity) {
          elProps.activity.id = nextID;
        }
        // does not allow copy/paste of events boundary
        elProps.boundaryEvents = [];
        // heres the magic...!?!
        const p = cloneDeep(elProps);
        const element = this._editor.createElement(
          el.elementType,
          el.x + this._PASTE_POSITION_OFFSET * this._pasteOffset,
          el.y + this._PASTE_POSITION_OFFSET * this._pasteOffset,
          p,
          false,
          false,
        );
        element.selected = true;
      }
      this._pasteOffset += 2;
    }
    return true;
  }
}
