import EventEmitter from 'eventemitter3';
import ExpanderComponent from '../../../components/ExpanderComponent';
import ListComponent from '../../../components/ListComponent';
import ConditionComponent from '../../../components/ConditionComponent';
import SearchComponent from '../../../components/SearchComponent';
import DateComponent from '../../../components/DateComponent';
import TabComponent from '../../../components/TabComponent';
import DropdownListComponent from '../../../components/DropdownListComponent';
import ToastMessage from '../../../components/ToastMessage';

export default class Manager extends EventEmitter {
  constructor(id, type, panel) {
    super();
    this._panel = panel;
    this._id = id;
    this._inputs = [];
    this._include(type, 'manager');
    if (!this.message) {
      this.message = new ToastMessage();
    }
  }

  get id() {
    return this._id;
  }

  get panel() {
    return this._panel;
  }

  get inputs() {
    return this._inputs;
  }

  set inputs(value) {
    this._inputs = value;
  }

  _cleanElements() {
    this.inputs.forEach((input) => {
      input.value = '';
    });
  }

  _getIndex(data, item, nameId = 'id') {
    const index = data.findIndex((value) => {
      return parseInt(item[nameId], 10) === parseInt(value[nameId], 10);
    });
    return index;
  }

  _include(type, kind) {
    const selector = `${type}_${kind}`;
    const template = document.getElementById(selector);
    const container = document.importNode(template.content, true);
    this.panel.appendChild(container);
  }

  _changeElementIDs(elements) {
    elements.forEach((element) => {
      element.setAttribute('id', `${this.id}_${element.id}`);
    });
  }

  _includeComponent(type, container, position, options) {
    this._include(type, 'template');
    const wrap = this.panel.querySelector(`#wrapper_${type}`);
    let component;
    switch (position) {
      case 'afterend':
      case 'beforebegin': {
        container.insertAdjacentElement(position, wrap);
        break;
      }
      default: {
        container.appendChild(wrap);
        break;
      }
    }
    const { nameId, label, placeholder } = options;
    switch (type) {
      case 'expander': {
        const { expandId } = options;
        component = new ExpanderComponent(nameId, this.panel, expandId, label);
        const expandZone = this.panel.querySelector(`#${expandId}`);
        wrap.insertAdjacentElement('afterend', expandZone);
        break;
      }
      case 'search': {
        const {
          add, responseTime, url, hasLabel,
        } = options;
        component = new SearchComponent(
          nameId,
          this.panel,
          label,
          add,
          responseTime,
          url,
          hasLabel,
        );
        break;
      }
      case 'list': {
        const { arrayList, add } = options;
        component = new ListComponent(
          nameId,
          this.panel,
          label,
          arrayList,
          add,
        );
        break;
      }
      case 'date': {
        component = new DateComponent(nameId, this.panel, label);
        break;
      }
      case 'tabpanel': {
        const { tabs } = options;
        component = new TabComponent(nameId, this.panel, tabs);
        break;
      }
      case 'dropdown': {
        const {
          add, url, hasLabel, canReload,
        } = options;
        component = new DropdownListComponent(
          nameId,
          this.panel,
          label,
          add,
          url,
          hasLabel,
          canReload,
          placeholder,
        );
        break;
      }
      default: {
        component = new ConditionComponent(
          nameId,
          this.panel,
          label,
          placeholder,
        );
        break;
      }
    }
    return component;
  }

  _setTimeoutOnPropertyChanged(
    executeFn,
    el,
    evt,
    waitTime = 1000,
    timeout = undefined,
  ) {
    el.addEventListener(evt, () => {
      clearTimeout(timeout);
      timeout = setTimeout(() => {
        executeFn();
        clearTimeout(timeout);
      }, waitTime);
    });
  }

  _getMaxId(data, nameId = 'id') {
    let max = 0;
    data.forEach((value) => {
      max = parseInt(value[nameId], 10) > max ? parseInt(value[nameId], 10) : max;
    });
    return max;
  }

  emitChange(data) {
    this.emit('change', data);
  }

  disabledAll(value) {
    this.inputs.forEach((input) => {
      input.disabled = value;
    });
  }
}
